import React from 'react';
import NumberFormat from 'react-number-format';
export default function CommissionComponent(props) {
    const {itemCommission}=props;
    return (
        <div>
            <div className="card-body row">
                <div className="col-12 padding-space">
                    <fieldset>
                        <legend> Commissions basis </legend>
                        <div className="row">
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="Commissionsradio1" name="commissionCustomRadio" className="custom-control-input" checked={itemCommission.commissionMode===0? true:false} disabled />
                                    <label className="custom-control-label" htmlFor="Commissionsradio1">Do not allow commission for this item</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="Commissionsradio2" name="commissionCustomRadio" className="custom-control-input" checked={itemCommission.commissionMode===1? true:false} disabled />
                                    <label className="custom-control-label" htmlFor="Commissionsradio2">Base this item's commission on the schedule defined for sales rep</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="Commissionsradio3" name="commissionCustomRadio" className="custom-control-input" checked={itemCommission.commissionMode===2? true:false} disabled />
                                    <label className="custom-control-label" htmlFor="Commissionsradio3">Base this item's commission on schedule defined below</label>
                                </div>
                            </div>

                        </div>
                    </fieldset>
                </div>
                <div className="col-12 mt-3 padding-space">
                    <fieldset>
                        <legend> Commission schedule </legend>
                        <div className="row">
                            <div className="col-12">
                                <div className="floating-label my-2">
                                    <NumberFormat fixedDecimalScale={true} value={itemCommission.commissionAmount} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} disabled />
                                    <span className="highlight"></span>
                                    <label>Fixed amount</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="floating-label my-2">
                                    <NumberFormat fixedDecimalScale={true} value={itemCommission.commissionPercentSale} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" suffix={'%'} disabled />
                                    <span className="highlight"></span>
                                    <label>Percent of sale(%)</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="floating-label my-2">
                                    <NumberFormat fixedDecimalScale={true} value={itemCommission.commissionPercentProfit} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" suffix={'%'} disabled />
                                    <span className="highlight"></span>
                                    <label>Percent of profit(%)</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="floating-label my-2">
                                    <NumberFormat fixedDecimalScale={true} value={itemCommission.commissionMaximum} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} disabled />
                                    <span className="highlight"></span>
                                    <label>Maximum commission</label>
                                </div>
                            </div>
                        </div>
                    </fieldset>
                </div>

            </div>
        </div>
    )
}