import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {viewPassword} from '../../../Custom';
export default function CentralUserComponent(props) {
	const {isLoading,centralUser,editPassword,isPassword,inputChangeHandler,onChangePass}=props;
	return (
		<>
		<div>
		<Helmet>
			<title>Central User | Modus Go!</title>
		</Helmet>
		{isLoading? <Loading />:""}
		<div className="container-fluid bg-white header-shadow mt-4">
				<div className="row my-3">
					<div className="col-12 p-0">
						<div className="table-responsive">
							<table id="tblCentralUser" className="table w-100" >
								<thead>
									<tr>
									<th>User Name</th>
									<th>Password</th>
									<th>Action</th>
									</tr>
								</thead>
								<tbody>

								{centralUser.map((data) =>
										<tr>
											<td hidden ><div >{data.id}</div></td>
											<td ><div >{data.userName}</div></td>
											<td > 
												 <div className="db-password">
													<input  type="password" className="floating-input floating-input-label db-input"
														id={`password`+data.id}
														name="password"
														defaultValue={data.password}
														disabled={isPassword==data.id?false:true}
														onChange={(e=>onChangePass(e))}
														onBlur={(e) => inputChangeHandler(e,data.id)}
													/>
													<span className="eye-icon"><i id="pass-status" className="zmdi zmdi-eye-off zmdi-hc-fw" onClick={()=>viewPassword(`password`+data.id)}></i></span>
												</div>
											</td>
											
											<td className="action-flex">
											<button type="button"  tabIndex="0" onClick={()=>editPassword(data.id)} className="round-icon-wrap border-blue"><i className="zmdi zmdi-edit blue-icon" ></i></button>
										</td>
										</tr> )}
								</tbody>
							</table>
						</div>

					</div>
				</div>
			</div>
		</div>	

	</>
	)
}