import React from 'react';
import APIColumnMappingComponent from '../component/APIColumnMappingComponent';
import { getAPINameList,insertUpdateAPIColumnMapping,getAPIColumnMappingList,deleteAPIColumnMapping,setSuccessDialogue,clearMessage
  ,getDBColumnNameList,GetTenderList,UpdateTender
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;
function handleCMGridDeleteIcon(classdata) {
  $("#tblColumnMapping").on('click','.ColumnMappingDelete',function() {
    var dataString=$(this).attr('data');
    classdata.deleteMapping(dataString);
  });
}
function handleCMGridEditIcon(classdata) {
  $("#tblColumnMapping").on('click','.ColumnMappingEdit',function() {
    var dataString=$(this).attr('data');
    classdata.showEditMapping(dataString);
  });
}
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class APIColumnMappingContainer extends React.Component {
  state={
    isShowMapping:false,
    formFields: {
      id: 0,
      apiName:''
    },
    CreateFormFields: {
    },
    updateColumnMappingParam:{
      id: 0,
      apiColumnName: '',
      dbColumnName: '',
      apiNameMasterID:0
    },
    disabled:false,
    isDisplayAddMapping:false,
    tenderDetails:{
      tenderId:0
    },
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    handleCMGridEditIcon(this);
    handleCMGridDeleteIcon(this);
    window.addEventListener('scroll',this.handleScroll);
    this.props.getAPINameList([],true);
    this.props.getDBColumnNameList([],true);
    this.props.GetTenderList(true);
    this.props.getAPIColumnMappingList({
      apiNameMasterID: -1
    },false);
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblColumnMapping').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
    //#region check user role validation 
    checkUserRoleValidation=() => {
      try {
        const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
        if(userDetails.role!==process.env.REACT_APP_UserRole_Admin) {
          this.props.history.push("/NotFound");
        }
      } catch(error) {
        console.log(error);
      }
    }
    //#endregion check user role validation
    showAddMapping=() => {
      if(this.state.formFields.id==0)
      {
        swal({
          title: "Error",
          text: types.REQUIRED_APINAME_MESSAGE,
          icon: "error",
          button: "Ok",
        })
        
      }
      else
      {
        let updateColumnMappingParam={
          id: 0,
          apiColumnName: '',
          dbColumnName: '',
        };
        this.setState({updateColumnMappingParam,isShowMapping: true})

      }

    }
    hideAddMapping=() => {
      let formFields={...this.state.formFields};
      this.setState({isShowMapping: false,formFields})
      this.props.getAPIColumnMappingList({
        apiNameMasterID: -1
      },false);
    }
    inputChangeHandlerAPIName=(e) => {
      let  isDisplayAddMapping=true;
      let formFields={...this.state.formFields};
      formFields["id"]=e.target.value;
      formFields["apiName"] = e.target.options[e.target.selectedIndex].text;

      let tenderDetails=this.state.tenderDetails;

      let apiNameList=this.props.apiNameList;
      let TenderId=apiNameList.filter(x=>x.apiName==e.target.options[e.target.selectedIndex].text)
      tenderDetails["tenderId"]=TenderId[0].tenderId;
      if(e.target.value=="-1")
      {
        isDisplayAddMapping=false
      }
     
      this.setState({
        formFields,isDisplayAddMapping:isDisplayAddMapping,tenderDetails
      });
     
      this.props.getAPIColumnMappingList({
        apiNameMasterID: parseInt(e.target.value)
      },true);
    }
    //#region all inpute onchange method   
    inputChangeHandler= (e) => {
      try {
        var value = e.target.value;
        let updateColumnMappingParam = { ...this.state.updateColumnMappingParam };
        updateColumnMappingParam[e.target.name] = value;
     
        this.setState({
          updateColumnMappingParam
        });
      } catch (error) {
        console.log(error);

      }
    }
    //#endregion all inpute onchange method

    inputChangeHandlerTender=(e) => {
     
      let tenderDetails={...this.state.tenderDetails};
     
      tenderDetails["tenderId"] = parseInt(e.target.value);
     
      this.setState({
        tenderDetails
      });
     
    //  this.props.getAPIColumnMappingList({
     //   tenderDetails:tenderDetails
     // },true);
    }
    saveTender = () => {
      if(parseInt(this.state.formFields.id)==0)
      {
        swal("","Please Select API Name","error")
      }
      else
      {
        let tenderParam={
          apiId:parseInt(this.state.formFields.id),
          tenderId:this.state.tenderDetails.tenderId
        }
        this.props.UpdateTender(tenderParam)
      }

    }
    createAPIColumnMapping = () => {
      if(this.state.updateColumnMappingParam.apiColumnName=="")
      {
        swal({
          title: "Error",
          text: "Please Enter API Column Name",
          icon: "error",
          button: "Ok",
        })
      }
      else if(this.state.updateColumnMappingParam.dbColumnName=="")
      {
        swal({
          title: "Error",
          text: "Please Enter DB Column Name",
          icon: "error",
          button: "Ok",
        })
      }
      else
      {
        const data = {
          id:this.state.updateColumnMappingParam.id,
          apiColumnName: this.state.updateColumnMappingParam.apiColumnName,
          dbColumnName: this.state.updateColumnMappingParam.dbColumnName,
          APINameMasterID:this.state.updateColumnMappingParam.id==0?parseInt(this.state.formFields.id):this.state.updateColumnMappingParam.apiNameMasterID,
          createdBy:parseInt(userDetails.userId),
          IsFixedEntry:false,
          isColumnMapping:true
        }
        let dataParam={
          apiNameMasterID:this.state.formFields.id
        }
        if(data.id==0){
          let updateColumnMappingParam=this.state.updateColumnMappingParam;
          updateColumnMappingParam["apiColumnName"]="";
          updateColumnMappingParam["dbColumnName"]="0";
          this.setState({updateColumnMappingParam:updateColumnMappingParam})
        }
        this.props.insertUpdateAPIColumnMapping(data,dataParam);
      }


    
    }

    showEditMapping=(id) => {
      var id=parseInt(id);
      var elementsIndex=this.props.apiColumnMappingList.findIndex(element => element.id===id);
      let updateColumnMappingParam={
        id: id,
        apiColumnName: this.props.apiColumnMappingList[elementsIndex].apiColumnName,
        dbColumnName: this.props.apiColumnMappingList[elementsIndex].dbColumnName,
        apiNameMasterID:this.props.apiColumnMappingList[elementsIndex].apiNameMasterID,
        isFixedEntry:this.props.apiColumnMappingList[elementsIndex].isFixedEntry
      };
      updateColumnMappingParam["apiColumnName"]=this.props.apiColumnMappingList[elementsIndex].apiColumnName;
      let formFields={...this.state.formFields};
      formFields["apiName"] = this.props.apiColumnMappingList[elementsIndex].apiNameMaster;
      this.setState({updateColumnMappingParam,formFields,isShowMapping: true})
    }

    deleteMapping=(id) => {
      try {
        var apiColumnMid=parseInt(id);
        swal({
          title: types.DELETE_TITLE,
          text: types.DELETE_MESSAGE,
          icon: "warning",
          buttons: {
            confirm: true,
            cancel: true
          },
          dangerMode: true,
        })
          .then((willDelete) => {
            if(willDelete) {


            
              var elementsIndex=this.props.apiColumnMappingList.findIndex(element => element.id===apiColumnMid);
              let deleteColumnMappingParam={
                apiColumnMappingId: apiColumnMid,
              };
              let data={
                apiNameMasterID:this.state.formFields.id
              }
              this.props.deleteAPIColumnMapping(deleteColumnMappingParam,data);

              $.each($.fn.dataTable.tables(true),function() {

                if($(this).DataTable().row('.parent').length) {
                  var parentLength=document.getElementsByClassName('parent').length;
                  for(var i=0;i<parentLength;i++) {
                    $('td.details-control',$(this).DataTable().row('.parent').node()).click();
                  }
  
                }
              });
            }
          });
  
      } catch(error) {
        console.log(error);
      }
    }
  render() {
    // const { searchData } = this.props;   
    const { isSuccessDialogue, updatestatus, updatemessage } = this.state;
    if (isSuccessDialogue) {
      this.props.setSuccessDialogue(false, updatestatus, updatemessage);
    }

    return (
      <APIColumnMappingComponent
       {...this.props}
       showAddMapping={this.showAddMapping}
       hideAddMapping={this.hideAddMapping}
       isShowMapping={this.state.isShowMapping}
       inputChangeHandlerAPIName={this.inputChangeHandlerAPIName}
       formFields={this.state.formFields}
       CreateFormFields={this.state.CreateFormFields}
       createAPIColumnMapping={this.createAPIColumnMapping}
       inputChangeHandler={this.inputChangeHandler}
       updateColumnMappingParam={this.state.updateColumnMappingParam}
       disabled={this.state.disabled}
       isDisplayAddMapping={this.state.isDisplayAddMapping}
       inputChangeHandlerTender={this.inputChangeHandlerTender}
       saveTender={this.saveTender}
       tenderDetails={this.state.tenderDetails}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getAPINameList: (data,isLoading) => dispatch(getAPINameList(data,isLoading)),
    insertUpdateAPIColumnMapping: (data,dataParam) => dispatch(insertUpdateAPIColumnMapping(data,dataParam)),
    getAPIColumnMappingList: (data,isLoading) => dispatch(getAPIColumnMappingList(data,isLoading)),
    deleteAPIColumnMapping: (deleteColumnMappingParam,data) => dispatch(deleteAPIColumnMapping(deleteColumnMappingParam,data)),
    setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    clearMessage: () => dispatch(clearMessage()),
    getDBColumnNameList: (isLoading) => dispatch(getDBColumnNameList(isLoading)),
    GetTenderList: (isLoading) => dispatch(GetTenderList(isLoading)),
    UpdateTender:(data)=>dispatch(UpdateTender(data))
  };
};

const mapStateToProps=(state) => {

  const {apiNameList,apiColumnMappingList,updatestatus, updatemessage, isSuccessDialogue,dbColumnNameList,isLoading,tenderList}=state.APIColumnMappingContainerReducer;
  return {
    apiNameList: apiNameList,
    apiColumnMappingList:apiColumnMappingList,
    updatestatus, updatemessage, isSuccessDialogue,dbColumnNameList,
    isLoading,tenderList
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(APIColumnMappingContainer);