import React from 'react';
import NoneScannedItemComponent from '../component/NoneScannedItemComponent';
import {
  getInventoryItemList, setInventoryItemListParam,
  getInventoryDetail,
  clearMessage, getSearchList, setSearchRequestParameter,
  setSearchValue, setSearchData,
  setSearchTerm, getDepartments, getCategories
} from '../state/actions/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import { history } from '../../../history'
import { encryptURL, decryptURL } from '../../../Custom'
import { withRouter } from 'react-router-dom';
window.$ = $;
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
function debounce(func, wait, immediate) {
  var timeout;

  return function executedFunction() {
    var context = this;
    var args = arguments;

    var later = function () {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };

    var callNow = immediate && !timeout;

    clearTimeout(timeout);

    timeout = setTimeout(later, wait);

    if (callNow) func.apply(context, args);
  };
};

class NoneScannedItemContainer extends React.Component {

  state = {
    isRender: true,
    isCategoryDisabled: true,
    isItemCode:true,
    isQuantity:true,
    isDescription:true,
    isDepartment:true,
    isCategory:true,
    filterColumn:
    {
      isSetItemCode:true,
      isSetQuantity:true,
      isSetDescription:true,
      isSetDepartment:true,
      isSetCategory:true,
    }
  }
  componentDidMount() {
    this.props.getDepartments();
    this.props.setSearchValue("");
    this.props.setSearchData([]);
    var searchRequestParameter = { ...this.props.searchRequestParameter };
    searchRequestParameter["searchText"] = "";
    searchRequestParameter["departmentId"] = 0;
    searchRequestParameter["categoryId"] = 0;
    this.props.setSearchRequestParameter(searchRequestParameter);
    this.props.getCategories(0);
    let inventoryItemListParam = { ...this.props.inventoryItemListParam };
    inventoryItemListParam["searchText"] = "";
    inventoryItemListParam["pageSize"] = 50;
    inventoryItemListParam["departmentId"] = 0;
    inventoryItemListParam["categoryId"] = 0;
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.props.getInventoryItemList(inventoryItemListParam, true);
    window.addEventListener('scroll', this.handleScroll);
    ColumnFilterCheckbox();
    try {
      this.clearMessageIntervalID = setInterval(async () => {
        this.props.clearMessage();
      }, 10 * 1000);
    } catch (e) {
      console.log(e);
    }
  }

  getInventoryItemList = () => {
    this.props.getInventoryItemList(this.props.inventoryItemListParam, false);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    $('#tblNoneScannedItem').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region scroll event to on demand loading
  handleScroll = scrolldebounce(() => {
    try {
     
      // console.log("handlescroll: None scanned item");
      // Checks that the page has scrolled to the bottom
      var scrollHeigt = (window.innerHeight + document.documentElement.scrollTop).toFixed(0);
      var offsetHeight = (document.documentElement.offsetHeight).toFixed(0);    
      if (scrollHeigt === offsetHeight || (parseInt(scrollHeigt)+1).toString() === offsetHeight || (parseInt(scrollHeigt)-1).toString() === offsetHeight) {
        if (this.props.totalRecord !== this.props.inventoryItemList.length) {
          let inventoryItemListParam = { ...this.props.inventoryItemListParam };
          let pagesize = this.props.inventoryItemListParam.pageSize + 50;
          inventoryItemListParam["pageSize"] = pagesize;
          this.props.setInventoryItemListParam(inventoryItemListParam);
          this.props.getInventoryItemList(inventoryItemListParam, false);
        }
      }
    } catch (error) {
      console.log(error);

    }
  }, 0);

  //#endregion scroll event to on demand loading

  //#region search handler
  searchHandler = (record, cb) => {
    try {
      let getdata;
      record = record.trim();
      // this.props.setSearchValue(record);

      if (record !== "" || this.props.inventoryItemListParam.searchText !== "") {
        let inventoryItemListParam = { ...this.props.inventoryItemListParam };
        inventoryItemListParam["searchText"] = record;
        this.props.setInventoryItemListParam(inventoryItemListParam);
        this.props.getInventoryItemList(inventoryItemListParam, true);
      }
      if (record !== "") {
        let searchRequestParameter = { ...this.props.searchRequestParameter };
        searchRequestParameter["searchText"] = record;
        this.props.setSearchRequestParameter(searchRequestParameter);
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata = [];
        let searchRequestParameter = { ...this.props.searchRequestParameter };
        searchRequestParameter["searchText"] = record;
        this.props.setSearchRequestParameter(searchRequestParameter);
        this.props.setSearchData(getdata);
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion search handler
  //#region search change handler
  onSearchChangeHandler = (value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value, (items) => {
        this.props.setSearchData(items);
      })
    } catch (error) {
      console.log(error);
    }
  }
  setSearchTerm = debounce((searchTerm, cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm, cb);
    } catch (error) {
      console.log(error);
    }
  }, 1000)
  //#endregion search change handler

  //#region sort event  handler
  sortHandler = (sortColumnName) => {
    try {
      var sortdirection = "";
      switch (sortColumnName) {
        case "itemLookupCode":
          sortdirection = this.props.inventoryItemListParam.sortColumnName !== "itemLookupCode" ? "asc" : this.props.inventoryItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "quantity":
          sortdirection = this.props.inventoryItemListParam.sortColumnName !== "quantity" ? "asc" : this.props.inventoryItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "description":
          sortdirection = this.props.inventoryItemListParam.sortColumnName !== "description" ? "asc" : this.props.inventoryItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "department":
          sortdirection = this.props.inventoryItemListParam.sortColumnName !== "department" ? "asc" : this.props.inventoryItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "category":
          sortdirection = this.props.inventoryItemListParam.sortColumnName !== "category" ? "asc" : this.props.inventoryItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        default:
          break;
      }
      var data = {
        searchText: this.props.inventoryItemListParam.searchText,
        pageNo: this.props.inventoryItemListParam.pageNo,
        pageSize: this.props.inventoryItemListParam.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        departmentIds: this.props.inventoryItemListParam.departmentIds,
        categoryIds: this.props.inventoryItemListParam.categoryIds,
        supplierIds: this.props.inventoryItemListParam.supplierIds

      };
      var inventoryItemListParam = { ...this.props.inventoryItemListParam };
      inventoryItemListParam["sortColumnName"] = sortColumnName;
      inventoryItemListParam["sortDirection"] = sortdirection;
      this.props.setInventoryItemListParam(inventoryItemListParam);
      this.props.getInventoryItemList(data, false);
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion sort event handler
  filterChangeHandler = (e) => {
    var value = e.target.value;
    var inventoryItemListParam = { ...this.props.inventoryItemListParam };
    if (e.target.name === "departmentId") {
      if (value !== "0") {
        this.setState({ isCategoryDisabled: false });
        this.props.getCategories(parseInt(value));
      }
      else {
        inventoryItemListParam["categoryId"] = 0;
        this.props.getCategories(parseInt(value));
        this.setState({ isCategoryDisabled: true });
      }
    }
    inventoryItemListParam[e.target.name] = parseInt(value);
    inventoryItemListParam["pageSize"] = 50;
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.props.getInventoryItemList(inventoryItemListParam, false);
    let searchRequestParameter = { ...this.props.searchRequestParameter };
    searchRequestParameter[e.target.name] = parseInt(value);
    this.props.setSearchRequestParameter(searchRequestParameter);
    this.props.getSearchList(searchRequestParameter);
  }
  filterHandler = () => {
    if (this.props.inventoryItemListParam.departmentId === 0 && this.props.inventoryItemListParam.categoryId === 0)
      swal({
        title: "Error",
        text: "Please select department or category.",
        icon: "error",
        button: "Ok",
      })
    else
      this.props.getInventoryItemList(this.props.inventoryItemListParam, false);
    if (!!this.props.searchRequestParameter.searchText)
      this.props.getSearchList(this.props.searchRequestParameter);
  }
  clearFilterHandler = () => {
    var inventoryItemListParam = { ...this.props.inventoryItemListParam };
    inventoryItemListParam["departmentId"] = 0;
    inventoryItemListParam["categoryId"] = 0;
    inventoryItemListParam["pageSize"] = 50;
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.props.getCategories(0);
    this.setState({ isCategoryDisabled: true });
    this.props.getInventoryItemList(inventoryItemListParam, false);
    var searchRequestParameter = { ...this.props.searchRequestParameter };
    searchRequestParameter["departmentId"] = 0;
    searchRequestParameter["categoryId"] = 0;
    this.props.setSearchRequestParameter(searchRequestParameter);
    this.props.getSearchList(searchRequestParameter);
  }

  toggleFinalInventoryDatatable=() => {this.setState({isShowFinalInventoryDatatable: !this.state.isShowFinalInventoryDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowFinalInventoryDatatable: false})}

  //#region filter checkbox onchange function
  setItemCodeCheckbox = () => {
    this.setState({ isItemCode: !this.state.isItemCode }, () => {
    let filterColumn = { ...this.state.filterColumn };
    filterColumn["isSetItemCode"]= this.state.isItemCode
    this.setState({ filterColumn });
    });
  }

  setQuantityCheckbox = () => {
    this.setState({ isQuantity: !this.state.isQuantity }, () => {
    let filterColumn = { ...this.state.filterColumn };
    filterColumn["isSetQuantity"]= this.state.isQuantity
    this.setState({ filterColumn });
    });
  }

  setDescriptionCheckbox = () => {
    this.setState({ isDescription: !this.state.isDescription }, () => {
    let filterColumn = { ...this.state.filterColumn };
    filterColumn["isSetDescription"]= this.state.isDescription
    this.setState({ filterColumn });
    });
  }

  setDepartmentCheckbox = () => {
    this.setState({ isDepartment: !this.state.isDepartment }, () => {
    let filterColumn = { ...this.state.filterColumn };
    filterColumn["isSetDepartment"]= this.state.isDepartment
    this.setState({ filterColumn });
    });
  }

  setCategoryCheckbox = () => {
    this.setState({ isCategory: !this.state.isCategory }, () => {
    let filterColumn = { ...this.state.filterColumn };
    filterColumn["isSetCategory"]= this.state.isCategory
    this.setState({ filterColumn });
    });
  }
  
  //#endregion filter checkbox onchange function
  render() {
    //  const {inventoryItemList}=this.props   
    return (
      <NoneScannedItemComponent
        {...this.props}
        userDetails={userDetails}
        getInventoryItemList={this.getInventoryItemList}
        isRender={this.state.isRender}
        sortHandler={this.sortHandler}
        searchHandler={this.searchHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        filterChangeHandler={this.filterChangeHandler}
        filterHandler={this.filterHandler}
        clearFilterHandler={this.clearFilterHandler}
        isCategoryDisabled={this.state.isCategoryDisabled}
        toggleFinalInventoryDatatable={this.toggleFinalInventoryDatatable}
        isShowFinalInventoryDatatable={this.state.isShowFinalInventoryDatatable}
        setItemCodeCheckbox={this.setItemCodeCheckbox}
        setQuantityCheckbox={this.setQuantityCheckbox}
        setDescriptionCheckbox={this.setDescriptionCheckbox}
        setDepartmentCheckbox={this.setDepartmentCheckbox}
        setCategoryCheckbox={this.setCategoryCheckbox}
        isItemCode={this.state.isItemCode}
        isQuantity={this.state.isQuantity}
        isDescription={this.state.isDescription}
        isDepartment={this.state.isDepartment}
        isCategory={this.state.isCategory}
        isSetItemCode={this.state.isSetItemCode}
        isSetQuantity={this.state.isSetQuantity}
        isSetDescription={this.state.isSetDescription}
        isSetDepartment={this.state.isSetDepartment}
        isSetCategory={this.state.isSetCategory}
        filterColumn={this.state.filterColumn}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
      />
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getInventoryItemList: (data, isLoading) => dispatch(getInventoryItemList(data, isLoading)),
    getInventoryDetail: (inventoryId) => dispatch(getInventoryDetail(inventoryId)),
    setInventoryItemListParam: (data) => dispatch(setInventoryItemListParam(data)),
    clearMessage: () => dispatch(clearMessage()),
    setSearchRequestParameter: (data) => dispatch(setSearchRequestParameter(data)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    getDepartments: () => dispatch(getDepartments()),
    getCategories: (data) => dispatch(getCategories(data)),
  };
};

const mapStateToProps = (state) => {
  const { isLoading, inventoryItemList, inventoryItemListParam,
    inventoryDetail, totalRecord, status, message, isModified, searchValue, searchData,
    searchRequestParameter, SearchTerm, departments, categories } = state.NoneScannedItemReducer;
  return {
    inventoryItemList: inventoryItemList.values.length > 0 ? inventoryItemList.values : [],
    inventoryItemListParam: inventoryItemListParam,
    inventoryDetail: inventoryDetail.values,
    isLoading: isLoading,
    totalRecord: totalRecord, status, message,
    isModified,
    searchValue: searchValue,
    searchData: searchData.values.length > 0 ? searchData.values : [],
    searchRequestParameter: searchRequestParameter,
    SearchTerm: SearchTerm,
    departments: departments.values.length > 0 ? departments.values : [],
    categories: categories.values.length > 0 ? categories.values : [],

  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NoneScannedItemContainer));