import React from 'react';
import ICashOutSettingComponent from '../component/ICashOutSettingComponent';
import { getICashOutSetting,setCodeAlias,CreateICashOutSetting,PostItem
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class ICashOutSettingContainer extends React.Component {
  state={
    cashOutParam:[]
  }
 
  componentDidMount() {
    window.addEventListener('scroll',this.handleScroll);
    this.props.getICashOutSetting();
  }
  codeALiasHandler= (e,name) => {
    debugger
      var value = e.target.id;
      if(value.includes('code'))
      {
        value=parseInt(1)
      }
      else
      {
        value=parseInt(2)
      }

      let iCashOutList = this.props.iCashOutList;
      let cashOutParam= this.props.iCashOutList;
      var index=iCashOutList.findIndex(x=>x.name==name)
   
      if (index > -1) {
        iCashOutList[index].itemCode_Alias_id=parseInt(value);
        cashOutParam[index].itemCode_Alias_id=parseInt(value);
      }
      this.setState({cashOutParam})
      this.props.setCodeAlias(iCashOutList) 
  }
  componentWillUnmount() {
    this.props.setCodeAlias(this.props.iCashOutList) 

    window.removeEventListener('scroll',this.handleScroll);
   
    clearInterval(this.clearMessageIntervalID);
  }
  
  SaveSettings=()=> {
    try {
        debugger;
        let data={
          table:this.props.iCashOutList,
        }
        this.props.CreateICashOutSetting(data,true)
        
    }catch(error) {
        console.log(error);
    }
  }
  PostItem=(e)=>{
    this.props.PostItem(true)
  }
  render() {
    // const { searchData } = this.props;   
   
    return (
      <ICashOutSettingComponent
       {...this.props}
       APIData={this.APIData}
       codeALiasHandler={this.codeALiasHandler}
       SaveSettings={this.SaveSettings}
       cashOutParam={this.state.cashOutParam}
       PostItem={this.PostItem}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getICashOutSetting: () => dispatch(getICashOutSetting()),
    setCodeAlias: (data) => dispatch(setCodeAlias(data)),
    CreateICashOutSetting: (data,isLoading) => dispatch(CreateICashOutSetting(data,isLoading)),
    PostItem: (isLoading) => dispatch(PostItem(isLoading)),
  };
};

const mapStateToProps=(state) => {

  const {iCashOutList,isLoading}=state.ICashOutSettingReducer;
  return {
    iCashOutList,isLoading
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(ICashOutSettingContainer);