import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        sortColumnName: "",
        sortDirection: "",
        pageNo: 0,
        pageSize: 50,
        searchText: "",
        departmentIds: [],
        categoryIds: [],
        supplierIds: []
    },
    searchData: [],
    searchRequestParameter: {
        searchText: "",
        departmentId: 0,
        categoryId: 0
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status: "",
    message: "",
    isModified: false,
    departments: [],
    categories: []
};

export default function NoneScannedItemReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_NONESCANNEDITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_NONESCANNEDITEM_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                isModified: action.data.values !== undefined || action.data.values !== null ? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_NONESCANNEDITEM_NONESCANNEDITEMLISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.SET_NONESCANNEDITEMS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_NONESCANNEDITEMS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_NONESCANNEDITEMS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_NONESCANNEDITEMS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEMSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_NONESCANNEDITEMSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEMSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_DEPARTMENTS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_DEPARTMENTS_SUCCESS:

            return {
                ...state,
                departments: action.data,
                isLoading: false,
            }
        case types.GET_DEPARTMENTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_CATEGORIES_SUCCESS:

            return {
                ...state,
                categories: action.data,
                isLoading: false,
            }
        case types.GET_CATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        default:
            return state;
    }
}