import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
import Autocomplete from 'react-autocomplete';
import ScanItemContainer from '../../scanItems/container/ScanItemContainer';
import NewScanItemContainer from '../../newScanItems/container/NewScanItemContainer';

export default function OrderComponent(props) {

	const {isRender,isLoading,orderItemList,isScanItem,isOpenCombineOrderModel,orders,
		orderDetail,searchValue,searchData,closeScanItem,isOpenSearchBox,
		isOpenWorkOrderModel,customers,getOrderItemList,userDetails,isNewlyCreated,
		isAskedQuantity,isOpenQuantityModel,insertItemParam,status,message,isOpenSupplierModal,supplierList,requestParameter,isShowOrderDatatable,
		isItemName,isSetItemName,isItemCode,isSetItemCode,isSetQuantity,isQuantity,isSetOnHandQuantity,isOnHandQuantity,isSetOnHandQuantity1}=props;

	return (
		<div>
			<Helmet><title>{orderDetail!==undefined&&orderDetail!==null&&orderDetail.name!==null? orderDetail.name:""} | Modus Go!</title></Helmet>

			{isLoading? <Loading />:""}
			{isRender?
				<div>
					<div className="sticky-filter-datatable" onClick={props.toggleOrderDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>
					<div className={`datatable-filter${isShowOrderDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.toggleOrderDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
						</div>
						<div className="datatable-head-list">
							<div className="datatable-list-title text-darkblue font-weight-bold">
								Order table
											</div>
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isItemName" name="isItemName" onChange={props.setItemNameCheckbox} checked={isItemName} />
										<label className="custom-control-label font-weight-bold" htmlFor="isItemName">ITEM NAME</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isItemCode" name="isItemCode" onChange={props.setItemCodeCheckbox} checked={isItemCode} />
										<label className="custom-control-label font-weight-bold" htmlFor="isItemCode">ITEM CODE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isQuantity" name="isQuantity" onChange={props.setQuantityCheckbox} checked={isQuantity} />
										<label className="custom-control-label font-weight-bold" htmlFor="isQuantity">QUANTITY</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOnHandQuantity" name="isOnHandQuantity" onChange={props.setOnHandQuantityCheckbox} checked={isOnHandQuantity} />
										<label className="custom-control-label font-weight-bold" htmlFor="isOnHandQuantity">QTY ON HAND</label>
									</div>
								</li>

							</ul>
						</div>
					</div>
					<div className="main-content-area order-space mobile-bottom-space " id="test" onClick={props.toggleHideOrderDatatable}>
						<div className="container">
							<div className="row">
								<div className="col edit-title-wrap">
									<div className="edit-title p-0 d-none d-md-block">
										<h3>Insert item for : <span>{orderDetail!==undefined&&orderDetail!==null? orderDetail.name:""} </span></h3>
									</div>
									<div className="col p-0 mobile-header-info">
										<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
										<p className="mobile-title-headline">Insert item for : <span>{orderDetail!==undefined&&orderDetail!==null? orderDetail.name:""} </span></p>
									</div>
									<div className="order-header">
										<div className="custom-control custom-checkbox top-head-btn mb-1 ordrer-checkbox">
											<div className="custom-checkbox">
												<input type="checkbox" className="custom-control-input" tabIndex="1" id="isAskedQuantity" onChange={props.setIsAskedQuantity} name="isAskedQuantity" checked={isAskedQuantity} />
												<label className="custom-control-label font-weight-bold" htmlFor="isAskedQuantity">Is ask quantity</label>
											</div>
										</div>

									</div>

								</div>
							</div>
						</div>



						<div className="container mt-3 ">
							<div className="row">
								<div id="OrderSearchboxHide" className="col-12 p-0">
									<div className="custom-checkbox top-head-btn mb-3">
										{/* <button className="btn btn-primary rounded-pill" type="button" onMouseDown={e => e.preventDefault()} onClick={props.showScanItem}>Scan item</button> */}
										<button className="itemscanshow btn btn-primary rounded-pill" type="button">Scan item</button>
										<button className="btn btn-primary rounded-pill ml-2" type="button" onMouseDown={e => e.preventDefault()} onClick={props.openCombineOrder}>Combine order</button>
									</div>
									<div className="searchbox-wrap header-shadow">
										<Autocomplete
											onBlur={props.closeSearchBox}
											wrapperStyle={{position: 'relative'}}
											open={isOpenSearchBox}
											value={searchValue}
											items={searchData}
											onSelect={(record) => props.onSearchCheckBoxHandler("","",record)}
											getItemValue={(item) => item.keys}
											inputProps={{id: 'orderitemsearch-autocomplete',onClick: props.openSearchBox,placeholder: 'item code, description, alias, {department/category} or combination'}}
											onChange={(event,value) => props.onSearchChangeHandler(value)}
											renderMenu={children => (
												<div className="search-menu" id="orderItemSearchMenu">
													{children}
												</div>
											)}
											renderItem={(item,isHighlighted) => (
												// <div className="orderItemSearchControl custom-control custom-checkbox pl-50" key={item.keys}>
												// 	<input type="checkbox" className="orderItemSearchControl custom-control-input" defaultChecked={item.itemChecked} onChange={(e) => props.onSearchCheckBoxHandler(e, item.itemID, item.value)} id={item.itemID} name={item.itemID} />
												// 	<label className="orderItemSearchControl custom-control-label" htmlFor={item.itemID}>{item.value}</label>
												// </div>
												<div className={`orderItemSearchControl ${item.itemChecked? "bg-autosearchValue":""}`} key={item.keys}
												>{item.value}</div>
											)}
										/>
										<div className="icon-box rounded-circle" onClick={props.clickOpenSearchBox}>
											<i className="material-icons">search</i>
										</div>

									</div>
								</div>
								<div className="col-12 p-0">
									<NewScanItemContainer onSearchCheckBoxHandler={props.onSearchCheckBoxHandler} isAskedQuantity={isAskedQuantity} isFromPO={false} isFromWO={false} getOrderItemList={getOrderItemList} closeScanItem={closeScanItem} isFromOrder={true} />
								</div>

							</div>
						</div>
						<div className="container mt-3">
							<div className="row">
								<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
							</div>
						</div>
						<div className="container bg-white header-shadow mt-1 mb-150" onClick={props.clickOpenSearchBox}>
							<div className="row ">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblOrderItem" className="table w-100 new-order-list wo-collasped" >
											<thead>
												<tr>
													<th className="plus-sort"></th>
													{/* <th>ITEM NAME</th> */}
													
													<th style={{display: isSetItemName ? '' : 'none' }}  className={requestParameter.sortColumnName==="name"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both itemLookupName"} onClick={() => props.sortHandler("name")}>ITEM NAME</th>
													
													<th style={{display: isSetItemCode ? '' : 'none' }}  className={requestParameter.sortColumnName==="itemLookupCode"? requestParameter.sortDirection==="asc"? "sort-top itemLookupCode":"sort-bottom itemLookupCode":"sort-both itemLookupCode"} onClick={() => props.sortHandler("itemLookupCode")}>ITEM CODE</th>
													
													<th style={{display: isSetQuantity ? '' : 'none' }}  className={requestParameter.sortColumnName==="quantity"? requestParameter.sortDirection==="asc"? "sort-top quantity":"sort-bottom quantity":"sort-both quantity"} onClick={() => props.sortHandler("quantity")}>QUANTITY</th>
													
													<th style={{display: isSetOnHandQuantity ? '' : 'none' }}  className={requestParameter.sortColumnName==="onHandQuantity"? requestParameter.sortDirection==="asc"? "sort-top onHandQuantity":"sort-bottom onHandQuantity":"sort-both onHandQuantity"} onClick={() => props.sortHandler("onHandQuantity")}>Qty On Hand</th>
													
													<th className=""><span className="d-md-none">Delete</span></th>
												</tr>
											</thead>
											<tbody>
												{orderItemList.map((itemList,index) =>
													<tr key={itemList.id}>
														<td className="details-control plus-sort" tabIndex="-1"></td>
														
														<td className="itemLookupName" style={{display: isSetItemName ? '' : 'none' }}  onClick={() => props.redirectItemEditComponent(itemList.itemId)}>{itemList.name}</td>
														
														
														<td className="itemLookupCode" style={{display: isSetItemCode ? '' : 'none' }}  onClick={() => props.redirectItemEditComponent(itemList.itemId)}>{itemList.itemLookupCode}</td>
														
														
														<td className="quantity" style={{display: isSetQuantity ? '' : 'none' }}  ><input type="number" className="floating-input floating-input-label" placeholder=" " onBlur={(e) => props.inlineEditBlurHandler(e,itemList.itemId)} onChange={(e) => props.inlineEditChangeHandler(e,itemList.itemId)} maxLength="8" min="0" value={itemList.quantity} /></td>
														
														
														<td className="onHandQuantity"  style={{display: isSetOnHandQuantity ? '' : 'none' }}    className="default-cursor">{itemList.onhandQuantity}</td>
														
														<td ><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del OrderITEMGridDeleteIcon" data={itemList.itemId}><i className="zmdi zmdi-delete "></i></button></td>
													</tr>
												)}
											</tbody>
										</table>
									</div>
									<div className="footer-box mobile-footer">
										{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <button className="btn btn-outline-primary mr-3" type="button" onClick={props.generatePOHandler} disabled={orderItemList.length>0? false:true}>Generate PO</button>:""}
										{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <button className="btn btn-outline-primary mr-3" type="button" onClick={props.generateTOHandler} disabled={orderItemList.length>0? false:true}>Generate TO</button>:""}
										
										{!isNewlyCreated? <button className="btn btn-outline-primary mr-3" type="button" onClick={props.openWorkOrder} disabled={orderItemList.length>0? false:true}>Generate WO</button>:""}
										<button className="btn btn-outline-primary" type="button" onClick={() => props.history.push("/Dashboard")}>Cancel</button>
									</div>
								</div>

							</div>
						</div>
					</div>

				</div>
				:""}
			{/* <! Modal combine order> */}
			<Modal backdrop="static" isOpen={isOpenCombineOrderModel} toggle={props.toggleCombineOrderModel} centered={true}>
				<ModalHeader toggle={props.toggleCombineOrderModel}>Combine order</ModalHeader>
				<ModalBody>

					<div className="row">
						<div className="col-12 p-0">
							<div className="table-responsive">
								<table id="tblCombineOrderItem" className="table w-100 new-order-list wo-collasped" >
									<thead>
										<tr>
											<th></th>
											<th>ITEM NAME</th>
										</tr>
									</thead>
									<tbody>
										{orders.map((order) =>
											<tr key={order.orderId}>
												<td>
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" defaultChecked={false} onChange={(e) => props.orderCheckBoxChangeHandler(e)} id={order.orderId} name={order.orderId} />
														<label className="custom-control-label" htmlFor={order.orderId}></label>
													</div>
												</td>
												<td>{order.name}</td>

											</tr>
										)}
									</tbody>
								</table>
							</div>
						</div>
					</div>


				</ModalBody>
				<ModalFooter>
					{orders.length>0? <button className="btn rounded-pill btn-primary" type="button" onClick={props.combineOrderHandler} >Combine order</button>:""}
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleCombineOrderModel}>Cancel</button>

				</ModalFooter>
			</Modal>
			{/* <! Modal work order> */}
			<Modal backdrop="static" isOpen={isOpenWorkOrderModel} toggle={props.toggleWorkOrderModel} centered={true} size="lg">
				<ModalHeader toggle={props.toggleWorkOrderModel}>Generate WO</ModalHeader>
				<ModalBody>
					<div className="row">
						<div className="col-12 p-1">
							<div className="table-responsive">
								<table id="tblWOCustomers" className="table w-100 new-order-list" >
									<thead>
										<tr>
											<th></th>
											<th>ACCOUNT NUMBER</th>
											<th>NAME</th>
											<th>COMPANY</th>

										</tr>
									</thead>
									<tbody>
										{customers.map((customer) =>
											<tr key={customer.id}>
												<td> <div className="custom-control custom-checkbox " key={customer.id}><input type="checkbox" className="custom-control-input customerCHK" onChange={(e) => props.customerCheckBoxHandler(e,customer.id)} id={customer.id} name={customer.id} />
													<label className="custom-control-label" htmlFor={customer.id}></label></div></td>
												<td>{customer.accountNumber}</td>
												<td>{customer.firstName} {customer.lastName}</td>
												<td>{customer.company}</td>

											</tr>
										)}
									</tbody>
								</table>
							</div>
						</div>
					</div>

				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.generateWOHandler} >Generate WO</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleWorkOrderModel}>Cancel</button>

				</ModalFooter>
			</Modal>
			{/* <! Modal Quantity> */}
			<Modal backdrop="static" isOpen={isOpenQuantityModel} toggle={props.toggleQuantityModel} centered={true}>
				<ModalHeader toggle={props.toggleQuantityModel}>Add quantity</ModalHeader>
				<ModalBody>

					<div className="row">
						<div className="col-12  container px-4 db-setting-table">
							<table className="table">
								<thead>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Order number</span>
										</td>
										<td className="col-6">
											{orderDetail!==undefined&&orderDetail!==null? orderDetail.name:""}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Item number</span>
										</td>
										<td className="col-6 ">
											{insertItemParam.itemLookupCode}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Total quantity</span>
										</td>
										<td className="col-6">
											{insertItemParam.oldQuantity}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Insert the new quantity</span>
										</td>
										<td className="col-6">
											<input type="number" autoFocus id="newQuantity" className="floating-label floating-input" placeholder=" " onChange={(e) => props.quantityModelChangeHandler(e)} maxLength="8" min="0" value={insertItemParam.quantity} />
										</td>
									</tr>
								</thead>
							</table>
						</div>




					</div>


				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.onSaveQuantityModel} >Ok</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleQuantityModel}>Cancel</button>

				</ModalFooter>
			</Modal>
			{/* <! Modal Supplier> */}
			<Modal backdrop="static" isOpen={isOpenSupplierModal} toggle={props.toggleSupplierModel} centered={true} size="lg">
				<ModalHeader toggle={props.toggleSupplierModel}>Supplier</ModalHeader>
				<ModalBody>
					<div className="row">
						<div className="col-12 p-1">
							<div className="table-responsive">
								<table id="tblOrderSupplierList" className="table w-100 new-order-list" >
									<thead>
										<tr>
											<th></th>
											<th>ID</th>
											<th>CODE</th>
											<th>SUPPLIER NAME</th>

										</tr>
									</thead>
									<tbody>
										{supplierList.map((supp) =>
											<tr key={supp.id}>
												<td> <div className="custom-control custom-checkbox "
													key={supp.id}>
													<input type="checkbox" className="custom-control-input orderSupplierCHK" onChange={(e) => props.supplierCheckBoxHandler(e,supp.id)} id={supp.id} name={supp.id} />
													<label className="custom-control-label" htmlFor={supp.id}></label></div></td>
												<td>{supp.id}</td>
												<td>{supp.code}</td>
												<td>{supp.supplierName}</td>
											</tr>
										)}
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.supplierModalSave} >OK</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleSupplierModel}>Cancel</button>

				</ModalFooter>
			</Modal>
			{isScanItem? <ScanItemContainer isFromPO={false} isFromWO={false} getOrderItemList={getOrderItemList} closeScanItem={closeScanItem} />:''}

		</div>
	)
}