const obj = new AuthenticateService();
export function isItemExist(data) {
    return (dispatch) => {
        dispatch({
            type: types.ITEM_EXIST_REQUEST
        });
        let url = "order/checkLookupCodeOrder";
        return post(url,data)
            .then((response) => {
                const { payload } = response;
              
                if (response.payload && response.payload.status === "Success") {
                    dispatch({
                        type: types.ITEM_EXIST_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.ITEM_EXIST_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

export function setSuccessDialogue(check, status, message) {
    return (dispatch) => {
        dispatch({
            type: types.RESET_SUCCESS_DIALOGUE,
            isSuccessDialogue: check,
            status: status,
            message: message,
        });
    }
}
export function insertItem(data) {

    return (dispatch) => {
                    dispatch({
                        type: types.INSERT_ORDER_REQUEST,
                    });
                    let url = 'Inventory/insertInventoryItem';
                    return post(url, data)
                        .then((response) => {                            
                            if (response.payload && response.payload.status === "Success") {
                                dispatch({
                                    type: types.INSERT_ORDER_SUCCESS,
                                    data: response.payload,
                                });

                                dispatch(setSuccessDialogue(true, response.payload.status, response.payload.message));

                                //    swal({
                                //     title: response.payload.status,
                                //     text: response.payload.message,
                                //     icon: response.payload.status.toLowerCase(),
                                //     timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                //     button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                // })

                            } else {
                                dispatch({
                                    type: types.INSERT_ORDER_FAILED,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }
                        })
                    // dispatch({
                    //     type: types.ITEM_EXIST_SUCCESS,
                    //     data: response.payload,
                    // });
              //  }
           // })
    }
}

export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}

export function GetsupplierList(data) {
    
    return (dispatch) => {

        let url = `order/PoSupplierList`;

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    $('#tblWOSupplier-PO').DataTable().destroy();
                    dispatch({
                        type: types.GET_SUPPLIER_LIST_SUCCESS,
                        data: response.payload.values,
                    });
                    $('#tblWOSupplier-PO').DataTable({
                        "paging": true,
                        "ordering": false,
                        "info": false,
                        "searching": true,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found",
                            "sZeroRecords": "No data found"
                        }
                    });
                }
            })
    }
}

//#region  getLookupCodeList
export function getLookupCodeList(data, isLoading) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_LOOKUPCODE_REQUEST,
            isLoading: isLoading,
        });
        let url = 'item/itemlist';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblLookupCodeList').DataTable().destroy();
                    dispatch({
                        type: types.GET_LOOKUPCODE_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblLookupCodeList').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found",
                            "sZeroRecords": "No data found"
                        }
                    })


                } else {
                    dispatch({
                        type: types.GET_LOOKUPCODE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion getLookupCodeList

