import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        sortColumnName: "lastTime",
        sortDirection: "desc",
        pageNo: 0,
        pageSize: 50,
        searchText: "",
        departmentIds: [],
        categoryIds: [],
        supplierIds: []
    },
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status: "",
    message: "",
    isModified: false,
    noneScannedItemList: [],
    departments: [],
    categories: [],
    suppliers: [],
    isCategoryDisabled: true
};

export default function FinalInventoryReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_INVENTORYITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYITEM_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_INVENTORYITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                isModified: action.data.values !== undefined || action.data.values !== null ? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.DELETE_INVENTORYITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_INVENTORYITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.DELETE_INVENTORYITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.SET_FINALINVENTORYS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_FINALINVENTORYS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_FINALINVENTORYS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_FINALINVENTORYS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_FINALINVENTORYSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALINVENTORYSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_FINALINVENTORYSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMMIT_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.COMMIT_INVENTORY_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMMIT_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEMCOMMIT_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_NONESCANNEDITEMCOMMIT_SUCCESS:
            return {
                ...state,
                noneScannedItemList: action.data,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEMCOMMIT_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.GET_FINALDEPARTMENTS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALDEPARTMENTS_SUCCESS:

            return {
                ...state,
                departments: action.data,
                isLoading: false,
            }
        case types.GET_FINALDEPARTMENTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALCATEGORIES_SUCCESS:

            return {
                ...state,
                categories: action.data,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_FINALSUPPLIERS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALSUPPLIERS_SUCCESS:

            return {
                ...state,
                suppliers: action.data,
                isLoading: false,
            }
        case types.GET_FINALSUPPLIERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_IS_CATEGORYDISABLED:

            return {
                ...state,
                isCategoryDisabled: action.data,
                isLoading: false,
            }
        default:
            return state;
    }
}