import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    getalltax:[],
    responseStatusProgress:"",
    responseStatusComplete:"",
    getkeyValue:[],
    apiBigCom:[],
    apiColumnMappingList:[],
    isLoading: false,
    status: "",
    message: "",
    dbColumnNameList:[],
 
};

export default function ThirdPartyAPIDataSetupReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
      
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:"",
                responseStatusComplete:""
            }
        case types.GETALLPRODUCT_REQUEST:
            return {
                ...state,
                isLoading: true,
             
            }
        case types.GETALLPRODUCT_SUCCESS:
           
            return {
                ...state,
                isLoading: false,
                responseStatusProgress:action.data.message!="Completed"?action.data.message:"",
                responseStatusComplete:action.data.message!="Completed"?action.data.message:""
            }
        case types.GETALLPRODUCT_FAILED:
            return {
                ...state,
                isLoading: false,
               
            }
        case types.GETALLTAX_BIGCOM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GETALLTAX_BIGCOM_SUCCESS:
           
            return {
              
                ...state,
                getalltax: action.data.values,
                isLoading: false,
            }
        case types.GETALLTAX_BIGCOM_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.GETALL_KEYVALUE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GETALL_KEYVALUE_SUCCESS:
            
            return {
                ...state,
                isLoading: false,
                responseStatusProgress:action.data.message!="Completed"?action.data.message:"",
                responseStatusComplete:action.data.message=="Completed"?action.data.message:""
            }
        case types.GETALL_KEYVALUE_FAILED:
            return {
                ...state,
                isLoading: false,
            }   
        case types.GET_APINAME_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_APINAME_SUCCESS:
        
            return {
                ...state,
                apiBigCom: action.data.values,
                isLoading: false,
            }
        case types.GET_APINAME_FAILED:
            return {
                ...state,
                isLoading: false,
            }
         
        case types.GET_APICOLUMNMAPPING_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_APICOLUMNMAPPING_SUCCESS:
            return {
                ...state,
                apiColumnMappingList: action.data.values,
                isLoading: false,
            }
        case types.GET_APICOLUMNMAPPING_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_APICOLUMNMAPPING_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_APICOLUMNMAPPING_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.DELETE_APICOLUMNMAPPING_FAILED:
            return {
                ...state,
                isLoading: false,
            }
       
        case types.GET_DBCOLUMNNAME_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_DBCOLUMNNAME_SUCCESS:
            return {
                ...state,
                dbColumnNameList: action.data.values,
                isLoading: false,
            }
        case types.GET_DBCOLUMNNAME_FAILED:
            return {
                ...state,
                isLoading: false,
            }     
        default:
            return state;
    }
}