export function UpdateSendRHHorBigcommerce(data,isLoading) {
  
    return (dispatch) => {
        dispatch({
            type: types.UPDATESEND_RMHORAPI_REQUEST,
            isLoading:isLoading
        });
        let url = 'APICommonIntegration/UpdateSendRHHorBigcommerce';
        return post(url, data)
            .then((response) => {

                const { payload } = response;
                if (response.status === 200) {
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.UPDATESEND_RMHORAPI_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.UPDATESEND_RMHORAPI_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

