import React from 'react';
import ConfigureDatabaseComponent from '../component/ConfigureDatabaseComponent';
import BigCommerceManagement from '../../../ThirdPartyAPIDataSetup/component/ThirdPartyAPIDataSetupComponent';
import { getDatabase, setFormfields, saveDatabase ,setSuccessDialogue,clearMessage,UpdateSendRHHorBigcommerce
    ,GetAllProductDetails,GetALLTax,UpdateConfigureStore,getConfigureStoreList,getAPINameList} from '../state/actions/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import swal from 'sweetalert';

class ConfigureDatabaseContainer extends React.Component {
    state={
        isShowMapping:false,
        isShowTax:false,
        formFields:
        {
          "taxName":""
    
        },
        storeFormFields: {
          serverName: "",
          username: "",
          password: "",
          port: "",
          databaseName: "",
          xmlnode:""
      },
      storeerrors:""
        
      }
    constructor(props) {
        super(props);
        this.formHandler = this.formHandler.bind(this);
        this.storeformHandler = this.storeformHandler.bind(this);
    }
    componentWillMount() {
        this.checkUserRoleValidation();
    }
  
    componentDidMount() {
      this.props.getDatabase();
      this.props.getConfigureStoreList(true)
      this.props.GetALLTax(true);
      this.props.getAPINameList();
      window.addEventListener('scroll',this.handleScroll);
     
      try {
        this.clearMessageIntervalID=setInterval(async () => {
          this.props.clearMessage();
        },10*1000);
      } catch(e) {
        console.log(e);
      }
    }
    componentWillUnmount() {
      this.checkUserRoleValidation();
      window.removeEventListener('scroll',this.handleScroll);
      clearInterval(this.clearMessageIntervalID);
    }
    //#region check user role validation 
    checkUserRoleValidation = () => {
        try {
            const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
            if (userDetails.role !== process.env.REACT_APP_UserRole_Admin) {
                this.props.history.push("/NotFound");
            }
        } catch (error) {
            console.log(error);

        }
    }
    //#endregion check user role validation
    formHandler = (data) => {
        this.props.saveDatabase({ ...this.props.formFields, ...data });
    }
    storeformHandler = () => {
      let storeerrors=this.state.storeerrors;
      let storeFields =this.state.storeFormFields;
      if(storeFields.databaseName=="" ||storeFields.username=="" ||storeFields.serverName=="" ||storeFields.password=="")
      {
        storeerrors="error";
        this.setState({storeerrors})
      }
      else
      {
        storeerrors="";
        this.setState({storeerrors})
        this.props.saveDatabase(this.state.storeFormFields );
      }
     
    
    }
    
    //#region all input change handle method
    inputChangeHandler = (e) => {
        const { formFields, formFieldsOldValue } = this.props
        let obj = {}
        obj[e.target.name] = formFields[e.target.name]
        if (e.target.value && e.target.value != '' && formFieldsOldValue[e.target.name] === '') {
            this.props.setFormfields(obj);
        }
    }
    //#endregion all input change handle method

    inputStoreChangeHandler = (e) => {
      const  storeFormFields  = this.state.storeFormFields
      var name=e.target.name;
      var fieldname=name.replace("store", "")
      storeFormFields[fieldname] = e.target.value;

      this.setState({
        storeFormFields
      });
      
   }
    APIData=()=>
  {
    this.props.GetAllProductDetails(true);
  }
  SaveSendRHMorAPIDetails=()=>
  {   
    let formFields = { ...this.state.formFields };
    if(formFields.RHMBigcommerce==undefined)
    {
      swal("","Please Select Bigcommerce or ModusGo!","error")

    }
    if(formFields.RHMBigcommerce==2 && formFields.taxName=="")
    {
        swal("","Please Select Tax!","error")
    }
    this.props.UpdateSendRHHorBigcommerce(formFields,true)
  }
  rmhBigcommerceHandler= (e) => {
    var value = e.target.id;
      let formFields = { ...this.state.formFields };
      formFields["RHMBigcommerce"] = parseInt(value);
      this.setState({
        formFields
      });
     if(value=="2")
     {
      this.setState({isShowTax:true});
     }
     else
     {
      this.setState({isShowTax:false});

     }
  }
  
  valueInputOnchange=(e)=>{
      var formFields= this.state.formFields
      formFields[e.target.name]=parseInt(e.target.value);
      this.setState({formFields})
  }
  inputChangeHandlerStore= (e) => {
    try {
      var value = e.target.value;
      let getFormField = this.props.getFormField;
      var disableServerURL=false;
      if(e.target.name=="isHQConnected")
      {
        getFormField[e.target.name] =e.target.checked;
        if(e.target.checked==false)
        {
          getFormField["serverURL"] = "";
          disableServerURL=true;
        }
      }
      else
      {
        getFormField[e.target.name] = value;
        disableServerURL=false;
      }
      this.setState({
        getFormField,centralServerURLDisable:disableServerURL
      });
    } catch (error) {
      console.log(error);

    }
  }

  saveCofigStore=() => {

      if(this.props.getFormField.isHQConnected && this.props.getFormField.serverURL=="")
      {
        swal("","Please Enter Server URL","error")
      }
      else
      {
        let createConfigParam={
          centralMode:this.props.getFormField.isHQConnected,
          serverURL:this.props.getFormField.serverURL,
        }

        this.props.UpdateConfigureStore(createConfigParam,true)
      }
     
  }
    render() {
        const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),
            process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
        const { isLoading, formFieldsOldValue, storeFormFieldsOldValue,formFields } = this.props;
        return (
            <ConfigureDatabaseComponent
                isLoading={isLoading}
                formFieldsOldValue={formFieldsOldValue}
               // formFields={formFields}
                userDetails={userDetails}
                formHandler={this.formHandler}
                inputChangeHandler={this.inputChangeHandler}
                APIData={this.APIData}
                rmhBigcommerceHandler={this.rmhBigcommerceHandler}
                SaveSendRHMorAPIDetails={this.SaveSendRHMorAPIDetails}
                valueInputOnchange={this.valueInputOnchange}
                formFields={this.state.formFields}
                storeFormFields={this.state.storeFormFields}
                inputStoreChangeHandler={this.inputStoreChangeHandler}
                storeFormFieldsOldValue={storeFormFieldsOldValue}
                storeformHandler={this.storeformHandler}
                isShowTax={this.state.isShowTax}
                inputChangeHandlerStore={this.inputChangeHandlerStore}
                saveCofigStore={this.saveCofigStore}
                centralServerURLDisable={this.state.centralServerURLDisable}
                apiBigCom={this.props.apiBigCom}
                storeerrors={this.state.storeerrors}
            />
           
           
        )
       
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getDatabase: () => dispatch(getDatabase()),
        setFormfields: (data) => dispatch(setFormfields(data)),
        saveDatabase: (data) => dispatch(saveDatabase(data)),
        setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
        clearMessage: () => dispatch(clearMessage()),
        GetAllProductDetails: (isLoading) => dispatch(GetAllProductDetails(isLoading)),
        UpdateSendRHHorBigcommerce:(data,isLoading)=>dispatch(UpdateSendRHHorBigcommerce(data,isLoading)),
        GetALLTax:(isLoading)=>dispatch(GetALLTax(isLoading)),
        UpdateConfigureStore:(data,isLoading)=>dispatch(UpdateConfigureStore(data,isLoading)),
        getConfigureStoreList:(isLoading)=>dispatch(getConfigureStoreList(isLoading)),
        getAPINameList:()=>dispatch(getAPINameList()),

    };
};

const mapStateToProps = (state) => {

    const { formFields,isLoading, formFieldsOldValue ,updatestatus, updatemessage,getFormField, isSuccessDialogue,getalltax,apiBigCom} = state.ConfigureDatabaseReducer;
    return {
        formFields: formFields,
        isLoading: isLoading,
        formFieldsOldValue: formFieldsOldValue,
        updatestatus, updatemessage, isSuccessDialogue,
        getalltax: getalltax!=undefined? getalltax.values:[],
        getFormField,
        apiBigCom:apiBigCom!=undefined? apiBigCom:[],
     
       
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfigureDatabaseContainer);