import React from 'react';
import NumberFormat from 'react-number-format';
export default function DiscountsComponent(props) {
    const { itemDiscount, priceList } = props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <div className="col-12 padding-space">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="discountradio1" name="discountCustomRadio" className="custom-control-input" checked={itemDiscount !== null ? itemDiscount.selectedDiscount === 1 ? true : false : false} disabled />
                            <label className="custom-control-label" htmlFor="discountradio1">There is no discount scheme for this item</label>
                        </div>
                    </div>
                    <div className="col-12 padding-space">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="discountradio2" name="discountCustomRadio" className="custom-control-input" checked={itemDiscount !== null ? itemDiscount.selectedDiscount === 2 ? true : false : false} disabled />
                            <label className="custom-control-label" htmlFor="discountradio2">Use a Mix and Match discount scheme</label>
                        </div>

                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label mt-3">
                            <select className="floating-select" value={itemDiscount !== null ? itemDiscount.description1 : ""} disabled>
                                <option value="">select Discount scheme</option>
                                <option value={itemDiscount !== null ? itemDiscount.description1 : ""}>{itemDiscount !== null ? itemDiscount.description1 : ""}</option>
                            </select>
                            <span className="highlight"></span>
                            <label>Discount scheme</label>
                        </div>
                    </div>
                    <div className="col-12 padding-space">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="discountradio3" name="discountCustomRadio" className="custom-control-input" checked={itemDiscount !== null ? itemDiscount.selectedDiscount === 3 ? true : false : false} disabled />
                            <label className="custom-control-label" htmlFor="discountradio3">Use a Buy X and get Y for Z discount scheme</label>
                        </div>

                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label mt-3">
                            <select className="floating-select" value={itemDiscount !== null ? itemDiscount.description2 : ""} disabled>
                                <option value="">select Discount scheme</option>
                                <option value={itemDiscount !== null ? itemDiscount.description2 : ""}>{itemDiscount !== null ? itemDiscount.description2 : ""}</option>
                            </select>
                            <span className="highlight"></span>
                            <label>Discount scheme</label>
                        </div>
                    </div>
                    <div className="col-12 padding-space">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="discountradio4" name="discountCustomRadio" className="custom-control-input" checked={itemDiscount !== null ? itemDiscount.selectedDiscount === 4 ? true : false : false} disabled />
                            <label className="custom-control-label" htmlFor="discountradio4">Use a quantity discount pricing table</label>
                        </div>
                    </div>
                    <div className="col-12 padding-space">
                        <div className="custom-control custom-checkbox ml-2 my-2">
                            <input type="checkbox" className="custom-control-input" id="discount-check" name="discount-check" checked={itemDiscount !== null ? itemDiscount.selectedDiscount === 4 ? itemDiscount.discountOddItems : false : false} disabled />
                            <label className="custom-control-label" htmlFor="discount-check">Discount odd items</label>
                        </div>

                    </div>
                </div>
                <div className="row">
                    <div className="col-12 pt-2">
                        <span className="heading-text"><b>Pricing table</b></span>
                    </div>
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblDiscountPriceList" className="table w-100" >
                                <thead>
                                    <tr>
                                        <th className="plus-sort"></th>
                                        <th>Quantity</th>
                                        <th>PRICE</th>
                                        <th>PRICE A</th>
                                        <th>PRICE B</th>
                                        <th>PRICE C</th>

                                    </tr>
                                </thead>
                                <tbody>
                                    {priceList.map((item, index) =>
                                        <tr key={"tblDiscountPriceList-" + index}>
                                            <td className="details-control plus-sort"></td>
                                            <td>{item.quantity}</td>
                                            <td><NumberFormat value={item.price} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><NumberFormat value={item.priceA} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><NumberFormat value={item.priceB} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><NumberFormat value={item.priceC} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}