import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'

export default function ConfigureStoreComponent(props) {
	const {isLoading,inputChangeHandler,formFields,centralServerURLDisable,getFormField}=props;
	return (
		<div>
			<Helmet>
				<title>Configure Store | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div className="bg-white header-shadow px-4 py-2">
					<div className="row pt-2 align-items-center">

						<div className="col-md-6">
							<div className="floating-label">
								<input className="floating-input" tabIndex="3" placeholder=" " disabled={getFormField.isHQConnected?false:centralServerURLDisable} id="storeID" name="storeID" value={getFormField.storeID} onChange={inputChangeHandler}></input>

								<span className="highlight"></span>
								<label>Store ID</label>
							</div>
						</div>
						<div className="col-md-6 text-md-right">
							<div className="custom-control custom-checkbox">
							<input type="checkbox" className="custom-control-input" id="isHQConnected" name="isHQConnected"  onChange={inputChangeHandler} checked={getFormField.isHQConnected} />
									<label className="custom-control-label font-weight-bold" htmlFor="isHQConnected">Enable Central Mode</label>
							</div>
						</div>
						<div className="col-md-6">
						<div className="floating-label">
						    <input className="floating-input" tabIndex="3" placeholder=" "  id="serverURL" name="serverURL" disabled={getFormField.isHQConnected?false:centralServerURLDisable} value={getFormField.serverURL} onChange={inputChangeHandler}></input>

							<span className="highlight"></span>
							<label>Central Server URL</label>
						</div>
						</div>
						<div className="col-md-6 text-md-right d-none">
							<button className="d-inline-block  btn btn-primary rounded-pill" type="button" >Test</button>
						</div>
						<div className="col-sm-12">
							<p class="text-muted py-2">eg: https:/ip_address_port</p>
							<p class="text-danger py-2">Note: Ensure RMH Central Server service must be running to test connection and save the settings!</p>
						</div>
						<div className="col-12 text-md-right py-2">
							<button className="d-inline-block btn btn-primary rounded-pill" type="button" onClick={() => props.saveCofigStore()}>Save</button>
						</div>

					</div>

				</div>
		</div>
	)
}