import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    isLoading: false,
    status: "",
    message: "",
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    allAPINameList:[]
    
};

export default function AdditionalSettingsReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
     
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:""
            }
        case types.GET_RESETALLSUPPLIER_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_RESETALLSUPPLIER_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_RESETALLSUPPLIER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ALLAPINAME_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ALLAPINAME_SUCCESS:
            return {
                ...state,
                allAPINameList: action.data.values,
                isLoading: false,
            }
        case types.GET_ALLAPINAME_FAILED:
            return {
                ...state,
                isLoading: false,
            }  

        case types.UPDATE_ISDISPLAYAPI_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.UPDATE_ISDISPLAYAPI_SUCCESS:
            return {
                ...state,
                isLoading: false,
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_ISDISPLAYAPI_FAILED:
            return {
                ...state,
                isLoading: false,
            }    
        default:
            return state;
    }
}