import { Component } from 'react';
import axios from "axios";
import { history } from '../history';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import apiGatewayEndpoint from '../config/index';
class AuthenticateServices extends Component {
    //#region login
    Login = async (formFields) => {
        return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "authentication/login", formFields).then((res) => {
            //on success
            if (res.data.code === "SH01")
                this.subscriptionFailed();
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion login
    //#region create XML
    CreateXML = async (formFields) => {

        //return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "authentication/createXMl", formFields).then((res) => {
            return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "ConfigSystem/createXMl", formFields).then((res) => {
            //on success
            if (res.data.code === "SH01")
                this.subscriptionFailed();
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion create xml 
    //#region configure system
    ConfigureSystem = async (formFields) => {

        //return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "authentication/configurSystem", formFields).then((res) => {
            return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "ConfigSystem/configurSystem", formFields).then((res) => {
            //on success
            if (res.data.code === "SH01")
                this.subscriptionFailed();
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion configure system 
    //#region load login page
    loadLoginPage = async () => {
       
        return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "authentication/loadLoginPage").then((res) => {
            //on success
            if (res.data.code === "SH01")
                this.subscriptionFailed();
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion load login page
    //#region logout
    Logout = (response) => {
        Cookies.set('sessionExpired', CryptoJS.AES.encrypt("true", process.env.REACT_APP_CryptojsSecretKey).toString());
        Cookies.remove('userDetails');
        Cookies.remove('jwtToken');
        Cookies.remove('styleLight');
        Cookies.remove('globalSearchValue');
        Cookies.remove('InventoryUser');
        // document.getElementById('style-lightdark').href = '/assets/css/style.css';
        //  swal({
        //        title:"Session expired",//response.status,
        //        text:"Your session is expired, Please login.",// response.message,
        //        icon: response.status.toLowerCase(),
        //        timer:3000,              
        //        button:false,
        //     })  
        history.push("/Login");
    }
    //#endregion logout
    subscriptionFailed = () => {
        history.push("/SubscriptionFailed");
    }
}
export default AuthenticateServices;