import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {history} from '../../../history';
export default function ThirdPartyAPIDataSetupComponent(props) {
	const {isLoading,isShowMapping,isDisplayAddMapping,apiColumnMappingList,rmhBigcommerceHandler,getalltax,updatemessage,updatestatus,dbColumnNameList,updateColumnMappingParam,valueInputOnchange,showEditMapping,deleteMapping,formFields,isShowTax,responseStatusProgress,inputChangeHandler,responseStatusComplete,ManageBigcommerce}=props;
	return (
		<>
		<div>
		<Helmet>
			<title>API Column Mapping | Modus Go!</title>
		</Helmet>
		{isLoading? <Loading />:""}
		<div className="bg-white header-shadow px-4 py-2">
				<div className="row align-items-center">
					<div className="col-lg-8 col-md-8">
						<span className="heading-text">Fetching all BigCommerce Product ID and update mapping with ModusGo Item ID</span>
					</div>
					<div className="col-lg-2 col-md-4 ">
						<h6 className={responseStatusComplete.toLowerCase()==="completed"? "value-green":"value-red"}>{responseStatusComplete}</h6>
			
					</div>
					<div className="col-lg-2 col-md-4 text-md-right py-2">
						<button className="d-inline-block btn btn-primary rounded-pill" disabled={responseStatusProgress==="In Progress"?true:false} type="button" onClick={props.APIData}>API Data</button>
					</div>



					<div className="col-lg-8 col-md-8">
						<span className="heading-text">Manage Bigcommerce Item History</span>
					</div>
				
					<div className="col-lg-4 col-md-4 text-md-right py-2">
					   <button className="d-inline-block btn btn-primary rounded-pill" type="button" onClick={() => history.push("/manageItemHistory")}>Manage History</button>
					</div>

					
					<div className="col-md-12 py-2">
						<span className="setting-highlight">Manage tax for bigcommerce</span>
					</div>
					<div className="col-md-4 col-6">
					<div className="custom-control custom-radio ml-2 my-2">
						<input type="radio" id="1" name="RHHBigcommerce" className="custom-control-input"  onChange={rmhBigcommerceHandler}/>
						<label className="custom-control-label" htmlFor="1">ModusGO</label>
					</div>
					</div>
					<div className="col-md-4 col-6">
						<div className="custom-control custom-radio ml-2 my-2">
						<input type="radio" id="2" name="RHHBigcommerce" className="custom-control-input" onChange={rmhBigcommerceHandler}/>
						<label className="custom-control-label" htmlFor="2">Bigcommerce</label>
						</div>
					</div>

					

					{isShowTax?
					<div className="col-md-4 col-12">
						<div className="floating-label">
							<select  className="floating-select" id="taxId" name="taxId" onChange={(e) => valueInputOnchange(e)}>
							<option key="D" value="">Select tax</option>
							{getalltax.map((tax) => <option key={tax.id} value={tax.id}>{tax.name}</option>)}
							</select>
							<span className="highlight"></span>
							<label>Tax </label>
						</div>
					</div>:""}
					<div className="col-12 text-md-right py-2">

						<button className="d-inline-block btn btn-primary rounded-pill" type="button" onClick={props.SaveSendRHMorAPIDetails}>Save</button>
					</div>
					
					{isDisplayAddMapping?
					
						<div className="col-lg-8 col-md-8">
								<span className="heading-text">Manage Bigcommerce API Fields with DB column Mapping</span>
						</div>
							:""}
					{isDisplayAddMapping?
						<div className="col-lg-4 col-md-4 ">
								<button className=" btn btn-primary rounded-pill" onClick={props.showAddMapping} type="button" >Add Mapping</button>
						</div>
							:""}
						
					
				
				</div>
				
			</div>
			</div>
			
				
				
{isShowMapping? 
<div>
<div className="bg-white header-shadow px-4 py-2">
					<div className="container-fluid d-none d-md-block">

						<div className="row mb-4">
							<div className="col-12 activity-title-block">
								<h2 className="text-left text-xl-center heading-text mt-2">Bigcommerce API Fields with DB column Mapping</h2>

							</div>
						</div>

					</div>
	                 <div className="bg-white header-shadow mt-3 p-3 mb-3">
	                        <div className="container pt-3 ml-1">
								<div className="row">
									<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
								</div>
							</div>
							<div className="row my-3">
								<div className="col-12">
									<span className="heading-text"><b>API Setting for: </b> {formFields.apiName}</span>
								</div>
							</div>
							
							<div className="row">
								<div className="col-md-6 col-12">
									<div className="floating-label">

										<input className="floating-input" disabled={updateColumnMappingParam.isFixedEntry} type="text" placeholder=" " name="apiColumnName" id="apiColumnName" onChange={(e) => inputChangeHandler(e)}  value={updateColumnMappingParam.apiColumnName}
										/>
										<span className="highlight"></span>
										<label>API Column Name</label>
									
									</div>
								</div>
								<div className="col-md-6 col-12">
								   <div className="floating-label">
										<select className="floating-select" tabIndex="2" id="dbColumnName" name="dbColumnName" defaultValue={updateColumnMappingParam.dbColumnName}  value={updateColumnMappingParam.dbColumnName} onChange={(e) => inputChangeHandler(e)} >
											<option key="I" value="0">Select DB Column Name</option> 
											{dbColumnNameList.map((apiName) => <option key={apiName.dbColumnName} value={apiName.dbColumnName} name={apiName.dbColumnName}>{apiName.dbColumnName}</option>)}
										
										</select>
										<span className="highlight"></span>
										<label>DB Column Name</label>
									</div>
								</div>
								<div className="itemscanbottom mt-2">
									<button className="btn rounded-pill btn-primary mr-1" id="btnScanPopupAccept" type="submit" onClick={() => props.createAPIColumnMapping()}>Save</button>
									<button type="button" className="btn rounded-pill btn-primary mr-1 closeitemscan-wrap" onClick={props.hideAddMapping}>Cancel</button></div>
							</div>
						</div>
						
						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblColumnMapping" className="table w-100" >
											<thead>
												<tr>
												<th>API Column Name</th>
												<th>DB Column Name</th>
												<th>Action</th>
												</tr>
											</thead>
											<tbody>

											{apiColumnMappingList.map((data) =>
													<tr>
														
														<td ><div >{data.apiColumnName}</div></td>
														<td ><div >{data.dbColumnName}</div></td>
														<td className="action-flex">
														<button type="button" disabled={data.isFixedEntry} tabIndex="0" className="ColumnMappingDelete round-icon-wrap border-red" data={data.id} onClick={()=>deleteMapping(data.id)} ><i className="zmdi zmdi-close red-icon"></i></button>
														<button type="button"  tabIndex="0" className="ColumnMappingEdit round-icon-wrap border-blue" data={data.id}><i className="zmdi zmdi-edit blue-icon" onClick={()=>showEditMapping(data.id)}></i></button>
													</td>
													</tr> )}
											</tbody>
										</table>
									</div>

								</div>
							</div>
						</div>
						</div>
						</div>
						:	
						""
				  
}
						
				
		
			

	</>
	)
}