import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import Autocomplete from 'react-autocomplete';
export default function PurchaseOrderComponent(props) {
	const {isLoading,purchaseOrderList,searchValue,searchData,requestParameter,isRender,isShowPOCDatatable,isDeleteBulkPO,selectAllDeleteBulkPO,
		isPONumber,isStatus,isSupplierName,isOrderNo,isDatePlaced,isSetPONumber,isSetStatus,isSetSupplierName,isSetOrderNo,isSetDatePlaced,
		isSetDeletePO}=props;


	return (
		<div>
			<Helmet>
				<title>Purchase order | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
				<div className="sticky-filter-datatable" onClick={props.togglePOCDatatable}>
					<img className="" src="/assets/images/filter-icon.svg" alt="" />
				</div>
				<div className={`datatable-filter${isShowPOCDatatable? " open":""}`}>
					<div className="close-datalist-popup" onClick={props.togglePOCDatatable}>
						<i className="zmdi zmdi-close-circle"></i>
					</div>
					<div className="datatable-head-list">
						<div className="datatable-list-title text-darkblue font-weight-bold">
							PO table
					</div>
						<ul>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isPONumber" name="isPONumber" onChange={props.setPONumberCheckbox} checked={isPONumber} />
									<label className="custom-control-label font-weight-bold" htmlFor="isPONumber">PO NUMBER</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isStatus" name="isStatus" onChange={props.setStatusCheckbox} checked={isStatus} />
									<label className="custom-control-label font-weight-bold" htmlFor="isStatus">STATUS</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isSupplierName" name="isSupplierName" onChange={props.setSupplierNameCheckbox} checked={isSupplierName} />
									<label className="custom-control-label font-weight-bold" htmlFor="isSupplierName">SUPPLIER NAME</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOrderNo" name="isOrderNo" onChange={props.setOrderNoCheckbox} checked={isOrderNo} />
									<label className="custom-control-label font-weight-bold" htmlFor="isOrderNo">ORDER NO.</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDatePlaced" name="isDatePlaced" onChange={props.setDatePlacedCheckbox} checked={isDatePlaced} />
									<label className="custom-control-label font-weight-bold" htmlFor="isDatePlaced">DATE PLACED</label>
								</div>
							</li>

						</ul>
					</div>
				</div>
				{isRender?
					<div className="main-content-area mobile-bottom-space" onClick={props.toggleHideOrderDatatable}>
						<div className="container-fluid d-none d-md-block">

							<div className="row">
								<div className="col position-relative px-0">
								<button className="position-absolute right-top btn btn-primary rounded-pill" type="button" onClick={props.DeleteBulkPO}>Delete Bulk PO</button>

									<h2 className="text-left text-xl-center heading-text">Purchase order</h2>
								</div>
							</div>
						</div>


						<div className="container-fluid d-block d-md-none">
							<div className="row">

								<div className="col p-0 mobile-header-info">
									<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
									<p className="text-darkblue font-weight-bold mobile-title-headline">Purchase order</p>
								</div>
							</div>
						</div>

						<div className="container-fluid mt-3">
							<div className="row">
								<div className="col p-0">
									<div className="searchbox-wrap header-shadow">
										<Autocomplete
											inputProps={{id: 'purchaseorderlist-autocomplete'}}
											wrapperStyle={{position: 'relative'}}
											//open={true}
											value={searchValue}
											items={searchData}
											getItemValue={(item) => item.key}
											inputProps={{placeholder: 'PO number or ref number or supplier order number or supplier name'}}
											onSelect={record => props.onSearchChangeHandler(record)}
											onChange={(event,value) => props.onSearchChangeHandler(value)}
											renderMenu={children => (
												<div className="search-menu">
													{children}
												</div>
											)}
											renderItem={(item,isHighlighted) => (
												<div
													key={item.key}
												>{item.value}</div>
											)}
										/>
										<div className="icon-box rounded-circle">
											<i className="material-icons">search</i>
										</div>

									</div>
								</div>
							</div>
						</div>

						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblPurchaseOrder" className="table w-100" >
											<thead>
												<tr>
													<th className="plus-sort"></th>

													<th style={{display: isSetPONumber? '':'none'}} className={requestParameter.sortColumnName==="poNumber"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("poNumber")}>PO NUMBER</th>

													<th style={{display: isSetStatus? '':'none'}}>STATUS</th>

													<th style={{display: isSetSupplierName? '':'none'}} className={requestParameter.sortColumnName==="supplierName"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("supplierName")}>SUPPLIER NAME</th>

													<th style={{display: isSetOrderNo? '':'none'}} className={requestParameter.sortColumnName==="supplierDocNo"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("supplierDocNo")}>ORDER NO.</th>

													<th style={{display: isSetDatePlaced? '':'none'}} className={requestParameter.sortColumnName==="datePlaced"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("datePlaced")}>DATE PLACED</th>
													<th style={{display: isSetDeletePO? '':'none'}} className={"sort-both"} >
															<div className="d-flex">
																<div className="apply-tax-table custom-control custom-checkbox top-head-btn mb-1">
																	<div className="mr-1 custom-checkbox ">
																		<input type="checkbox" className="custom-control-input" id="isDeleteBulkPO" onClick={(e)=>props.selectAllDeleteBulkPO(e,"all")} name="isDeleteBulkPO" checked={isDeleteBulkPO} />
																		<label className="custom-control-label font-weight-bold" htmlFor="isDeleteBulkPO"></label>
																	</div>
																</div><div>Delete PO</div></div>
														</th>
													<th >ACTION</th>

												</tr>
											</thead>
											<tbody>
												{purchaseOrderList.map((data) =>
													<tr key={data.id}>
														<td className="details-control plus-sort"></td>

														<td style={{display: isSetPONumber? '':'none'}} className="poNumber" onClick={() => props.onEditHandler(data.id)}>{data.poNumber}</td>

														<td style={{display: isSetStatus? '':'none'}}>Open</td>

														<td style={{display: isSetSupplierName? '':'none'}}>{data.supplierName}</td>

														<td style={{display: isSetOrderNo? '':'none'}}>{data.supplierDocNo}</td>

														<td style={{display: isSetDatePlaced? '':'none'}}>{data.datePlaced}</td>
													
													
														<td style={{display: isSetDeletePO? '':'none'}}>
															<div className="custom-control custom-checkbox">
																<input type="checkbox" className={"custom-control-input"}  id={data.id} name={data.id} onClick={(e)=>props.selectAllDeleteBulkPO(e,"single")} checked={data.isDeleteBulkPO} />
																<label className="custom-control-label" htmlFor={data.id}></label>
															</div>
														</td>
														
														<td ><button type="button" tabIndex="0" className="POGridEditIcon round-icon-wrap border-blue" data={data.id} ><i className="zmdi zmdi-edit blue-icon"></i></button></td>

													</tr>)}
											</tbody>
										</table>
									</div>

								</div>
							</div>
						</div>
					</div>
					:""}
			</div>
		</div>
	)
}