import axios from 'axios';
import apiGatewayEndpoint from '../config/index';
import Cookies from 'js-cookie';
import AuthenticateService from '../Services/AuthenticateServices';
const obj = new AuthenticateService();
export const post = (url, post_data) =>
    axios.post(
        apiGatewayEndpoint.apiGatewayEndpoint + url,
        post_data,
        {
            headers: { 'Authorization': `Bearer ${Cookies.get('jwtToken')}` }
        }
    ).then(data => {
        if (data.data.code === "SH01")
            obj.subscriptionFailed();
        if (data.data.code === "A01" || data.data.code === "A02")
            obj.Logout(data.data);

        if (data.status === 200) {
            return {
                status: data.status,
                payload: data.data
            };
        }
    }).catch(err => {
        return {
            status: (err && err.response) ? err.response.data : { error: "error" },
            payload: null
        };
    });

export const get = (url) =>
    axios.get(
        apiGatewayEndpoint.apiGatewayEndpoint + url,

        {
            headers: { 'Authorization': `Bearer ${Cookies.get('jwtToken')}` }
        }
    ).then(data => {
        if (data.data.code === "SH01")
            obj.subscriptionFailed();
        if (data.data.code === "A01" || data.data.code === "A02")
            obj.Logout(data.data);
        if (data.status === 200) {
            return {
                status: data.status,
                payload: data.data
            };
        }
    }).catch(err => {
        return {
            status: (err && err.response) ? err.response.data : { error: "error" },
            payload: null
        };
    });