import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { history } from '../../../../history'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
//#region get order item list
export function getInventoryItemList(data, isLoading) {
    return (dispatch) => {
        dispatch({
            type: types.GET_NONESCANNEDITEM_REQUEST,
            isLoading: isLoading
        });
        let url = 'Inventory/ListItemWithMinusQty';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblManageInventory').DataTable().destroy();
                    dispatch({
                        type: types.GET_NONESCANNEDITEM_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblManageInventory').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_NONESCANNEDITEM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
//#region set order item list param
export function setInventoryItemListParam(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_NONESCANNEDITEM_NONESCANNEDITEMLISTPARAM,
            data: data,


        });
    }
}
