//#region update item
export function updateQuantity(data, itemListParam) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_MANAGEQUANTITY_REQUEST,
        });
        let url = 'Inventory/updateItemQty';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getInventoryItemList(itemListParam, true));
                    dispatch({
                        type: types.UPDATE_MANAGEQUANTITY_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_MANAGEQUANTITY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
