import React from 'react';
import {
  gettransferOutList,setRequestParameter,
  getSearchList,setSearchRequestParameter,
  setSearchValue,setSearchData,
  setSearchTerm
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import {encryptURL} from '../../../Custom'
import swal from 'sweetalert';
import TransferOutComponent from '../component/transferOutComponent';

window.$=$;
function handlePOGridEditIcon() {
  $("#tbltransferOut").on('click','.POGridEditIcon',function() {
    var dataString=$(this).attr('data');
    window.open("/transferOutEdit/"+encryptURL(dataString),'_blank');
  });
}
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
function debounce(func,wait,immediate) {
  var timeout;

  return function executedFunction() {
    var context=this;
    var args=arguments;

    var later=function() {
      timeout=null;
      if(!immediate) func.apply(context,args);
    };

    var callNow=immediate&&!timeout;

    clearTimeout(timeout);

    timeout=setTimeout(later,wait);

    if(callNow) func.apply(context,args);
  };
};
class transferOutContainer extends React.Component {
  state={
    isRender: false,
    isShowPOCDatatable: false,
    isPONumber:true,
    isStatus:true,
    isSupplierName:true,
    isOrderNo:true,
    isDatePlaced:true,
    isAction:true,
    isSetPONumber:true,
    isSetStatus:true,
    isSetSupplierName:true,
    isSetOrderNo:true,
    isSetDatePlaced:true,
    isSetAction:true,
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    handlePOGridEditIcon();
    this.props.gettransferOutList(this.props.requestParameter,true);
    window.addEventListener('scroll',this.handleScroll);
    ColumnFilterCheckbox();
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tbltransferOut').DataTable().destroy();
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager) {
        // swal({
        //   title: "Warning",
        //   text: "Please get permission to view purchase orders.",
        //   icon: "warning",
        //   button: "Ok"
        // }).then(()=> {
        //   if (userDetails.role !== process.env.REACT_APP_UserRole_Admin)
        //   this.props.history.push("/Dashboard");
        // else
        //   this.props.history.push("/AdminConfigureDatabase");
        // });
        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        let pagesize=this.props.requestParameter.pageSize+50;
        let requestParameter={...this.props.requestParameter};
        requestParameter["pageSize"]=pagesize;
        this.props.setRequestParameter(requestParameter);
        this.props.gettransferOutList(requestParameter,false);
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading

  //#region search handler
  searchHandler=(record,cb) => {
    try {
      let getdata;
      // this.props.setSearchValue(record);
      record=record.trim();
      if(record!==""||this.props.requestParameter.searchText!=="") {
        let requestParameter={...this.props.requestParameter};
        requestParameter["searchText"]=record;
        this.props.setRequestParameter(requestParameter);
        this.props.gettransferOutList(requestParameter,true);
      }
      if(record!=="") {
        let searchRequestParameter={...this.props.searchRequestParameter};
        searchRequestParameter["searchText"]=record;
        this.props.setSearchRequestParameter(searchRequestParameter);
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata=[];
        this.props.setSearchData(getdata);
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion search handler
  //#region search change handler
  onSearchChangeHandler=(value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value,(items) => {
        this.props.setSearchData(items);
      })
    } catch(error) {
      console.log(error);
    }
  }
  setSearchTerm=debounce((searchTerm,cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm,cb);
    } catch(error) {
      console.log(error);
    }
  },1000)
  //#endregion search change handler

  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";

      switch(sortColumnName) {
        case "poNumber":
          sortdirection=this.props.requestParameter.sortColumnName!=="poNumber"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "supplierName":
          sortdirection=this.props.requestParameter.sortColumnName!=="supplierName"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "datePlaced":
          sortdirection=this.props.requestParameter.sortColumnName!=="datePlaced"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "supplierDocNo":
          sortdirection=this.props.requestParameter.sortColumnName!=="supplierDocNo"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        default:
          break;
      }
      var data={
        pageNo: this.props.requestParameter.pageNo,
        pageSize: this.props.requestParameter.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        searchText: this.props.requestParameter.searchText
      };
      var requestParameter={...this.props.requestParameter};
      requestParameter["sortColumnName"]=sortColumnName;
      requestParameter["sortDirection"]=sortdirection;
      this.props.setRequestParameter(requestParameter);
      this.props.gettransferOutList(data,false);
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler
  //#region edit handler
  onEditHandler=(id) => {
    window.open("/transferOutEdit/"+encryptURL(id),'_blank');
  }
  togglePOCDatatable=() => {this.setState({isShowPOCDatatable: !this.state.isShowPOCDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowPOCDatatable: false})}

  //#endregion edit handler


  //#region filter checkbox onchange function
  setPONumberCheckbox = () => {
    this.setState({ isPONumber: !this.state.isPONumber }, () => {
    this.setState({ isSetPONumber: this.state.isPONumber });
    });
  }

  setStatusCheckbox = () => {
    this.setState({ isStatus: !this.state.isStatus }, () => {
    this.setState({ isSetStatus: this.state.isStatus });
    });
  }

  setSupplierNameCheckbox = () => {
    this.setState({ isSupplierName: !this.state.isSupplierName }, () => {
    this.setState({ isSetSupplierName: this.state.isSupplierName });
    });
  }

  setOrderNoCheckbox = () => {
    this.setState({ isOrderNo: !this.state.isOrderNo }, () => {
    this.setState({ isSetOrderNo: this.state.isOrderNo });
    });
  }
  setDatePlacedCheckbox = () => {
    this.setState({ isDatePlaced: !this.state.isDatePlaced }, () => {
    this.setState({ isSetDatePlaced: this.state.isDatePlaced });
    });
  }
 
  //#endregion filter checkbox onchange function

  render() {
    // const { searchData } = this.props;   
    return (
      <TransferOutComponent
        {...this.props}
        searchHandler={this.searchHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        sortHandler={this.sortHandler}
        onEditHandler={this.onEditHandler}
        isRender={this.state.isRender}
        togglePOCDatatable={this.togglePOCDatatable}
        isShowPOCDatatable={this.state.isShowPOCDatatable}
        isPONumber={this.state.isPONumber}
        isStatus={this.state.isStatus}
        isSupplierName={this.state.isSupplierName}
        isOrderNo={this.state.isOrderNo}
        isDatePlaced={this.state.isDatePlaced}
        isAction={this.state.isAction}
        isSetPONumber={this.state.isSetPONumber}
        isSetStatus={this.state.isSetStatus}
        isSetSupplierName={this.state.isSetSupplierName}
        isSetOrderNo={this.state.isSetOrderNo}
        isSetDatePlaced={this.state.isSetDatePlaced}
        setPONumberCheckbox={this.setPONumberCheckbox}
        setStatusCheckbox={this.setStatusCheckbox}
        setSupplierNameCheckbox={this.setSupplierNameCheckbox}
        setOrderNoCheckbox={this.setOrderNoCheckbox}
        setDatePlacedCheckbox={this.setDatePlacedCheckbox}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    gettransferOutList: (data,isLoading) => dispatch(gettransferOutList(data,isLoading)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    setSearchRequestParameter: (data) => dispatch(setSearchRequestParameter(data)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),

  };
};

const mapStateToProps=(state) => {
  const {transferOutList,requestParameter,isLoading,searchValue,searchData,searchRequestParameter,SearchTerm}=state.TransferOutReducer;
  return {
    requestParameter: requestParameter,
    transferOutList: transferOutList.values.length>0? transferOutList.values:[],
    isLoading: isLoading,
    searchValue: searchValue,
    searchData: searchData.values.length>0? searchData.values:[],
    searchRequestParameter: searchRequestParameter,
    SearchTerm: SearchTerm,
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(transferOutContainer);