import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet';
import InventoryScanItemContainer from '../../inventoryScanItem/container/InventoryScanItemContainer';
import NewItemListContainer from '../../newItemList/container/NewItemListContainer';
import NoneScannedItemContainer from '../../noneScannedItem/container/NoneScannedItemContainer';
import DeltaContainer from '../../delta/container/DeltaContainer';
import Autocomplete from 'react-autocomplete';
import {Modal,ModalBody} from 'reactstrap';
import {Multiselect} from 'multiselect-react-dropdown';
import NewInventoryScanItemContainer from '../../newInventoryScanItem/container/NewInventoryScanItemContainer';
export default function FinalInventoryComponent(props) {

	const {isRender,isLoading,inventoryItemList,isInventoryScanItem,
		closeInventoryScanItem,inventoryItemListParam,searchValue,searchData,
		getInventoryItemList,userDetails,status,message,selectedInventory,
		isOpenAgreeModel,isAgree,categories,departments,suppliers,isCategoryDisabled,isShowFinalInventoryDatatable,
		isItemCode,isQuantity,isDescription,isDepartment,isCategory,isLastTime,
		isSetItemCode,isSetQuantity,isSetDescription,isSetDepartment,isSetCategory,isSetLastTime,
		isSetOnHandQuantity,isOnHandQuantity,filterColumn,isActualQuantity,isFinalQuantity}=props;

	return (
		<div>
			<Helmet><title>Final Inventory| Modus Go!</title></Helmet>

			{isLoading? <Loading />:""}
			{isRender?
				<div>
				{selectedInventory==="Final inventory" ?
					<div className="sticky-filter-datatable" onClick={props.toggleFinalInventoryDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>:''}
					{selectedInventory==="Final inventory" ?
					<div className={`datatable-filter${isShowFinalInventoryDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.toggleFinalInventoryDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
							</div>
							<div className="datatable-head-list" id="FITableColumnFilter">
								<div className="datatable-list-title text-darkblue font-weight-bold">
									Final inventory
							</div>
								<ul>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isItemCode" name="isChecked" onChange={props.setItemCodeCheckbox} checked={isItemCode} />
											<label className="custom-control-label font-weight-bold" htmlFor="isItemCode">ITEM CODE</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isQuantity" name="isChecked" onChange={props.setQuantityCheckbox} checked={isQuantity} />
											<label className="custom-control-label font-weight-bold" htmlFor="isQuantity">QUANTITY</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isActualQuantity" name="isChecked" onChange={props.setActualQuantityCheckbox} checked={isActualQuantity} />
											<label className="custom-control-label font-weight-bold" htmlFor="isActualQuantity">ACTUAL QUANTITY</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isFinalQuantity" name="isChecked" onChange={props.setFinalQuantityCheckbox} checked={isFinalQuantity} />
											<label className="custom-control-label font-weight-bold" htmlFor="isFinalQuantity">FINAL QUANTITY</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDescription" name="isChecked" onChange={props.setDescriptionCheckbox} checked={isDescription} />
											<label className="custom-control-label font-weight-bold" htmlFor="isDescription">DESCRIPTION</label>
										</div>
									</li>

									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDepartment" name="isChecked" onChange={props.setDepartmentCheckbox} checked={isDepartment} />
											<label className="custom-control-label font-weight-bold" htmlFor="isDepartment">DEPARTMENT</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCategory" name="isChecked" onChange={props.setCategoryCheckbox} checked={isCategory} />
											<label className="custom-control-label font-weight-bold" htmlFor="isCategory">CATEGORY</label>
										</div>
									</li>
									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isLastTime" name="isChecked" onChange={props.setLastTimeCheckbox} checked={isLastTime} />
											<label className="custom-control-label font-weight-bold" htmlFor="isLastTime">LAST TIME</label>
										</div>
									</li>

									<li>
										<div className="custom-control custom-checkbox">
											<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOnHandQuantity" name="isChecked" onChange={props.setOnHandQuantityCheckbox} checked={isOnHandQuantity} />
											<label className="custom-control-label font-weight-bold" htmlFor="isOnHandQuantity">QTY ON HAND</label>
										</div>
									</li>
								</ul>
							</div>
					</div>
						:''}
					<div className="main-content-area mt-md-3 mt-auto mobile-bottom-space" onClick={props.toggleHideOrderDatatable}>
						<div className="container-fluid d-block d-md-none">
							<div className="row">
								{/* <div className="col position-relative p-0">
									<p className="text-darkblue font-weight-bold">{selectedInventory}</p>
								</div> */}
								<div className="col p-0 mobile-header-info">
									<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
									<p className="text-darkblue font-weight-bold mobile-title-headline">{selectedInventory}</p>
								</div>
							</div>
						</div>
						<div className="container-fluid ">
							<div className="row">
								<div className="col position-relative inventoryFilter px-0">

									<div className="order-list-wrap py-0">

										{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <div>
											<div className="checkbox-dropdown w-220 position-absolute right-top " tabIndex="0">
												{selectedInventory}
											</div>
											<div id="open-check-list" className="">
												<ul className="checkbox-dropdown-list finalInventoryFilter right-1">
													<li className={selectedInventory==="Final inventory"? "bg-autosearchValue":""} onClick={() => props.setSelectedInventory("Final inventory")}>
														<label className="control--checkbox">Final inventory</label>
													</li>
													<li className={selectedInventory==="New item list"? "bg-autosearchValue":""} onClick={() => props.setSelectedInventory("New item list")}>
														<label className="control--checkbox">New item list</label>
													</li>
													<li className={selectedInventory==="Delta"? "bg-autosearchValue":""} onClick={() => props.setSelectedInventory("Delta")}>
														<label className="control--checkbox">Delta</label>
													</li>
													<li className={selectedInventory==="None scanned item"? "bg-autosearchValue":""} onClick={() => props.setSelectedInventory("None scanned item")}>
														<label className="control--checkbox">None scanned item</label>
													</li>
												</ul>
											</div>
										</div>:''}

										<h2 className="text-left text-xl-center heading-text d-none d-md-block">{selectedInventory}</h2>

									</div>


								</div>
							</div>
						</div>




						{selectedInventory==="Final inventory"?
							<div>
								<div className="container-fluid p-0">

									<NewInventoryScanItemContainer isFromFinal={true} getInventoryItemList={getInventoryItemList} closeInventoryScanItem={closeInventoryScanItem} />
								</div>

								<div className="" id="DivDescriptionID">
									<div className="container-fluid mt-3">
										<div className="row">
											<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
										</div>
									</div>
									{/* <div className={`container-fluid mt-3 ${selectedInventory!=="New item list"? " ":" display-none"}`}>
										<div className="row">
											<div className="col-lg-12 col-md-12 col-sm-12">
												<div className="row bg-white header-shadow py-3 align-items-center">
													<div className="col-lg-12 col-md-12 col-sm-12">
														<div className="row">
															<div className="col-lg-3 col-md-12 col-12 mt-3 ">
																<div className="floating-label mb-0">
																	<Multiselect className="floating-select"
																		options={departments}
																		displayValue="name"
																		selectedValues={inventoryItemListParam.departmentIds}
																		onSelect={props.selectDepartmentHandler}
																		onRemove={props.removeDepartmentHandler}
																		placeholder="Select department"
																		name="departmentIds"
																		id="departmentIds"
																		avoidHighlightFirstOption={true}
																		showCheckbox={true}
																		hidePlaceholder={true}
																		closeOnSelect={false}
																	/>

																	<span className="highlight"></span>
																	<label >Department</label>
																</div>
															</div>
															<div className="col-lg-3 col-md-12 col-12 mt-3 ">
																<div className="floating-label mb-0">
																	<Multiselect className="floating-select"
																		options={categories}
																		displayValue="name"
																		selectedValues={inventoryItemListParam.categoryIds}
																		onSelect={props.selectCategoryHandler}
																		onRemove={props.removeCategoryHandler}
																		placeholder="None"
																		name="categoryIds"
																		id="categoryIds"
																		avoidHighlightFirstOption={true}
																		showCheckbox={true}
																		disable={isCategoryDisabled}
																		hidePlaceholder={true}
																		closeOnSelect={false}
																	/>
																	<span className="highlight"></span>
																	<label >Category</label>
																</div>
															</div>
															<div className="col-lg-3 col-md-12 col-12 mt-3 ">
																<div className="floating-label mb-0">
																	<Multiselect className="floating-select"
																		options={suppliers}
																		displayValue="supplierName"
																		selectedValues={inventoryItemListParam.supplierIds}
																		onSelect={props.selectSupplierHandler}
																		onRemove={props.removeSupplierHandler}
																		placeholder="Select supplier"
																		name="supplierIds"
																		id="supplierIds"
																		avoidHighlightFirstOption={true}
																		showCheckbox={true}
																		hidePlaceholder={true}
																		closeOnSelect={false}
																	/>
																	<span className="highlight"></span>
																	<label >Supplier</label>
																</div>
															</div>
															<div className="col-lg-1 col-md-2 col-2 mt-3">
																<div className="icon-box icon-box1 rounded-circle left-0 left-15" onKeyPress={(e) => {
																	if(e.key==="Enter")
																		props.filterHandler();
																}} tabIndex="0" onClick={props.filterHandler}>
																	<i className="material-icons">search</i>
																</div>

															</div>

															<div className="col-lg-2 col-md-10 col-10 mt-3">
																<button className="btn btn-primary rounded-pill text-center mr-2" onMouseDown={e => e.preventDefault()} type="button" onClick={props.clearFilterHandler}>Clear filter</button>
															</div>
														</div>
													</div>
												</div>
											</div>

										</div>
									</div> */}
									<div className="container-fluid mt-3">
										<div className="row">
											<div className="col p-0">
												<div className="searchbox-wrap header-shadow">
													<Autocomplete
														inputProps={{id: 'inventoryItemList-autocomplete'}}
														wrapperStyle={{position: 'relative'}}
														value={searchValue}
														items={searchData}
														getItemValue={(item) => item.key}
														inputProps={{placeholder: 'Item lookup code or description'}}
														onSelect={record => props.onSearchChangeHandler(record)}
														onChange={(event,value) => props.onSearchChangeHandler(value)}
														renderMenu={children => (
															<div className="search-menu">
																{children}
															</div>
														)}
														renderItem={(item,isHighlighted) => (
															<div
																key={item.key}
															>{item.value}</div>
														)}
													/>
													<div className="icon-box rounded-circle">
														<i className="material-icons">search</i>
													</div>

												</div>
											</div>
										</div>
									</div>
								</div>


								<div className="container-fluid bg-white header-shadow mt-4 po-bottom1">
									<div className="row my-3">
										<div className="col-12 p-0">
											<div className="table-responsive">
												<table id="tblInventoryItem" className="table w-100 new-order-list wo-collasped" >
													<thead>
														<tr>
															<th className="plus-sort"></th>
														
																<th style={{display: filterColumn.isSetItemCode ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="itemLookupCode"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetItemCode":"sort-bottom isSetItemCode":"sort-both isSetItemCode"} onClick={() => props.sortHandler("itemLookupCode")}>ITEM CODE</th>
															
																<th style={{display: filterColumn.isSetQuantity ? '' : 'none' }}  className={inventoryItemListParam.sortColumnName==="quantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetQuantity":"sort-bottom isSetQuantity":"sort-both isSetQuantity"} onClick={() => props.sortHandler("quantity")}>QUANTITY</th>
																<th style={{display: filterColumn.isSetActualQuantity ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="actualQuantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetActualQuantity":"sort-bottom isSetActualQuantity":"sort-both isSetActualQuantity"} onClick={() => props.sortHandler("actualQuantity")}>Actual Quantity</th>
																<th style={{display: filterColumn.isSetFinalQuantity ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="finalQuantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetFinalQuantity":"sort-bottom isSetFinalQuantity":"sort-both isSetFinalQuantity"} onClick={() => props.sortHandler("finalQuantity")}>Final Quantity</th>
																<th style={{display: filterColumn.isSetDescription ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="description"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetDescription":"sort-bottom isSetDescription":"sort-both isSetDescription"} onClick={() => props.sortHandler("description")}>Description</th>
															
																<th style={{display: filterColumn.isSetDepartment ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="department"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetDepartment":"sort-bottom isSetDepartment":"sort-both isSetDepartment"} onClick={() => props.sortHandler("department")}>Department</th>
															
																<th style={{display: filterColumn.isSetCategory ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="category"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetCategory":"sort-bottom isSetCategory":"sort-both isSetCategory"} onClick={() => props.sortHandler("category")}>Category</th>
																
																<th style={{display: filterColumn.isSetLastTime ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="lastTime"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetLastTime":"sort-bottom isSetLastTime":"sort-both isSetLastTime"} onClick={() => props.sortHandler("lastTime")}>Last Time</th>
																
														    	<th style={{display: filterColumn.isSetOnHandQuantity ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="onHandQuantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetOnHandQuantity":"sort-bottom isSetOnHandQuantity":"sort-both isSetOnHandQuantity"} onClick={() => props.sortHandler("onHandQuantity")}>Qty On Hand</th>
																
															{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <th className=""><span className="d-md-none">Delete</span></th>:''}
														</tr>
													</thead>
													<tbody>
														{inventoryItemList.map((itemList,index) =>
															<tr key={"tblInventoryItem"+index}>
																<td className="details-control plus-sort" tabIndex="-1"></td>
															
																	<td style={{display: filterColumn.isSetItemCode ? '' : 'none' }} className="default-cursor isSetItemCode">{itemList.itemLookupCode}</td>
																	
																	<td style={{display: filterColumn.isSetQuantity ? '' : 'none' }} className="default-cursor isSetQuantity">{itemList.quantity}</td>
																	<td style={{display: filterColumn.isSetActualQuantity ? '' : 'none' }} className="default-cursor isSetActualQuantity">{itemList.actualQuantity}</td>
																	<td style={{display: filterColumn.isSetFinalQuantity ? '' : 'none' }} className="default-cursor isSetFinalQuantity">{itemList.finalQuantity}</td>
																	
																	<td style={{display: filterColumn.isSetDescription ? '' : 'none' }} className="default-cursor isSetDescription">{itemList.description}</td>
																	
																	<td style={{display: filterColumn.isSetDepartment ? '' : 'none' }} className="default-cursor isSetDepartment">{itemList.department}</td>
																
																	<td style={{display: filterColumn.isSetCategory ? '' : 'none' }} className="default-cursor isSetCategory">{itemList.category}</td>
																	
																	<td style={{display: filterColumn.isSetLastTime ? '' : 'none' }} className="default-cursor isSetLastTime">{itemList.lastTime}</td>
																	
																<td style={{display: filterColumn.isSetOnHandQuantity ? '' : 'none' }} className="default-cursor isSetOnHandQuantity">{itemList.onhandQuantity}</td>
																
																{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <td><button disabled={userDetails.role===process.env.REACT_APP_UserRole_Cashier? true:false} type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del InventoryItemGridDeleteIcon" data={itemList.itemId}><i className="zmdi zmdi-delete "></i></button></td>:''}
															</tr>
														)}
													</tbody>
												</table>

											</div>
											{userDetails.role!==process.env.REACT_APP_UserRole_Cashier? <div className="footer-box">
												<button className="btn btn-outline-primary mx-1 d-none" type="button" onMouseDown={e => e.preventDefault()} onClick={props.showInventoryScanItem}>Scan item</button>
												<button className="itemscanshow btn btn-outline-primary mx-1" type="button" >Scan item</button>
												{/* <button className="btn btn-outline-primary mx-1" type="button" onMouseDown={e => e.preventDefault()} onClick={props.commitInventoryHandler} disabled={inventoryItemList.length>0? false:true}>Cycle commit </button> */}
												 <button className="btn btn-outline-primary mx-1" type="button" onMouseDown={e => e.preventDefault()} onClick={props.newcommitInventoryHandler} disabled={inventoryItemList.length>0? false:true}>Cycle commit </button> 
												 <button className="btn btn-outline-primary mx-1" type="button" onMouseDown={e => e.preventDefault()} onClick={props.newFullcommitInventoryHandler} disabled={inventoryItemList.length>0? false:true}>Full Cycle commit </button> 
											
											</div>:''}
										</div>

									</div>
								</div>
								{/* <!-- Agree model --> */}
								<Modal isOpen={isOpenAgreeModel} centered={true} >

									<ModalBody>
										<div className="swal-icon swal-icon--warning">
											<span className="swal-icon--warning__body">
												<span className="swal-icon--warning__dot"></span>
											</span>
										</div>
										<div className="swal-title" >Warning</div>
										<div className="swal-text swal-custom-center" >If you commit none scanned item, it will reset all none scanned item quantity as 0.</div>
										<div className="swal-content swal-custom-center">
											<div className="custom-control custom-checkbox">
												<input type="checkbox" className="custom-control-input" id="isAgree" name="isAgree" onChange={(e) => props.isAgreeCHKHandler(e)} />
												<label className="custom-control-label" htmlFor="isAgree">I agree to commit none scanned item.</label>
											</div>
										</div>
										<div className="swal-footer">
											<div className="swal-button-container">
												<button disabled={!isAgree} className="swal-button swal-button--confirm swal-button--danger" type="button" onClick={props.commitNoneScannedHandler} >Yes</button>
											</div>
											<div className="swal-button-container">
												<button type="button" className="swal-button swal-button--cancel" onClick={props.toggleAgreeModel}>No</button>
											</div>
										</div>

									</ModalBody>

								</Modal>
								{/* <!-- Agree model --> */}

							</div>:''}
						{selectedInventory==="New item list"? <NewItemListContainer isSetItemCode={isSetItemCode}/>:''}
						{selectedInventory==="Delta"? <DeltaContainer />:''}
						{selectedInventory==="None scanned item"? <NoneScannedItemContainer />:''}
					</div>
				</div>
				:""}
			{isInventoryScanItem? <InventoryScanItemContainer isFromFinal={true} getInventoryItemList={getInventoryItemList} closeInventoryScanItem={closeInventoryScanItem} />:''}

		</div>
	)
}
