import React from 'react';
import ActivityLogComponent from '../component/ActivityLogComponent';
import {
  getActivityLogList,setRequestParameter,
  clearActivityLog,getUserList
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import moment from 'moment';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
class ActivityLogContainer extends React.Component {
  state={
    inpDate: null,
    searchDate: null,
    clearActivityLogFilterBoxClass: "",
    isUserName:true,
    isDateTime:true,
    isModule:true,
    isActivity:true,
    isIPAddress:true,
    isDevice:true,
    isSetUserName:true,
    isSetDateTime:true,
    isSetModule:true,
    isSetActivity:true,
    isSetIPAddress:true,
    isSetDevice:true
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.props.getUserList();
    this.props.getActivityLogList(this.props.requestParameter,true);
    window.addEventListener('scroll',this.handleScroll);
    window.addEventListener('mouseup',this.handleMouseUp);
    ColumnFilterCheckbox();
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    window.removeEventListener('mouseup',this.handleMouseUp);
    $('#tblActivityLog').DataTable().destroy();
  }
  setClearActivityLogFilterBoxClass=() => {

    if(this.state.clearActivityLogFilterBoxClass==="")
      this.setState({clearActivityLogFilterBoxClass: "open-checkbox"});
    else {
      this.setState({clearActivityLogFilterBoxClass: "",inpDate: null});
    }
  }
  //#region handle mouse up event to close filter box
  handleMouseUp=(e) => {
    try {

      const menu=$("#open-check");
      if(!menu.is(e.target) // if the target of the click isn't the container...
        &&menu.has(e.target).length===0&&e.target.classList[0]!=="checkbox-dropdown") // ... nor a descendant of the container
      {
        this.setState({clearActivityLogFilterBoxClass: "",inpDate: null});
      }
    } catch(error) {
      console.log(error);

    }
  }
  //#endregion handle mouse up event to close filter box
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Admin) {
        this.props.history.push("/NotFound");
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  filterKeyPressHandler=(e) => {
    if(e.key==="Enter")
      this.setClearActivityLogFilterBoxClass()
  }
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        let pagesize=this.props.requestParameter.pageSize+50;
        let requestParameter={...this.props.requestParameter};
        requestParameter["pageSize"]=pagesize;
        this.props.setRequestParameter(requestParameter);
        this.props.getActivityLogList(requestParameter,false);
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading

  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";

      switch(sortColumnName) {
        case "userName":
          sortdirection=this.props.requestParameter.sortCol!=="userName"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        case "createdOn":
          sortdirection=this.props.requestParameter.sortCol!=="createdOn"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        case "module":
          sortdirection=this.props.requestParameter.sortCol!=="module"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        case "deviceName":
          sortdirection=this.props.requestParameter.sortCol!=="deviceName"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        case "action":
          sortdirection=this.props.requestParameter.sortCol!=="action"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        case "ipAddress":
          sortdirection=this.props.requestParameter.sortCol!=="ipAddress"? "asc":this.props.requestParameter.sortDir==="asc"? "desc":"asc";

          break;
        default:
          break;
      }
      var data={
        pageNo: this.props.requestParameter.pageNo,
        pageSize: this.props.requestParameter.pageSize,
        sortCol: sortColumnName,
        sortDir: sortdirection,
        searchUserId: this.props.requestParameter.searchUserId,
        searchDate: this.props.requestParameter.searchDate!==""&&this.props.requestParameter.searchDate!==null? moment(this.props.requestParameter.searchDate).format('YYYY-MM-DD'):null,
      };
      var requestParameter={...this.props.requestParameter};
      requestParameter["sortCol"]=sortColumnName;
      requestParameter["sortDir"]=sortdirection;
      this.props.setRequestParameter(requestParameter);
      this.props.getActivityLogList(data,false);
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler
  //#region inpDate change handler
  inpDateChangeHandler=date => {
    try {
      this.setState({inpDate: date});
    } catch(error) {
      console.log(error);

    }

  };

  //#endregion inpDate change handler
  //#region ClearActivityLog handler
  ClearActivityLogHandler=() => {
    try {
      if(this.state.inpDate===null) {
        swal({
          title: "Error",
          text: types.REQUIRED_CLEARLOGDATE_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else {
        if(this.props.activityLogList.length>0) {
          var param={
            inpDate: this.state.inpDate!==""&&this.state.inpDate!==null? moment(this.state.inpDate).format('YYYY-MM-DD'):null
          };

          swal({
            title: types.DELETE_TITLE,
            text: types.CONFIRMCLEAR_ACTIVITYLOG_MESSAGE,
            icon: "warning",
            buttons: {
              confirm: true,
              cancel: true
            },
            dangerMode: true,
          })
            .then((willDelete) => {
              if(willDelete) {

                this.props.clearActivityLog(param,this.props.requestParameter);
                this.setState({inpDate: null});
              }
            });
        }
        else {
          swal({
            title: "Warning",
            text: "There is no data available for clear.",
            icon: "warning",
            buttons: "Ok",

          })

        }
      }
    } catch(error) {
      console.log(error);

    }
  }
  //#endregion ClearActivityLog handler
  //#region search date change handler
  searchDateChangeHandler=date => {
    try {
      var requestParameter={...this.props.requestParameter};
      requestParameter["searchDate"]=date!==""&&date!==null? moment(date).format('YYYY-MM-DD'):null;
      requestParameter["pageSize"]=50;
      this.setState({searchDate: date});
      this.props.setRequestParameter(requestParameter);
    } catch(error) {
      console.log(error);

    }

  };
  //#endregion search date chage handler
  //#region search user change handler
  searchUserChangeHandler=(e) => {
    try {
      var requestParameter={...this.props.requestParameter};
      requestParameter["searchUserId"]=e.target.value;
      requestParameter["pageSize"]=50;
      this.props.setRequestParameter(requestParameter);
    } catch(error) {
      console.log(error);

    }
  }
  //#endregion search user change handler
  //#region  clear search handler
  clearSearchHandler=() => {
    try {
      var requestParameter={...this.props.requestParameter};
      requestParameter["searchUserId"]="";
      this.setState({searchDate: null});
      requestParameter["searchDate"]=null;
      requestParameter["pageSize"]=50;
      this.props.setRequestParameter(requestParameter);
      this.props.getActivityLogList(requestParameter,true);
    } catch(error) {
      console.log(error);

    }
  }
  //#endregion clear search handler
  //#region search handler
  searchHandler=() => {
    if((this.props.requestParameter.searchDate===null||this.props.requestParameter.searchDate==="")&&this.props.requestParameter.searchUserId==="")
      swal({
        title: "Error",
        text: types.REQUIRED_SEARCHACTIVITYLOG_MESSAGE,
        icon: "error",
        button: "Ok",
      })
    else {
      this.props.getActivityLogList(this.props.requestParameter,true);
      console.log("request:"+this.props.requestParameter.searchDate);
    }
  }
  //#endregion search handler
  searchIconKeyPress=(e) => {
    if(e.key==="Enter")
      this.searchHandler();
  }
  toggleActivityLogDatatable=() => {this.setState({isShowActivityLogDatatable: !this.state.isShowActivityLogDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowActivityLogDatatable: false})}

 //#region filter checkbox onchange function

 setUserNameCheckbox = () => {
    this.setState({ isUserName: !this.state.isUserName }, () => {
    this.setState({ isSetUserName: this.state.isUserName });
    });
  }
  
  setDateTimeCheckbox = () => {
    this.setState({ isDateTime: !this.state.isDateTime }, () => {
    this.setState({ isSetDateTime: this.state.isDateTime });
    });
  }
  setModuleCheckbox = () => {
    this.setState({ isModule: !this.state.isModule }, () => {
    this.setState({ isSetModule: this.state.isModule });
    });
  }
  setActivityCheckbox = () => {
    this.setState({ isActivity: !this.state.isActivity }, () => {
    this.setState({ isSetActivity: this.state.isActivity });
    });
  }
  setDeviceCheckbox = () => {
    this.setState({ isDevice: !this.state.isDevice }, () => {
    this.setState({ isSetDevice: this.state.isDevice });
    });
  }
  setIPAddressCheckbox = () => {
    this.setState({ isIPAddress: !this.state.isIPAddress }, () => {
    this.setState({ isSetIPAddress: this.state.isIPAddress });
    });
  }
  //#endregion filter checkbox onchange function
  
  render() {
    //const { activityLogList } = this.props;   
    return (
      <ActivityLogComponent
        {...this.props}
        searchIconKeyPress={this.searchIconKeyPress}
        sortHandler={this.sortHandler}
        inpDate={this.state.inpDate}
        searchDate={this.state.searchDate}
        inpDateChangeHandler={this.inpDateChangeHandler}
        ClearActivityLogHandler={this.ClearActivityLogHandler}
        searchDateChangeHandler={this.searchDateChangeHandler}
        searchUserChangeHandler={this.searchUserChangeHandler}
        searchHandler={this.searchHandler}
        clearActivityLogFilterBoxClass={this.state.clearActivityLogFilterBoxClass}
        setClearActivityLogFilterBoxClass={this.setClearActivityLogFilterBoxClass}
        clearSearchHandler={this.clearSearchHandler}
        filterKeyPressHandler={this.filterKeyPressHandler}
        toggleActivityLogDatatable={this.toggleActivityLogDatatable}
        isShowActivityLogDatatable={this.state.isShowActivityLogDatatable}

        isUserName={this.state.isUserName}
        isDateTime={this.state.isDateTime}
        isModule={this.state.isModule}
        isActivity={this.state.isActivity}
        isIPAddress={this.state.isIPAddress}
        isDevice={this.state.isDevice}
        isSetUserName={this.state.isSetUserName}
        isSetDateTime={this.state.isSetDateTime}
        isSetModule={this.state.isSetModule}
        isSetActivity={this.state.isSetActivity}
        isSetIPAddress={this.state.isSetIPAddress}
        isSetDevice={this.state.isSetDevice}
        setIPAddressCheckbox={this.setIPAddressCheckbox}
        setDeviceCheckbox={this.setDeviceCheckbox}
        setActivityCheckbox={this.setActivityCheckbox}
        setModuleCheckbox={this.setModuleCheckbox}
        setDateTimeCheckbox={this.setDateTimeCheckbox}
        setUserNameCheckbox={this.setUserNameCheckbox}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
          />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getActivityLogList: (data,isLoading) => dispatch(getActivityLogList(data,isLoading)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    clearActivityLog: (param,requestParameter) => dispatch(clearActivityLog(param,requestParameter)),
    getUserList: () => dispatch(getUserList()),
  };
};

const mapStateToProps=(state) => {
  const {activityLogList,userList,requestParameter,isLoading,requestedSearchDate}=state.ActivityLogReducer;
  return {
    requestParameter: requestParameter,
    activityLogList: activityLogList.values.length>0? activityLogList.values:[],
    userList: userList.values.length>0? userList.values:[],
    isLoading: isLoading,
    requestedSearchDate: requestedSearchDate,

  };
};

export default connect(mapStateToProps,mapDispatchToProps)(ActivityLogContainer);