import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        searchText: "",
        sortColumnName: "",
        sortDirection: "",
        pageNo: 0,
        pageSize: 50,

    },
    purchaseOrderList: [],
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status:"",
    isDeleteBulkPO:false
};

export default function PurchaseOrderReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_PURCHASEORDERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_PURCHASEORDERS_SUCCESS:
            return {
                ...state,
                purchaseOrderList: action.data.values,
                isLoading: false,

            }
        case types.GET_PURCHASEORDERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_PURCHASEORDERSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_PURCHASEORDERSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_PURCHASEORDERSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERSLIST:
          
            return {
                ...state,
                purchaseOrderList: action.data,
                isDeleteBulkPO:action.isChecked

            }
        case types.GET_DELETEBULKPO_REQUEST:
            return {
                ...state,
                isLoading: true,
               
            }
        case types.GET_DELETEBULKPO_SUCCESS:
            return {
                ...state,
                status: action.data,
                isLoading: false,
                isDeleteBulkPO:false
            }
        case types.GET_DELETEBULKPO_FAILED:
            return {
                ...state,
                isLoading: false,
            }   
            
        default:
            return state;
    }
}