import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
import {useForm} from "react-hook-form";
import {viewPassword} from '../../../Custom';
export default function InventoryListComponent(props) {
	const {isRender,isLoading,InventoryList,userDetails,isOpenLogin,formFields,inputChangeHandler,isCombine,AllInventoryCount,iscreateNewInventory}=props;

	const {register,errors,handleSubmit}=useForm()

	const onSubmit=(data) => {
		props.formHandler({...data});
	};
	return (
		<div>
			<Helmet>
				<title>Inventory-list | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			{isRender&&!isOpenLogin?
				<div>
					<div className="add-item-btn-mobile d-md-none" onClick={props.createNewInventoryHandler}>
						<i className="material-icons">add</i>
					</div>
					<div className="main-content-area pt-4 mobile-bottom-space">
						<div className="container-fluid d-none d-md-block">
							<div className="row">
								<div className="col position-relative px-0">
									<button className={`position-absolute mr-3 btn btn-primary rounded-pill ${userDetails.role!==process.env.REACT_APP_UserRole_Manager? "  right-top":" right-top"}`} type="button" onClick={props.createNewInventoryHandler}>Create inventory</button>
									{/* {userDetails.role === process.env.REACT_APP_UserRole_Manager?<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.combineInventoryHandler} disabled={(userDetails.role === process.env.REACT_APP_UserRole_Manager && isCombine)?false:true}>Combine inventory</button>:''}  */}


									{InventoryList.length>0?
										<div className='d-none'>

											{userDetails.role===process.env.REACT_APP_UserRole_Manager&&InventoryList.length===1?
												<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={() => props.combineInventoryHandler("S",AllInventoryCount)} >Final inventory</button>:

												<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={() => props.combineInventoryHandler("M",AllInventoryCount)} >Combine inventory</button>
											}

										</div>
										:
										''
									}






									<h2 className="text-left text-xl-center heading-text">Inventory list</h2>
								</div>
							</div>
						</div>

						<div className="container-fluid d-block d-md-none">
							<div className="row">
								<div className="col p-0">
									<p className="text-darkblue font-weight-bold">Inventory list</p>
									{/* {userDetails.role === process.env.REACT_APP_UserRole_Manager?<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.combineInventoryHandler} disabled={(userDetails.role === process.env.REACT_APP_UserRole_Manager && isCombine)?false:true}>Combine inventory</button>:''} */}
									{userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.combineInventoryHandler} disabled={(userDetails.role===process.env.REACT_APP_UserRole_Manager&&isCombine)? false:true}>Combine inventory</button>:''}
								</div>
							</div>
						</div>


						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12">
									<div className="order-list-wrap">
										<div className="order-list-record">
											<ul>
												{InventoryList.length>0? InventoryList.map((list) =>
													<li key={list.inventoryId}>
														<div onClick={() => props.onRowClickHandler(list.inventoryId)} className="order-list-title">{list.name}</div>
														<div onClick={() => props.onRowClickHandler(list.inventoryId)} className="order-list-info"><span><i className="zmdi zmdi-account-circle"></i></span> {list.userName}</div>
														{list.isDeleted? <div className="delete-btn-wrap text-danger" onClick={() => props.deleteInventoryHandler(list.inventoryId)}><i className="zmdi zmdi-close-circle-o mr-1"></i> Remove</div>:""}


													</li>
												):<li><div className="order-list-empty text-center">No data found</div></li>}
												{/* {AllInventoryCount} */}
											</ul>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				:""}
			<Modal backdrop="static" isOpen={isOpenLogin} toggle={props.toggleLoginModel} centered={true}>

				<ModalBody className="mainbox box-shadow-none">
					<button type="button" className="close" aria-label="Close" onClick={props.toggleLoginModel}><span aria-hidden="true"></span></button>
					<div className="p-3 pl-4 ">
						<div className="row">
							<h2 className="login-title text-gray">Login with inventory !!!</h2>
							<form onSubmit={handleSubmit(onSubmit)}>
								<div className="col-12">

									<div className="floating-label">
										<input type="password" className="floating-input" placeholder=" "
											ref={register({required: true,maxLength: 50})}
											id="password"
											name="password"
											defaultValue={formFields.password}
											onChange={(e) => inputChangeHandler(e)}
										/><span className="highlight"></span>
										<label>Password *</label>
										<span className="eye-icon"><i id="pass-status" className="zmdi zmdi-eye-off zmdi-hc-fw" onClick={()=>viewPassword("")}></i></span>
										<span className="d-block text-danger db-error">{errors.password&&"Please enter the password."}{errors.password&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
									</div>
								</div>
								<div className="col-12 d-flex justify-content-end">
									<button className="btn rounded-pill btn-primary" type="submit" >Login</button>
								</div>
							</form>
						</div>
					</div>

				</ModalBody>

			</Modal>

			{/* <!-- added description field --> */}


			<Modal backdrop="static" toggle={props.toggleModel} isOpen={iscreateNewInventory} centered={true}>
				<ModalHeader toggle={props.toggleModel}>Create new inventory </ModalHeader>

				<ModalBody>
					<div className="row justify-content-center">
						<div className="col-12">

							<div className="floating-label">
								<textarea className="floating-input" tabIndex="3" placeholder=" " onChange={(e) => inputChangeHandler(e)} defaultValue={formFields.description} id="description" name="description" ></textarea>
								<span className="highlight"></span>
								<label>Inventory description</label>
							</div>
						</div>

					</div>

				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={() => props.createInventoryHandler(true)}>Save</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={() => props.createInventoryHandler(false)}>Skip</button>
				</ModalFooter>
			</Modal>

		</div>
	)
}