import $ from 'jquery';
import CryptoJS from 'crypto-js'
window.$ = $;

export function Custom() {
	try {
		$(".userbox").click(function () {
			$(".user-profile").slideToggle('slow');
			$(this).toggleClass("active");
			$(".userbox i").toggleClass("move");
		});
		$(".mobile-menu li").click(function () {
			$(this).children(".mobile-submenu").toggle();
		});


	} catch (error) {
		console.log(error);

	}
};
export function showSubMenu(menu) {
	$("#" + menu).children(".mobile-submenu").toggle();
}
export function viewPassword(id) {
	try {
		
		var passwordInput="";
		if(id=="")
		{
			 passwordInput = document.getElementById('password');
		}
		else
		{
			 passwordInput = document.getElementById(id);
		}
		var passStatus = document.getElementById('pass-status');

		if (passwordInput.type === 'password') {
			passwordInput.type = 'text';
			passStatus.className = 'zmdi zmdi-eye zmdi-hc-fw text-primary';

		}
		else {
			passwordInput.type = 'password';
			passStatus.className = 'zmdi zmdi-eye-off zmdi-hc-fw';
		}
	} catch (error) {
		console.log(error);

	}
}

