export function toTitleCase(str) {
	return str.substring(0, 1).toUpperCase() + str.substring(1).toLowerCase();

}
export function toShowHideFilterBox() {
	const $menu = $("#open-check-list");
	$(".checkbox-dropdown").on("click", function () {
		$("#open-check-list").toggleClass("open-checkbox");
	});
	$(".checkbox-dropdown").on("keypress", function (e) {
		if (e.keyCode === 13)
			$("#open-check-list").toggleClass("open-checkbox");
	});
	$(document).mouseup(e => {

		if (!$menu.is(e.target) // if the target of the click isn't the container...
			&& $menu.has(e.target).length === 0 && e.target.classList[0] !== "checkbox-dropdown") // ... nor a descendant of the container
		{
			$menu.removeClass("open-checkbox");
		}
	});

}
