export function toShowHideActivityFilterBox() {
	const $menu = $("#open-check");
	$(".checkbox-dropdown").on("click", function () {
		$("#open-check").toggleClass("open-checkbox", 1000);
	});
	$(".checkbox-dropdown").on("keypress", function (e) {
		if (e.keyCode === 13)
			$("#open-check").toggleClass("open-checkbox");
	});
	$(document).mouseup(e => {
		if (!$menu.is(e.target) // if the target of the click isn't the container...
			&& $menu.has(e.target).length === 0 && e.target.className !== "checkbox-dropdown") // ... nor a descendant of the container
		{
			$menu.removeClass("open-checkbox");
		}
	});
}

