export function getValueColorClass(oldValue, newValue) {
	oldValue = oldValue !== "" ? parseFloat(oldValue) : 0;
	newValue = newValue !== "" ? parseFloat(newValue) : 0;
	var colorClass = "";
	if (oldValue > newValue)
		colorClass = " value-green"
	else if (newValue > oldValue)
		colorClass = " value-red"
	return colorClass;
}
export function getWOPriceColorClass(fullPrice, newPrice) {
	fullPrice = fullPrice !== "" ? parseFloat(fullPrice) : 0;
	newPrice = newPrice !== "" ? parseFloat(newPrice) : 0;
	var colorClass = " price-orange";
	if (fullPrice > newPrice)
		colorClass = " price-blue"
	else if (newPrice > fullPrice)
		colorClass = " price-green"
	return colorClass;
}
export function encryptURL(id) {
	var ecryptedText = CryptoJS.AES.encrypt(id.toString(), process.env.REACT_APP_CryptojsSecretKey).toString();
	ecryptedText = ecryptedText.replace(/\//g, "Por21Ld").replace(/\+/g, "xMl3Jk").replace(/=/g, "Ml32");
	return ecryptedText;
}
export function decryptURL(id) {
	id = (id).toString().replace(/Por21Ld/g, "/").replace(/xMl3Jk/g, "+").replace(/Ml32/g, "=");
	var data = CryptoJS.AES.decrypt(id, process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8);
	if (data === "")
		data = 0;
	return data;
}
export function getTimezoneName() {
	const today = new Date();
	const short = today.toLocaleDateString(undefined);
	const full = today.toLocaleDateString(undefined, { timeZoneName: 'long' });
  
	// Trying to remove date from the string in a locale-agnostic way
	const shortIndex = full.indexOf(short);
	if (shortIndex >= 0) {
	  const trimmed = full.substring(0, shortIndex) + full.substring(shortIndex + short.length);
	  
	  // by this time `trimmed` should be the timezone's name with some punctuation -
	  // trim it from both sides
	  return trimmed.replace(/^[\s,.\-:;]+|[\s,.\-:;]+$/g, '');
  
	} else {
	  // in some magic case when short representation of date is not present in the long one, just return the long one as a fallback, since it should contain the timezone's name
	  return full;
	}
  }