import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
export default function ActivityLogComponent(props) {
	const {isLoading,activityLogList,requestParameter,inpDate,userList,searchDate,clearActivityLogFilterBoxClass,isShowActivityLogDatatable,
		isUserName,isDateTime,isModule,isActivity,isIPAddress,isDevice,isSetUserName,isSetDateTime,isSetModule,isSetActivity,isSetIPAddress,isSetDevice}=props;


	return (
		<div>
			<Helmet>
				<title>Activity Log | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
				<div className="sticky-filter-datatable" onClick={props.toggleActivityLogDatatable}>
					<img className="" src="/assets/images/filter-icon.svg" alt="" />
				</div>
				<div className={`datatable-filter${isShowActivityLogDatatable? " open":""}`}>
					<div className="close-datalist-popup" onClick={props.toggleActivityLogDatatable}>
						<i className="zmdi zmdi-close-circle"></i>
					</div>
					<div className="datatable-head-list">
						<div className="datatable-list-title text-darkblue font-weight-bold">
							Activity log
					</div>
						<ul>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isUserName" name="isUserName" onChange={props.setUserNameCheckbox} checked={isUserName} />
									<label className="custom-control-label font-weight-bold" htmlFor="isUserName">USER NAME</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDateTime" name="isDateTime" onChange={props.setDateTimeCheckbox} checked={isDateTime} />
									<label className="custom-control-label font-weight-bold" htmlFor="isDateTime">DATE TIME</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isModule" name="isModule" onChange={props.setModuleCheckbox} checked={isModule} />
									<label className="custom-control-label font-weight-bold" htmlFor="isModule">MODULE</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isActivity" name="isActivity" onChange={props.setActivityCheckbox} checked={isActivity} />
									<label className="custom-control-label font-weight-bold" htmlFor="isActivity">ACTIVITY</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDevice" name="isDevice" onChange={props.setDeviceCheckbox} checked={isDevice} />
									<label className="custom-control-label font-weight-bold" htmlFor="isDevice">DEVICE</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isIPAddress" name="isIPAddress" onChange={props.setIPAddressCheckbox} checked={isIPAddress} />
									<label className="custom-control-label font-weight-bold" htmlFor="isIPAddress">IP ADDRESS</label>
								</div>
							</li>
						</ul>
					</div>
				</div>
				<div className="main-content-area mobile-bottom-space" onClick={props.toggleHideOrderDatatable}>
					<div className="container-fluid d-none d-md-block">

						<div className="row mb-4">
							<div className="col-12 activity-title-block">
								<h2 className="text-left text-xl-center heading-text mt-2">Activity log</h2>

							</div>
						</div>

					</div>
					<div className="container-fluid d-block d-md-none">
						<div className="row">
							<div className="col p-0 mobile-header-info">
								<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
								<p className="text-darkblue font-weight-bold mobile-title-headline">Activity log</p>

							</div>
						</div>
					</div>


					<div className="container-fluid mt-3">
						<div className="row">

							<div className="col-lg-12 col-md-12 col-sm-12">
								<div className="row bg-white header-shadow py-3 align-items-center">
									<div className="col-lg-9 col-md-12 col-sm-12">
										<div className="row">
											<div className="col-lg-3 col-md-5 col-5">
												<div className="floating-label mb-0">
													<DatePicker className="floating-input" id="searchDate"
														disabledKeyboardNavigation
														onFocus={e => e.target.blur()}
														onChange={props.searchDateChangeHandler}
														placeholderText="mm/dd/yyyy"
														selected={searchDate}
														autocomplete="off"
													/>
													<span className="highlight"></span>
													<label>Date</label>
												</div>
											</div>
											<div className="col-lg-4 col-md-5 col-5 ">
												<div className="floating-label mb-0">
													<select className="floating-select" id="searchUserId" name="searchUserId" onChange={(e) => props.searchUserChangeHandler(e)} value={requestParameter.searchUserId}>
														<option key="u" value="">Select name</option>
														{userList.map((user) => <option key={user.key} value={user.key}>{user.value}</option>)}

													</select>
													<span className="highlight"></span>
													<label >User name</label>
												</div>
											</div>
											<div className="col-lg-1 col-md-2 col-2 ">
												<div className="icon-box icon-box1 rounded-circle left-0" onKeyPress={props.searchIconKeyPress} tabIndex="0" onClick={props.searchHandler}>
													<i className="material-icons">search</i>
												</div>

											</div>
											<div className="col-lg-4 desktop-show">
												<button className="btn btn-primary rounded-pill text-center d-inline mr-2" onMouseDown={e => e.preventDefault()} type="button" onClick={props.clearSearchHandler}>Clear search</button>
											</div>
										</div>
									</div>



									<div className="order-list-wrap col-lg-3 col-md-12 col-sm-12 py-0 pr-3 desktop-clear-action">
										<div className="row">


											<div className="col-md-12 activity-log-filter">
												<div className="mobile-show">
													<button className="btn btn-primary rounded-pill text-center d-inline mr-2" onMouseDown={e => e.preventDefault()} type="button" onClick={props.clearSearchHandler}>Clear search</button>
												</div>
												<div className="checkbox-dropdown d-inline-flex" tabIndex="0" onKeyPress={props.filterKeyPressHandler} onClick={props.setClearActivityLogFilterBoxClass}>
													Clear activity logs
								 				</div>
												<div id="open-check" className={clearActivityLogFilterBoxClass}>
													<ul className="checkbox-dropdown-list">
														<li>
															<div className="filterBoxDate floating-label mb-2">
																<DatePicker className="floating-input" id="inpDate"
																	disabledKeyboardNavigation
																	onFocus={e => e.target.blur()}
																	onChange={props.inpDateChangeHandler}
																	placeholderText="mm/dd/yyyy"
																	selected={inpDate}
																	autocomplete="off"
																/>
																<span className="highlight"></span>
																<label>Date</label>
															</div>
														</li>
														<li className="text-center mt-3">
															<button className="btn btn-primary rounded-pill text-center d-inline" onMouseDown={e => e.preventDefault()} type="button" onClick={props.ClearActivityLogHandler}>Clear All</button>
														</li>
													</ul>

												</div>
											</div>
										</div>
									</div>

								</div>
							</div>

						</div>
					</div>
					<div className="container-fluid bg-white header-shadow mt-4">
						<div className="row my-3">
							<div className="col-12 p-0">
								<div className="table-responsive">
									<table id="tblActivityLog" className="table w-100" >
										<thead>
											<tr>
												<th className="plus-sort"></th>

												<th style={{display: isSetUserName? '':'none'}} className={requestParameter.sortCol==="userName"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("userName")}>User Name</th>

												<th style={{display: isSetDateTime? '':'none'}} className={requestParameter.sortCol==="createdOn"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("createdOn")}>Date Time</th>

												<th style={{display: isSetModule? '':'none'}} className={requestParameter.sortCol==="module"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("module")}>Module</th>

												<th style={{display: isSetActivity? '':'none'}} className={requestParameter.sortCol==="action"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("action")}>Activity</th>

												<th style={{display: isSetDevice? '':'none'}} className={requestParameter.sortCol==="deviceName"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("deviceName")}>Device</th>

												<th style={{display: isSetIPAddress? '':'none'}} className={requestParameter.sortCol==="ipAddress"? requestParameter.sortDir==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("ipAddress")}>IP Address</th>

											</tr>
										</thead>
										<tbody>

											{activityLogList.map((data,index) =>
												<tr key={index}>
													<td className="details-control plus-sort"></td>

													<td style={{display: isSetUserName? '':'none'}}>{data.userName}</td>

													<td style={{display: isSetDateTime? '':'none'}}>{data.createdOn}</td>

													<td style={{display: isSetModule? '':'none'}}>{data.module}</td>

													<td style={{display: isSetActivity? '':'none'}}>{data.action}</td>

													<td style={{display: isSetDevice? '':'none'}}>{data.deviceName}</td>

													<td style={{display: isSetIPAddress? '':'none'}}>{data.ipAddress}</td>


												</tr>
											)}
										</tbody>
									</table>
								</div>
							</div>
						</div>
					</div>
				</div>

			</div>
		</div>
	)
}