//#region get order item list
export function getWorkOrderItemList(data, isLoading,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) {
  
    return (dispatch) => {
        dispatch({
            type: types.GET_ORDERITEM_REQUEST,
            isLoading: isLoading
        });
        let url = 'workorder/WorkOrderItemList';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblWOOrderItem').DataTable().destroy();
                    dispatch({
                        type: types.GET_ORDERITEM_SUCCESS,
                        data: response.payload
                    });
                    $('#tblWOOrderItem').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        "initComplete": function( settings, json ) {
                           
                            if(!isSetCode)
                            {
                                $(".isSetCode").hide()
                            }
                            if(!isSetName)
                            {
                                $(".isSetName").hide()
                            }
                           
                            if(!isSetPrice)
                            {
                                $(".isSetPrice").hide()
                            }
                            if(!isSetExtPrice)
                            {
                                $(".isSetExtPrice").hide()
                            }

                            if(!isSetOnOrder)
                            {
                                $(".isSetOnOrder").hide()
                            }
                            else
                            {
                                $(".isSetOnOrder").show()
                            }
                            if(!isSetTax)
                            {
                                $(".isSetTax").hide()
                            }
                           
                            if(!isSetOnHandQuantity)
                            {
                                $(".isSetOnHandQuantity").hide()
                            }
                           
                           
                          },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_ORDERITEM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
