//#region update wo item inline
export function updateWOItemInline(updateInlineItemParam, requestParameter, isGetItem,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) {

    return (dispatch) => {
        dispatch({
            type: types.UPDATE_WOITEMINLINE_REQUEST,
        });
        let url = 'workorder/updateWOItem';
        return post(url, updateInlineItemParam)
            .then((response) => {

                const { payload } = response;
              
                var status = "";
                var message = "";
                // if (isGetItem)
                //     swal({
                //         title: payload.status,
                //         text: payload.message,
                //         icon: payload.status.toLowerCase(),
                //         timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //         button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                //     })
                // else {
                status = response.payload.status;
                message = response.payload.message;
                //}
                if (response.status === 200) {
                    /* Logic */
                    if (isGetItem)
                        dispatch(getWorkOrderItemList(requestParameter, true,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity));
                    dispatch({
                        type: types.UPDATE_WOITEMINLINE_SUCCESS,
                        data: response.payload,
                        status: status,
                        message: message
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_WOITEMINLINE_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
