//#region get  order  detail
export function getOrderDetail(orderId) {
    return (dispatch) => {
        dispatch({
            type: types.GET_ORDERDETAIL_REQUEST,

        });
        let url = 'workorder/getWorkOrderById/' + orderId;
        return get(url)
            .then((response) => {

                const { payload } = response;
              

                if (response.status === 200) {
                    /* Logic */
                    if (payload.code === "E08" || payload.code === "E03" || payload.code === "E05")
                        history.push("/Dashboard");
                    dispatch(getCustomerDetail(parseInt(payload.values.customerId)));
                    dispatch({
                        type: types.GET_ORDERDETAIL_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.GET_ORDERDETAIL_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get  order detail
