//#region save comment
export function saveComment(commentModelReqParam, showMessage) {
    
    return (dispatch) => {
        dispatch({
            type: types.SAVE_COMMENT_REQUEST,
        });
        let url = 'workorder/UpdateWOCommentRef';
        return post(url, commentModelReqParam)
            .then((response) => {
                var status = "";
                var message = "";
                const { payload } = response;
              
                if (showMessage) {
                    status = response.payload.status;
                    message = response.payload.message;
                }
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    if (showMessage)
                        dispatch(getOrderDetail(commentModelReqParam.woid));
                    dispatch({
                        type: types.SAVE_COMMENT_SUCCESS,
                        data: response.payload,
                        status: status,
                        message: message
                    });

                } else {
                    dispatch({
                        type: types.SAVE_COMMENT_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion save comment
//#region duplicate item
export function duplicateItem(updateInlineItemParam, requestParameter, isGetItem,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) {
    return (dispatch) => {
        dispatch({
            type: types.DUPLICATE_ITEM_REQUEST,
        });
        let url = 'workorder/DuplicateWOItem';
        return post(url, updateInlineItemParam)
            .then((response) => {
                const { payload } = response;
              

                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })

                if (response.status === 200) {
                    /* Logic */
                    if (isGetItem)
                        dispatch(getWorkOrderItemList(requestParameter, true,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity));
                    dispatch({
                        type: types.DUPLICATE_ITEM_SUCCESS,
                        data: response.payload
                    });

                } else {
                    dispatch({
                        type: types.DUPLICATE_ITEM_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
