import React from 'react';
import PriceConfigurationComponent from '../component/PriceConfigurationComponent';
import { clearMessage,getDepartments,getCategories,setIsCategoryDisabled,setDropdownListParam,CreatePriceConfig,setSuccessDialogue,
  getPriceConfigDetails,getAPINameList
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;

class PriceConfigurationContainer extends React.Component {
  state={
    departmentsParam:{
    id:0
    },
    isSelectAll: true,
    checkboxInput:{
      ColumnPriceName:[]
    },
    priceSavedName:[],
    formFields: {
      priceValue: 0,
      PriceValueCal:"",
      PriceValueTerm:"",
    },
    displaySetAPI:"",
    dollarPerLabel:"",
   
      priceList: [
       // { id: "PriceB",value:"Price B"  , isChecked: false},
       // { id: "PriceC",value:"Price C"  , isChecked: false},
        { id: "PriceD",value:"Price D"  , isChecked: false},
        { id: "PriceE",value:"Price E"  , isChecked: false},
        { id: "PriceF",value:"Price F"  , isChecked: false},
        { id: "PriceG",value:"Price G"  , isChecked: false},
        { id: "PriceH",value:"Price H"  , isChecked: false},
        { id: "PriceI",value:"Price I"  , isChecked: false},
        { id: "PriceJ",value:"Price J"  , isChecked: false},
        { id: "PriceK",value:"Price K"  , isChecked: false},
        { id: "PriceL",value:"Price L"  , isChecked: false},
        { id: "PriceM",value:"Price M"  , isChecked: false},
        { id: "PriceN",value:"Price N"  , isChecked: false},
        { id: "PriceO",value:"Price O"  , isChecked: false},
        { id: "PriceP",value:"Price P"  , isChecked: false},
        { id: "PriceQ",value:"Price Q"  , isChecked: false},
        { id: "PriceR",value:"Price R"  , isChecked: false},
        { id: "PriceS",value:"Price S"  , isChecked: false},
        { id: "PriceT",value:"Price T"  , isChecked: false},
        { id: "PriceU",value:"Price U"  , isChecked: false},
        { id: "PriceV",value:"Price V"  , isChecked: false},
        { id: "PriceW",value:"Price W"  , isChecked: false},
        { id: "PriceX",value:"Price X"  , isChecked: false},
        { id: "PriceY",value:"Price Y"  , isChecked: false},
        { id: "PriceZ",value:"Price Z"  , isChecked: false},
      ]
    
  }
  componentWillMount() {
  }
  componentDidMount() {
    this.props.getPriceConfigDetails();
    this.props.getAPINameList();

    this.props.getDepartments();
    window.addEventListener('scroll',this.handleScroll);
    let priceList = this.state.priceList;
  
      this.setState({
        priceList
      });

    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    clearInterval(this.clearMessageIntervalID);
  }
  selectDepartmentHandler=(selectedList,selectedItem) => {
    var value=selectedItem.id;
    var dropdownListParam=this.props.dropdownListParam;
    dropdownListParam["departmentIds"].push(value);
    this.props.setIsCategoryDisabled(false);
    this.props.getCategories({
      departmentId: dropdownListParam.departmentIds
    });
    this.props.setDropdownListParam(dropdownListParam);
  }

  removeDepartmentHandler=(selectedList,removedItem) => {
    var value=removedItem.id;
    var dropdownListParam=this.props.dropdownListParam;
    var elementsIndex=dropdownListParam["departmentIds"].findIndex(element => element===value);
    dropdownListParam["departmentIds"].splice(elementsIndex,1);
    if(dropdownListParam["departmentIds"].length>0) {
      this.props.setIsCategoryDisabled(false);
      this.props.getCategories({
        departmentId: dropdownListParam.departmentIds
      });
    }
    else {
      dropdownListParam["categoryIds"]=[];
      this.props.getCategories({
        departmentId: dropdownListParam.departmentIds
      });
      this.props.setIsCategoryDisabled(true);

    }
    this.props.setDropdownListParam(dropdownListParam);

  }

  selectCategoryHandler=(selectedList,selectedItem) => {
    var value=selectedItem.id;
    var dropdownListParam=this.props.dropdownListParam;
    dropdownListParam["categoryIds"].push(value);
    dropdownListParam["pageSize"]=50;
    this.props.setDropdownListParam(dropdownListParam);
  

  }
  removeCategoryHandler=(selectedList,removedItem) => {
    var value=removedItem.id;
    var dropdownListParam=this.props.dropdownListParam;
    var elementsIndex=dropdownListParam["categoryIds"].findIndex(element => element===value);
    dropdownListParam["categoryIds"].splice(elementsIndex,1);
    dropdownListParam["pageSize"]=50;
    this.props.setDropdownListParam(dropdownListParam);

  }

  inputChangeHandler= (e) => {
    try {
      var value = e.target.value;
      let formFields = { ...this.state.formFields };
      formFields["PriceValue"] = value;
      this.setState({
        formFields
      });
    } catch (error) {
      console.log(error);

    }
  }
   //#region setSelectAll
   selectAll = (e) => {

    let priceList = this.state.priceList
   
    priceList.forEach(priceList => priceList.isChecked = e.target.checked) 
    var checkboxInput=this.state.checkboxInput;
    this.setState({priceList: priceList})
    if(e.target.checked)
    {
      checkboxInput["ColumnPriceName"]=priceList.map(x=>x.id); 
    }
    else
    {
     
      checkboxInput["ColumnPriceName"]=[];
     
    }
    this.setState({checkboxInput: checkboxInput})
  }
  //#endregion setSelectAll

  savePriceCofig=() => {
  
    var checkboxInput=this.state.checkboxInput;
    var priceconfig=this.props.priceconfig;
    priceconfig=priceconfig.filter(x=>x.isSetAPIPrice==true);
    for(var i=0;i<priceconfig.length;i++)
    {
      checkboxInput["ColumnPriceName"].push(priceconfig[i].price);
    }


    if(this.state.checkboxInput.ColumnPriceName.length==0)
    {
      swal("","Please Select Price","error")
    }
    else if(this.state.formFields.PriceValueCal=="")
    {
      swal("","Please Select Up or Down Operation","error")
    }
    else if(this.state.formFields.PriceValueTerm=="")
    {
      swal("","Please Select Dollar($) or Percentage","error")
    }
    else if(this.state.formFields.PriceValue==undefined)
    {
      swal("","Please Enter Price Value","error")
    }
    else
    {
      let uniqueChars = [];
      this.state.checkboxInput.ColumnPriceName.forEach((c) => {
          if (!uniqueChars.includes(c)) {
              uniqueChars.push(c);
          }
      });

      let createConfigParam={
        ColumnPriceName:uniqueChars,
        PriceValueCal:this.state.formFields.PriceValueCal,
        PriceValueTerm:this.state.formFields.PriceValueTerm,
        PriceValue:parseInt(this.state.formFields.PriceValue),
        departmentIds: this.props.dropdownListParam.departmentIds,
        categoryIds: this.props.dropdownListParam.categoryIds,
      }
       this.props.CreatePriceConfig(createConfigParam,true)
    }
  
  }
  
  onCheckboxChange= (e) => {
    const value = e.target.name;
    var checkboxInput =this.state.checkboxInput;
    let priceconfig = this.props.priceconfig

     if(e.target.checked && priceconfig.filter(x => x.price===value ).length==0)
     {
        
           priceconfig.push({price: value,isSetAPIPrice:e.target.checked});
        
       
     }
     else
     {
      

      for (var i in priceconfig) {
      if (priceconfig[i].price == value) {
           priceconfig[i].isSetAPIPrice = e.target.checked;
         //  break; //Stop this loop, we found it!
        }
      }
     }


    let priceList = this.state.priceList
    priceList.forEach(priceList => {
      if (priceList.id === e.target.name)
      priceList.isChecked =  e.target.checked
      })
      
   
     
    this.setState({priceList: priceList,priceconfig:priceconfig})
 
  }
  upDownHandler= (e) => {
    var value = e.target.id;
      let formFields = { ...this.state.formFields };
      formFields["PriceValueCal"] = value;
      this.setState({
        formFields
      });
      this.props.priceconfig.forEach(priceList => {
        priceList.isSetAPIPrice =  false
        })
      this.state.priceList.forEach(priceList => {
        priceList.isChecked =  false
        })
  }
  dollarPercentageHandler= (e) => {
    var value = e.target.id;
    var dollarPerLabel=this.state.dollarPerLabel;
      let formFields = { ...this.state.formFields };
      formFields["PriceValueTerm"] = value;
      if(value=="Percentage")
      {
        dollarPerLabel="in (%)"
      }
      else
      {
        dollarPerLabel="in $"

      }
      this.setState({
        formFields,dollarPerLabel
      });
      
      this.props.priceconfig.forEach(priceList => {
        priceList.isSetAPIPrice =  false
        })
      this.state.priceList.forEach(priceList => {
        priceList.isChecked =  false
        })
  }

  displayAPIName=(id) => {
    let displaySetAPI=this.state.displaySetAPI;
    let apiNameList=this.props.apiNameList;
    apiNameList=apiNameList.filter(x=>x.price==id);
    var apiName=apiNameList.filter(x=>x.price==id).map(x=>x.apiName).join(',')
    
    displaySetAPI=apiNameList[0].price +" is Connected with :"+apiName;

    this.setState({
      displaySetAPI,apiNameList
    });
    
  }
  render() {
    const { isSuccessDialogue, updatestatus, updatemessage } = this.state;
    if (isSuccessDialogue) {
      this.props.setSuccessDialogue(false, updatestatus, updatemessage);
    }

   
    return (
      <PriceConfigurationComponent
       {...this.props}
        selectDepartmentHandler={this.selectDepartmentHandler}
        removeDepartmentHandler={this.removeDepartmentHandler}
        selectAll={this.selectAll}
        isSelectAll={this.state.isSelectAll}
        savePriceCofig={this.savePriceCofig}
        onCheckboxChange={this.onCheckboxChange}
        inputChangeHandler={this.inputChangeHandler}
        upDownHandler={this.upDownHandler}
        dollarPercentageHandler={this.dollarPercentageHandler}
        priceList={this.state.priceList}
        selectCategoryHandler={this.selectCategoryHandler}
        removeCategoryHandler={this.removeCategoryHandler}
        displayAPIName={this.displayAPIName}
        displaySetAPI={this.state.displaySetAPI}
        dollarPerLabel={this.state.dollarPerLabel}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    clearMessage: () => dispatch(clearMessage()),
    getDepartments: () => dispatch(getDepartments()),
    getCategories: (data) => dispatch(getCategories(data)),
    setIsCategoryDisabled: (data) => dispatch(setIsCategoryDisabled(data)),
    setDropdownListParam: (data) => dispatch(setDropdownListParam(data)),
    CreatePriceConfig:(data,isLoading)=>dispatch(CreatePriceConfig(data,isLoading)),
    setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    getPriceConfigDetails:()=>dispatch(getPriceConfigDetails()),
    getAPINameList:()=>dispatch(getAPINameList())
  };
};

const mapStateToProps=(state) => {

  const {departments,categories,dropdownListParam,isLoading, updatestatus, updatemessage, isSuccessDialogue,isCategoryDisabled
  ,priceconfig,apiNameList,priceName}=state.PriceConfigurationContainerReducer;
  return {
    departments: departments.values.length>0? departments.values:[],
    categories: categories.values.length>0? categories.values:[],
    dropdownListParam: dropdownListParam,
    isLoading: isLoading,
    updatestatus, updatemessage, isSuccessDialogue,
    isCategoryDisabled,
    priceconfig:priceconfig,
    priceName:priceName,
    apiNameList:apiNameList==undefined?[]: apiNameList.values,
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(PriceConfigurationContainer);