import React,{Component} from 'react';
import './ItemGeneral.css'
import CommonServices from '../../Services/CommonServices'
import ItemsServices from '../../Services/ItemsServices'
import swal from 'sweetalert';
import {FormWithConstraints,FieldFeedbacks,FieldFeedback} from 'react-form-with-constraints'
import {showLoader,hideLoader,toTitleCase} from '../../Custom';
import NumberFormat from 'react-number-format';
class ItemGeneral extends Component {
  constructor(props) {
    super(props);
    this.state={
      ItemTypes: [],
      Departments: [],
      Categories: [],
      isCategoryDisabled: true,
      Barcodes: [],
      ItemTax: [],
      DiscriptionLabel: {
        subDescription1: "",
        subDescription2: "",
        subDescription3: ""
      },
      memberOf: [],
      formFields: {
        itemId: 0,
        itemLookupCode: "",
        description: "",
        extendedDescription: "",
        subDescription1: "",
        subDescription2: "",
        subDescription3: "",
        itemType: "",
        departmentID: "",
        categoryID: "",
        price: "",
        cost: "",
        taxId: "",
        barcodeFormat: "",
        profitMargin: ""
      },
      formFieldsOldValue: {
        itemLookupCode: "",
        description: "",
        extendedDescription: "",
        subDescription1: "",
        subDescription2: "",
        subDescription3: "",
        itemType: "",
        departmentID: "",
        categoryID: "",
        price: "",
        cost: "",
        taxId: "",
        barcodeFormat: "",
        profitMargin: ""
      }
    }
  }

  async componentDidMount() {
    showLoader();
    this.getItemSubDescription();
    this.getItemTypes();
    this.getDepartments();
    this.getItemTax();
    this.getBarcode();
    if(!!this.props.itemGeneral) {
      let formFields={
        itemId: this.props.itemGeneral.itemId,
        itemLookupCode: this.props.itemGeneral.itemLookupCode,
        description: this.props.itemGeneral.description,
        extendedDescription: this.props.itemGeneral.extendedDescription,
        subDescription1: this.props.itemGeneral.subDescription1,
        subDescription2: this.props.itemGeneral.subDescription2,
        subDescription3: this.props.itemGeneral.subDescription3,
        itemType: this.props.itemGeneral.itemType,
        departmentID: this.props.itemGeneral.departmentID,
        categoryID: this.props.itemGeneral.categoryID,
        price: this.props.itemGeneral.price.toFixed(2),
        cost: this.props.itemGeneral.cost.toFixed(4),
        taxId: this.props.itemGeneral.taxId,
        barcodeFormat: this.props.itemGeneral.barcodeFormat,
        profitMargin: ""
      };
      this.setState({formFields: formFields,memberOf: this.props.itemGeneral.memberOf},() => {
        if(this.state.formFields.departmentID!==""&&this.state.formFields.departmentID>0) {
          this.setState({isCategoryDisabled: false});
          this.getCategories(this.state.formFields.departmentID);
        }
        this.setProfitMargin("");
      });
    }
    hideLoader();

    // Auto Save Draft after 2 min
    try {
      this.intervalID=setInterval(async () => {
        this.formHandler(false,'interval')
      },120*1000);
    } catch(e) {
      console.log(e);
    }

  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  //#region get sub discription label 
  getItemSubDescription=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.getItemSubDescription().then((data) => {
        if(!!data.values)
          this.setState({
            DiscriptionLabel: data.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion  get sub discription label   
  //#region get item types to bind dropdown
  getItemTypes=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.GetAllItemTypes().then((itemTypes) => {
        if(!!itemTypes.values)
          this.setState({
            ItemTypes: itemTypes.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion  get item types to bind dropdown
  //#region get item tax to bind dropdown
  getItemTax=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.GetAllItemTax().then((itemTax) => {
        if(!!itemTax.values)
          this.setState({
            ItemTax: itemTax.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion  get item tax to bind dropdown
  //#region  get departments to bind dropdown
  getDepartments=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.GetAllDepartments().then((departments) => {
        if(!!departments.values)
          this.setState({
            Departments: departments.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get departments to bind dropdown
  //#region  get categories to bind dropdown
  getCategories=async (id) => {
    try {
      var Obj=new CommonServices();
      await Obj.GetAllCategories(id).then((categories) => {
        if(!!categories.values) {
          var formFields={...this.state.formFields}
         //formFields["categoryID"]=categories.values.length>0? categories.values[0].id:0
          this.setState({
            Categories: categories.values,
            formFields
          });
        }
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get categories to bind dropdown
  //#region get barcodes to bind members
  getBarcode=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.GetAllBarcodes().then((barcodes) => {
        if(!!barcodes.values)
          this.setState({
            Barcodes: barcodes.values
          });
        if(barcodes.status.toLowerCase()==="error")
          swal({
            title: barcodes.status,
            text: barcodes.message,
            icon: barcodes.status.toLowerCase(),
            button: "Ok"
          })
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get members to bind members

  //#region all controll onChange handle method  
  inputChangeHandler(e) {
    try {
      let formFields={...this.state.formFields};
      var value=e.target.value;
      if(e.target.name==="price"||e.target.name==="cost"||e.target.name==="profitMargin")
        value=value.replace(/,/g,"");
      if(e.target.name==="departmentID") {
        if(value!=="") {
          this.setState({isCategoryDisabled: false});
          this.getCategories(parseInt(value));
        }
        else {
          formFields["categoryID"]="";
          this.getCategories(parseInt(value));
          this.setState({isCategoryDisabled: true});
        }
      }
      //#region displayed old value
      let formFieldsOldValue={...this.state.formFieldsOldValue};
      let itemGeneralProps={...this.props.itemGeneral};
      if(itemGeneralProps[e.target.name]!==undefined) {

        if(itemGeneralProps[e.target.name].toString()!==value) {
          var itemGeneralPropsField="";
          switch(e.target.name) {
            case 'itemType':
              itemGeneralPropsField="itemTypeName";
              this.props.setDisabledPODs(e.target.options[e.target.selectedIndex].text);
              break;
            case 'barcodeFormat':
              itemGeneralPropsField="barcodeFormatName";
              break;
            case 'departmentID':
              itemGeneralPropsField="departmentName";
              break;
            case 'categoryID':
              itemGeneralPropsField="categoryName";
              break;
            case 'taxID':
              itemGeneralPropsField="taxName";
              break;

            default:
              itemGeneralPropsField=e.target.name;
              break;
          }
          formFieldsOldValue[e.target.name]=itemGeneralProps[itemGeneralPropsField]!==""&&itemGeneralProps[itemGeneralPropsField]!==null? itemGeneralProps[itemGeneralPropsField]:"";
          this.props.setcurrentPODChanged("General");
        }
        else
          formFieldsOldValue[e.target.name]="";

      }
      //#endregion displayed old value 
      // var value = e.target.value;
      if(e.target.name==="itemLookupCode")
        value=value.trim();
      if(e.target.maxLength>=0) {
        if(value.length>e.target.maxLength) {
          value=value.substring(0,e.target.maxLength);
        }
      }
      this.form.validateFields(e.target);

      formFields[e.target.name]=value;
      this.setState({
        formFields,formFieldsOldValue
      });
      if(e.target.name==="price") {
        this.props.setitemCommonPrice(value);
        this.props.setitemCommonPriceOldValue(formFieldsOldValue.price);
      }
      if(e.target.name==="cost") {
        this.props.setitemCommonCost(value);
        this.props.setitemCommonCostOldValue(formFieldsOldValue.cost);
      }
      if(e.target.name==="profitMargin") {
        this.props.setitemCommonProfit(value);
        this.props.setitemCommonProfitOldValue(this.state.formFieldsOldValue.profitMargin);
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }

  //#endregion all controll onChange handle method
  //#region clear form fields old values
  clearFormFieldsOldValues=() => {
    var formFieldsOldValue={
      itemLookupCode: "",
      description: "",
      extendedDescription: "",
      subDescription1: "",
      subDescription2: "",
      subDescription3: "",
      itemType: "",
      departmentID: "",
      categoryID: "",
      price: "",
      cost: "",
      taxId: "",
      barcodeFormat: "",
      profitMargin: ""
    }
    this.setState({formFieldsOldValue: formFieldsOldValue});

  }
  //#endregion clear form fields old values
  setProfitMargin(e) {
    try {
      let formFields={...this.state.formFields};
      var profitMargin=0.00;
      if(this.state.formFields.price!=="") {
        profitMargin=(((parseFloat(this.state.formFields.price!==""? this.state.formFields.price:0)-parseFloat(this.state.formFields.cost!==""? this.state.formFields.cost:0))*100)/parseFloat(this.state.formFields.price!==""? this.state.formFields.price:0)).toFixed(2);
        profitMargin=profitMargin==="NaN"? 0:profitMargin;
      }
      formFields["profitMargin"]=profitMargin;
      this.setState({
        formFields
      });
      this.props.setitemCommonProfit(profitMargin);
      // this.props.setitemCommonProfitOldValue(profitMargin);  
      //#region displayed old value
      let formFieldsOldValue={...this.state.formFieldsOldValue};

      let itemGeneralProps={...this.props.itemGeneral};
      var oldProfitMargin=(((parseFloat(itemGeneralProps.price!==""? itemGeneralProps.price:0)-parseFloat(itemGeneralProps.cost!==""? itemGeneralProps.cost:0))*100)/parseFloat(itemGeneralProps.price!==""? itemGeneralProps.price:0)).toFixed(2);
      oldProfitMargin=oldProfitMargin==="NaN"? 0:oldProfitMargin;
      if(oldProfitMargin!==profitMargin) {

        formFieldsOldValue["profitMargin"]=oldProfitMargin!==""? oldProfitMargin:"";
        this.props.setitemCommonProfitOldValue(oldProfitMargin);
      }
      else {
        formFieldsOldValue["profitMargin"]="";
        this.props.setitemCommonProfitOldValue("");
      }

      this.setState({
        formFieldsOldValue
      });
      //#endregion displayed old value
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#region post form data to save

  //#region set price
  setPrice=(e) => {
    try {
      let formFields={...this.state.formFields};
      var price=((parseFloat(this.state.formFields.cost!==""? this.state.formFields.cost:0))/((100-parseFloat(this.state.formFields.profitMargin!==""? this.state.formFields.profitMargin:0))/100)).toFixed(2);
      formFields["price"]=price;
      this.props.setitemCommonPrice(price);
      this.setState({formFields});

    } catch(error) {
      console.log(error);
    }
  }

  //#endregion set price
  formHandler=(showMessage,click) => {
    try {
      if(showMessage)
        showLoader();
      var formFields={...this.state.formFields};
      formFields.itemType=formFields.itemType!==""? parseInt(formFields.itemType):0;
      formFields.departmentID=formFields.departmentID!==""? parseInt(formFields.departmentID):0;
      formFields.categoryID=formFields.categoryID!==""? parseInt(formFields.categoryID):0;
      formFields.taxId=formFields.taxId!==""? parseInt(formFields.taxId):0;
      formFields.price=parseFloat(formFields.price!==""? formFields.price:"0");
      formFields.cost=parseFloat(formFields.cost!==""? formFields.cost:"0");
      formFields.barcodeFormat=formFields.barcodeFormat!==""? parseInt(formFields.barcodeFormat):0;
      this.form.validateFields();
      if(this.form.isValid()) {
        var Obj=new ItemsServices();
        Obj.UpdateItem(formFields).then((response) => {
          if(click==='saveClick') {
            if(showMessage||response.status.toLowerCase()==="error")
              swal({
                title: response.status,
                text: response.message,
                icon: response.status.toLowerCase(),
                timer: response.status.toLowerCase()!=="error"? 3000:0,
                button: response.status.toLowerCase()!=="error"? false:"Ok",
              })
            if(response.status.toLowerCase()!=="error") {
              this.clearFormFieldsOldValues();
              this.props.setCODNULL();
            }
          }

          //   if(response.status.toLowerCase()!=="error")
          //   this.props.history.push("/Items");
        }).catch(function(error) {
          console.log(error);
          //Perform action based on error
        });
      }
      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion post form data to save

  render() {
    return (


      <div>
        <div className="card-body row">
          <FormWithConstraints ref={form => this.form=form} noValidate >
            <div className="col-12">
              <div className="floating-label">
                <input disabled={this.props.isHQConnected} autoFocus className="floating-input" type="text" maxLength="25" placeholder=" " required id="itemLookupCode" name="itemLookupCode" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.itemLookupCode} />
                <span className="highlight"></span>
                <label>Item lookup code * {this.state.formFieldsOldValue.itemLookupCode!==""? " : "+this.state.formFieldsOldValue.itemLookupCode:""}</label>
              </div>
              <FieldFeedbacks for="itemLookupCode">
                <FieldFeedback when="*">Please enter the item lookup code. <i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
              </FieldFeedbacks>
            </div>
            <div className="col-12">

              <div className="floating-label">
                <select disabled={this.props.isHQConnected} className="floating-select" id="itemType" name="itemType" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.itemType}>
                  {/* <option key="I" value="">Select item type</option> */}
                  {this.state.ItemTypes.map((itemtypes) => <option key={itemtypes.id} value={itemtypes.id}>{itemtypes.itemTypeName}</option>)}
                </select>

                <span className="highlight"></span>
                <label>Item type {this.state.formFieldsOldValue.itemType!==""? " : "+this.state.formFieldsOldValue.itemType:""}</label>
              </div>
            </div>
            <div className="col-12">

              <div className="floating-label">
                <textarea disabled={this.props.isHQConnected} className="floating-input" placeholder=" " maxLength="30" id="description" name="description" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.description}></textarea>
                <span className="highlight"></span>
                <label>Description {this.state.formFieldsOldValue.description!==""? " : "+this.state.formFieldsOldValue.description:""}</label>
              </div>
            </div>
            <div className="col-12">
              <div className="floating-label">
                <textarea disabled={this.props.isHQConnected} className="floating-input" placeholder=" " id="extendedDescription" name="extendedDescription" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.extendedDescription}></textarea>
                <span className="highlight"></span>
                <label>Extended description {this.state.formFieldsOldValue.extendedDescription!==""? " : "+this.state.formFieldsOldValue.extendedDescription:""}</label>
              </div>
            </div>
            <div className="col-12">

              <div className="floating-label">
                <textarea disabled={this.props.isHQConnected} className="floating-input" placeholder=" " maxLength="30" rows="1" id="subDescription1" name="subDescription1" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.subDescription1}></textarea>
                <span className="highlight"></span>
                <label>{this.state.DiscriptionLabel.subDescription1!==null? toTitleCase(this.state.DiscriptionLabel.subDescription1):""}{this.state.formFieldsOldValue.subDescription1!==""? " : "+this.state.formFieldsOldValue.subDescription1:""}</label>
              </div>
            </div>
            <div className="col-12">
              <div className="floating-label">
                <textarea disabled={this.props.isHQConnected} className="floating-input" placeholder=" " maxLength="30" rows="1" id="subDescription2" name="subDescription2" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.subDescription2}></textarea>
                <span className="highlight"></span>
                <label>{this.state.DiscriptionLabel.subDescription2!==null? toTitleCase(this.state.DiscriptionLabel.subDescription2):""} {this.state.formFieldsOldValue.subDescription2!==""? " : "+this.state.formFieldsOldValue.subDescription2:""}</label>
              </div>
            </div>
            <div className="col-12">
              <div className="floating-label">
                <textarea disabled={this.props.isHQConnected} className="floating-input" placeholder=" " rows="1" maxLength="30" id="subDescription3" name="subDescription3" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.subDescription3}></textarea>
                <span className="highlight"></span>
                <label>{this.state.DiscriptionLabel.subDescription3!==null? toTitleCase(this.state.DiscriptionLabel.subDescription3):""} {this.state.formFieldsOldValue.subDescription3!==""? " : "+this.state.formFieldsOldValue.subDescription3:""}</label>
              </div>
            </div>
            <div className="col-12">
              <div className="floating-label">
                <select disabled={this.props.isHQConnected} className="floating-select" id="barcodeFormat" name="barcodeFormat" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.barcodeFormat}>
                  <option key="C" value="">Select barcodes</option>
                  {this.state.Barcodes.map((barcodes) => <option key={barcodes.id} value={barcodes.id}>{barcodes.name}</option>)}
                </select>
                <span className="highlight"></span>
                {/* <img src="/assets/images/barcode.svg" alt="" />  */}
                <label>Barcode {this.state.formFieldsOldValue.barcodeFormat!==""? " : "+this.state.formFieldsOldValue.barcodeFormat:""}</label>
              </div>
            </div>
            <div className="col-12">
              <fieldset className="m-0">
                <legend>Member of </legend>
                <div className="row">
                  <div className="col-12 table-responsive">

                    <table className="table  nowrap w-100">
                      <thead>
                        <tr>
                          <th>Type</th>
                          <th>Item lookup code</th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.memberOf.map((memberof) =>
                          <tr key={memberof.itemLookupCode}>
                            <td>{memberof.type}</td>
                            <td>{memberof.itemLookupCode}</td>
                          </tr>)}

                      </tbody>
                    </table>


                  </div>
                </div>
              </fieldset>
            </div>

            <div className="col-12 mt-3">
              <div className="floating-label">
                <select disabled={this.props.isHQConnected} className="floating-select" id="departmentID" name="departmentID" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.departmentID}>
                  <option key="D" value="">Select department</option>
                  {this.state.Departments.map((departments) => <option key={departments.id} value={departments.id}>{departments.name}</option>)}
                </select>
                <span className="highlight"></span>
                <label>Departments {this.state.formFieldsOldValue.departmentID!==""? " : "+this.state.formFieldsOldValue.departmentID:""}</label>
              </div>
            </div>
            <div className="col-12">
              <div className="floating-label">
                <select className="floating-select" id="categoryID" disabled={this.props.isHQConnected?this.props.isHQConnected:this.state.isCategoryDisabled} name="categoryID" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.categoryID}>
                  {this.state.Categories.length<=0? <option key="C" value="">None</option>:''}
                  {this.state.Categories.map((categories) => <option key={categories.id} value={categories.id}>{categories.name}</option>)}
                </select>
                <span className="highlight"></span>
                <label>Categories {this.state.formFieldsOldValue.categoryID!==""? " : "+this.state.formFieldsOldValue.categoryID:""}</label>
              </div>
            </div>
            <div className="col-6">

              <div className="floating-label">
                <select disabled={this.props.isHQConnected} className="floating-select" id="taxId" name="taxId" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.taxId}>
                  <option key="T" value="">None</option>
                  {this.state.ItemTax.map((itemTax) => <option key={itemTax.id} value={itemTax.id}>{itemTax.name}</option>)}
                </select>
                <span className="highlight"></span>
                <label>Item tax {this.state.formFieldsOldValue.taxId!==""? " : "+this.state.formFieldsOldValue.taxId:""}</label>
              </div>
            </div>
            <div className="col-6">

              <div className="floating-label">
                <span className="dolar">{!!this.props.itemCommonPrice&&"$"}</span>
                {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3 clsitemPrice" id="price" onBlur={(e) => this.setProfitMargin(e)} name="price" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonPrice} /> */}
                <NumberFormat   thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemPrice" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="price" onBlur={(e) => this.setProfitMargin(e)} name="price" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonPrice} />
                <span className="highlight"></span>
                <label>Price {this.props.itemCommonPriceOldValue!==""?
                  // " : " +this.props.itemCommonPriceOldValue 
                  <NumberFormat value={this.props.itemCommonPriceOldValue} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                  :""}</label>
              </div>
            </div>
            <div className="col-sm-6 col-12">

              <div className="floating-label ">
                <span className="dolar">{!!this.props.itemCommonCost&&"$"}</span>
                {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3 clsitemCost" id="cost" name="cost" onBlur={(e) => this.setProfitMargin(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonCost} /> */}
                <NumberFormat  thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemCost" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="4" maxLength="11" min="0" id="cost" name="cost" onBlur={(e) => this.setProfitMargin(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonCost} />
                <span className="highlight"></span>
                <label>Cost {this.props.itemCommonCostOldValue!==""?
                  // " : " + this.props.itemCommonCostOldValue
                  <NumberFormat value={this.props.itemCommonCostOldValue} displayType={'text'} decimalScale="4" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                  :""}</label>
              </div>
            </div>
            <div className="col-sm-6 col-12">
              <div className="floating-label d-flex">
                {/* <input type="number" step=".01" placeholder=" " maxLength="5" className="floating-input" id="profitMargin" name="profitMargin" onBlur={(e) => this.setPrice(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonProfit !== null ? this.props.itemCommonProfit : ""} /> */}
                <NumberFormat disabled={this.props.isHQConnected} fixedDecimalScale={true} thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="5" min="0" id="profitMargin" name="profitMargin" onBlur={(e) => this.setPrice(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonProfit!==null? this.props.itemCommonProfit:""} />
                <span className="dolar per-right">%</span>
                <span className="highlight"></span>
                <label>Profit margin {this.props.itemCommonProfitOldValue!==""?
                  // " : " + this.props.itemCommonProfitOldValue
                  <NumberFormat value={this.props.itemCommonProfitOldValue} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': '} suffix={'%'} />
                  :""}</label>
              </div>
            </div>

          </FormWithConstraints>
        </div>
      </div>


    );
  }
}
export default ItemGeneral;