import React from 'react';
import DatePicker from "react-datepicker";
import moment from 'moment';
import NumberFormat from 'react-number-format';

export default function ExpandedPropertiesComponent(props) {
    const { itemExtendedProperty,saveForm,getCustomList ,CustomDropList,onChangeHandler,formFields} = props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <button className='d-none' id="ItemExtSave_ID"  onClick={() => props.formHandler()} >Save</button>
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblExtendedProperties" className="table w-100" >
                                <thead>
                                    <tr>
                                       <th>Name</th>
                                        <th>value</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                {getCustomList.map((List,index) =>
									<tr key={List.id}>
                                        <td>{List.name}</td>
                                       
                                        <td>
                                            {List.type==0?
                                            <input  type="text" className="floating-input floating-input-label db-input"
                                            name="custom_value" onChange={(e) => props.onChangeHandler(e,List.type,List.name)} value={List.value}/>:""
                                            }
                                           
                                             {List.type==1?
                                                <DatePicker className="floating-input" id="fromDate"
                                                onChange={(e) => props.onChangeHandler(e,List.type,List.name)}
                                                placeholderText="mm/dd/yyyy"
                                                dateFormat="MM/DD/YYYY"
                                                value={moment(List.value).format('MM/DD/YYYY')=='Invalid date'? '':moment(List.value).format('MM/DD/YYYY')}
                                                autocomplete="off"
                                                />:""
                                             }
                                             {List.type==2?
                                                <div className="floating-label">
                                                <span className="dolar">{"$"}</span>
                                                {/* <input type="number" step=".01" maxLength="8" placeholder=" " onBlur={(e) => this.setpricingProfitMargin(e)} className="floating-input pl-3 clsitemPrice" id="itemPricingPrice" name="itemPricingPrice" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonPrice} /> */}
                                                <NumberFormat autoFocus value={List.value} thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemCost" onChange={(e) => props.onChangeHandler(e,List.type,List.name)} type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="itemPricingPrice" name="itemPricingPrice"  />
                                               
                                                </div>
                                             :"" }
                                             {List.type==3?
                                                <input inputMode="numeric" value={List.value} className="floating-input quantitylength" maxLength="5" min="0" id="quantity" name="quanity" type="number" pattern="[0-9]*" placeholder=" " 
                                                onChange={(e) => props.onChangeHandler(e,List.type,List.name)} />
                                              :"" }
                                             {List.type==4?
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id={List.name} name={List.name} onChange={(e) => props.onChangeHandler(e,List.type,List.name)} checked={List.value=="true"?true:false} />
                                                    <label className="custom-control-label font-weight-bold" htmlFor={List.name}></label>
                                                </div>:""
                                             }
                                             {List.type==5?
                                                <div className="floating-label">
                                                    <select className="floating-select" tabIndex="2" onChange={(e) => props.onChangeHandler(e,List.type,List.name)}  id={List.name} name={List.name} value={List.value}>
                                                    {/* <option key="I" value="">Select item type</option> */}
                                                    {List.listValue.split('|').map((data) => <option key={data} value={data}>{data}</option>)
                                                    }
                                                    </select>
                                                </div>
                                             :"" }
                                        </td>

                                    </tr>
                                )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}