import React,{Component} from 'react';
import './Sidebar.css'
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import MenuServices from '../../Services/MenuServices'
import {history} from '../../history';
import {showmenu,showSubMenu,hidemenu,toTitleCase} from '../../Custom';
class Sidebar extends Component {
	state={
		MenuList: [],
		activeMenu: "Items",
		parentActiveMenu: "",
		moreBtnClass: ""
	}

	async componentDidMount() {
		try {
			const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
			var role=userDetails!==""&&userDetails.role!==null? userDetails.role.split(','):"";
			if(role.length>1)
				role=role.filter(name => name.includes(process.env.REACT_APP_UserRole_Manager)).length>0? role.filter(name => name.includes(process.env.REACT_APP_UserRole_Manager)):role[0];
			this.GetMenu(role);
			this.setActiveMenu(window.location.pathname.toLowerCase());
			this.setState({userDetails: userDetails});
		} catch(error) {
			console.log(error);

		}
	}
	setActiveMenu=(pathname) => {
		try {
	
			pathname=pathname.substring(1,pathname.length).split("/")[0];

			this.setState({moreBtnClass: ""});
			switch(pathname) {
				case "dashboard":
					this.setState({activeMenu: "Dashboard"});
					break;
				case "order":
					this.setState({activeMenu: "Dashboard"});
					this.setState({moreBtnClass: "more-btn-wrap"});
					break;
				case "items":
					this.setState({activeMenu: "Items"});
					break;
				case "itemcreate":
					this.setState({activeMenu: "Items"});
					break;
				case "itemedit":
					this.setState({activeMenu: "Items"});
					break;
				case "workorder":
					this.setState({activeMenu: "Work order"});
					break;
				case "workorderedit":
					this.setState({activeMenu: "Work order"});
					this.setState({moreBtnClass: "more-btn-wrap"});
					break;
				case "purchaseorder":
					this.setState({activeMenu: "Purchase order"});
					break;
				case "purchaseorderedit":
					this.setState({activeMenu: "Purchase order"});
					this.setState({moreBtnClass: "more-btn-wrap"});
					break;
				case "activitylog":
					this.setState({activeMenu: "Activity log"});
					break;
				case "podsetting":
					this.setState({activeMenu: "Settings"});
					break;
				case "configuredatabase":
					this.setState({activeMenu: "Settings"});
					break;
				case "adminconfiguredatabase":
					this.setState({activeMenu: "Settings"});
					break;
				case "inventorylist":
					this.setState({activeMenu: "Inventory List",parentActiveMenu: "Inventory"});
					break;
				case "inventorydetail":
					this.setState({activeMenu: "Inventory List",parentActiveMenu: "Inventory"});
					break;
				case "finalinventory":
					this.setState({activeMenu: "Inventory List",parentActiveMenu: "Inventory"});
					break;
				case "pastinventory":
					this.setState({activeMenu: "Past Inventory Counts",parentActiveMenu: "Inventory"});
					break;
				case "apicolumnmapping":
					this.setState({activeMenu: "Column Mapping"});
					break;
				case "updatebulkitem":
					this.setState({activeMenu: "Update Bulk Item"});
					break;
				case "transferout":
					this.setState({activeMenu: "Transfer Out"});
					break;
				case "transferoutedit":
					this.setState({activeMenu: "Transfer Out"});
					this.setState({moreBtnClass: "more-btn-wrap"});
					break;	
				default:
					break;
			}
			return;
		} catch(error) {
			console.log(error);

		}
	}
	GetMenu=async (role) => {

		try {
			const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
			
			var Obj=new MenuServices();
			await Obj.GetMenu(role).then((menulist) => {

				if(!!menulist.values) {
					if(!userDetails.isThirdPartyAPI)
					{
						menulist.values=menulist.values.filter(x=>x.name.toLowerCase()!="apicolumnmapping")

					}
					
					this.setState({
						MenuList: menulist.values
					});
				}

			});
		} catch(error) {
			console.log(error);

		}
	}
	logOut=() => {
		Cookies.remove('userDetails');
		Cookies.remove('styleLight');
		document.getElementById('style-lightdark').href='/assets/css/style.css';
		Cookies.remove('jwtToken');
		Cookies.remove('InventoryUser');
		Cookies.remove('globalSearchValue');
		history.push("/Login");
	}
	render() {
		const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";

		return (
			<div>
				<aside className="bg-sidebar d-none d-md-block">
					<div className="sidebar-menu">
						<ul id="mainmenu" >
							{this.state.MenuList.filter((item) => item.parentId===0).map((menulist) =>
								<li key={menulist.name} className={`text-center py-2 ${(this.state.activeMenu===menulist.displayName||this.state.parentActiveMenu===menulist.displayName)? " active":""}`} onClick={() => {
									if(menulist.link!==null) {
										this.setState({activeMenu: menulist.displayName});
										if(window.location.patname===menulist.link)
											window.location.reload(false);
										else
											history.push(menulist.link);
									}
								}}>
									<a><i className="material-icons">{menulist.icon}</i>
										<span>{toTitleCase(menulist.displayName)}</span></a>
									{this.state.MenuList.filter((subitem) => subitem.parentId===menulist.menuId).length>0?
										<div className="submenu-wrapper">
											<ul className="submenu text-left">
												{this.state.MenuList.filter((subitem) => subitem.parentId===menulist.menuId).map((submenulist,index) =>


													<li key={submenulist.displayName} className={this.state.activeMenu===submenulist.displayName? " active":""} onClick={() => {
														if(submenulist.link!==null) {
															this.setState({activeMenu: submenulist.displayName});
															if(window.location.patname===submenulist.link)
																window.location.reload(false);
															else
																history.push(submenulist.link);
														}
													}}>
														<a><span>{toTitleCase(submenulist.displayName)}</span></a>
													</li>

												)}
											</ul>
										</div>:null}
								</li>
							)}

						</ul>
					</div>
				</aside>

				<div onClick={showmenu} className={`menu-btn-mobile d-md-none ${this.state.moreBtnClass}`} id="mobilemenu">
					<i className="material-icons">more_horiz</i>
				</div>

				<div id="mobmenu" className="black-screen d-md-none">
					<div onClick={hidemenu} className="menu-btn-mobile">
						<i className="material-icons">clear</i>
					</div>
					<ul className="mobile-menu d-md-none">
						{this.state.MenuList.filter((item) => item.parentId===0).map((menulist) =>
							<li key={menulist.displayName} id={menulist.name} className={`text-center py-2 ${(this.state.activeMenu===menulist.displayName||this.state.parentActiveMenu===menulist.displayName)? " active":""}`} onClick={() => {
								showSubMenu(menulist.name);
								if(menulist.link!==null) {
									hidemenu();
									this.setState({activeMenu: menulist.displayName});
									if(window.location.patname===menulist.link)
										window.location.reload(false);
									else
										history.push(menulist.link);
								}
							}}>
								<a><span>{toTitleCase(menulist.displayName)}</span><div className="icon-wrap-mobile"> <i className="material-icons">{menulist.icon}</i></div>
								</a>
								{this.state.MenuList.filter((subitem) => subitem.parentId===menulist.menuId).length>0?
									<div className="mobile-submenu">
										{this.state.MenuList.filter((subitem) => subitem.parentId===menulist.menuId).map((submenulist) =>


											<a key={submenulist.displayName} className={this.state.activeMenu===submenulist.displayName? " active":""} onClick={() => {
												if(submenulist.link!==null) {
													hidemenu();
													this.setState({activeMenu: submenulist.displayName});
													if(window.location.patname===submenulist.link)
														window.location.reload(false);
													else
														history.push(submenulist.link);
												}
											}}>
												{toTitleCase(submenulist.displayName)}</a>

										)}

									</div>:null}
							</li>

						)}
						<li className="text-center py-2 ">
							<a onClick={this.logOut}><span>Logout</span>
								<div className="icon-wrap-mobile"> <i className="material-icons">exit_to_app</i></div>
							</a>
						</li>
					</ul>

				</div>
			</div>

		);
	}
}

export default Sidebar;