import React, { Component } from 'react';
import './ConfigureDatabase.css';
import AuthenticateServices from '../Services/AuthenticateServices'
import swal from 'sweetalert';
import { FormWithConstraints, FieldFeedbacks, FieldFeedback } from 'react-form-with-constraints'
import { showLoader, hideLoader, viewPassword } from '../Custom';
import { Helmet } from 'react-helmet'
import { history } from '../history';
class ConfigureDatabase extends Component {
  constructor(props) {
    super(props);

    this.state = {

      formFields: {
        serverName: "",
        username: "",
        password: "",
        port: "",
        databaseName: ""
      },
      isRender: false
    }
    this.formHandler = this.formHandler.bind(this);

  }
  async componentWillMount() {
    showLoader();
    await this.loadLoginPage();
    hideLoader();
  }
  //#region load login page
  loadLoginPage = async () => {

    var Obj = new AuthenticateServices();
    await Obj.loadLoginPage().then((response) => {
      if (response !== "") {
        if (response.code !== "000" && response.code !== "E09" && response.code !== "E10")
          swal({
            title: response.status,
            text: response.message,
            icon: response.status.toLowerCase(),
            timer: 0,
            button: "Ok",
          })
        if (response.code === "000") {

          history.push("/Login");
        }
        else if (response.code === "E10")
          history.push("/ConfigureSystem");
        else if (response.code === "E09")
          this.setState({ isRender: true });

      }
    }).catch(function (error) {
      console.log(error);
      //Perform action based on error
    });

  }
  //#endregion load login page   
  //#region all inpute onchange method   
  inputChangeHandler(e) {
    try {
      var value = e.target.value;
      this.form.validateFields(e.target);
      let formFields = { ...this.state.formFields };
      formFields[e.target.name] = value;
      this.setState({
        formFields
      });
    } catch (error) {
      console.log(error);
      hideLoader();
    }

  }
  //#endregion all inpute onchange method
  //#region Post method for save ConfigureDatabase on save & continue button click 
  formHandler = async (e) => {
    try {
      showLoader();
      e.preventDefault();
      this.form.validateFields();

      if (this.form.isValid()) {
        var Obj = new AuthenticateServices();
        await Obj.CreateXML(this.state.formFields).then((response) => {

          swal({
            title: response.status,
            text: response.message,
            icon: response.status.toLowerCase(),
            timer: response.status.toLowerCase() !== "error" ? 3000 : 0,
            button: response.status.toLowerCase() !== "error" ? false : "Ok",
          })
          hideLoader();
          if (response.code === "000")
            this.props.history.push("/Login");

        }).catch(function (error) {
          hideLoader();
          console.log(error);
          //Perform action based on error
        });
      }
      hideLoader();
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion Post method for save ConfigureDatabase on save & continue button click 

  //#region render html   
  render() {
    return (
      <div>
        <Helmet>
          <title>Database configuration | Modus Go!</title>
        </Helmet>
        {this.state.isRender && <div>
          <div className="float-left back-img">
            <img className="mb-4 d-block mx-auto logo-set" src="/assets/images/logo_blue.svg" alt="" />
          </div>
          <div className="right-wrap float-left">
            <div className="brand-logo-center">
              <img src="/assets/images/logo-full.svg" alt="" />
            </div>
            <div className="login-wrap">
              <div className="mainbox p-3 pl-4 ">
                <FormWithConstraints ref={form => this.form = form} noValidate onSubmit={(e) => this.formHandler(e)}>
                  <h2 className="login-title text-gray">Database configuration</h2>
                  <div className="row">
                    <div className="col-12">
                      <div className="floating-label">
                        <input type="text" autoFocus className="floating-input" maxLength="100" placeholder=" " required name="serverName" id="serverName" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.serverName} /><span className="highlight"></span>
                        <label>Server name / IP *</label><FieldFeedbacks for="serverName">
                          <FieldFeedback when="*">Please enter the server name / IP.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    <div className="col-sm-6">

                      <div className="floating-label ">
                        <input type="text" className="floating-input" placeholder=" " maxLength="50" required name="username" id="username" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.username} /><span className="highlight"></span>
                        <label>Username *</label>
                        <FieldFeedbacks for="username">
                          <FieldFeedback when="*">Please enter the username.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    <div className="col-sm-6">

                      <div className="floating-label ">
                        <input type="password" className="floating-input" placeholder=" " maxLength="50" required name="password" id="password" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.password} /><span className="highlight"></span>
                        <label>Password *</label>
                        <span className="eye-icon"><i id="pass-status" className="zmdi zmdi-eye-off zmdi-hc-fw" onClick={()=>viewPassword("")}></i></span>
                        <FieldFeedbacks for="password">
                          <FieldFeedback when="*">Please enter the password.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    <div className="col-sm-6">

                      <div className="floating-label ">
                        <input type="text" className="floating-input" placeholder=" " maxLength="10" name="port" id="port" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.port} /><span className="highlight"></span>
                        <label>Port </label>
                      </div>
                    </div>
                    <div className="col-sm-6">

                      <div className="floating-label">
                        <input type="text" className="floating-input" placeholder=" " maxLength="50" required name="databaseName" id="databaseName" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.databaseName} /><span className="highlight"></span>
                        <label>Database name *</label>
                        <FieldFeedbacks for="databaseName">
                          <FieldFeedback when="*">Please enter the database name.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    <div className="col-12 d-flex justify-content-end">
                      <button className="btn rounded-pill btn-primary" type="submit">Save</button>
                    </div>
                  </div>
                  <p className="copyright-text">&copy; 2020 Modus technologies. All right reserved.</p>
                </FormWithConstraints>
              </div>
            </div>
          </div>
        </div>}
      </div>



    );
  }
}
//#endregion render html
export default ConfigureDatabase;