import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    ManageItemHistoryList: [],
    totalRecord: 0,
    ManageItemHistoryListParam: {
        userRole:'',
        pageNo: 0,
        pageSize: 50
    },
    isLoading: false,
    status: "",
    message: "",
    responseStatusProgress:"",
    responseStatusComplete:"",
};

export default function manageItemHistoryReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_MANAGEHISTORY_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_MANAGEHISTORY_SUCCESS:
            return {
                ...state,
                ManageItemHistoryList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_MANAGEHISTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.GET_MANAGEHISTORYLIST_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_MANAGEHISTORYLIST_SUCCESS:
            return {
                ...state,
                isLoading: false,
                responseStatusComplete:action.data.message!="Completed"?action.data.message:"",
                status:action.data.status
            }
        case types.GET_MANAGEHISTORYLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }


        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                ManageItemHistoryListParam: action.data,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                responseStatusComplete:"",
            }
        case types.GETALL_KEYVALUE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GETALL_KEYVALUE_SUCCESS:
         
            return {
                ...state,
                isLoading: false,
                responseStatusProgress:action.data.message!="Completed"?action.data.message:"",
                responseStatusComplete:action.data.message=="Completed"?action.data.message:""
            }
        case types.GETALL_KEYVALUE_FAILED:
            return {
                ...state,
                isLoading: false,
            }  
        default:
            return state;
    }
}