import React from 'react';
import KitComponent from '../component/KitComponent';
import { } from '../state/action/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { withRouter } from 'react-router-dom';

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class KitContainer extends React.Component {

    componentDidMount() {
        $('#tblKit').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });

    }
    componentWillUnmount() {
        $('#tblKit').DataTable().destroy();
    }
    render() {


        return (
            <KitComponent
                {...this.props}
            />

        )

    }
}

const mapDispatchToProps = (dispatch) => {
    return {

    };
};

const mapStateToProps = (state) => {
    const { } = state.KitReducer;
    return {


    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(KitContainer));