const obj = new AuthenticateService();
//#region get order item list
export function getInventoryItemList(data, isLoading,filterColumn) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_INVENTORYITEM_REQUEST,
            isLoading: isLoading
        });
        let url = 'Inventory/FinalInventoryItemList';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    // if (payload.values.length <= 0)
                    //     history.push("/NotFound");
                    $('#tblInventoryItem').DataTable().destroy();
                    dispatch({
                        type: types.GET_INVENTORYITEM_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblInventoryItem').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        "initComplete": function( settings, json ) {
                           
                            if(!filterColumn.isSetItemCode)
                            {
                                $(".isSetItemCode").hide()
                            }
                            if(!filterColumn.isSetQuantity)
                            {
                                $(".isSetQuantity").hide()
                            }
                            if(!filterColumn.isSetDescription)
                            {
                                $(".isSetDescription").hide()
                            }
                            if(!filterColumn.isSetDepartment)
                            {
                                $(".isSetDepartment").hide()
                            }
                            if(!filterColumn.isSetCategory)
                            {
                                $(".isSetCategory").hide()
                            }
                            if(!filterColumn.isSetLastTime)
                            {
                                $(".isSetLastTime").hide()
                            }
                            if(!filterColumn.isSetOnHandQuantity)
                            {
                                $(".isSetOnHandQuantity").hide()
                            }
                           
                            if(!filterColumn.isSetActualQuantity)
                            {
                                $(".isSetActualQuantity").hide()
                            }
                            if(!filterColumn.isSetFinalQuantity)
                            {
                                $(".isSetFinalQuantity").hide()
                            }
                          },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_INVENTORYITEM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
//#region set order item list param
export function setInventoryItemListParam(data) {

    return (dispatch) => {
        var ItemListParam={
            sortColumnName: "lastTime",
            sortDirection: "desc",
            pageNo: 0,
            pageSize: 50,
            searchText: "",
            departmentIds: data.departmentIds,
            categoryIds: data.categoryIds,
            supplierIds: data.supplierIds
        }
        dispatch({
            type: types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM,
            data: data,
        });
        dispatch({
            type: types.SET_DELTA_DELTALISTPARAM,
            data: ItemListParam,
        });
        dispatch({
            type: types.SET_NONESCANNEDITEM_NONESCANNEDITEMLISTPARAM,
            data: ItemListParam,
        });
    }
}
//#endregion set order item list param
//#region get  order  detail
export function getInventoryDetail(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_INVENTORYDETAIL_REQUEST,

        });
        let url = 'Inventory/GetInventoryById';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              

                if (response.status === 200) {
                    /* Logic */

                    if (payload.code === "E08" || payload.code === "E03" || payload.code === "E05")
                        history.push("/InventoryList");
                    dispatch({
                        type: types.GET_INVENTORYDETAIL_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.GET_INVENTORYDETAIL_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get  order detail
//#region delete order item
export function deleteInventoryItem(deleteRequestParameter, orderItemListParam,filterColumn) {
    return (dispatch) => {
        dispatch({
            type: types.DELETE_INVENTORYITEM_REQUEST,
        });
        let url = 'Inventory/DeleteInventoryItem';
        return post(url, deleteRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getInventoryItemList(orderItemListParam, true,filterColumn));
                    dispatch({
                        type: types.DELETE_INVENTORYITEM_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.DELETE_INVENTORYITEM_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
