//#region commit Inventory
export function commitInventory(commitInventoryRequestParameter,filterColumn) {
    return (dispatch) => {
        dispatch({
            type: types.COMMIT_INVENTORY_REQUEST,
        });
        let url = 'Inventory/CommitFinalInventory';
        return post(url, commitInventoryRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    if (payload.status.toLowerCase() === "error" || payload.isFinal)
                        swal({
                            title: payload.status,
                            text: payload.message,
                            icon: payload.status.toLowerCase(),
                            timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                            button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                        })
                    if (!payload.isFinal) {
                        if (payload.status.toLowerCase() !== "error")
                            history.push("/InventoryList");
                    }
                    else{
                        var ItemListParam={
                            sortColumnName: "lastTime",
                            sortDirection: "desc",
                            pageNo: 0,
                            pageSize: 50,
                            searchText: "",
                            departmentIds: [],
                            categoryIds: [],
                            supplierIds: []
                        }
                        dispatch(getInventoryItemList(ItemListParam, true,filterColumn));
                        dispatch(getCategories({
                            departmentId: []
                          }));
                          dispatch(setIsCategoryDisabled(true));
                        dispatch(setInventoryItemListParam(ItemListParam));
                    }
                    dispatch({
                        type: types.COMMIT_INVENTORY_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.COMMIT_INVENTORY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
