import React from 'react';
import NumberFormat from 'react-number-format';
export default function KitComponent(props) {
    const { itemKit } = props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblKit" className="table w-100" >
                                <thead>
                                    <tr>
                                        <th className="plus-sort"></th>
                                        <th>Item Lookup</th>
                                        <th>Description</th>
                                        <th>Price</th>
                                        <th>Quantity</th>


                                    </tr>
                                </thead>
                                <tbody>
                                    {itemKit.map((item, index) =>
                                        <tr key={"tblItemKit-" + index}>
                                            <td className="details-control plus-sort"></td>
                                            <td>{item.itemLookupCode}</td>
                                            <td>{item.description}</td>
                                            <td><NumberFormat value={item.price} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td>{item.quantity}</td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}