import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    orderItemList: [],
    orders: [],
    orderDetail: {},
    customers: [],
    totalRecord: 0,
    customerRequestParam: {
        searchText: "",
        pageNo: 0,
        pageSize: 0
    },
    orderItemListParam: {
        orderId: 0,
        searchText: "",
        pageNo: 0,
        pageSize: 50
    },
    generateWORequestParam: {
        orderId: 0,
        customerId: 0
    },
    isNewlyCreated: false,
    combineOrderRequestParam: {
        orderId: 0,
        userId: 0,
        orderIds: ""
    },
    isLoading: false,
    searchValue: "",
    searchTerm: "",
    searchData: [],
    status: "",
    message: "",
    supplierList: [],
    isOrderExist: true,
    requestParameter: {
        orderId: 0,
        pageNo: 0,
        pageSize: 50,
        sortColumnName: "",
        sortDirection: "",
        searchText: "",
    },
};

export default function OrderReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ORDERITEM_SUCCESS:
            return {
                ...state,
                orderItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isNewlyCreated: action.isNewlyCreated,
                isLoading: false,
            }
        case types.GET_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ORDERDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ORDERDETAIL_SUCCESS:
            return {
                ...state,
                orderDetail: action.data,
                isLoading: false,
            }
        case types.GET_ORDERDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDERITEM_ORDERITEMLISTPARAM:
            return {
                ...state,
                orderItemListParam: action.data,
                isLoading: false,
            }
        case types.SET_ORDER_COMBINEORDERPARAM:
            return {
                ...state,
                combineOrderRequestParam: action.data,
                isLoading: false,
            }
        case types.UPDATE_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_ORDERITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.INSERT_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.INSERT_ORDERITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.INSERT_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMBINE_ORDER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.COMBINE_ORDER_SUCCESS:
            return {
                ...state,
                isLoading: false,
                combineOrderRequestParam: action.combineOrderRequestParam,
            }
        case types.COMBINE_ORDER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_EXISTINGORDER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_EXISTINGORDER_SUCCESS:
            return {
                ...state,
                orders: action.data,
                isLoading: false,
            }
        case types.GET_EXISTINGORDER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_ORDERITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.DELETE_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_ORDERITEMSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ORDERITEMSEARCH_SUCCESS:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_ORDERITEMSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_CUSTOMERS_SUCCESS:
            return {
                ...state,
                customers: action.data,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_WO_REQUESTPARAM:
            return {
                ...state,
                generateWORequestParam: action.data,
                isLoading: false,
            }
        case types.GENERATE_WO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GENERATE_WO_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.GENERATE_WO_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GENERATE_PO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GENERATE_PO_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.GENERATE_PO_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.GET_SUPPLIERLIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_SUPPLIERLIST_SUCCESS:
            return {
                ...state,
                supplierList: action.data,
                isLoading: false,
            }
        case types.GET_SUPPLIERLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ORDEREXIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ORDEREXIST_SUCCESS:
            return {
                ...state,
                isOrderExist: action.data.result,
                isLoading: false,
            }
        case types.GET_ORDEREXIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDER_REQUESTPARAMETER:
            return {
                ...state,
                 requestParameter: action.data,
                  isLoading: false,
            }
        default:
            return state;
    }
}