import React from 'react';
import InventoryDetailComponent from '../component/InventoryDetailComponent';
import {
  getInventoryItemList,setInventoryItemListParam,
  deleteInventoryItem,getInventoryDetail,setScrenLock,
  clearMessage,updateInventory,setdescription,setSuccessDialogue,endInventory,uploadInventory
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {history} from '../../../history'
import {encryptURL,decryptURL} from '../../../Custom'
window.$=$;


function handleInventoryItemGridDeleteIcon(classdata) {
  $("#tblInventoryItem").on('click','.InventoryItemGridDeleteIcon',function() {
    var dataString=$(this).attr('data');
    classdata.deleteInventoryItemHandler(dataString);
  });
}

function ScanItemContiner() {
    $('#btnUpload').attr('disabled', false);
    $('#btnEndInv').attr('disabled', false);
    $('#btnBack').attr('disabled', false);
  $("#buttonId").on('click','.itemscanshow',function() {
    $("#InventoryDetailItemScanWrap").show();
    $(".itemscanshow").hide();
    $("#DivDescriptionID").hide();
    $('#btnUpload').attr('disabled', true);
    $('#btnEndInv').attr('disabled', true);
    $('#btnBack').attr('disabled', true);
    $('#mobilemenu').attr('disabled', true);
  });
 
  $(".closeitemscan-wrap").click(function() {
    $("#InventoryDetailItemScanWrap").hide();
    $(".itemscanshow").show();
    $("#DivDescriptionID").show();
    $('#btnUpload').attr('disabled', false);
    $('#btnEndInv').attr('disabled', false);
    $('#btnBack').attr('disabled', false);
    $('#mobilemenu').attr('disabled', false);
  });
}
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
class InventoryDetailContainer extends React.Component {

  state={
    isInventoryScanItem: false,
    isRender: false,
    status: "",
    message: "",
    isSuccessDialogue: false,
    updatemessage:"",
    isItemCode:true,
    isQuantity:true,
    isDescription:true,
    isLastTime:true,
    isSetItemCode:true,
    isSetQuantity:true,
    isSetDescription:true,
    isSetLastTime:true,
    isOnHandQuantity:true,
    isSetOnHandQuantity:true,
    selectedFile:null,
    isScreenLock:false,
  }

  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    let inventoryItemListParam={...this.props.inventoryItemListParam};
    inventoryItemListParam["inventoryId"]=parseInt(decryptURL(this.props.match.params.listId));
    let inventoryDetailReqParm={
      userId: parseInt(userDetails.userId),
      inventoryId: parseInt(decryptURL(this.props.match.params.listId))
    }
    this.props.getInventoryDetail(inventoryDetailReqParm);
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.getInventoryItemList();
    window.addEventListener('scroll',this.handleScroll);
    handleInventoryItemGridDeleteIcon(this);
    ScanItemContiner();
    ColumnFilterCheckbox();
    let mobilemenu= document.getElementById("mobilemenu");
    if(!!mobilemenu)
    mobilemenu.classList.add('d-none')
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager&&userDetails.role!==process.env.REACT_APP_UserRole_Cashier) {

        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  getInventoryItemList=() => {
    let inventoryItemListParam={...this.props.inventoryItemListParam};
    inventoryItemListParam["inventoryId"]=parseInt(decryptURL(this.props.match.params.listId));
    this.props.getInventoryItemList(inventoryItemListParam,false,this.state.isSetItemCode,this.state.isSetDescription,this.state.isSetLastTime,this.state.isSetOnHandQuantity,this.state.isSetQuantity);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblInventoryItem').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        if(this.props.totalRecord!==this.props.inventoryItemList.length) {
          let inventoryItemListParam={...this.props.inventoryItemListParam};
          let pagesize=this.props.inventoryItemListParam.pageSize+50;
          inventoryItemListParam["inventoryId"]=parseInt(decryptURL(this.props.match.params.listId));
          inventoryItemListParam["pageSize"]=pagesize;
          this.props.setInventoryItemListParam(inventoryItemListParam);
          this.props.getInventoryItemList(inventoryItemListParam,false,this.state.isSetItemCode,this.state.isSetDescription,this.state.isSetLastTime,this.state.isSetOnHandQuantity,this.state.isSetQuantity);
        }
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading
  //#region delete order item
  deleteInventoryItemHandler=(id) => {
    try {
      if(this.props.isModified) {
        swal({
          title: types.DELETE_TITLE,
          text: types.DELETE_MESSAGE,
          icon: "warning",
          buttons: {
            confirm: true,
            cancel: true
          },
          dangerMode: true,
        })
          .then((willDelete) => {
            if(willDelete) {
              let deleteRequestParameter={
                itemId: parseInt(id),
                inventoryId: parseInt(decryptURL(this.props.match.params.listId))
              };

              this.props.deleteInventoryItem(deleteRequestParameter,this.props.inventoryItemListParam,this.state.isSetItemCode,this.state.isSetDescription,this.state.isSetLastTime,this.state.isSetOnHandQuantity,this.state.isSetQuantity);
              $.each($.fn.dataTable.tables(true),function() {

                if($(this).DataTable().row('.parent').length) {
                  var parentLength=document.getElementsByClassName('parent').length;
                  for(var i=0;i<parentLength;i++) {
                    $('td.details-control',$(this).DataTable().row('.parent').node()).click();
                  }

                }
              });
            }
          });
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion delete order item
  showInventoryScanItem=() => {
    this.setState({
      isInventoryScanItem: true,
    })



  }
  closeInventoryScanItem=() => {
    this.setState({
      isInventoryScanItem: false,
    })
  }
  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";
      switch(sortColumnName) {
        case "lastTime":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="lastTime"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "itemLookupCode":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="itemLookupCode"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "quantity":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="quantity"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "description":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="description"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        default:
          break;
      }
      var data={
        inventoryId: this.props.inventoryItemListParam.inventoryId,
        pageNo: this.props.inventoryItemListParam.pageNo,
        pageSize: this.props.inventoryItemListParam.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection

      };
      var inventoryItemListParam={...this.props.inventoryItemListParam};
      inventoryItemListParam["sortColumnName"]=sortColumnName;
      inventoryItemListParam["sortDirection"]=sortdirection;
      this.props.setInventoryItemListParam(inventoryItemListParam);
      this.props.getInventoryItemList(data,false,this.state.isSetItemCode,this.state.isSetDescription,this.state.isSetLastTime,this.state.isSetOnHandQuantity,this.state.isSetQuantity);
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler


  //#region all inpute onchange method   
  inputChangeHandler=(e) => {
    try {
      var value=e.target.value;
      this.props.setdescription(value);
    } catch(error) {
      console.log(error);

    }
  }
  //#endregion all inpute onchange method   

  updateInventoryHandler=() => {

    let updateInventoryRequestParameter={
      InventoryId: this.props.inventoryDetail.inventoryId,
      description: this.props.description
    }
    this.props.updateInventory(updateInventoryRequestParameter);
  }
  toggleInventoryDetailDatatable=() => {this.setState({isShowInventoryDetailDatatable: !this.state.isShowInventoryDetailDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowInventoryDetailDatatable: false})}

  //#region filter checkbox onchange function
  setItemCodeCheckbox = () => {
    this.setState({ isItemCode: !this.state.isItemCode }, () => {
    this.setState({ isSetItemCode: this.state.isItemCode });
    });
  }

  setQuantityCheckbox = () => {
    this.setState({ isQuantity: !this.state.isQuantity }, () => {
    this.setState({ isSetQuantity: this.state.isQuantity });
    });
  }

  setDescriptionCheckbox = () => {
    this.setState({ isDescription: !this.state.isDescription }, () => {
    this.setState({ isSetDescription: this.state.isDescription });
    });
  }
  setLastTimeCheckbox = () => {
    this.setState({ isLastTime: !this.state.isLastTime }, () => {
    this.setState({ isSetLastTime: this.state.isLastTime });
    });
  }
  setOnHandQuantityCheckbox = () => {
    this.setState({ isOnHandQuantity: !this.state.isOnHandQuantity }, () => {
    this.setState({ isSetOnHandQuantity: this.state.isOnHandQuantity });
    });
  }
  //#endregion filter checkbox onchange function
  endInventoryHandler=() => {
    if(this.props.inventoryItemList.length>0)
    {
      let endInventoryRequestParameter={
        InventoryId: this.props.inventoryDetail.inventoryId,
      }
      this.props.endInventory(endInventoryRequestParameter);
    }
    else
    {
      swal("","No item found!","error")
    }
   
  }
  onFileChange = (event) => {
    
    // Update the state
    this.setState({ selectedFile: event.target.files[0] });
  
  };
  uploadInventoryHandler=()=>{
    if(this.state.selectedFile==null)
    {
      swal("","Please select file","error")
    }
    else
    {
      const formData = new FormData();
      // Update the formData object 
      formData.append( 
        "itemFile", 
        this.state.selectedFile, 
        this.state.selectedFile.name 
      );
      formData.append( 
        "inventoryId", 
        parseInt(decryptURL(this.props.match.params.listId))      
        );
      formData.append( 
        "userId", 
        parseInt(userDetails.userId)   
        );
      this.props.uploadInventory(formData,false,this.props.inventoryItemListParam,this.state.isSetItemCode,this.state.isSetDescription,this.state.isSetLastTime,this.state.isSetOnHandQuantity,this.state.isSetQuantity);
    }
   
  }
  setScreenLock =() => {
    let mobilemenu= document.getElementById("mobilemenu");

    this.setState({ isScreenLock: !this.state.isScreenLock }, () => {
        this.setState({ isScreenLock: this.state.isScreenLock });
        if(this.state.isScreenLock && !!mobilemenu)
        {
          mobilemenu.classList.add('d-none')
        }
        else if(!!mobilemenu)
        {
          mobilemenu.classList.remove('d-none')
        }
        this.props.setScrenLock(this.state.isScreenLock)
    }); 
  }
  scanItem=()=>
  {
    this.setState({ isScreenLock: true });
    this.props.setScrenLock(true)

  }
  render() {
    //  const {inventoryItemList}=this.props   
    const { isSuccessDialogue, updatestatus, updatemessage } = this.state;
    if (isSuccessDialogue) {
      this.props.setSuccessDialogue(false, updatestatus, updatemessage);
    }

    return (
      <InventoryDetailComponent
        {...this.props}
        userDetails={userDetails}
        showInventoryScanItem={this.showInventoryScanItem}
        isInventoryScanItem={this.state.isInventoryScanItem}
        deleteInventoryItemHandler={this.deleteInventoryItemHandler}
        closeInventoryScanItem={this.closeInventoryScanItem}
        getInventoryItemList={this.getInventoryItemList}
        toggleInventoryDetailDatatable={this.toggleInventoryDetailDatatable}
        isRender={this.state.isRender}
        sortHandler={this.sortHandler}
        inputChangeHandler={this.inputChangeHandler}
        updateInventoryHandler={this.updateInventoryHandler}
        isShowInventoryDetailDatatable={this.state.isShowInventoryDetailDatatable}

        setDescriptionCheckbox={this.setDescriptionCheckbox}
        setQuantityCheckbox={this.setQuantityCheckbox}
        setItemCodeCheckbox={this.setItemCodeCheckbox}
        setLastTimeCheckbox={this.setLastTimeCheckbox}
        isItemCode={this.state.isItemCode}
        isQuantity={this.state.isQuantity}
        isDescription={this.state.isDescription}
        isLastTime={this.state.isLastTime}
        isSetItemCode={this.state.isSetItemCode}
        isSetQuantity={this.state.isSetQuantity}
        isSetDescription={this.state.isSetDescription}
        isSetLastTime={this.state.isSetLastTime}
        isOnHandQuantity={this.state.isOnHandQuantity}
        isSetOnHandQuantity={this.state.isSetOnHandQuantity}
        ScreenLock={this.state.ScreenLock}
        setOnHandQuantityCheckbox={this.setOnHandQuantityCheckbox}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
        endInventoryHandler={this.endInventoryHandler}
        uploadInventoryHandler={this.uploadInventoryHandler}
        onFileChange={this.onFileChange}
        setScreenLock={this.setScreenLock}
        isScreenLock={this.state.isScreenLock}
        scanItem={this.scanItem}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getInventoryItemList: (data,isLoading,ItemCode,Description,LastTime,OnHandQuantity,Quantity) => dispatch(getInventoryItemList(data,isLoading,ItemCode,Description,LastTime,OnHandQuantity,Quantity)),
    getInventoryDetail: (inventoryId) => dispatch(getInventoryDetail(inventoryId)),
    setInventoryItemListParam: (data) => dispatch(setInventoryItemListParam(data)),
    deleteInventoryItem: (deleteeRequestParameter,inventoryItemListParam,ItemCode,Description,LastTime,OnHandQuantity,Quantity) => dispatch(deleteInventoryItem(deleteeRequestParameter,inventoryItemListParam,ItemCode,Description,LastTime,OnHandQuantity,Quantity)),
    clearMessage: () => dispatch(clearMessage()),
    updateInventory: (updateInventoryRequestParameter) => dispatch(updateInventory(updateInventoryRequestParameter)),
    setdescription: (data) => dispatch(setdescription(data)),
    setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    endInventory: (endInventoryRequestParameter) => dispatch(endInventory(endInventoryRequestParameter)),
    uploadInventory:(data,isLoading,inventoryItemListParam,ItemCode,Description,LastTime,OnHandQuantity,Quantity)=>dispatch(uploadInventory(data,isLoading,inventoryItemListParam,ItemCode,Description,LastTime,OnHandQuantity,Quantity)),
    setScrenLock:(data)=>dispatch(setScrenLock(data))

  };
};

const mapStateToProps = (state) => {
  const { isLoading, inventoryItemList, inventoryItemListParam,screenLock,
    inventoryDetail, totalRecord, status, message,isSuccessDialogue, isModified,description ,updatemessage,updatestatus} = state.InventoryDetailReducer;
  const{updatedValue}=state.NewInventoryScanItemReducer;
  return {
    inventoryItemList: inventoryItemList.values.length>0? inventoryItemList.values:[],
    inventoryItemListParam: inventoryItemListParam,
    inventoryDetail: inventoryDetail.values,
    description: description,
    isLoading: isLoading,
    totalRecord: totalRecord,status,message,
    isModified,
    updatestatus, updatemessage, isSuccessDialogue,
    updatedValue,screenLock

  };
};

export default connect(mapStateToProps,mapDispatchToProps)(InventoryDetailContainer);