export function getSearchItemCode(data,isLoading) {
 
    return (dispatch) => {
        dispatch({
            type: types.GET_ITEMSEARCHBYCODE_REQUEST,
            isLoading: isLoading,
        });
        let url = 'item/UpdateBulkItemSearchItemCode';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    dispatch(getAllCategories(response.payload.values[0].departmentId));

                    /* Logic */
                    dispatch({
                        type: types.GET_ITEMSEARCHBYCODE_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_ITEMSEARCHBYCODE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}



export function UpdateBulkItemInlineEdit(data,dataParam,isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_UPDATEBULKITEM_INLINE_REQUEST,
            isLoading: isLoading,
        });
        let url = 'item/UpdateBulkItemInlineEdit';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    
                    dispatch(getAllItems(dataParam,true));
                    /* Logic */
                    dispatch({
                        type: types.GET_UPDATEBULKITEM_INLINE_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_UPDATEBULKITEM_INLINE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
