import React, { Component } from 'react';
import './PODSetting.css'
import SettingServices from '../Services/SettingServices'
import swal from 'sweetalert';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import { Helmet } from 'react-helmet';
import { showLoader, hideLoader } from '../Custom';

class PODSetting extends Component {

  constructor(props) {
    super(props);
    this.state = {
      userId: "",
      isShowSaveBtn: false,
      PODData: [],
      UserPODData: [],
      formFields: {
        userPods: [],
        userId: 0
      },
      isSelectAll: false,
      isRender: false,
    }

  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    showLoader();
    const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
    this.getAllPOD();
    this.setState({ userId: userDetails.userId });

    hideLoader();

  }
  //#region check user role validation 
  checkUserRoleValidation = () => {
    try {
      const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
      if (userDetails.role !== process.env.REACT_APP_UserRole_Manager) {
        this.props.history.push("/NotFound");
      }
      else {
        this.setState({ isRender: true });
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion check user role validation

  //#region get all POD data
  getAllPOD = async () => {
    try {
      var Obj = new SettingServices();
      await Obj.GetAllPOD().then((poddata) => {
        if (!!poddata.values) {
          var data = poddata.values.map(poddata => {
            return {
              podId: poddata.podId,
              podName: poddata.podName,
              checked: false
            }
          }
          );

          this.GetPODByUser(this.state.userId, data);

        }
      });
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion  get all POD data
  //#region get user POD data
  GetPODByUser = async (userId, allpoddata) => {
    try {
      var Obj = new SettingServices();
      await Obj.GetPODByUser(userId).then((poddata) => {

        if (!!poddata.values) {
          poddata.values.map(userpoddata => {
            let lists = this.state.formFields.userPods;
            let obj = {
              userId: parseInt(this.state.userId),
              podId: parseInt(userpoddata.podId)
            };
            lists.push(obj);
            let formFields = { ...this.state.formFields };
            formFields["userPods"] = lists;
            this.setState({
              formFields
            })
          });
          var data = allpoddata;
          data.map(allpoddata => {
            poddata.values.map(userpoddata => {
              if (allpoddata.podId === userpoddata.podId) {
                allpoddata.checked = true;

              }

            });
          }
          );
          this.setState({ PODData: data }, () => {
            var unSelectList = this.state.PODData.filter((d) => d.checked === false);
            if (unSelectList.length > 0)
              this.setState({ isSelectAll: false });
            else
              this.setState({ isSelectAll: true });
          });
        }
        else {

          var data = allpoddata;
          data.map(allpoddata => {
            allpoddata.checked = true;
          }
          );
          this.setState({ PODData: data, isSelectAll: true });
        }
      });
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get user POD data
  //#region all controll onChange handle method  
  inputChangeHandler(e) {
    try {
      this.setState({ isShowSaveBtn: true });
      if (e.target.checked) {
        let lists = this.state.formFields.userPods;
        let obj = {
          userId: parseInt(this.state.userId),
          podId: parseInt(e.target.name)
        };
        lists.push(obj);
        let formFields = { ...this.state.formFields };
        formFields["userPods"] = lists;
        formFields["userId"] = parseInt(this.state.userId);
        this.setState({
          formFields
        })
      }
      else {
        let formFields = { ...this.state.formFields };
        formFields["userPods"] = formFields.userPods.filter((item) => item.podId !== parseInt(e.target.name));
        formFields["userId"] = parseInt(this.state.userId);
        this.setState({
          formFields, isSelectAll: false
        })
      }
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion all controll onChange handle method
  //#region select all
  setIsSelectAll = (e) => {
    try {
      this.setState({ isSelectAll: !this.state.isSelectAll, isShowSaveBtn: true });
      var isChecked = e.target.checked;
      var cbs = document.getElementsByClassName("PODCHK");
      for (var i = 0; i < cbs.length; i++) {
        cbs[i].checked = isChecked;
      }
      let lists = [];
      if (e.target.checked) {

        for (var i = 0; i < cbs.length; i++) {
          let obj = {
            userId: parseInt(this.state.userId),
            podId: parseInt(cbs[i].name)
          };
          lists.push(obj);
        }
      }
      let formFields = { ...this.state.formFields };
      formFields["userPods"] = lists;
      formFields["userId"] = parseInt(this.state.userId);
      this.setState({ formFields });
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion select all
  //#region post form data to save
  formHandler = async (formFields) => {
    try {
      showLoader();
      var Obj = new SettingServices();
      await Obj.insertOrUpdateUserPods(formFields).then((response) => {

        swal({
          title: response.status,
          text: response.message,
          icon: response.status.toLowerCase(),
          timer: response.status.toLowerCase() !== "error" ? 3000 : 0,
          button: response.status.toLowerCase() !== "error" ? false : "Ok",
        })
        hideLoader();
      }).catch(function (error) {
        hideLoader();
        console.log(error);
        //Perform action based on error
      });

      hideLoader();
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion post form data to save
  //#region render html
  render() {
    return (
      <div>
        {this.state.isRender ?
          <div>
            <Helmet>
              <title>Settings | Modus Go!</title>
            </Helmet>
            <div className="main-content-area mobile-bottom-space">
              <div className="container-fluid d-none d-md-block">
                <div className="row">
                  <div className="col position-relative px-0">
                    <h2 className="text-left text-xl-center heading-text">POD Settings</h2>
                  </div>
                </div>
              </div>


              <div className="container-fluid d-block d-md-none">
                <div className="row">

                  <div className="col p-0 mobile-header-info">
                    <img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
                    <p className="text-darkblue font-weight-bold mobile-title-headline">POD Settings</p>
                  </div>
                </div>
              </div>

              <div className="container bg-white header-shadow mt-1 mb-4 p-3">
                <div className="row">
                  <div className="col-12">
                    <ul className="setting-checkbox pod-setting-wrap">
                      <li className="d-none d-md-inline-block"></li>
                      <li className="d-none d-md-inline-block" ></li>
                      <li>
                        <div className="pod-setting-tabs">
                          <div className="custom-control custom-checkbox">
                            <input type="checkbox" className="custom-control-input" id="isSelectAll" onChange={(e) => this.setIsSelectAll(e)} name="isSelectAll" checked={this.state.isSelectAll} />
                            <label className="custom-control-label font-weight-bold" htmlFor="isSelectAll">Select all</label>
                          </div>
                        </div>
                      </li>
                      {this.state.PODData.map((poddata) =>
                        <li key={poddata.podId}>
                          <div className="pod-setting-tabs">
                            <div className="custom-control custom-checkbox">
                              <input type="checkbox" className="custom-control-input PODCHK" defaultChecked={poddata.checked} onChange={(e) => this.inputChangeHandler(e)} id={poddata.podId} name={poddata.podId} />
                              <label className="custom-control-label" htmlFor={poddata.podId}>{poddata.podName}</label>
                            </div>
                          </div>
                        </li>
                      )}
                    </ul>
                  </div>
                </div>
              </div>
              <div className="container">
                <div className="row justify-content-end mb-5">
                  <button onMouseDown={e => e.preventDefault()} className="btn rounded-pill btn-primary mr-2" type="button" onClick={() => this.formHandler(this.state.formFields)}>Save</button>
                </div>
              </div>

            </div>
          </div>
          : ""}
      </div>


    );
  }
}
//#endregion render html
export default PODSetting;