import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    isLoading: false,
    itemExistPayload: false,
    mpqQuantity: 1,
    isItemChecked: false,
    isSuccessDialogue: false,
    isSupplierFound: true,
    products: [],
    status: "",
    message: "",
    priceScanMustBeEntered: false,
    codeList: [],
    taxList: [],
    lookupCodeSearchValue: "",
    searchTerm: "",
    lookupCodeSearchData: [],
    totalRecord: 0,
    totalPage: 0,
    supplierList: [],
};

export default function NewScanItemReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.ITEM_EXIST_REQUEST:
            return {
                ...state,
            }
        case types.ITEM_EXIST_SUCCESS:
            return {
                ...state,
                itemExistPayload: action.data.result,
                isItemChecked: true,
                products: action.data,
                isSupplierFound: action.data.isSupplier

            }
        case types.ITEM_EXIST_FAILED:
            return {
                ...state,
                isItemChecked: true
            }
        case types.GET_MPQ_REQUEST:
            return {
                ...state,
            }
        case types.GET_MPQ_SUCCESS:
            return {
                ...state,
                mpqQuantity: action.data.quantity,
            }
        case types.RESET_MPQ_QUANTITY:
            return {
                ...state,
                mpqQuantity: action.data.quantity,
            }
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,
                isItemChecked: false,
                itemExistPayload: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
                priceScanMustBeEntered: false,
            }
        case types.SET_PRICE_MUSTBE:
            return {
                ...state,
                priceScanMustBeEntered: action.priceScanMustBeEntered,
            }
        case types.SET_QUANTITY:
            return {
                ...state,
                mpqQuantity: action.quantity,
            }
        case types.GET_ITEMOPTION_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ITEMOPTION_SUCCESS:

            return {
                ...state,
                priceScanMustBeEntered: action.data.values !== undefined && action.data.values !== null ? action.data.values.priceMustBeEntered !== null ? action.data.values.priceMustBeEntered : false : false,
                isLoading: false,

            }
        case types.GET_ITEMOPTION_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.GET_LOOKUPCODE_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_LOOKUPCODE_SUCCESS:
            return {
                ...state,
                codeList: action.data,
                isLoading: false,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                totalPage: action.data.pageInfo !== null ? action.data.pageInfo.totalPage : 0
            }
        case types.GET_LOOKUPCODE_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_TAXLIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_TAXLIST_SUCCESS:
            return {
                ...state,
                taxList: action.data,
                isLoading: false,
            }
        case types.GET_TAXLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_LOOKUPITEM_SEARCHVALUE:

            return {
                ...state,
                lookupCodeSearchValue: action.data,
                isLoading: false,
            }
        case types.SET_LOOKUPITEM_SEARCHDATA:
            return {
                ...state,
                lookupCodeSearchData: action.data,
                isLoading: false,
            }
        case types.SET_LOOKUPITEM_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_LOOKUPITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_LOOKUPITEM_SUCCESS:
            return {
                ...state,
                lookupCodeSearchData: action.data,
                isLoading: false,
            }
        case types.GET_LOOKUPITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.GET_SUPPLIER_LIST_SUCCESS:
            return {
                ...state,
                isLoading: false,
                supplierList: action.data

            }

        default:
            return state;
    }
}