import React from 'react';
import PurchaseComponent from '../component/PurchaseComponent';
import { updateMPQ, setSupplierList } from '../state/action/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { withRouter } from 'react-router-dom';
import swal from 'sweetalert';

function handlePurchaseSuppliersGridEditIcon(classdata) {
    $("#tblPurchaseSuppliers").on('click', '.PurchaseSuppliersGridEditIcon', function () {
        var dataString = $(this).attr('data');
        classdata.openQuantityModel(dataString);
    });
}
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class PurchaseContainer extends React.Component {
    state = {
        isOpenQuantityModel: false,
        updateItemParam: {
            itemId: 0,
            mpq: 0,
            oldmpq: 0
        },
        supplierListState: []
    }
    componentWillMount() {
        //this.props.setSupplierList(this.props.itemPurchase.supplierList);
        this.setState({ supplierListState: this.props.itemPurchase.supplierList });
    }
    componentDidMount() {


        $('#tblPurchaseSuppliers').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });
        handlePurchaseSuppliersGridEditIcon(this);
        $('#tblPurchaseSupplierItems').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });

    }
    componentWillUnmount() {
        $('#tblPurchaseSuppliers').DataTable().destroy();
        $('#tblPurchaseSupplierItems').DataTable().destroy();
    }
    //#region toggleQuantityModel
    toggleQuantityModel = () => { this.setState({ isOpenQuantityModel: !this.state.isOpenQuantityModel }); }
    //#endregion toggleQuantityModel
    //#region quantityModelChangeHandler
    quantityModelChangeHandler = (e) => {
        var value = e.target.value;
        if (value.length > e.target.maxLength) {
            value = value.substring(0, e.target.maxLength);
        }
        if (value >= 0 || value === "") {
            let updateItemParam = { ...this.state.updateItemParam }
            updateItemParam["mpq"] = value !== "" ? parseInt(value) : "";
            this.setState({ updateItemParam });
        }
    }
    //#endregion quantityModelChangeHandler
    //#region onSaveQuantityModel
    onSaveQuantityModel = () => {
        if (this.state.updateItemParam.mpq === "" || this.state.updateItemParam.mpq < 0) {
            swal({
                title: "Error",
                text: "Please enter the MPQ.",
                icon: "error",
                button: "Ok",
            })
        }
        else {
            swal({
                title: "Are you sure?",
                text: "Do you want to update the MPQ for all suppliers?",
                icon: "warning",
                buttons: {
                    confirm: true,
                    cancel: true
                },
                dangerMode: true,
            })
                .then((willDelete) => {
                    if (willDelete) {
                        $.each($.fn.dataTable.tables(true), function () {
                            $(this).DataTable().rows(':not(.parent)').nodes().to$().find('td:first-child').trigger('click');
                        });
                        const result = this.props.updateMPQ(this.state.updateItemParam).then(() => {

                            if (this.props.status === "Success") {
                                var supplierList = this.state.supplierListState;

                                supplierList.map((item) => {
                                    item.masterPackQuantity = this.state.updateItemParam.mpq
                                });
                                
                                                            
                                var MPQs = document.getElementsByClassName("SMPQS");
                                for (var i = 0; i < MPQs.length; i++) {
                                    if(document.getElementsByClassName("supplierMPQ-"+i).length>0)
                                    document.getElementsByClassName("supplierMPQ-"+i)[1].innerHTML =this.state.updateItemParam.mpq;
                                }
                                $('#tblPurchaseSuppliers').DataTable().destroy();
                                this.setState({ supplierListState: supplierList });
                                $('#tblPurchaseSuppliers').DataTable({
                                    "paging": false,
                                    "ordering": false,
                                    "info": false,
                                    "searching": false,
                                    "responsive": true,
                                    "retrieve": true,
                                    "oLanguage": {
                                        "sEmptyTable": "No data found"
                                    },
                                    fixedHeader: true
                                });
                            }
                            var currentMpq=this.state.updateItemParam.mpq;
                            this.toggleQuantityModel();
                            $.each($.fn.dataTable.tables(true), function () {
    
                                if ($(this).DataTable().row('.parent').length) {
                                    var parentLength = document.getElementsByClassName('parent').length;
                                    for (var i = 0; i < parentLength; i++) {                                       
                                        $('td.details-control', $(this).DataTable().row('.parent').node()).click();
                                    }
    
                                }
                            });
                        });
                      
                    }
                });

        }
    }
    //#endregion onSaveQuantityModel
    openQuantityModel = (itemId) => {

        var itemId = parseInt(itemId);
        var elementsIndex = this.state.supplierListState.findIndex(element => element.itemID === itemId);
        let updateItemParam = {
            itemId: itemId,
            mpq: 0,
            oldmpq: this.state.supplierListState[elementsIndex].masterPackQuantity
        };
        this.setState({ updateItemParam, isOpenQuantityModel: true }, () => {
            setTimeout(() => {
                if (!!document.getElementById("newmpq"))
                    document.getElementById("newmpq").focus();
            }, 500)
        });
    }

    render() {
        // const {supplierList}=this.props;

        return (
            <PurchaseComponent
                {...this.props}
                isOpenQuantityModel={this.state.isOpenQuantityModel}
                toggleQuantityModel={this.toggleQuantityModel}
                updateItemParam={this.state.updateItemParam}
                quantityModelChangeHandler={this.quantityModelChangeHandler}
                onSaveQuantityModel={this.onSaveQuantityModel}
                supplierListState={this.state.supplierListState}

            />
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateMPQ: (data) => dispatch(updateMPQ(data)),
        setSupplierList: (data) => dispatch(setSupplierList(data)),
    };
};

const mapStateToProps = (state) => {
    const { isLoading, supplierList, status } = state.PurchaseReducer;
    return {

        isLoading: isLoading,
        supplierList: supplierList.length > 0 ? supplierList : [],
        status: status,
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(PurchaseContainer));