import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
export function setSupplierList(data) {
    return (dispatch) => {
        dispatch({
            type: types.SET_SUPPLIERLIST,
            data: data
        });
        $('#tblPurchaseSuppliers').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });
    }
}
//#region update item
export function updateMPQ(data) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_MPQ_REQUEST,
        });
        let url = 'item/UpdateSupplierItemMPQ';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 200) {
                    /* Logic */
                   // dispatch(getInventoryItemList(itemListParam, true));
                    dispatch({
                        type: types.UPDATE_MPQ_SUCCESS,
                        data: response.payload, 
                        status:  payload.status,                     
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_MPQ_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion update item
