import React, { Component } from 'react';
import './ConfigureSystem.css';
import AuthenticateServices from '../Services/AuthenticateServices'
import swal from 'sweetalert';
import { FormWithConstraints, FieldFeedbacks, FieldFeedback } from 'react-form-with-constraints'
import { showLoader, hideLoader } from '../Custom';
import { Helmet } from 'react-helmet'
import { history } from '../history';
class ConfigureSystem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formFields: {
        productKey: "",
        macAddress:"",
      },
      isRender: false
    }
    this.formHandler = this.formHandler.bind(this);
  }
  async componentWillMount() {
    showLoader();
    await this.loadLoginPage();
    hideLoader();
  }
  //#region load login page
  loadLoginPage = async () => {
    var Obj = new AuthenticateServices();
    await Obj.loadLoginPage().then((response) => {
      if (response !== "") {
        if (response.code !== "000" && response.code !== "E09" && response.code !== "E10")
          swal({
            title: response.status,
            text: response.message,
            icon: response.status.toLowerCase(),
            timer: 0,
            button: "Ok",
          })
        if (response.code === "000") {

          history.push("/Login");
        }
        else if (response.code === "E09")
          history.push("/ConfigureDatabase");
        else if (response.code === "E10")
          this.setState({ isRender: true });

      }
    }).catch(function (error) {
      console.log(error);
      //Perform action based on error
    });

  }
  //#endregion load login page  
  //#region all inpute onchange method   
  inputChangeHandler(e) {
    try {
      var value = e.target.value;
      this.form.validateFields(e.target);
      let formFields = { ...this.state.formFields };
      formFields[e.target.name] = value;
      this.setState({
        formFields
      });
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion all inpute onchange method
  //#region Post method for save ConfigureDatabase on save & continue button click 
  formHandler = async (e) => {
    try {
      showLoader();
      e.preventDefault();
      this.form.validateFields();
      if (this.form.isValid() && !!this.state.formFields.productKey && !!this.state.formFields.macAddress) {
        var Obj = new AuthenticateServices();
        await Obj.ConfigureSystem(this.state.formFields).then((response) => {

          swal({
            title: response.status,
            text: response.message,
            icon: response.status.toLowerCase(),
            timer: response.status.toLowerCase() !== "error" ? 3000 : 0,
            button: response.status.toLowerCase() !== "error" ? false : "Ok",
          })
          hideLoader();
          if (response.code === "000")
            this.props.history.push("/Login");

        }).catch(function (error) {
          hideLoader();
          console.log(error);
          //Perform action based on error
        });
      }
      hideLoader();
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion Post method for save ConfigureDatabase on save & continue button click 
  //#region render html   
  render() {
    return (
      <div>
        <Helmet>
          <title>Product key | Modus Go!</title>
        </Helmet>
        {this.state.isRender && <div>
          <div className="float-left back-img">
            <img className="mb-4 d-block mx-auto logo-set" src="/assets/images/logo_blue.svg" alt="" />
          </div>
          <div className="right-wrap float-left">
            <div className="brand-logo-center">
              <img src="/assets/images/logo-full.svg" alt="" />
            </div>
            <div className="login-wrap">
              <div className="mainbox p-3 pl-4 ">
                <FormWithConstraints ref={form => this.form = form} noValidate onSubmit={(e) => this.formHandler(e)}>
                  <h2 className="login-title text-gray">Configuration your system</h2>
                  <div className="row">
                    <div className="col-12">
                      <div className="floating-label">
                        <input type="text" autoFocus className="floating-input" maxLength="17" placeholder=" " required name="productKey" id="productKey" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.productKey} /><span className="highlight"></span>
                        <label>Enter product key *</label>
                        <FieldFeedbacks for="productKey">
                          <FieldFeedback when="*">Please enter the product key.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    
                    <div className="col-12">
                      <div className="floating-label">
                        <input type="text"  className="floating-input" maxLength="17" placeholder=" " required name="macAddress" id="macAddress" onChange={(e) => this.inputChangeHandler(e)} value={this.state.formFields.macAddress} /><span className="highlight"></span>
                        <label>Enter mac address *</label>
                        <FieldFeedbacks for="macAddress">
                          <FieldFeedback when="*">Please enter the mac address.<i className="zmdi zmdi-alert-triangle float-right"></i></FieldFeedback>
                        </FieldFeedbacks>
                      </div>
                    </div>
                    <div className="col-12">

                      <div className="floating-label">

                        <div className="col-12 d-flex justify-content-end">
                          <button className="btn rounded-pill btn-primary" type="submit">Next <i className="zmdi zmdi-hc-fw"></i></button>
                        </div>
                      </div>
                    </div>

                    <p className="copyright-text">&copy; 2020 Modus technologies. All right reserved.</p>
                  </div>
                </FormWithConstraints>
              </div>
            </div>
          </div>
        </div>}
      </div>
    );
  }
}
//#endregion render html
export default ConfigureSystem;