import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        searchText: "",
        sortColumnName: "",
        sortDirection: "",
        pageNo: 0,
        pageSize: 50,

    },
    workOrderList: [],
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    customers: [],
    customerRequestParam: {
        searchText: "",
        pageNo: 0,
        pageSize: 0
    },
    createWORequestParam: {
        customerId: 0
    },
    apiNameList:[],
    successResult:[],
    isSucessDiv:false,
    apiDisplayCnt:[]
};

export default function WorkOrderReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_WORKORDERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_WORKORDERS_SUCCESS:
            return {
                ...state,
                workOrderList: action.data,
                isLoading: false,

            }
        case types.GET_WORKORDERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_WORKORDERS_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_WORKORDERSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_WORKORDERSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_WORKORDERSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_CUSTOMERS_SUCCESS:
            return {
                ...state,
                customers: action.data,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_WO_REQUESTPARAM:
            return {
                ...state,
                createWORequestParam: action.data,
                isLoading: false,
            }
        case types.CREATE_WO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CREATE_WO_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.CREATE_WO_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CHECKONLINE_WO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CHECKONLINE_WO_SUCCESS:
          
            return {
                ...state,
                isLoading: false,
                successResult: action.data.orderResponseMessage,
                isSucessDiv:true
            }
        case types.CHECKONLINE_WO_FAILED:
            return {
                ...state,
                isLoading: false,
                isSucessDiv:false,
                successResult:[]
            }
        case types.CLOSEAPI_WO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CLOSEAPI_WO_SUCCESS:
            return {
                ...state,
                isLoading: false,
                successResult: action.data.orderResponseMessage,
                isSucessDiv:true
            }
        case types.CLOSEAPI_WO_FAILED:
            return {
                ...state,
                isLoading: false,
                successResult:[],
                isSucessDiv:false
            }  
        case types.GET_APINAME_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_APINAME_SUCCESS:
            return {
                ...state,
                apiNameList: action.data.values,
                apiDisplayCnt:action.data.values.filter(x=>x.isDisplay==true),
                isLoading: false,
            }
        case types.GET_APINAME_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
        case types.SET_WORKORDERS_RESPONSEPARAMETER:
            return {
                ...state,
                isSucessDiv:false,
                isLoading: false,
            }         
        default:
            return state;
    }
}