import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class SubstitutesContainer extends React.Component {
    componentDidMount() {
        $('#tblSubstitutes').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });

    }
    componentWillUnmount() {
        $('#tblSubstitutes').DataTable().destroy();
    }
    render() {

        return (
            <SubstitutesComponent
                {...this.props}
            />
        )
    }
}

