import React from 'react';
import ManageInventoryComponent from '../component/ManageInventoryComponent';
import {
  getInventoryItemList,setInventoryItemListParam,
  clearMessage,getDepartments,getCategories,getSuppliers,updateQuantity
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {history} from '../../../history'
import {encryptURL,decryptURL} from '../../../Custom'
window.$=$;
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
function handleManageInventoryGridEditIcon(classdata) {
  $("#tblManageInventory").on('click','.ManageInventoryGridEditIcon',function() {
    var dataString=$(this).attr('data');
    classdata.openQuantityModel(dataString);
  });
}
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
class ManageInventoryContainer extends React.Component {

  state={
    isRender: true,
    isCategoryDisabled: true,
    isOpenQuantityModel: false,
    updateItemParam: {
      itemId: 0,
      userId: 0,
      itemLookupCode: "",
      description: "",
      quantity: 0,
      oldQuantity: 0
    },
    isShowManageInventoryDatatable: false,
    isSupplier:true,isCategory:true,isDepartment:true,isOnHandQty:true,isDescription:true,isCode:true
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.props.getDepartments();
    let data={
      pageNo: 0,
      pageSize: 0,
      searchText: ""
    }
    this.props.getSuppliers(data);
    this.props.getInventoryItemList(this.props.inventoryItemListParam,true);
    handleManageInventoryGridEditIcon(this);
    window.addEventListener('scroll',this.handleScroll);
    ColumnFilterCheckbox();
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }

  getInventoryItemList=() => {
    this.props.getInventoryItemList(this.props.inventoryItemListParam,false);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblManageInventory').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager) {

        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {
      //console.log("handlescroll: None scanned item");
      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        if(this.props.totalRecord!==this.props.inventoryItemList.length) {
          let inventoryItemListParam={...this.props.inventoryItemListParam};
          let pagesize=this.props.inventoryItemListParam.pageSize+50;
          inventoryItemListParam["pageSize"]=pagesize;
          this.props.setInventoryItemListParam(inventoryItemListParam);
          this.props.getInventoryItemList(inventoryItemListParam,false);
        }
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading

  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";
      switch(sortColumnName) {
        case "itemLookupCode":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="itemLookupCode"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "qtyOnHand":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="qtyOnHand"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "description":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="description"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "department":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="department"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "category":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="category"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        case "supplier":
          sortdirection=this.props.inventoryItemListParam.sortColumnName!=="supplier"? "asc":this.props.inventoryItemListParam.sortDirection==="asc"? "desc":"asc";
          break;
        default:
          break;
      }
      var data={
        pageNo: this.props.inventoryItemListParam.pageNo,
        pageSize: this.props.inventoryItemListParam.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        departmentId: this.props.inventoryItemListParam.departmentId,
        categoryId: this.props.inventoryItemListParam.categoryId,
        supplierId: this.props.inventoryItemListParam.supplierId
      };
      var inventoryItemListParam={...this.props.inventoryItemListParam};
      inventoryItemListParam["sortColumnName"]=sortColumnName;
      inventoryItemListParam["sortDirection"]=sortdirection;
      this.props.setInventoryItemListParam(inventoryItemListParam);
      this.props.getInventoryItemList(data,false);
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler
  filterChangeHandler=(e) => {
    var value=e.target.value;
    var inventoryItemListParam={...this.props.inventoryItemListParam};
    if(e.target.name==="departmentId") {
      if(value!=="") {
        this.setState({isCategoryDisabled: false});
        this.props.getCategories(parseInt(value));
      }
      else {
        inventoryItemListParam["categoryId"]="";
        this.props.getCategories(parseInt(value));
        this.setState({isCategoryDisabled: true});
      }
    }
    inventoryItemListParam[e.target.name]=parseInt(value);
    inventoryItemListParam["pageSize"]=50;
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.props.getInventoryItemList(inventoryItemListParam,false);
  }
  filterHandler=() => {
    if(this.props.inventoryItemListParam.departmentId===0&&this.props.inventoryItemListParam.categoryId===0&&this.props.inventoryItemListParam.supplierId===0)
      swal({
        title: "Error",
        text: "Please select department,category or supplier.",
        icon: "error",
        button: "Ok",
      })
    else
      this.props.getInventoryItemList(this.props.inventoryItemListParam,false);
  }
  clearFilterHandler=() => {
    var inventoryItemListParam={...this.props.inventoryItemListParam};
    inventoryItemListParam["departmentId"]=0;
    inventoryItemListParam["categoryId"]=0;
    inventoryItemListParam["supplierId"]=0;
    inventoryItemListParam["pageSize"]=50;
    this.props.setInventoryItemListParam(inventoryItemListParam);
    this.props.getInventoryItemList(inventoryItemListParam,false)
    this.props.getCategories(0);
    this.setState({isCategoryDisabled: true});
  }
  //#region toggleQuantityModel
  toggleQuantityModel=() => {this.setState({isOpenQuantityModel: !this.state.isOpenQuantityModel});}
  toggleManageInventoryDatatable=() => {this.setState({isShowManageInventoryDatatable: !this.state.isShowManageInventoryDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowManageInventoryDatatable: false})}

  //#endregion toggleQuantityModel
  //#region quantityModelChangeHandler
  quantityModelChangeHandler=(e) => {
    var value=e.target.value;
    if(value.length>e.target.maxLength) {
      value=value.substring(0,e.target.maxLength);
    }
    if(value>=0||value==="") {
      let updateItemParam={...this.state.updateItemParam}
      updateItemParam["quantity"]=value!==""? parseInt(value):"";
      this.setState({updateItemParam});
    }
  }
  //#endregion quantityModelChangeHandler
  //#region onSaveQuantityModel
  onSaveQuantityModel=() => {
    if(this.state.updateItemParam.quantity===""||this.state.updateItemParam.quantity<0) {
      swal({
        title: "Error",
        text: types.REQUIRED_QUANTITY_MESSAGE,
        icon: "error",
        button: "Ok",
      })
    }
    else {
      swal({
        title: "Are you sure?",
        text: "Do you want to update the quantity for selected item?",
        icon: "warning",
        buttons: {
          confirm: true,
          cancel: true
        },
        dangerMode: true,
      })
        .then((willDelete) => {
          if(willDelete) {
            this.props.updateQuantity(this.state.updateItemParam,this.props.inventoryItemListParam);
            this.toggleQuantityModel();
            $.each($.fn.dataTable.tables(true),function() {

              if($(this).DataTable().row('.parent').length) {
                var parentLength=document.getElementsByClassName('parent').length;
                for(var i=0;i<parentLength;i++) {
                  $('td.details-control',$(this).DataTable().row('.parent').node()).click();
                }

              }
            });
          }
        });

    }
  }
  //#endregion onSaveQuantityModel
  openQuantityModel=(itemId) => {
    var itemId=parseInt(itemId);
    var elementsIndex=this.props.inventoryItemList.findIndex(element => element.itemID===itemId);
    let updateItemParam={
      itemId: itemId,
      userId: parseInt(userDetails.userId),
      itemLookupCode: this.props.inventoryItemList[elementsIndex].itemLookupCode,
      quantity: 0,
      description: this.props.inventoryItemList[elementsIndex].description,
      oldQuantity: this.props.inventoryItemList[elementsIndex].qtyOnHand
    };
    this.setState({updateItemParam,isOpenQuantityModel: true},() => {
      setTimeout(() => {
        if(!!document.getElementById("newQuantity"))
          document.getElementById("newQuantity").focus();
      },500)
    });
  }

  //#region filter checkbox onchange function
  setCodeCheckbox = () => {
    this.setState({ isCode: !this.state.isCode }, () => {
    this.setState({ isCode: this.state.isCode });
    });
  }

  setDescriptionCheckbox = () => {
    this.setState({ isDescription: !this.state.isDescription }, () => {
    this.setState({ isDescription: this.state.isDescription });
    });
  }

  setDepartmentCheckbox = () => {
    this.setState({ isDepartment: !this.state.isDepartment }, () => {
    this.setState({ isDepartment: this.state.isDepartment });
    });
  }
  setCategoryCheckbox = () => {
    this.setState({ isCategory: !this.state.isCategory }, () => {
    this.setState({ isCategory: this.state.isCategory });
    });
  }
  setSupplierCheckbox = () => {
    this.setState({ isSupplier: !this.state.isSupplier }, () => {
    this.setState({ isSupplier: this.state.isSupplier });
    });
  }
  setOnHandQtyCheckbox = () => {
    this.setState({ isOnHandQty: !this.state.isOnHandQty }, () => {
    this.setState({ isOnHandQty: this.state.isOnHandQty });
    });
  }
  
  //#endregion filter checkbox onchange function

  render() {
    //  const {inventoryItemList}=this.props   
    return (
      <ManageInventoryComponent
        {...this.props}
        userDetails={userDetails}
        getInventoryItemList={this.getInventoryItemList}
        isRender={this.state.isRender}
        sortHandler={this.sortHandler}
        filterChangeHandler={this.filterChangeHandler}
        filterHandler={this.filterHandler}
        clearFilterHandler={this.clearFilterHandler}
        isCategoryDisabled={this.state.isCategoryDisabled}
        isOpenQuantityModel={this.state.isOpenQuantityModel}
        toggleQuantityModel={this.toggleQuantityModel}
        updateItemParam={this.state.updateItemParam}
        quantityModelChangeHandler={this.quantityModelChangeHandler}
        onSaveQuantityModel={this.onSaveQuantityModel}
        toggleManageInventoryDatatable={this.toggleManageInventoryDatatable}
        isShowManageInventoryDatatable={this.state.isShowManageInventoryDatatable}
        setSupplierCheckbox={this.setSupplierCheckbox}
        setCategoryCheckbox={this.setCategoryCheckbox}
        setDepartmentCheckbox={this.setDepartmentCheckbox}
        setDescriptionCheckbox={this.setDescriptionCheckbox}
        setCodeCheckbox={this.setCodeCheckbox}
        setOnHandQtyCheckbox={this.setOnHandQtyCheckbox}
        isSupplier={this.state.isSupplier}
        isCategory={this.state.isCategory}
        isDepartment={this.state.isDepartment}
        isOnHandQty={this.state.isOnHandQty}
        isDescription={this.state.isDescription}
        isCode={this.state.isCode}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getInventoryItemList: (data,isLoading) => dispatch(getInventoryItemList(data,isLoading)),
    setInventoryItemListParam: (data) => dispatch(setInventoryItemListParam(data)),
    clearMessage: () => dispatch(clearMessage()),
    getDepartments: () => dispatch(getDepartments()),
    getCategories: (data) => dispatch(getCategories(data)),
    getSuppliers: (data) => dispatch(getSuppliers(data)),
    updateQuantity: (data,inventoryItemListParam) => dispatch(updateQuantity(data,inventoryItemListParam)),
  };
};

const mapStateToProps=(state) => {
  const {isLoading,inventoryItemList,inventoryItemListParam,
    totalRecord,status,message,isModified,departments,categories,suppliers}=state.ManageInventoryReducer;
  return {
    inventoryItemList: inventoryItemList.values.length>0? inventoryItemList.values:[],
    inventoryItemListParam: inventoryItemListParam,
    isLoading: isLoading,
    totalRecord: totalRecord,status,message,
    isModified,
    departments: departments.values.length>0? departments.values:[],
    categories: categories.values.length>0? categories.values:[],
    suppliers: suppliers.values.length>0? suppliers.values:[],
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(ManageInventoryContainer);