import * as types from "../../../../constants/AppConstants";
import { post,get } from "../../../../service/index";
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import AuthenticateService from '../../../../Services/AuthenticateServices';
import { encryptURL, decryptURL } from '../../../../Custom'
const obj = new AuthenticateService();
//#region to get order list
export function getWorkOrderList(data, isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_WORKORDERS_REQUEST,
            isLoading: isLoading,
        });
        let url = 'workorder/WorkorderList';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblWorkOrder').DataTable().destroy();
                    dispatch({
                        type: types.GET_WORKORDERS_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblWorkOrder').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_WORKORDERS_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get order list
