//#region create wo handler
export function createWO(data) {
    return (dispatch) => {
        dispatch({
            type: types.CREATE_WO_REQUEST,
        });
        let url = 'workorder/createWorkOrder';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    if (payload.status.toLowerCase() !== "error") {
                        window.open("/WorkOrderEdit/" + encryptURL(payload.workOrderId), "_blank");
                    }

                    dispatch({
                        type: types.CREATE_WO_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.CREATE_WO_FAILED,
                        data: response.payload,
                    });

                }

            })
    }
}
//#endregion create wo handler

//#region checkOnline wo handler
export function checkOnlineWorkOrder(data,requestParameter,isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.CHECKONLINE_WO_REQUEST,
        });
      //  let url = 'LBApi/GetOrderDetails';
        let url = 'APICommonIntegration/GetAPIOrder';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (payload.code === "APIORD01") {
                   
                    dispatch(getWorkOrderList(requestParameter, isLoading)); 
                   
                    dispatch({
                        type: types.CHECKONLINE_WO_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.CHECKONLINE_WO_FAILED,
                        data: response.payload,
                    });

                }
            })
    }
}
//#region checkOnline wo handler


