import React,{Component} from 'react';
import './OnlineItemSettings.css'
import CommonServices from '../../Services/CommonServices'
import ItemsServices from '../../Services/ItemsServices'
import swal from 'sweetalert';
import {FormWithConstraints,FieldFeedbacks,FieldFeedback} from 'react-form-with-constraints'
import {showLoader,hideLoader,toTitleCase} from '../../Custom';
import NumberFormat from 'react-number-format';
class OnlineItemSettings extends Component {
  constructor(props) {
    super(props);
    this.state={
      apiNameList: [],
    }
  }

  async componentDidMount() {
    showLoader();
    this.getAPINameList();
    hideLoader();
    let bigPriceSetting=this.props.itemOnlineSetting.filter(x=>x.apiName=="BigCommerce")[0];
    if(bigPriceSetting!=undefined)
    {
      this.props.ItemBigcommerceDetail["retail_price"]=parseInt(bigPriceSetting.priceValue); 
      this.props.ItemBigcommerceDetail["priceSetting"]=(bigPriceSetting.price+":"+bigPriceSetting.priceValue); 
    }
  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  getAPINameList=async () => {
    try {
      var Obj=new CommonServices();
      await Obj.getAPINameList().then((apiNameList) => {
        if(!!apiNameList.values)
          this.setState({
            apiNameList: apiNameList.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
//#region all controll onChange handle method  
inputChangeHandler(e) {
 
  const value = e.target.name;
 
  let itemOnlineSetting = this.props.itemOnlineSetting;
  let ItemBigcommerceDetail=this.props.ItemBigcommerceDetail;
    itemOnlineSetting.forEach(itemOnlineSetting => {
      if (itemOnlineSetting.apiName === e.target.name)
      itemOnlineSetting["isAvailable"] =  e.target.checked
      if (itemOnlineSetting.price === e.target.name)
                itemOnlineSetting["priceValue"] =  parseFloat((e.target.value).replace(/,/g,""));
      })
  let bigPriceSetting=itemOnlineSetting.filter(x=>x.apiName=="BigCommerce")[0];
  ItemBigcommerceDetail["retail_price"]=parseInt(bigPriceSetting.priceValue); 
  ItemBigcommerceDetail["priceSetting"]=(bigPriceSetting.price+":"+bigPriceSetting.priceValue); 
  this.props.setcurrentPODChanged("OnlineItemSettings");
  this.setState({itemOnlineSetting: itemOnlineSetting})

}
//#endregion all controll onChange handle method
//#region post form data to save
formHandler=async (shoeMessage,click) => {
  try {
      var formFieldsFilter=this.props.itemOnlineSetting;//.filter(x=>x.isAvailable==true);
      var str="";
      for (var i = 0; i < formFieldsFilter.length; i++) {
          
          let price=formFieldsFilter[i].price
          if(str.includes(price)==false)
          {
            str = str + ',' +price+"="+ parseFloat(formFieldsFilter[i].priceValue);
          }
          
      }
      str=str.substring(1);
     // PricingformFields.ColumnPriceName=str;

      let ItemOnlineSettingTable={
        itemOnlineSettingTable:formFieldsFilter,
        itemId:this.props.itemId,
        columnPriceValue:str,
        
      }
      if(shoeMessage)
          showLoader();
     // this.form.validateFields();
     // if(this.form.isValid()) {
          var Obj=new ItemsServices();
          await Obj.UpdateItemOnlineSetting(ItemOnlineSettingTable).then((response) => {
              if(click==='saveClick') {
                  if(shoeMessage||response.status.toLowerCase()==="error")
                      swal({
                          title: response.status,
                          text: response.message,
                          icon: response.status.toLowerCase(),
                          timer: response.status.toLowerCase()!=="error"? 3000:0,
                          button: response.status.toLowerCase()!=="error"? false:"Ok",
                      })
                  hideLoader();
                  if(response.status.toLowerCase()!=="error") {
                      this.clearFormFieldsOldValues();
                      this.props.setCODNULL();
                  }
              }

          }).catch(function(error) {
              hideLoader();
              console.log(error);
              //Perform action based on error
          });
      //}
      hideLoader();
  } catch(error) {
      console.log(error);
      hideLoader();
  }
}
//#endregion post form data to save
  render() {
    return (

      <div>
        <div className="card-body">
        {this.props.itemOnlineSetting.map((data) =>
          
            <div className="row">
               <div className="col-md-6">
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input" id={data.apiName} name={data.apiName} checked={data.isAvailable} onChange={(e) => this.inputChangeHandler(e)}/>
                  <label className="custom-control-label font-weight-bold" htmlFor={data.apiName}>{data.apiName}</label>
                </div>
                </div>
                <div className="col-md-6">
                <div className="floating-label w-100">
                <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id={data.price} name={data.price} onChange={(e) => this.inputChangeHandler(e)} value={data.priceValue.toFixed(2)} />

                <span className="highlight"></span>
                <label>{data.price}</label>
                </div>	
                </div>
            </div>
        
        )}
        </div>
      </div>


    );
  }
}
export default OnlineItemSettings;