    //#region get all item types    
    GetAllItemTypes = async () => {
        return await axios.get(apiGatewayEndpoint.apiGatewayEndpoint + "item/get/itemtypeList", { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
