import React, { Component } from 'react';
import './Aliases.css'
import ItemsServices from '../../Services/ItemsServices'
import swal from 'sweetalert';
import { showLoader, hideLoader } from '../../Custom';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class Aliases extends Component {

  constructor(props) {
    super(props);
    this.state = {
      formFields: {
        itemAlias: []

      },
      isOpenModel: false,
      alias: ""
    }
    this.formHandler = this.formHandler.bind();
  }

  componentDidMount() {
    try {
      showLoader();
      var formFields = this.state.formFields;
      formFields["itemAlias"] = this.props.itemAliasDetail;
      this.setState({ formFields });
      hideLoader();
      // Auto Save Draft after 2 min
      try {
        this.intervalID = setInterval(async () => {
          this.formHandler(false, 'interval')
        }, 120 * 1000);
      } catch (e) {
        console.log(e);
      }
    } catch (error) {
      console.log(error);
      hideLoader();
    }

  }
  componentWillUnmount() {
    clearInterval(this.intervalID);
  }
  toggleModel = () => this.setState({ isOpenModel: !this.state.isOpenModel, alias: "" }, () => {
    setTimeout(() => {
      if (!!document.getElementById("alias"))
        document.getElementById("alias").focus();
    }, 500)
  });
  //#region all controll onChange handle method  
  inputChangeHandler(e) {
    try {
      var value = e.target.value.trim();
      this.setState({ alias: value })
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion all controll onChange handle method
  //#region insert alias
  insertAlias = async () => {
    try {
      if (this.state.alias === "") {
        swal({
          title: "Error",
          text: "Please enter code",
          icon: "error",
          button: "Ok",
        })
        return;
      }
      var existCode = true;
      var Obj = new ItemsServices();
      await Obj.GetItemByCode(this.state.alias).then((response) => {
        existCode = response.result;
        if (response.status.toLowerCase() === "error")
          swal({
            title: response.status,
            text: response.message,
            icon: response.status.toLowerCase(),
            button: "Ok"
          })
        if (!existCode) {
          var codefound = this.state.formFields.itemAlias.filter((item) => item.alias === this.state.alias);
          if (codefound.length <= 0) {
            let itemAlias = this.state.formFields.itemAlias;
            let obj = {
              id: 0,
              itemID: parseInt(this.props.itemId),
              alias: this.state.alias
            };
            itemAlias.push(obj);
            let formFields = { ...this.state.formFields };
            formFields["itemAlias"] = itemAlias;
            this.setState({ formFields });
            this.setState({ alias: "" });
            this.toggleModel();
            this.props.setcurrentPODChanged("Aliases");
          }
          else {
            swal({
              title: "Error",
              text: "Item lookup code must be unique.",
              icon: "error",
              button: "Ok",
            })
            return;
          }
        }
        else {
          swal({
            title: "Error",
            text: "Item lookup code must be unique.",
            icon: "error",
            button: "Ok",
          })
          return;
        }
      })

    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion insert alias
  //#region delete alias
  deleteAlias = (alias) => {
    try {
      swal({
        title: "Are you sure?",
        text: "Once deleted, you will not be able to recover this data!",
        icon: "warning",
        buttons: {
          confirm: true,
          cancel: true
        },
        dangerMode: true,
      })
        .then((willDelete) => {
          if (willDelete) {
            let formFields = { ...this.state.formFields };
            formFields["itemAlias"] = formFields.itemAlias.filter((item) => item.alias !== alias);
            this.setState({
              formFields
            })
            this.props.setcurrentPODChanged("Aliases");
          }
        });

    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion delete alias
  //#region post form data to save
  formHandler = async (showMessage, click) => {
    try {
      if (showMessage)
        showLoader();
      var formFields = this.state.formFields;
      if (formFields.itemAlias.length > 0) {
        var Obj = new ItemsServices();
        await Obj.insertAlias(formFields).then((response) => {
          if (click === 'saveClick') {
            if (showMessage || response.status.toLowerCase() === "error")
              swal({
                title: response.status,
                text: response.message,
                icon: response.status.toLowerCase(),
                timer: response.status.toLowerCase() !== "error" ? 3000 : 0,
                button: response.status.toLowerCase() !== "error" ? false : "Ok",
              })
            hideLoader();
            if (response.status.toLowerCase() !== "error") {
              this.props.setCODNULL();
            }
          }

        }).catch(function (error) {
          hideLoader();
          console.log(error);
          //Perform action based on error
        });
      }
      hideLoader();
    } catch (error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion post form data to save
  //#region render html
  render() {
    return (
      <div>
        <div className="card-body row">
          <div className="col-12">
            <div className="headline-list pt-0">
              <h3>Item alias</h3>

              <button className="btn rounded-pill btn-primary" onMouseDown={e => e.preventDefault()} disabled={this.props.isHQConnected} type="button" onClick={this.toggleModel}>New alias</button>
            </div>
          </div>
          <div className="col-12">
            <div className="">
              <table className="table">
                <thead>
                  <tr>
                    <th className="left-alias-width">ALIAS</th>
                    <th>ACTION</th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.formFields.itemAlias.map((alias) =>
                    <tr key={alias.alias}>
                      <td>{alias.alias}</td>
                      <td>
                        <button type="button" tabIndex="0" className="round-icon-wrap border-red" disabled={this.props.isHQConnected} onClick={() => this.deleteAlias(alias.alias)}><i className="zmdi zmdi-close red-icon"></i></button>
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
          {/* <div className="col-12">
                                            <div className="justify-content-end d-flex pr-4">
                                            <button className="btn rounded-pill btn-primary mr-2" type="button" onClick={()=>this.formHandler(true)}>Save</button>
                                            <button className="btn rounded-pill btn-primary" type="button" onClick={()=>history.push("/Items")}>Cancel</button>
                                           </div>
                                        </div> */}
        </div>

        {/* <! Modal > */}
        <Modal backdrop="static" isOpen={this.state.isOpenModel} toggle={this.toggleModel} centered={true}>
          <ModalHeader toggle={this.toggleModel}>Manage aliases</ModalHeader>
          <ModalBody>
            <div className="floating-label">
              <input autoFocus className="floating-input" type="text" placeholder=" " maxLength="25" id="alias" name="alias" onChange={(e) => this.inputChangeHandler(e)} value={this.state.alias} />
              <span className="highlight"></span>
              <label>Enter code</label>
            </div>
          </ModalBody>
          <ModalFooter>
            <button className="btn rounded-pill btn-primary" type="button" onClick={this.insertAlias}>Save</button>
            <button type="button" className="btn rounded-pill btn-primary" onClick={this.toggleModel}>Cancel</button>

          </ModalFooter>
        </Modal>

      </div>


    );
  }
}
//#endregion render html
export default Aliases;