import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    departments: [],
    categories: [],
    itemtype:[],
    isCategoryDisabled: true,
    dropdownListParam: {
        departmentIds: [],
        categoryIds: [],
    },
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    isLoading: false,
    searchRequestParameter: {
        searchText: "",
    },
    searchData: [],
    searchValue: "",
    searchTerm: "",
    itemData:[],
  
    itemListParam: {
        sortColumnName: "",
        sortDirection: "desc",
        pageNo: 0,
        pageSize: 50,
        searchText: "",
        departmentIds: [],
        categoryIds: [],
        supplierIds: []
    },
    allcategories:[],
    suppliers:[],
    itemEditData:[],
    ItemTax:[],
   
    formField:{
        fieldNameValue:"",
        description:"",
        extendedDescription:"",
        subDescription1: "",
        subDescription2: "",
        subDescription3: "",
        itemType: "",
        departmentID: "",
        categoryID: "",
        price: "",
        cost: "",
        onHold:"",
        itemTypeValue:"",
        valueInput:"",
        weight:"",
        isDepartmentChecked:false,
        pricingProfitMargin:""
      },
      UpdateBulkItemInline:[],
      itemDataMode:[],
      storelist:[]
      
};

export default function UpdateBulkItemReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:""
            }
        case types.GET_FINALDEPARTMENTS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALDEPARTMENTS_SUCCESS:

            return {
                ...state,
                departments: action.data,
                isLoading: false,
            }
        case types.GET_FINALDEPARTMENTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALCATEGORIES_SUCCESS:

            return {
                ...state,
                categories: action.data,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.SET_IS_CATEGORYDISABLED:

            return {
                ...state,
                isCategoryDisabled: action.data,
                isLoading: false,
            }
        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                dropdownListParam: action.data,
                isLoading: false,
            }

        case types.GET_ITEMTYPE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ITEMTYPE_SUCCESS:

            return {
                ...state,
                itemtype: action.data.values,
                isLoading: false,
            }
        case types.GET_ITEMTYPE_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
        case types.SET_FINALINVENTORYS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }   
        case types.GET_ITEMSEARCH_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ITEMSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data.values,
                isLoading: false,
            }
        case types.GET_ITEMSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ALLITEM_DATAMODE:
            {
              
              
                    action.data.values.forEach(obj => {
                        
                        obj["code"]={mode:'view',value:obj.code}
                    });
                return {
                    ...state,
                    itemDataMode: action.data,
                    isLoading: false,
                }  
            }
          
        case types.GET_ITEMLIST_SUCCESS:
            {
              
                
                action.data.values.forEach(element => {
                    element['IsSelected'] = false;
                    element["description"]={mode:'view',value:element.description}
                    element["code"]={mode:'view',value:element.code}
                    element["price"]={mode:'view',value:element.price}
                    element["categoryName"]={mode:'view',value:element.categoryName}
                    element["departmentName"]={mode:'view',value:element.departmentName}
                    element["itemTypeName"]={mode:'view',value:element.itemTypeName}
                    
                });
            
               
              
                return {
                    ...state,
                    itemData: action.data,
                    isLoading: false,
                }
            }
            
        case types.SET_FINALINVENTORYS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_FINALINVENTORYS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_FINALINVENTORYS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }  
        case types.SET_UPDATEBULK_ITEMLISTPARAM:
           
            return {
                ...state,
                itemListParam: action.data,
                isLoading: false,
            }  
        case types.GET_ALLCATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ALLCATEGORIES_SUCCESS:

            return {
                ...state,
                allcategories: action.data.values,
                isLoading: false,
            }
        case types.GET_ALLCATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }  
            
        case types.INSERT_UPDATEBULKITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }  
            
        case types.INSERT_UPDATEBULKITEM_SUCCESS:

            return {
                ...state,
                isLoading: false,
            }
        case types.INSERT_UPDATEBULKITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_UBISUPPLIERS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_UBISUPPLIERS_SUCCESS:

            return {
                ...state,
                suppliers: action.data,
                isLoading: false,
            }
        case types.GET_UBISUPPLIERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }     
            
        case types.GET_ITEMBYID_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ITEMBYID_SUCCESS:
            return {
                ...state,
                itemEditData: action.data.values,
                isLoading: false,
            }
        case types.GET_ITEMBYID_FAILED:
            return {
                ...state,
                isLoading: false,
            }   
        case types.GET_TAX_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_TAX_SUCCESS:
            return {
                ...state,
                ItemTax: action.data.values,
                isLoading: false,
            }
        case types.GET_TAX_FAILED:
            return {
                ...state,
                isLoading: false,
            }  
            
        case types.GET_ITEMSEARCHBYCODE_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ITEMSEARCHBYCODE_SUCCESS:

            return {
                ...state,
                formField: action.data.values[0],
                isLoading: false,
            }
        case types.GET_ITEMSEARCHBYCODE_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
            
        case types.GET_UPDATEBULKITEM_INLINE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_UPDATEBULKITEM_INLINE_SUCCESS:
            return {
                ...state,
                UpdateBulkItemInline: action.data.values,
               
            }
        case types.GET_UPDATEBULKITEM_INLINE_FAILED:
            return {
                ...state,
                isLoading: false,
            }  
        case types.SET_IS_SELECTED_FLAG:
           
            return {
                ...state,
                itemData: action.data,
                isLoading: false,
            }    
        case types.GET_STORELIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_STORELIST_SUCCESS:

            return {
                ...state,
                storelist: action.data.values,
                isLoading: false,
            }
        case types.GET_STORELIST_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
        default:
            return state;
    }
}