import React, { Component } from 'react';
import './Header.css'
import { history } from '../../history';
import { showLoader, hideLoader } from '../../Custom';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import ItemsServices from '../../Services/ItemsServices'
import swal from 'sweetalert';
import { FormWithConstraints } from 'react-form-with-constraints'
import Autocomplete from 'react-autocomplete';
function debounce(func, wait, immediate) {
	var timeout;

	return function executedFunction() {
		var context = this;
		var args = arguments;

		var later = function () {
			timeout = null;
			if (!immediate) func.apply(context, args);
		};

		var callNow = immediate && !timeout;

		clearTimeout(timeout);

		timeout = setTimeout(later, wait);

		if (callNow) func.apply(context, args);
	};
};
class Header extends Component {
	constructor(props) {
		super(props);

		this.state = {
			searchDataAll: [],
			styleLight: true,
			Pagination: {
				pageNo: 1,
				pageSize: 20,
				value: "",
				sortColumnName: "",
				sortDirection: "",
				searchText: ""
			},
			scrollPos: 0,
			totalRecord: 0,
			totalPage: 0,
			rowData: [],
			isRender: false

		}
		this.hideSearchBoxDropdown = this.hideSearchBoxDropdown.bind(this);
	}
	logOut = () => {
		Cookies.remove('userDetails');
		Cookies.remove('styleLight');
		document.getElementById('style-lightdark').href = '/assets/css/style.css';
		Cookies.remove('jwtToken');
		Cookies.remove('InventoryUser');
		Cookies.remove('globalSearchValue');
		history.push("/Login");
	}
	componentWillMount() {
		var styleLight = Cookies.get('styleLight') !== undefined ? Cookies.get('styleLight') : "true";
		const style = document.getElementById('style-lightdark');
		if (styleLight === "true")
			style.href = '/assets/css/style.css';
		else
			style.href = '/assets/css/style-dark.css';
		this.setState({ styleLight: styleLight === "true" ? true : false });
	}
	componentDidMount() {
		var globalSearchValue = Cookies.get('globalSearchValue') !== undefined ? CryptoJS.AES.decrypt(Cookies.get('globalSearchValue'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8) : "";
		if (globalSearchValue !== "" && window.location.pathname === '/Items') {
			this.setState({ value: globalSearchValue });
			var Obj = new ItemsServices();
			let param = {
				itemVal: globalSearchValue
			};
			Obj.GetAllSearchByValueData(param).then((searchdata) => {
				let getdata = searchdata.values !== undefined ? searchdata.values : [];
				this.setState({
					searchDataAll: getdata
				});
			});
		}
		else {
			this.setState({ value: "" });
			Cookies.remove('globalSearchValue');
		}
	}
	//#region  get search data to bind search box dropdown
	getAllSearchData = async (record, cb) => {
		try {

			showLoader();
			let Pagination = { ...this.state.Pagination };

			Pagination["searchText"] = record;
			this.setState({ Pagination });
			let getdata;
			record = record.trim();
			if (record !== "") {
				var Obj = new ItemsServices();
				let param = {
					itemVal: record
				};
				Obj.GetAllSearchByValueData(param).then((searchdata) => {
					getdata = searchdata.values !== undefined ? searchdata.values : [];
					this.setState({
						searchDataAll: getdata
					});
				});
			}
			else {
				getdata = [];
				this.setState({
					searchDataAll: getdata
				});
			}
			hideLoader();
		} catch (error) {
			console.log(error);
			hideLoader();
		}
	}

	setSearchTermHeader = debounce((searchTerm, cb) => {
		Cookies.remove('globalSearchValue');
		this.setState({ searchTerm })
		this.getAllSearchData(searchTerm, cb);
	}, 1000)
	//#endregion get search data to bind search box dropdown
	//#region searching
	searchHandler = async (record, redirect) => {
		try {
			let Pagination = { ...this.state.Pagination };
			if (this.state.value !== undefined) {
				Pagination["searchText"] = record !== "" ? record : this.state.value;
				//Pagination["pageSize"] =this.state.searchDataAll.length+1;
				this.setState({ Pagination }, () => {
					this.getAllItems(this.state.Pagination, redirect);

				});
			}
			else {
				swal({
					title: "Error",
					text: "Please enter the text.",
					icon: "error",
					button: "Ok",
				})
			}
		} catch (error) {
			console.log(error);
			hideLoader();
		}
	}
	//#endregion searching
	hideSearchBoxDropdown = (e) => {
		e.preventDefault();
		this.getAllItems(this.state.Pagination, true);
	}
	//#region get all items data 
	getAllItems = async (Pagination, redirect) => {
		try {
			var Obj = new ItemsServices();
			await Obj.GetAllItems(Pagination).then((items) => {
				if (!!items.values) {
					if (redirect) {
						localStorage.setItem('ItemDetails', CryptoJS.AES.encrypt(JSON.stringify(items), process.env.REACT_APP_CryptojsSecretKey).toString());
						Cookies.set('ItemDetails', CryptoJS.AES.encrypt(JSON.stringify(items), process.env.REACT_APP_CryptojsSecretKey).toString());
						Cookies.set('globalSearchValue', CryptoJS.AES.encrypt(this.state.Pagination.searchText, process.env.REACT_APP_CryptojsSecretKey).toString());
						if (window.location.pathname === '/Items')
							window.location.reload(false);
						if (Pagination["searchText"].trim() !== "")
							history.push('/Items');

					}
				}

				if (items.status.toLowerCase() === "error" && items.code !== "E08")
					swal({
						title: items.status,
						text: items.message,
						icon: items.status.toLowerCase(),
						button: "Ok"
					})
			}).catch(function (error) {
				hideLoader();
				console.log(error);
				//Perform action based on error
			});
			hideLoader();
		} catch (error) {
			console.log(error);
			hideLoader();
		}
	}
	//#endregion get all items data
	mobilesearchHandler = () => {
		try {
			document.getElementById("topsearch").style.display = "block";
			document.getElementById("logo-navbar").style.display = "none";
			document.getElementById("mobicon").style.display = "none";
			document.getElementById("theme-action").style.display = "none";
		} catch (error) {
			console.log(error);
			hideLoader();
		}

	}
	mobilesearchcloseHandler = () => {
		try {
			document.getElementById("topsearch").style.display = "none";
			document.getElementById("logo-navbar").style.display = "block";
			document.getElementById("mobicon").style.display = "block";
			document.getElementById("theme-action").style.display = "block";
		} catch (error) {
			console.log(error);
			hideLoader();
		}
	}
	changeCSS = () => {
		this.setState({ styleLight: !this.state.styleLight }, () => {
			const style = document.getElementById('style-lightdark');
			if (this.state.styleLight)
				style.href = '/assets/css/style.css';
			else
				style.href = '/assets/css/style-dark.css';
			Cookies.set('styleLight', this.state.styleLight);
		})


	}
	render() {

		const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
		return (


			<header className="stickey-header">
				<nav className="navbar bg-header navbar-expand header-shadow1">

					<a href="" id="logo-navbar" tabIndex="-1" className="navbar-brand"><img src="/assets/images/logo-full-white.svg" alt="" /></a>
					<h6 className='version-navbar'>{this.props.version}</h6>

					<div className=" navsearchbar">

						<div id="topsearch" className="top-search-bar">
							<FormWithConstraints ref={form => this.form = form} noValidate onSubmit={(e) => this.hideSearchBoxDropdown(e)}>
								<Autocomplete
									inputProps={{ id: 'states-autocomplete' }}
									wrapperStyle={{ position: 'relative' }}
									value={this.state.value}
									items={this.state.searchDataAll}
									getItemValue={(item) => item.key}
									onSelect={record => this.searchHandler(record, true)}
									inputProps={{ placeholder: 'Search item' }}
									onChange={(event, value) => {
										this.setState({ value })
										//clearTimeout(this.requestTimer)
										this.setSearchTermHeader(value, (items) => {
											this.setState({ searchDataAll: items })
										})
									}}
									renderMenu={children => (
										<div className="search-menu">
											{children}
										</div>
									)}
									renderItem={(item, isHighlighted) => (
										<div
											key={item.key}
										>{item.value}</div>
									)}
								/>
							</FormWithConstraints>
							<div className="top-search-icon" onClick={() => this.searchHandler("", true)}>
								<i className="text-white material-icons">search</i>
							</div>

							<div className="close-search" onClick={this.mobilesearchcloseHandler}><i className="zmdi zmdi-close"></i></div>
						</div>

						<div id="mobicon" className="mobile-search" onClick={this.mobilesearchHandler}>
							<i className="text-white material-icons" id="cls">search</i>
						</div>
						<div id="theme-action" className="theme-mode" onClick={this.changeCSS}><img src={this.state.styleLight === true ? "/assets/images/moon-light.svg" : "/assets/images/moon-dark.svg"} alt="" /></div>
						<div className="userbox d-flex align-items-center float-right border border-light rounded p-1 ml-3">
							<img src="/assets/images/user_avatar.svg" className="rounded-circle" alt="" />
							<span className="text-light d-none d-md-block">{userDetails.name}</span>
							<i className="text-light material-icons d-none d-md-block">keyboard_arrow_down</i>
						</div>
					</div>

					<div className="user-profile shadow-sm">
						<img src="/assets/images/user_avatar.svg" alt="" className="w-50 d-block mx-auto mb-2" />
						<h5>{userDetails.name}</h5>
						<p>{userDetails.role}</p>
						<hr />
						<div className="logout" onClick={this.logOut}>
							<span className="text-left d-flex"><i className="material-icons mr-2">exit_to_app</i>Logout</span>
						</div>

					</div>
				</nav>
			</header>


		);
	}
}
export default Header;