import React from 'react';
import NewInventoryScanItemComponent from '../component/NewInventoryScanItemComponent';
import { decryptURL } from '../../../Custom'
import {clearMessage,isItemExist,insertItem, setSuccessDialogue, GetsupplierList,getLookupCodeList,setQuantity,
    setlookupCodeSearchData,setlookupCodeSearchValue,getSearchList,
    setLookupCodeSearchTerm,setUpdatedValueColor
} from '../state/action/index';
import {setScrenLock} from '../../inventoryDetail/state/actions/index'
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import { withRouter } from 'react-router-dom';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import $ from 'jquery';
function scanItemDebounce(func, wait, immediate) {
    var timeout;

    return function executedFunction() {
        var context = this;
        var args = arguments;

        var later = function () {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };

        var callNow = immediate && !timeout;

        clearTimeout(timeout);

        timeout = setTimeout(later, wait);

        if (callNow) func.apply(context, args);
    };
};
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";

class NewInventoryScanItemContainer extends React.Component {
    state = {
        itemCode: '',
        quantity: 1,
        showDescription: false,
        description: "",
        isDisabled: false,
        status: "",
        message: "",
        isOpenModel: false,
        isCheckItemCodeCall: false,
        isSuccessDialogue: false,
        isOpenProductModel: false,
        selectedProduct: '',
        formFields: {
            supplier:""
          },
        oldFormFields: {
        supplier:"",
        isSupplier:false,
        },
        isOpenCodeModel: false,
        addLookupCodeParam: {
            lookupCode: ""
        },
        isScannerUse: false,
        Pagination: {
            pageNo: 1,
            pageSize: 5,
            sortColumnName: "",
            sortDirection: "",
            searchText: ""
        },
        setUpdatedValue:""
    }
    constructor(props) {
        super(props)
    }
    componentDidMount() {
        this.setState({ isOpenModel: false }, () => document.getElementById("itemCode").focus());
        if (this.state.isScannerUse)
            this.setState({ isDisabled: true });
        this.clearScanMessageIntervalID = setInterval(async () => {
            this.props.clearMessage();
        }, 30 * 1000);
    }
    componentWillUnmount() {
        clearInterval(this.clearScanMessageIntervalID);
    }
    static getDerivedStateFromProps(props, state) {
        return {
            isSuccessDialogue: props.isSuccessDialogue,
            status: props.status,
            message: props.message,
        }
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.props.isSuccessDialogue !== prevProps.isSuccessDialogue) {
            if (this.props.isFromFinal)
                this.props.getInventoryItemList();
            else
                this.props.getInventoryItemList();

        }
    }
    handleKeyDown = (e) => {
        //console.log("key :" + e.key);
        // if (this.state.isScannerUse) { 
        if(!userDetails.isHQConnected )
        {         
            if ((e.key.toLowerCase() === "enter" || e.key.toLowerCase() === "tab")  && !this.state.isDisabled) {
                document.getElementById("quantity").focus();
                document.getElementById("quantity").select();
            // this.acceptHandler();
            }
        }
        else
        {
            this.setState({itemCode: '' })
            this.props.setSuccessDialogue(true, "failed", "Item not found!");
        }  
        //}
    }
    handleQuantityKeyDown = (e) => {
        if ((e.key.toLowerCase() === "enter") && !this.state.isDisabled) {
            this.acceptHandler();
        }
    }
    handlePaste = () => {
        //console.log("Paste :");
        
        // if (this.state.isScannerUse) { 
        setTimeout(() => {
            document.getElementById("quantity").focus();
            document.getElementById("quantity").select();
            //this.acceptHandler();

        }, 200);
        //}
    }
    handlechange = (e) => {
       // console.log("handleChange :");
        const { value } = e.target;
        this.setState({
            itemCode: value.trim(""),
            status: "",
            message: "",
            description: "",
            showDescription: false,
            isCheckItemCodeCall: false,
        })
        this.props.setSuccessDialogue(false, "", "");
    }
    setQuantity = (e) => {
        const { value } = e.target;
        
        if ((value > -1 || value === '') && value.length<=5 ) {
            this.setState({
                quantity: value,
            })
        }
    }

    checkItemCode = (e) => {
        try {
            const { value } = e.target;
            if (value) {
                let param = {
                    poId: 0,
                    itemLookupCode: value
                }

                const result = this.props.isItemExist(param).then(() => {
                    if (this.props.products.length > 0) {
                        this.openProductModel();
                    }
                    else {
                        if (this.props.itemExistPayload)
                            this.setState({ isCheckItemCodeCall: true });
                        else
                            this.setState({isOpenModel:true, isCheckItemCodeCall: false, showDescription: true, isDisabled: true ,isSupplier:false,comboBoxSelection:''});
                            let formFields={...this.state.formFields};
                            formFields["supplier"]="";
                            this.setState({
                              formFields
                            });
                    }

                });

                return result;


            }

        } catch (error) {
            console.log(error);
        }
    }

    closeItemScan = () => {
        this.setState({
           itemCode:"",   quantity:1,isOpenModel:false
        }); 
        this.props.setScrenLock(false)
    }
    //#region acceptHandler
    acceptHandler = () => {
        if(!userDetails.isHQConnected)
        {
            if (this.state.itemCode) {
                if (!this.state.isCheckItemCodeCall) {
                    
                    let target = { target: { value: this.state.itemCode } };
                    const result = this.checkItemCode(target).then((result) => {
    
                        setTimeout(() => {
                            if (this.state.isCheckItemCodeCall)
                                this.accept();
                        }, 200);
    
                    });
                }
                else {
                    this.accept();
                }
            }
        }
        else
        {
            this.resetHandler();
            this.props.setSuccessDialogue(true, "failed", "Item not found!");
        }
        
        document.getElementById("itemCode").focus();
    }
    accept = () => {

        if (this.state.quantity.length > 5) {
            swal({
                title: types.DELETE_TITLE,
                text: "Item quantity is beyond the 5 digits. Still you want to scan this item?",
                icon: "warning",
                buttons: {
                    catch: {
                        text: "Yes",
                        value: "catch",
                    },
                    defeat: {
                        text: "No",
                        value: "defeat",
                    },
                },
                dangerMode: true,

            })
                .then((value) => {
                    switch (value) {
                        case "catch":
                            this.setState({ isCheckItemCodeCall: false, selectedProduct: "",isOpenModel:false });
                            if(!this.props.isScreenLock)
                            {
                                this.saveInventoryItem();
                                this.props.setUpdatedValueColor("updated-value");
                            }
                            break;

                        case "defeat":
                            this.setState({                            
                                quantity: 1,
                            })

                            break;
                    }
                });
        }
        else {
            this.setState({ isCheckItemCodeCall: false, selectedProduct: "" ,isOpenModel:false});
            if(!this.props.isScreenLock)
            {
                this.saveInventoryItem();
                this.props.setUpdatedValueColor("updated-value");

            }
            
        }
    }
    //#endregion acceptHandler
    saveInventoryItem = () => {
       
        if (this.state.itemCode) {
            const { location } = this.props
            var id = 0;
            if (!this.props.isFromFinal)
            {
                id = decryptURL(this.props.match.params.listId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1)

            }
            else
            {
                id = decryptURL(this.props.match.params.inventory);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1)

            }
            const data = {
                inventoryId: parseInt(id),
                userId: parseInt(userDetails.userId),
                itemLookupCode: this.state.itemCode,
                quantity: parseInt(this.state.quantity),
                description: this.state.description,
                isNewlyCreated: this.state.showDescription,
                mpq: parseInt(this.state.quantity),
                supplierID:this.state.formFields.supplierID,
            }
            this.props.insertItem(data);
            this.resetHandler();
        }
    }
    resetHandler = () => {
        this.setState({
            itemCode: '',
            quantity: 1,
            description: "",
            showDescription: false,
            isCheckItemCodeCall: false,
            isDisabled: false
        })
    }
    setDescription = (e) => {
        var value = e.target.value;
        if (e.target.maxLength >= 0) {
            if (value.length > e.target.maxLength) {
                value = value.substring(0, e.target.maxLength);
            }
        }
        this.setState({ description: value })
        if (!!value) {
            this.setState({
                isDisabled: false,
                isCheckItemCodeCall: true
            })
        } else {
            this.setState({
                isDisabled: true,
                isCheckItemCodeCall: false
            })
        }
    }
  
    closeDialogue = () => {
        this.setState({
            quantity: 1,
            showDescription: false,
            isSupplier:false,
            comboBoxSelection:''
        });
        let formFields={...this.state.formFields};
        formFields["supplier"]="";
        this.setState({
          formFields
        });
        this.props.closeInventoryScanItem();
        this.props.setSuccessDialogue(false, "", "");
        this.toggleOpenModel();
    }


    toggleOpenModel = () => {
        this.setState({ isOpenModel: !this.state.isOpenModel });
    }
    //#region toggleProductModel
    openProductModel = () => {
        this.setState({
            isOpenProductModel: true, itemCode: '',
            selectedProduct: ''
        });

    }
    toggleProductModel = () => {
        if (this.state.isOpenProductModel) {
            this.setState({
                itemCode: '',
                selectedProduct: '',
            });

        }
        this.setState({ isOpenProductModel: !this.state.isOpenProductModel });
    }
    //#endregion toggleProductModel
    //#region  productCheckBoxChangeHandler
    productCheckBoxChangeHandler = (e) => {
        try {
            var isChecked = e.target.checked;
            var cbs = document.getElementsByClassName("productCHK");
            for (var i = 0; i < cbs.length; i++) {
                cbs[i].checked = false;
            }
            if (isChecked) {
                document.getElementById(e.target.name).checked = true;
                this.setState({ selectedProduct: e.target.name });
            }
            else
                this.setState({ selectedProduct: "" });


        } catch (error) {
            console.log(error);
        }
    }
    //#endregion productCheckBoxChangeHandler
    //#region ProductHandler
    ProductHandler = () => {
        if (this.state.selectedProduct !== "") {
            this.setState({ itemCode: this.state.selectedProduct, isCheckItemCodeCall: true, isOpenProductModel: false });

        }
        else
            swal({
                title: "Error",
                text: "Please select the product.",
                icon: "error",
                button: "Ok",
            })

    }
    //#endregion ProductHandler

    inputChangeHandlerSupplier=(e) => {
        let formFields={...this.state.formFields};
        formFields[e.target.name]=e.target.value;
        this.setState({
          formFields
        });
      }
    toggleSupplierModel=() => {
        let data={
          pageNo: 0,
          pageSize: 0,
          searchText: ""
        }
        this.props.GetsupplierList(data);
        this.setState({isOpenSupplierModal: !this.state.isOpenSupplierModal});
      }

      supplierCheckBoxHandler=(e,supp) => {
        var isChecked=e.target.checked;
        var cbs=document.getElementsByClassName("supplierCHK");
        for(var i=0;i<cbs.length;i++) {
          cbs[i].checked=false;
        }
        if(isChecked) {
          document.getElementById(e.target.name).checked=true;
          this.setState({
            supplierInfo: supp
          })
        }
      }

      supplierModalSave=() => {
            this.setState({
                ...this.state,
                formFields: {
                ...this.state.formFields,
                supplierID: this.state.supplierInfo.id,
                supplier: this.state.supplierInfo.code,
                supplierName: this.state.supplierInfo.supplierName,
                },
                isOpenSupplierModal: false
            });
            
      }

      getComboBoxValue = e => {    
        const { value } = e.target;
        this.setState({
            comboBoxSelection: value
        })
        if (this.state.itemCode) {
            if (value === 'supplier_mpq') {
                this.setState({IsMPQSelected:true,isSupplier:true});
               
            } else {
                this.setState({IsMPQSelected:false,isSupplier:false});
               
            }
        }
    }
    
    toggleCodeModel = () => {
        let addLookupCodeParam = {
            lookupCode: ""
        };
        this.setState({ isOpenCodeModel: !this.state.isOpenCodeModel, addLookupCodeParam });
    }
      //#region open code model
      openCodeModel = () => {
        
        let Pagination = { ...this.state.Pagination };
        Pagination["searchText"] = "";
        this.setState({
            itemCode: '',
            Pagination
        }, () => { this.props.getLookupCodeList(this.state.Pagination, true); });
        this.props.setlookupCodeSearchValue("");
        this.props.setlookupCodeSearchData([]);
        this.props.setQuantity(1);

        this.toggleCodeModel();

    }
    //#endregion open code model
    //#region addLookupCode
    addLookupCode = () => {
        try {
            if (this.state.addLookupCodeParam.lookupCode === "") {
                swal({
                    title: "Error",
                    text: "Please select code.",
                    icon: "error",
                    button: "Ok",
                })
            }
            else {
                this.setState({ itemCode: this.state.addLookupCodeParam.lookupCode }, () => this.toggleCodeModel());

            }
        } catch (error) {
            console.log(error);
        }

    }
    //#endregion addLookupCode
     //#region search change handler
     onLookupCodeSearchChangeHandler = (value) => {
        try {
            this.props.setlookupCodeSearchValue(value);
            this.setLookupCodeSearchTerm(value, (items) => {
                this.props.setlookupCodeSearchData(items);
            })
        } catch (error) {
            console.log(error);
        }
    }
    setLookupCodeSearchTerm = scanItemDebounce((searchTerm, cb) => {
        try {
            this.props.setLookupCodeSearchTerm(searchTerm);
            this.lookupCodeSearchHandler(searchTerm, cb);
        } catch (error) {
            console.log(error);
        }
    }, 1000)
    //#endregion search change handler 
    //#region search checkbox handler
    onLookupCodeSearchCheckBoxHandler = (value) => {
        try {
            this.props.setlookupCodeSearchValue(value);
            this.lookupCodeSearchHandler(value);
        } catch (error) {
            console.log(error);
        }
    }
    //#endregion search checkbox handler
    toggleOpenModel = () => {
        this.setState({ isOpenModel: !this.state.isOpenModel });
    }

     //#region  code check box handler
     codeCheckBoxHandler = (e, id) => {
        try {
            var isChecked = e.target.checked;
            var cbs = document.getElementsByClassName("lookupCodeCHK");
            for (var i = 0; i < cbs.length; i++) {
                cbs[i].checked = false;
            }
            if (isChecked) {
                document.getElementById(e.target.name).checked = true;
                let addLookupCodeParam = {
                    lookupCode: id
                };
                this.setState({ addLookupCodeParam });
            }
            else {
                let addLookupCodeParam = {
                    lookupCode: ""
                };
                this.setState({ addLookupCodeParam });
            }

        } catch (error) {
            console.log(error);
        }
    }
    //#endregion  code check box handler

    handlePageClick = data => {
        let Pagination = { ...this.state.Pagination };
        let pageno = data.selected;
        Pagination["pageNo"] = pageno + 1;
        this.setState({ Pagination }, () => {
            this.props.getLookupCodeList(this.state.Pagination, true);
        });
    };
    pageSizeChangeHandler = (e) => {
        let Pagination = { ...this.state.Pagination };
        Pagination["pageSize"] = parseInt(e.target.value);
        this.setState({ Pagination }, () => {
            this.props.getLookupCodeList(this.state.Pagination, true);
        });
    };
    //#endregion pagination click events
    //#region search handler
    lookupCodeSearchHandler = (record, cb) => {
        try {
            let getdata;
            record = record.trim();
            if (record !== "" || this.state.Pagination.searchText !== "") {

                let Pagination = { ...this.state.Pagination };
                Pagination["searchText"] = record;
                this.setState({ Pagination }, () => {
                    this.props.getLookupCodeList(this.state.Pagination, true);
                });
            }
            if (record !== "") {
                let searchRequestParameter = {
                    searchText: record,
                    orderId: parseInt(decryptURL(this.props.match.params.listId))
                };
                this.props.getSearchList(searchRequestParameter);
            }
            else {
                getdata = [];
                this.props.setlookupCodeSearchData(getdata);
            }
        } catch (error) {
            console.log(error);
        }
    }
    //#endregion search handler
   
    render() {

        const { showDescription, description, isDisabled,isScannerUse, isOpenCodeModel,isSuccessDialogue, status, message,comboBoxSelection } = this.state;
        if (isSuccessDialogue) {
            // swal({
            //     title: 'Success',
            //     text: 'Success',
            //     icon: 'Success'.toLowerCase(),
            //     timer: 'Success'.toLowerCase() !== "error" ? 3000 : 0,
            //     button: 'Success'.toLowerCase() !== "error" ? false : "Ok",
            // })
            this.props.setSuccessDialogue(false, status, message);
        }

        return (
            <NewInventoryScanItemComponent
                handlechange={this.handlechange}
                handleKeyDown={this.handleKeyDown}
                handleQuantityKeyDown={this.handleQuantityKeyDown}
                handlePaste={this.handlePaste}
                itemCode={this.state.itemCode}
                checkItemCode={this.checkItemCode}
                quantity={this.state.quantity}
                setQuantity={this.setQuantity}
                saveInventoryItem={this.saveInventoryItem}
                closeDialogue={this.closeDialogue}
                showDescription={showDescription}
                setDescription={this.setDescription}
                description={description}
                resetHandler={this.resetHandler}
                userRole={userDetails.role}
                isDisabled={isDisabled}
                acceptHandler={this.acceptHandler}
                status={status}
                message={message}
                isOpenModel={this.state.isOpenModel}
                toggleOpenModel={this.toggleOpenModel}
                isLoading={this.props.isLoading}
                isOpenProductModel={this.state.isOpenProductModel}
                toggleProductModel={this.toggleProductModel}
                ProductHandler={this.ProductHandler}
                productCheckBoxChangeHandler={this.productCheckBoxChangeHandler}
                products={this.props.products}
                onSearchCheckBoxHandler={this.props.onSearchCheckBoxHandler}
                supplierList={this.props.supplierList}
                toggleSupplierModel={this.toggleSupplierModel}
                isOpenSupplierModal={this.state.isOpenSupplierModal}
                supplierCheckBoxHandler={this.supplierCheckBoxHandler}
                inputChangeHandlerSupplier={this.inputChangeHandlerSupplier}
                formFields={this.state.formFields}
                oldFormFields={this.state.oldFormFields}
                supplierModalSave={this.supplierModalSave}
                isSupplier={this.state.isSupplier}
                getComboBoxValue={this.getComboBoxValue}
                comboBoxSelection={comboBoxSelection}
                isFromInventory={this.props.isFromInventory}
                closeItemScan={this.closeItemScan}
                isOpenCodeModel={isOpenCodeModel}
                codeCheckBoxHandler={this.codeCheckBoxHandler}
                toggleCodeModel={this.toggleCodeModel}
                openCodeModel={this.openCodeModel}
                addLookupCode={this.addLookupCode}
                isScannerUse={isScannerUse}
                Pagination={this.state.Pagination}
                codeList={this.props.codeList}
                totalRecord={this.props.totalRecord}
                totalPage={this.props.totalPage}
                lookupCodeSearchData={this.props.lookupCodeSearchData}
                lookupCodeSearchValue={this.props.lookupCodeSearchValue}
                onLookupCodeSearchChangeHandler={this.onLookupCodeSearchChangeHandler}
                onLookupCodeSearchCheckBoxHandler={this.onLookupCodeSearchCheckBoxHandler}
                setScreenLock={this.props.setScreenLock}
                isScreenLock={this.props.isScreenLock}

            />
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        isItemExist: (data) => dispatch(isItemExist(data)),
        insertItem: (data) => dispatch(insertItem(data)),
        setSuccessDialogue: (data, status, message) => dispatch(setSuccessDialogue(data, status, message)),
        clearMessage: () => dispatch(clearMessage()),
        GetsupplierList: (data) => dispatch(GetsupplierList(data)),
        getLookupCodeList: (data, isLoading) => dispatch(getLookupCodeList(data, isLoading)),
        setlookupCodeSearchData: (data) => dispatch(setlookupCodeSearchData(data)),
        setLookupCodeSearchTerm: (data) => dispatch(setLookupCodeSearchTerm(data)),
        setQuantity: (data) => dispatch(setQuantity(data)),
        setlookupCodeSearchValue: (data) => dispatch(setlookupCodeSearchValue(data)),
        getSearchList: (data) => dispatch(getSearchList(data)),
        setUpdatedValueColor: (data) => dispatch(setUpdatedValueColor(data)),
        setScrenLock:(data)=>dispatch(setScrenLock(data))

    };
};

const mapStateToProps = (state) => {
    const { isLoading, itemExistPayload, isSuccessDialogue, status,lookupCodeSearchValue, lookupCodeSearchData, message,codeList, products,supplierList ,
        updatedValue} = state.NewInventoryScanItemReducer;
    const{screenLock} =state.InventoryDetailReducer;   
    return {
        itemExistPayload: itemExistPayload,
        products: products.values !== null ? products.values : [],
        status, message, isLoading, isSuccessDialogue,
        supplierList: supplierList,
        codeList: codeList.values.length > 0 ? codeList.values : [],
        lookupCodeSearchValue: lookupCodeSearchValue,
        lookupCodeSearchData: lookupCodeSearchData.values.length > 0 ? lookupCodeSearchData.values : [],
        updatedValue,screenLock
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NewInventoryScanItemContainer));