import React from 'react';
import NumberFormat from 'react-number-format';
export default function TareWeightsComponent(props) {
    const {itemTareWeight}=props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <div className="col-12">
                        <div className="custom-control custom-checkbox mb-2 ml-2">
                            <input type="checkbox" className="custom-control-input" id="option-radio1" name="option-radio1" checked={itemTareWeight.allowOpenTare===1? true:false} disabled />
                            <label className="custom-control-label" htmlFor="option-radio1">Allow open tare</label>
                        </div>

                    </div>
                    <div className="col-12">
                        <div className="floating-label my-2">
                            <input className="floating-input" type="text" placeholder=" " value={itemTareWeight.rounding} disabled />
                            <span className="highlight"></span>
                            <label>Open tare rounding</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="custom-control custom-checkbox mb-2 ml-2">
                            <input type="checkbox" className="custom-control-input" id="option-radio2" name="option-radio2" checked={itemTareWeight.allowFixed===1? true:false} disabled />
                            <label className="custom-control-label" htmlFor="option-radio2">Fixed tare weight</label>
                        </div>

                    </div>
                    <div className="col-12 display-flex">
                        <div className="custom-control custom-radio ml-2 my-2 ">
                            <input type="radio" id="option-radio2" name="tareWeightsCustomRadio" className="custom-control-input" checked={itemTareWeight.tareWeight>0? true:false} disabled />
                            <label className="custom-control-label" htmlFor="option-radio2"></label>

                        </div>
                        <div className="floating-label mt-3">
                            <NumberFormat fixedDecimalScale={true} value={itemTareWeight.tareWeight} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="4" thousandSeparator={true} thousandsGroupStyle="lakh" disabled />
                            <span className="highlight"></span>
                            <label>Tare weight</label>
                        </div>
                    </div>

                    <div className="col-12 display-flex">
                        <div className="custom-control custom-radio ml-2 my-2 ">
                            <input type="radio" id="option-radio3" name="tareWeightsCustomRadio" className="custom-control-input" checked={itemTareWeight.tareWeightPercent>0? true:false} disabled />
                            <label className="custom-control-label" htmlFor="option-radio3"></label>
                        </div>
                        <div className="floating-label mt-3 d-flex">
                            <NumberFormat fixedDecimalScale={true} value={itemTareWeight.tareWeightPercent} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="4" thousandSeparator={true} thousandsGroupStyle="lakh" suffix={'%'} disabled />
                            <span className="highlight"></span>
                            <label>Tare weight percent</label>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}