import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet';
import {history} from "../../../history";


export default function manageItemHistoryComponent(props) {

	const {isLoading,ManageItemHistoryList,
		responseStatusProgress,responseStatusComplete,status,message,isShowManageItemHistoryDatatable,
		isUserName,isDate,isFile,isSetDate,isSetFile}=props;

	return (
		<div>
			<Helmet><title>Manage Item History| Modus Go!</title></Helmet>

			{isLoading? <Loading />:""}
		
				<div>
					<div className="sticky-filter-datatable" onClick={props.toggleManageItemHistoryDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>
					<div className={`datatable-filter${isShowManageItemHistoryDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.toggleManageItemHistoryDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
						</div>
						<div className="datatable-head-list">
							<div className="datatable-list-title text-darkblue font-weight-bold">
								Manage History
											</div>
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isUserName" name="isUserName" onChange={props.setUserNameCheckbox} checked={isUserName} />
										<label className="custom-control-label font-weight-bold" htmlFor="isUserName">USER NAME</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDate" name="isDate" onChange={props.setDateCheckbox} checked={isDate} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDate">DATE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isFile" name="isFile" onChange={props.setFileCheckbox} checked={isFile} />
										<label className="custom-control-label font-weight-bold" htmlFor="isFile">FILE</label>
									</div>
								</li>
							</ul>
						</div>
					</div>
					<div className="main-content-area order-space mobile-bottom-space " id="test">
					
						<div className="container-fluid d-none d-md-block">
							<div className="row">
								<div className="col position-relative px-0">
								<button className="position-absolute  btn btn-primary rounded-pill right-top" type="button" onClick={props.CheckHistory}>Check History</button>

									<h2 className="text-left text-xl-center heading-text">Manage Item History</h2>
									<h6 className={responseStatusComplete.toLowerCase()==="completed"? "value-green":"value-red"}>{responseStatusComplete}</h6>

								</div>
							</div>
						</div>



						
						<div className="container-fluid bg-white header-shadow mt-1 mb-150">
							<div className="row ">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblManageItemHistory" className="table w-100 new-order-list wo-collasped" >
											<thead>
												<tr>
													<th className="plus-sort"></th>


													<th style={{display: isSetDate? '':'none'}}>DATE</th>

													<th style={{display: isSetFile? '':'none'}}>FILE</th>

													<th className=""><span className="d-md-none">Download</span></th>

												</tr>
											</thead>
											<tbody>
												{ManageItemHistoryList.map((itemList,index) =>
													<tr key={itemList.id}>
														<td className="details-control plus-sort" tabIndex="-1"></td>


														<td style={{display: isSetDate? '':'none'}} className="default-cursor">{itemList.date}</td>

														<td style={{display: isSetFile? '':'none'}} className="default-cursor">{itemList.fileName}</td>

														<td><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del ManageItemHistoryGridDownloadIcon" data={itemList.pathName}><i className="zmdi zmdi-download "></i></button></td>

													</tr>
												)}
											</tbody>
										</table>
									</div>

								</div>

							</div>
						</div>
					</div>

				</div>
				


		</div>
	)
}