import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        searchText: "",
        sortColumnName: "",
        sortDirection: "",
        pageNo: 0,
        pageSize: 50,

    },
    transferOutList: [],
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
};

export default function TransferOutReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_TRANSFEROUTS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_TRANSFEROUTS_SUCCESS:
           
            return {
                ...state,
                transferOutList: action.data,
                isLoading: false,

            }
        case types.GET_TRANSFEROUTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_TRANSFEROUTS_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.SET_TRANSFEROUTS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_TRANSFEROUTS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_TRANSFEROUTS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_TRANSFEROUTS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_TRANSFEROUTSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_TRANSFEROUTSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_TRANSFEROUTSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}