import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        inventoryId: 0,
        sortColumnName: "lastTime",
        sortDirection: "desc",
        pageNo: 0,
        pageSize: 50
    },
    isLoading: false,
    status: "",
    message: "",
    isModified: false,
    description:"",
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    screenLock:false
};

export default function InventoryDetailReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_INVENTORYITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYITEM_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_INVENTORYITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                description:action.description,
                isModified: action.data.values !== undefined || action.data.values !==null? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.DELETE_INVENTORYITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_INVENTORYITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.DELETE_INVENTORYITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:""
            }
        case types.SET_FORMFIELDS_MESSAGE:
            return {
                ...state,
                description:action.data
            }
        case types.RESET_SUCCESS_DIALOGUE:
            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.UPLOAD_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.UPLOAD_INVENTORY_SUCCESS:
            return {
                ...state,
                  isLoading: false,
            }
        case types.UPLOAD_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
        case types.SET_SCREENLOCK:
            return {
                ...state,
                screenLock: action.data,
                isLoading: false,
            }             
        default:
            return state;
    }
}