import { Component } from 'react';
import axios from "axios";
import Cookies from 'js-cookie'
import AuthenticateService from './AuthenticateServices'
import apiGatewayEndpoint from '../config/index';
const obj = new AuthenticateService();
class SettingServices extends Component {
    //#region get all pod 
    GetAllPOD = async () => {
        return await axios.get(apiGatewayEndpoint.apiGatewayEndpoint + "Setting/getAllPod", { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion get all pod
    //#region get pod by user
    GetPODByUser = async (userId) => {
        return await axios.get(apiGatewayEndpoint.apiGatewayEndpoint + "Setting/getPodbyUser/" + userId, { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion get pod by user
    //#region insertOrUpdateUserPods
    insertOrUpdateUserPods = async (formFields) => {
        return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "Setting/insertOrUpdateUserPods", formFields, { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }

            return response;
        });
    }
    //#endregion insertOrUpdateUserPods

    //#region get pod settings for item edit page by user
    GetPodSettingbyUserId = async (userId) => {
        return await axios.get(apiGatewayEndpoint.apiGatewayEndpoint + "Setting/getPodSettingbyUserId/" + userId, { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            let response = {
                status: "Error",
                message: error.message
            }
            return response;
        });
    }
    //#endregion get pod settings for item edit page by user
    //#region insertOrUpdateUserPodsetting for item edit page
    insertOrUpdateUserPodsetting = async (formFields) => {
        return await axios.post(apiGatewayEndpoint.apiGatewayEndpoint + "Setting/insertOrUpdateUserPodsetting",
            formFields, { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }
        ).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message
            }

            return response;
        });
    }
    //#endregion insertOrUpdateUserPodsetting for item edit page

}
export default SettingServices;