import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { history } from '../../../../history'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
//#region get order item list
export function getManageItemHistoryList(data, isLoading) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_MANAGEHISTORY_REQUEST,
            isLoading: isLoading
        });
        let url = 'BigCommerceAPI/ListPastBicommerceItemHistory';
        return post(url, data)
            .then((response) => {
                const { payload } = response;                          
              
                if (response.status === 200) {
                    /* Logic */                  
                    $('#tblManageItemHistory').DataTable().destroy();
                    dispatch({
                        type: types.GET_MANAGEHISTORY_SUCCESS,
                        data: response.payload,                       
                    });
                    $('#tblManageItemHistory').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_MANAGEHISTORY_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
export function getCheckItemHistoryList(data,isLoading) {
 
    return (dispatch) => {
        dispatch({
            type: types.GET_MANAGEHISTORYLIST_REQUEST,
            isLoading: isLoading
        });
        let url = 'BigCommerceAPI/get/BigCommerceManageItemHistory';
        return get(url)
            .then((response) => {
                const { payload } = response;                          
              
                if (response.status === 200) {
                    /* Logic */                  
                    dispatch({
                        type: types.GET_MANAGEHISTORYLIST_SUCCESS,
                        data: response.payload,                       
                    });
                    dispatch(getManageItemHistoryList(data, isLoading)); 
                  
                } else {
                    dispatch({
                        type: types.GET_MANAGEHISTORYLIST_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

//#region set order item list param
export function setManageItemHistoryListParam(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM,
            data: data,


        });
    }
}
//#endregion set order item list param
export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}


export function GetKeyValuePair(isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GETALL_KEYVALUE_REQUEST,
            isLoading:isLoading
        });
        let url = 'APICommonIntegration/get/GetKeyValuePair';


        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */

                    dispatch({
                        type: types.GETALL_KEYVALUE_SUCCESS,
                        data: response.payload,
                    });


                } else {
                    dispatch({
                        type: types.GETALL_KEYVALUE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

