import { Component } from 'react';
import axios from "axios";
import Cookies from 'js-cookie'
import AuthenticateService from './AuthenticateServices'
import apiGatewayEndpoint from '../config/index';
const obj = new AuthenticateService();
class MenuServices extends Component {
    //#region get menu from role  
    GetMenu = async (role) => {
        
        return await axios.get(apiGatewayEndpoint.apiGatewayEndpoint + "menu/get/" + role, { headers: { "Authorization": `Bearer ${Cookies.get('jwtToken')}` } }).then((res) => {
            //on success
            if (res.data.code === "SH01")
                obj.subscriptionFailed();
            if (res.data.code === "A01" || res.data.code === "A02")
                obj.Logout(res.data);
            return res.data;
        }).catch((error) => {
            //on error
            let response = {
                status: "Error",
                message: error.message

            }

            return response;
        });
    }
    //#endregion get menu from role
}
export default MenuServices;