import React,{Component} from 'react';
import './Items.css'
import ItemsServices from '../Services/ItemsServices';
import CommonServices from '../Services/CommonServices';
import swal from 'sweetalert';
import {showLoader,hideLoader,showitemgridLoader,hideitemgridLoader,encryptURL} from '../Custom';
import {Helmet} from 'react-helmet'
import {history} from '../history'
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import Autocomplete from 'react-autocomplete';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import NumberFormat from 'react-number-format';

window.$=$;
function debounce(func,wait,immediate) {
  var timeout;

  return function executedFunction() {
    var context=this;
    var args=arguments;

    var later=function() {
      timeout=null;
      if(!immediate) func.apply(context,args);
    };

    var callNow=immediate&&!timeout;

    clearTimeout(timeout);

    timeout=setTimeout(later,wait);

    if(callNow) func.apply(context,args);
  };
};
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
    if($("input:checkbox").filter(":checked").length==0) {
      swal("","Please select at least one column","error");
      return false;
    }
  });
}
class Items extends Component {
  constructor(props) {
    super(props);

    this.state={
      searchData: [],
      Pagination: {
        pageNo: 1,
        pageSize: 50,
        sortColumnName: "code",
        sortDirection: "asc",
        searchText: ""
      },
      value: "",
      sortDirection: {
        code: "asc",
        Type: "",
        description: "",
        department: "",
        category: "",
        availableQuantity: "",
        price: ""
      },
      scrollPos: 0,
      totalRecord: 0,
      totalPage: 0,
      rowData: [],
      isRender: false,
      searchTerm: props.searchTerm,
      isShowPOCDatatable: false,
      isName: true,
      isSetName: true,
      isCode: true,
      isType: true,
      isDepartment: true,
      isCategory: true,
      isAvailable: true,
      isPrice: true,
      isSetCode: true,
      isSetType: true,
      isSetDepartment: true,
      isSetCategory: true,
      isSetAvailable: true,
      isSetPrice: true,
      apiNameList: [],
      priceList: [
        {id: "priceA",value: "Price A",isChecked: false},
        {id: "priceB",value: "Price B",isChecked: false},
        {id: "priceC",value: "Price C",isChecked: false},
        {id: "priceD",value: "Price D",isChecked: false},
        {id: "priceE",value: "Price E",isChecked: false},
        {id: "priceF",value: "Price F",isChecked: false},
        {id: "priceG",value: "Price G",isChecked: false},
        {id: "priceH",value: "Price H",isChecked: false},
        {id: "priceI",value: "Price I",isChecked: false},
        {id: "priceJ",value: "Price J",isChecked: false},
        {id: "priceK",value: "Price K",isChecked: false},
        {id: "priceL",value: "Price L",isChecked: false},
        {id: "priceM",value: "Price M",isChecked: false},
        {id: "priceN",value: "Price N",isChecked: false},
        {id: "priceO",value: "Price O",isChecked: false},
        {id: "priceP",value: "Price P",isChecked: false},
        {id: "priceQ",value: "Price Q",isChecked: false},
        {id: "priceR",value: "Price R",isChecked: false},
        {id: "priceS",value: "Price S",isChecked: false},
        {id: "priceT",value: "Price T",isChecked: false},
        {id: "priceU",value: "Price U",isChecked: false},
        {id: "priceV",value: "Price V",isChecked: false},
        {id: "priceW",value: "Price W",isChecked: false},
        {id: "priceX",value: "Price X",isChecked: false},
        {id: "priceY",value: "Price Y",isChecked: false},
        {id: "priceZ",value: "Price Z",isChecked: false}
      ],
      apiReserveBar: []
    }

  }

  togglePOCDatatable=() => {this.setState({isShowPOCDatatable: !this.state.isShowPOCDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowPOCDatatable: false})}

  componentDidMount() {
    showLoader();
    this.getAPINameList();
    this.getAllItems(this.state.Pagination);

    window.addEventListener('scroll',this.handleScroll);
    ColumnFilterCheckbox()
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblItemList').DataTable().destroy();
  }
  //#region sort event  
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";
      var sortDirection={...this.state.Pagination};
      let priceList=this.state.priceList;
      switch(sortColumnName) {
        case "code":
          sortdirection=this.state.sortDirection.code===""? "asc":this.state.sortDirection.code==="asc"? "desc":"asc";
          sortDirection["code"]=sortdirection;
          break;
        case "Type":
          sortdirection=this.state.sortDirection.Type===""? "asc":this.state.sortDirection.Type==="asc"? "desc":"asc";
          sortDirection["Type"]=sortdirection;
          break;
        case "description":
          sortdirection=this.state.sortDirection.description===""? "asc":this.state.sortDirection.description==="asc"? "desc":"asc";
          sortDirection["description"]=sortdirection;
          break;
        case "department":
          sortdirection=this.state.sortDirection.department===""? "asc":this.state.sortDirection.department==="asc"? "desc":"asc";
          sortDirection["department"]=sortdirection;
          break;
        case "category":
          sortdirection=this.state.sortDirection.category===""? "asc":this.state.sortDirection.category==="asc"? "desc":"asc";
          sortDirection["category"]=sortdirection;
          break;
        case "availableQuantity":
          sortdirection=this.state.sortDirection.availableQuantity===""? "asc":this.state.sortDirection.availableQuantity==="asc"? "desc":"asc";
          sortDirection["availableQuantity"]=sortdirection;
          break;
        case "price":
          sortdirection=this.state.sortDirection.price===""? "asc":this.state.sortDirection.price==="asc"? "desc":"asc";
          sortDirection["price"]=sortdirection;
          break;
        default:
          break;
      }
      for(let i=0;i<priceList.length;i++) {
        switch(sortColumnName) {
          case priceList[i].id:
            sortdirection=this.state.sortDirection.price===""? "asc":this.state.sortDirection.price==="asc"? "desc":"asc";
            sortDirection[priceList[i].id]=sortdirection;
            break;
          default:
            break;
        }
      }
      var data={
        pageNo: this.state.Pagination.pageNo,
        pageSize: this.state.Pagination.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        searchText: this.state.Pagination.searchText
      };
      var Pagination={...this.state.Pagination};
      Pagination["sortColumnName"]=sortColumnName;
      Pagination["sortDirection"]=sortdirection;
      this.setState({
        Pagination
      });
      this.setState({
        sortDirection
      });
      this.getAllItems(data);
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion sort event

  //#region scroll event 
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        let pagesize=this.state.Pagination.pageSize+50;
        let data={
          pageNo: this.state.Pagination.pageNo,
          pageSize: pagesize,
          sortColumnName: this.state.Pagination.sortColumnName,
          sortDirection: this.state.Pagination.sortDirection,
          searchText: this.state.Pagination.searchText
        };
        var Pagination={...this.state.Pagination};
        Pagination["pageSize"]=pagesize;
        this.setState({
          Pagination
        });
        this.getAllItems(data);
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  },0);

  //#endregion scroll event
  //#region get all items data 
  getAllItems=async (Pagination) => {
    try {
      var Obj=new ItemsServices();
      await Obj.GetAllItems(Pagination).then((items) => {
        if(!!items.values) {
          $('#tblItemList').DataTable().destroy();
          var historydata=localStorage.getItem('ItemDetails')!==null? JSON.parse(CryptoJS.AES.decrypt(localStorage.getItem('ItemDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";

          if(historydata!=="") {
            var globalSearchValue=Cookies.get('globalSearchValue')!==undefined? CryptoJS.AES.decrypt(Cookies.get('globalSearchValue'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8):"";
            let Pagination={...this.state.Pagination};
            Pagination["searchText"]=globalSearchValue;
            this.setState({rowData: historydata.values,Pagination});
            Cookies.remove('ItemDetails');
            localStorage.removeItem('ItemDetails');
            hideLoader();
          }
          else {
            this.setState({rowData: items.values});

          }
          $('#tblItemList').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
              "sEmptyTable": "No data found"
            },
            fixedHeader: true
          })

        }

        if(items.status.toLowerCase()==="error"&&items.code!=="E08")
          swal({
            title: items.status,
            text: items.message,
            icon: items.status.toLowerCase(),
            button: "Ok"
          })
      }).catch(function(error) {
        hideLoader();
        console.log(error);
        //Perform action based on error
      });
      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get all items data  


  //#region searching

  searchHandler=async (record,cb) => {
    try {
      showitemgridLoader();
      let getdata;
      record=record.trim();
      //this.setState({value:record});
      if(record!==""||this.state.Pagination.searchText!=="") {

        let Pagination={...this.state.Pagination};
        //console.log(Pagination);
        Pagination["searchText"]=record;
        this.setState({Pagination},() => {
          this.getAllItems(this.state.Pagination);
        });
      }
      if(record!=="") {
        var Obj=new ItemsServices();
        let param={
          itemVal: record
        };
        Obj.GetAllSearchByValueData(param).then((searchdata) => {
          getdata=searchdata.values!==undefined? searchdata.values:[];
          this.setState({
            searchData: getdata
          });
          hideitemgridLoader();
        });
      }
      else {
        getdata=[];
        this.setState({
          searchData: getdata
        });
        hideitemgridLoader();
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion searching
  setSearchTerm=debounce((searchTerm,cb) => {
    this.setState({searchTerm})
    this.searchHandler(searchTerm,cb);
  },1000)

  redirectItemEditComponent=(itemId) => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager && userDetails.role!==process.env.REACT_APP_UserRole_CentralManager  ) {
        swal({
          title: "warning",
          text: "You do not have permission to access item details.",
          icon: "warning",
          button: "Ok"
        })

      }
      else {
        window.open("/ItemEdit/"+encryptURL(itemId),'_blank');
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }

  //#region filter checkbox onchange function
  setNameCheckbox=() => {
    this.setState({isName: !this.state.isName},() => {
      this.setState({isSetName: this.state.isName});
    });
  }
  setCodeCheckbox=() => {
    this.setState({isCode: !this.state.isCode},() => {
      this.setState({isSetCode: this.state.isCode});
    });
  }
  setTypeCheckbox=() => {
    this.setState({isType: !this.state.isType},() => {
      this.setState({isSetType: this.state.isType});
    });
  }
  setDepartmentCheckbox=() => {
    this.setState({isDepartment: !this.state.isDepartment},() => {
      this.setState({isSetDepartment: this.state.isDepartment});
    });
  }
  setCategoryCheckbox=() => {
    this.setState({isCategory: !this.state.isCategory},() => {
      this.setState({isSetCategory: this.state.isCategory});
    });
  }

  setAvailableCheckbox=() => {
    this.setState({isAvailable: !this.state.isAvailable},() => {
      this.setState({isSetAvailable: this.state.isAvailable});
    });
  }
  setPriceCheckbox=() => {
    this.setState({isPrice: !this.state.isPrice},() => {
      this.setState({isSetPrice: this.state.isPrice});
    });
  }
  onCheckboxChange=(e) => {
    let priceList=this.state.priceList
    priceList.forEach(priceList => {
      if(priceList.id===e.target.name)
        priceList.isChecked=e.target.checked
    })
    this.setState({priceList: priceList})

  }

  getAPINameList=async () => {
    try {

      var Obj=new CommonServices();
      await Obj.getAPINameList().then((apiNameList) => {
        if(!!apiNameList.values) {
          let apiReserveBar=apiNameList.values.filter(x => x.isDisplay==true&&x.apiName.toLowerCase()=="reservebar")
          this.setState({
            apiNameList: apiNameList.values,
            apiReserveBar: apiReserveBar
          });
        }
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }

  sendItemToToReservebar=async () => {
    try {
      showLoader();

      var Obj=new ItemsServices();
      await Obj.sendItemToToReservebar().then((response) => {

        swal({
          title: response.status,
          text: response.message,
          icon: response.status.toLowerCase(),
          timer: response.status.toLowerCase()!=="error"? 3000:0,
          button: response.status.toLowerCase()!=="error"? false:"Ok",
        })

        hideLoader();
      }).catch(function(error) {
        hideLoader();
        console.log(error);
        //Perform action based on error
      });

      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }

  render() {
    let styles={
      marginLeft: '17%',

    };
    const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
    //#region render html 
    return (
      <div>
        <Helmet>
          <title>Items-list | Modus Go!</title>
        </Helmet>
        {userDetails.role!==process.env.REACT_APP_UserRole_Manager? "":<div className="add-item-btn-mobile d-md-none" onClick={() => history.push("/ItemCreate")}>
          <i className="material-icons">add</i>
        </div>}
        <div className="sticky-filter-datatable" onClick={this.togglePOCDatatable}>
          <img className="" src="/assets/images/filter-icon.svg" alt="" />
        </div>
        <div className={`datatable-filter${this.state.isShowPOCDatatable? " open":""}`}>
          <div className="close-datalist-popup" onClick={this.togglePOCDatatable}>
            <i className="zmdi zmdi-close-circle"></i>
          </div>
          <div className="datatable-head-list">
            <div className="datatable-list-title text-darkblue font-weight-bold">
              Items table
            </div>
            <ul>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isName" name="isName" onChange={this.setNameCheckbox} checked={this.state.isName} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isName">NAME</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCode" name="isCode" onChange={this.setCodeCheckbox} checked={this.state.isCode} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isCode">CODE</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isType" name="isType" onChange={this.setTypeCheckbox} checked={this.state.isType} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isType">TYPE</label>
                </div>
              </li>

              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDepartment" name="isDepartment" onChange={this.setDepartmentCheckbox} checked={this.state.isDepartment} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isDepartment">DEPARTMENT</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCategory" name="isCategory" onChange={this.setCategoryCheckbox} checked={this.state.isCategory} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isCategory">CATEGORY</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isAvailable" name="isAvailable" onChange={this.setAvailableCheckbox} checked={this.state.isAvailable} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isAvailable">AVAILABLE</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isPrice" name="isPrice" onChange={this.setPriceCheckbox} checked={this.state.isPrice} />
                  <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor="isPrice">PRICE</label>
                </div>
              </li>


              {this.state.priceList.map((priceList) =>
                <li>
                  <div className="custom-control custom-checkbox">
                    <input type="checkbox" className="custom-control-input ColumnFilterCheckbox " id={priceList.id} name={priceList.id} onChange={this.onCheckboxChange} checked={priceList.isChecked} />
                    <label className="custom-control-label font-weight-bold columnFilterSize" htmlFor={priceList.id}>{priceList.value} {this.state.apiNameList.filter(x => x.price.toLowerCase()==priceList.id.toLowerCase()).length>0? '('+this.state.apiNameList.filter(x => x.price.toLowerCase()==priceList.id.toLowerCase()).map(x => x.apiName).join(',')+')':""}</label>
                  </div>
                </li>
              )}
            </ul>
          </div>
        </div>
        <div className="main-content-area mobile-bottom-space" onClick={this.toggleHideOrderDatatable}>
          <div className="container-fluid d-none d-md-block">
            {/* <div className="row">
              <div className="col position-relative px-0">
                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button type="button" onClick={() => this.props.history.push("/ItemCreate")} className="position-absolute btn btn-primary rounded-pill" disabled={userDetails.isHQConnected}>Create new item</button>:''}
                {userDetails.isThirdPartyAPI&&this.state.apiReserveBar.length>0? <button type="button" onClick={() => this.sendItemToToReservebar()} style={styles} className="position-absolute btn btn-primary rounded-pill" >Send All Item To ReserveBar</button>:''}

                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="position-absolute right210-top mr-3 btn btn-primary rounded-pill" type="button" onClick={() => this.props.history.push("/priceConfiguration")}>Manage Price</button>:''}

                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={() => this.props.history.push("/ManageInventory")}>Manage inventory</button>:''}
                <h2 className="text-left text-xl-center heading-text">Items lookup</h2>
              </div>
            </div> */}
            <div className="row align-items-center justify-content-between">
              <div className="order-2 order-xl-1 col-xl-5 col-lg-6 col-md-6 d-flex p-0 mb-2">
                {userDetails.role===process.env.REACT_APP_UserRole_Manager || userDetails.role===process.env.REACT_APP_UserRole_CentralManager? <button type="button" onClick={() => this.props.history.push("/ItemCreate")} className="mr-3 btn btn-primary rounded-pill" disabled={userDetails.isHQConnected}>Create new item</button>:''}
                {/* {userDetails.isThirdPartyAPI&&this.state.apiReserveBar.length>0? <button type="button" onClick={() => this.sendItemToToReservebar()} className=" btn btn-primary rounded-pill" >Send All Item To ReserveBar</button>:''} */}
              </div>
              <div className="order-1 order-xl-2 col-xl-2 col-lg-12 col-md-12 text-center p-0 mb-2">
                <h2 className="text-left text-xl-center heading-text">Items lookup</h2>
              </div>
              <div className="order-3 order-xl-3 col-xl-5 col-lg-6 col-md-6 justify-content-lg-end d-flex p-0 mb-2">
                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="mr-3 btn btn-primary rounded-pill" type="button" onClick={() => this.props.history.push("/priceConfiguration")}>Manage Price</button>:''}

                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="btn btn-primary rounded-pill" type="button" onClick={() => this.props.history.push("/ManageInventory")}>Manage inventory</button>:''}

              </div>
            </div>
          </div>

          <div className="container-fluid d-block d-md-none">
            <div className="row">

              <div className="col p-0 mobile-header-info">
                <img className="mobile-logo-icon-items" src="/assets/images/logo-icon.png" alt="" />
                <p className="text-darkblue font-weight-bold mobile-title-headline-items">Item lookup</p>
                {userDetails.role===process.env.REACT_APP_UserRole_Manager? <button className="position-absolute right-top-items mr-sm-3 btn btn-primary rounded-pill" type="button" onClick={() => this.props.history.push("/ManageInventory")}>Manage inventory</button>:''}
              </div>
            </div>
          </div>

          <div className="container-fluid mt-3">
            <div className="row">
              <div className="col p-0">


                <div className="searchbox-wrap header-shadow">
                  <Autocomplete
                   // inputProps={{id: 'states-autocomplete'}}
                    wrapperStyle={{position: 'relative'}}
                    value={this.state.value}
                    items={this.state.searchData}
                    getItemValue={(item) => item.key}
                    inputProps={{placeholder: 'item code, description, alias, {department/category} or combination'}}
                    onSelect={record => {this.setState({value: record}); this.searchHandler(record)}}
                    onChange={(event,value) => {
                      this.setState({value})
                      this.setSearchTerm(value,(items) => {
                        this.setState({searchData: items})
                      })
                    }}
                    renderMenu={children => (
                      <div className="search-menu">
                        {children}
                      </div>
                    )}
                    renderItem={(item,isHighlighted) => (
                      <div
                        key={item.key}
                      >{item.value}</div>
                    )}
                  />


                  <div className="icon-box rounded-circle" type="button" >
                    <i className="material-icons">search</i>
                  </div>

                </div>
              </div>
            </div>
          </div>

          <div className="container-fluid bg-white header-shadow mt-4">
            <div className="row">
              <div id="itemgridloader">
                <div>
                </div>
              </div>
            </div>
            <div className="row my-3">
              <div className="col-12 pt-2">
                <span className="heading-text"><b>Item records</b></span>
              </div>
              <div className="col-12 p-0">
                <div className="table-responsive">

                  <table id="tblItemList" className="table nowrap w-100" >
                    <thead>
                      <tr>
                        <th className="plus-sort"></th>

                        <th style={{display: this.state.isSetName? '':'none'}} className={this.state.Pagination.sortColumnName==="description"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("description")}>NAME</th>

                        <th style={{display: this.state.isSetCode? '':'none'}} className={this.state.Pagination.sortColumnName==="code"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("code")}>CODE</th>

                        <th style={{display: this.state.isSetType? '':'none'}} className={this.state.Pagination.sortColumnName==="Type"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("Type")}>TYPE</th>

                        <th style={{display: this.state.isSetDepartment? '':'none'}} className={this.state.Pagination.sortColumnName==="department"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("department")}>DEPARTMENT</th>

                        <th style={{display: this.state.isSetCategory? '':'none'}} className={this.state.Pagination.sortColumnName==="category"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("category")}>CATEGORY</th>

                        <th style={{display: this.state.isSetAvailable? '':'none'}} className={this.state.Pagination.sortColumnName==="availableQuantity"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("availableQuantity")}>AVAILABLE</th>

                        <th style={{display: this.state.isSetPrice? '':'none'}} className={this.state.Pagination.sortColumnName==="price"? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => this.sortHandler("price")}>PRICE</th>
                        {this.state.priceList.map((priceList) =>
                          <th className={priceList.isChecked? "":"d-none"} style={{display: priceList.isChecked? "":"none"}} className={this.state.Pagination.sortColumnName===priceList.id? this.state.Pagination.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"&&priceList.isChecked? "":"d-none"} onClick={() => this.sortHandler(priceList.id)}>{priceList.value}</th>

                        )}

                      </tr>
                    </thead>
                    <tbody>
                      {this.state.rowData.map((rowdata,index) =>

                        <tr key={rowdata.itemID}>
                          <td className="details-control plus-sort"></td>

                          <td style={{display: this.state.isSetName? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.description}</td>

                          <td style={{display: this.state.isSetCode? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.itemLookupCode}</td>

                          <td style={{display: this.state.isSetType? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.itemTypeName}</td>

                          <td style={{display: this.state.isSetDepartment? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.department}</td>

                          <td style={{display: this.state.isSetCategory? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.category}</td>

                          <td style={{display: this.state.isSetAvailable? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}>{rowdata.availableQuantity}</td>

                          <td style={{display: this.state.isSetPrice? '':'none'}} onClick={() => this.redirectItemEditComponent(rowdata.itemID)}><NumberFormat value={rowdata.price} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>

                          {this.state.priceList.map((priceList) =>
                            <td style={{display: priceList.isChecked? '':'none'}} className={priceList.isChecked? "":"d-none"}><NumberFormat value={rowdata[priceList.id]} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                          )}


                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>


      </div>
    );
    //#endregion render html   
  }
}
export default Items;