import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {Multiselect} from 'multiselect-react-dropdown';
import {history} from "../../../history";
export default function PriceConfigurationComponent(props) {
	const {isLoading,departments,categories,dropdownListParam,isCategoryDisabled,isSelectAll,onCheckboxChange,inputChangeHandler,upDownHandler,dollarPercentageHandler,updatestatus,updatemessage,priceList,priceconfig,displaySetAPI,apiNameList,dollarPerLabel}=props;
	return (
		<div>
			<Helmet>
				<title>Price Configuration | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
					<div className="main-content-area mobile-bottom-space">
					    <div className="container-fluid d-none d-md-block">

							<div className="row">
						    	<div className="col position-relative px-0">
								<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button"  onClick={() => props.savePriceCofig()}>Save</button>

									<h2 className="text-left text-xl-center heading-text">Price Configuration</h2>

								</div>
							</div>

						</div>
					
					
				
			
					<div className="container-fluid mt-3">
						<div className="row">
							<div className="col-lg-12 col-md-12 col-sm-12">
								<div className="row bg-white header-shadow py-3 align-items-center">
							
									<div className="col-lg-12 col-md-12 col-sm-12">
										<div className="container pt-2 ml-1 mb-2">
											<div className="row">
												<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
											</div>
										</div>
										<div class="row ml-1 mb-1">
											<h6 className="value-red">*Note:Price will be updated based on Price column in item</h6>
										</div>
										<div className="row">
											<div className="col-sm-3 col-12">
												<div class="row">
													<div className="col-sm-5 col-12">
														<div className="custom-control custom-radio ml-2 my-2">
															<input type="radio" id="Up" name="UpDown" className="custom-control-input"  onChange={upDownHandler}/>
															<label className="custom-control-label" htmlFor="Up">Up (+)</label>
														</div>
													</div>
													<div className="col-sm-7 col-12">
														<div className="custom-control custom-radio ml-2 my-2">
															<input type="radio" id="Down" name="UpDown" className="custom-control-input"  onChange={upDownHandler} />
															<label className="custom-control-label" htmlFor="Down">Down (-)</label>
														</div>
													</div>
												</div>
											</div>

											<div className="col-sm-3 col-12">
												<div class="row">
													<div className="col-sm-5 col-12">
														<div className="custom-control custom-radio ml-2 my-2">
															<input type="radio" id="Dollar" name="DollarPercentage" className="custom-control-input" onChange={dollarPercentageHandler} />
															<label className="custom-control-label" htmlFor="Dollar">Dollar($)</label>
														</div>
													</div>
													<div className="col-sm-7 col-12">
														<div className="custom-control custom-radio ml-2 my-2">
															<input type="radio" id="Percentage" name="DollarPercentage" className="custom-control-input"  onChange={dollarPercentageHandler}/>
															<label className="custom-control-label" htmlFor="Percentage">Percentage (%)</label>
														</div>
													</div>
												</div>
											</div>
										</div>
										<div className="row">
											<div className="col-lg-5 col-md-12 col-12 mt-3 ">
												<div className="floating-label mb-0">
													<Multiselect className="floating-select"
														options={departments}
														displayValue="name"
														selectedValues={dropdownListParam.departmentIds}
														onSelect={props.selectDepartmentHandler}
														onRemove={props.removeDepartmentHandler}
														placeholder="Select department"
														name="departmentIds"
														id="departmentIds"
														avoidHighlightFirstOption={true}
														showCheckbox={true}
														hidePlaceholder={true}
														closeOnSelect={false}
													/>

													<span className="highlight"></span>
													<label >Department</label>
												</div>
											</div>
											<div className="col-lg-5 col-md-12 col-12 mt-3 ">
												<div className="floating-label mb-0">
													<Multiselect className="floating-select"
														options={categories}
														displayValue="name"
														selectedValues={dropdownListParam.categoryIds}
														onSelect={props.selectCategoryHandler}
														onRemove={props.removeCategoryHandler}
														placeholder="None"
														name="categoryIds"
														id="categoryIds"
														avoidHighlightFirstOption={true}
														showCheckbox={true}
														hidePlaceholder={true}
														closeOnSelect={false}
														disable={isCategoryDisabled}
													/>
													<span className="highlight"></span>
													<label >Category</label>
												</div>
											</div>
										
										</div>
										
										<div class="row">
											<div className="col-lg-5 col-md-12 col-12 mt-3 ">
												<div class="row">
													<div className="col-lg-6 col-md-12 col-12">
														<div className="floating-label">
															<input className="floating-input" tabIndex="3" type="number" placeholder=" " maxLength="30" id="priceValue" name="priceValue" onChange={inputChangeHandler}></input>
															<span className="highlight"></span>
															<label>Enter Value:</label>
														</div>
													</div> 
													<div className="col-lg-2 col-md-2 col-12 mt-4">
														<span>{dollarPerLabel}</span>
													</div>
													<div className="col-lg-4 col-md-12 col-12 mt-3 ">
														<button className="btn btn-link ml-2"  type="button" onClick={() => history.push("/Items")}>Show Items </button>
													</div>
												</div>
											</div>
										</div>
										</div>
										
</div>
							
							</div>

						</div>
					
					</div>



					<div className="container-fluid mt-3">
						<div className="row">
							<div className="col-lg-12 col-md-12 col-sm-12">
								<div className="row bg-white header-shadow py-3 align-items-center">
							
									<div className="col-lg-12 col-md-12 col-sm-12">

										<div class="row">
										    <div className="col-12">
												<div class="headline-list pt-0">
												<h6>Please Select Columns :</h6>
													<div className="custom-control custom-checkbox ml-2 my-2">
														<input type="checkbox" className="custom-control-input"  onChange={props.selectAll} id="selectall-check" name="selectall-check" />
														<label className="custom-control-label" htmlFor="selectall-check">Select All</label>
													</div>
												</div>
											</div>
										</div>
										<div className="row mb-3">
											<div className="col-12">
											<h6 className="value-green">{displaySetAPI}  </h6>
											</div>
										</div>
										<div class="row">
										{priceList.map((data) =>
											<div className="col-2">
												<div className="custom-control custom-checkbox ml-2 my-2">
													<input type="checkbox" className="custom-control-input" id={data.id} name={data.id} checked={priceconfig.filter(x=>x.price.toLowerCase()==data.id.toLowerCase() && x.isSetAPIPrice==true).length>0?
													 true:data.isChecked} onChange={onCheckboxChange}/>
													<label className="custom-control-label " htmlFor={data.id}>{data.value}</label>
													
												</div>
												{apiNameList.filter(x=>x.price.toLowerCase()==data.id.toLowerCase()).length>0?
													<label className={"value-red columnFilterSize"}>({apiNameList.filter(x=>x.price==data.id).map(x=>x.apiName).join(',')})</label>
													:""}
											</div>
											)}
										</div>
									
							</div>

						</div>
						</div>
						</div>
										
									
					</div>
				
				</div>
		
			</div>
		</div>
	)
}