import React from 'react';
import CentralUserComponent from '../component/CentralUserComponent';
import { setSuccessDialogue,clearMessage,GetCentralUser,UpdateCentralUser
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import {viewPassword} from '../../../Custom';

import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class CentralUserContainer extends React.Component {
 
  constructor(props) {
    super(props);
    this.state={
      isPassword:0,
      oldValue:false
    }
   
}
  componentDidMount() {
    window.addEventListener('scroll',this.handleScroll);
    this.props.GetCentralUser();
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
   
    clearInterval(this.clearMessageIntervalID);
  }
  
 editPassword= (id) => 
 {
    this.setState({isPassword:id})
    var passwordInput = document.getElementById("password"+id);
    var passStatus = document.getElementById('pass-status');
    passwordInput.type = 'text';
		passStatus.className = 'zmdi zmdi-eye zmdi-hc-fw text-primary';
    document.getElementById("password"+id).focus();
 }

 onChangePass=(e)=>
 {
  let oldValue = this.state.oldValue; 
  this.setState({oldValue:true})
 }
 inputChangeHandler=(e,id)=>
 {
   
 
    let data={
     password:e.target.value,
     id:parseInt(id)
   }
    this.setState({isPassword:true,oldValue:false})
   if(this.state.oldValue==true)
   {
    this.props.UpdateCentralUser(data);

   }

    
 }
  render() {
    // const { searchData } = this.props;   
   
    return (
      <CentralUserComponent
       {...this.props}
       editPassword={this.editPassword}
       isPassword={this.state.isPassword}
       inputChangeHandler={this.inputChangeHandler}
       onChangePass={this.onChangePass}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    GetCentralUser:()=>dispatch(GetCentralUser()),
    clearMessage: () => dispatch(clearMessage()),
    UpdateCentralUser: (data) => dispatch(UpdateCentralUser(data)),

  };
};

const mapStateToProps=(state) => {

  const {updatestatus, updatemessage, isSuccessDialogue,centralUser}=state.CentralUserReducer;
 
  return {
    updatestatus, updatemessage, isSuccessDialogue,
    centralUser:centralUser.length>0?centralUser:[]
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(CentralUserContainer);