  supplierCheckBoxHandler=(e,supp) => {
    var isChecked=e.target.checked;
    var cbs=document.getElementsByClassName("supplierCHK");
    for(var i=0;i<cbs.length;i++) {
      cbs[i].checked=false;
    }
    if(isChecked) {
      document.getElementById(e.target.name).checked=true;
      this.setState({
        supplierInfo: supp
      })
    }
  }

  supplierModalSave=() => {
    if(Object.entries(this.state.supplierInfo).length) {
      if(this.state.formFields.supplierID!==this.state.supplierInfo.id) {
        swal({
          title: types.DELETE_TITLE,
          text: "Supplier change: Recalculate order?",
          icon: "warning",
          buttons: {
            catch: {
              text: "Yes",
              value: "catch",
            },
            defeat: {
              text: "No",
              value: "defeat",
            },
          },
          dangerMode: true,

        })
          .then((value) => {
            switch(value) {
              case "catch":
                var reqParam={
                  poid: parseInt(decryptURL(this.props.match.params.orderId)),
                  supplierId: parseInt(this.state.supplierInfo.id)
                }
                this.setState({
                  ...this.state,
                  formFields: {
                    ...this.state.formFields,
                    supplierID: this.state.supplierInfo.id,
                    supplier: this.state.supplierInfo.code,
                    supplierName: this.state.supplierInfo.supplierName,
                  },
                  isOpenSupplierModal: false
                });
                this.props.changePOSupplier(reqParam);
                break;

              case "defeat":
                this.setState({
                  ...this.state,
                  formFields: {
                    ...this.state.formFields,
                    supplierID: this.state.supplierInfo.id,
                    supplier: this.state.supplierInfo.code,
                    supplierName: this.state.supplierInfo.supplierName,
                  },
                  isOpenSupplierModal: false
                });
                break;
            }
          });
      }
    }
    this.setState({
      isOpenSupplierModal: false
    })
  }

