  savePO=(click) => {
    let {storeID,rmsID,lastUpdated,docType,docOption,status,delStatus,invStatus,allocationID,supplierTaxID,
      dateCreated,orderDate,supplierDelDate,supplierInvDate,datePlaced,
      locationType,locationID,reference,addrTo,shipTo,purchaserID,requisitioner,shipViaID,fobPoint,freight,
      payTermID,currencyID,exchangeRate,invDiscMode,invDiscValue,comment,externalDocNo,supplierDocNo,supplierDelNo,
      supplierInvNo,supplierInvAmt,postingComment,id,totalTax,supplierID,requiredDate}=this.state.formFields;
    lastUpdated=(lastUpdated==null||''||0? '':moment(lastUpdated).format('YYYY-MM-DD'));
    orderDate=(orderDate==(null||'')? '':moment(orderDate).format('YYYY-MM-DD'));
    supplierDelDate=(supplierDelDate==null||''||0? '':moment(supplierDelDate).format('YYYY-MM-DD'));
    supplierInvDate=(supplierInvDate==null||''||0? '':moment(supplierInvDate).format('YYYY-MM-DD'));
    datePlaced=(datePlaced==(null||'')? '':moment(datePlaced).format('YYYY-MM-DD'));
    dateCreated=(dateCreated==null||''||0? '':moment(dateCreated).format('YYYY-MM-DD'));
    requiredDate=(requiredDate==(null||'')? '':moment(requiredDate).format('YYYY-MM-DD'));
    const totalAmount=0;
    const original_ID=0;
    const original_LastUpdated='';
    locationType=locationType=='Customer'? 1:0;
    let data={
      storeID,rmsID,lastUpdated,dateCreated,orderDate,supplierDelDate,supplierInvDate,datePlaced,
      docType,docOption,status,delStatus,invStatus,allocationID,supplierTaxID,
      locationType,locationID,reference,addrTo,shipTo,purchaserID,requisitioner,shipViaID,fobPoint,freight,
      payTermID,currencyID,exchangeRate,comment,externalDocNo,supplierDocNo,supplierDelNo,
      supplierInvNo,supplierInvAmt,postingComment,id,totalTax,supplierID,requiredDate,
      totalAmount: totalAmount,
      original_ID: original_ID,
      original_LastUpdated: original_LastUpdated,
      invDiscMode: parseInt(invDiscMode),
      invDiscValue: parseFloat(invDiscValue)
    }
    this.props.updatePurchaseOrderItem(data,click);
  }

