  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";

      switch(sortColumnName) {
        case "entryCode":
          sortdirection=this.props.requestParameter.sortColumnName!=="entryCode"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "description":
          sortdirection=this.props.requestParameter.sortColumnName!=="description"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "cost":
          sortdirection=this.props.requestParameter.sortColumnName!=="cost"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "price":
          sortdirection=this.props.requestParameter.sortColumnName!=="price"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "orderNumber":
          sortdirection=this.props.requestParameter.sortColumnName!=="orderNumber"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "itemQtyOnHand":
          sortdirection=this.props.requestParameter.sortColumnName!=="itemQtyOnHand"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "quantity":
          sortdirection=this.props.requestParameter.sortColumnName!=="quantity"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "extendedCost":
          sortdirection=this.props.requestParameter.sortColumnName!=="extendedCost"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "profitMargin":
          sortdirection=this.props.requestParameter.sortColumnName!=="profitMargin"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "srNo":
          sortdirection=this.props.requestParameter.sortColumnName!=="srNo"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "lastWeekQuantity":
          sortdirection=this.props.requestParameter.sortColumnName!=="lastWeekQuantity"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "lastMonthQuantity":
          sortdirection=this.props.requestParameter.sortColumnName!=="lastMonthQuantity"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        default:
          break;
      }
      var data={
        poId: parseInt(decryptURL(this.props.match.params.orderId)),
        pageNo: this.props.requestParameter.pageNo,
        pageSize: this.props.requestParameter.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        searchText: this.props.requestParameter.searchText
      };
      var requestParameter={...this.props.requestParameter};
      requestParameter["sortColumnName"]=sortColumnName;
      requestParameter["sortDirection"]=sortdirection;
      this.props.setRequestParameter(requestParameter);
      this.props.getPurchaseOrderItemList(data,false,this.state.isSetSerialNumber,this.state.isSetLookupCode,this.state.isSetDescription,this.state.isSetCost,this.state.isSetPrice,this.state.isSetOrderNumber
        ,this.state.isSetQtyOnHand,this.state.isSetLastWeekQty,this.state.isSetLastMonthQty,this.state.isSetQtyOnOrder,this.state.isSetExtendedCost,this.state.isSetProfitMargin);
    } catch(error) {
      console.log(error);
    }
  }
