  showScanItem=() => {
    this.setState({
      isScanItem: true,
    })
  }

  closeScanItem=() => {
    this.setState({
      isScanItem: false,
    })
  }
  //#endregion open close scan item
  //#region cost price model onChange handle method  
  inputChangeHandler=(e) => {
    try {

      var value=e.target.value;
      value=value.replace(/,/g,"");
      if(value.length>e.target.maxLength) {
        value=value.substring(0,e.target.maxLength);
      }
      var updateItem={...this.state.updateItem};
      updateItem[e.target.name]=value!==""? parseFloat(value):"";
      this.setState({updateItem});

    } catch(error) {
      console.log(error);

    }
  }
  //#endregion cost price model onChange handle method
  //#region set profit margin
  setProfitMargin=(e) => {
    try {
      let updateItem={...this.state.updateItem};
      var profitMargin=(((parseFloat(this.state.updateItem.newPrice!==""? this.state.updateItem.newPrice:0)-parseFloat(this.state.updateItem.newCost!==""? this.state.updateItem.newCost:0))*100)/parseFloat(this.state.updateItem.newPrice!==""? this.state.updateItem.newPrice:0)).toFixed(2);

      updateItem["profitMargin"]=parseFloat(profitMargin);
      this.setState({updateItem},() => this.setExtendedCost());
    } catch(error) {
      console.log(error);
    }
  }

  //#endregion set profit margin  
  //#region set cost
  setCost=(e) => {
    try {
      let updateItem={...this.state.updateItem};
      let extCost=parseFloat(this.state.updateItem.extendedCost!==""? this.state.updateItem.extendedCost:0);
      let qty=parseFloat(this.state.updateItem.quantity!==""? this.state.updateItem.quantity:0);
      var cost=qty>0? parseFloat(extCost/qty):0;
      updateItem["newCost"]=cost;
      this.setState({updateItem},() => this.setProfitMargin());
    } catch(error) {
      console.log(error);
    }
  }

  //#endregion set cost 
  //#region set extended cost
  setExtendedCost=(e) => {
    try {
      let updateItem={...this.state.updateItem};
      var cost=(((parseFloat(this.state.updateItem.newCost!==""? this.state.updateItem.newCost:0)))*parseFloat(this.state.updateItem.quantity!==""? this.state.updateItem.quantity:0));
      updateItem["extendedCost"]=parseFloat(cost);
      this.setState({updateItem});
    } catch(error) {
      console.log(error);
    }
  }

  //#endregion set extended cost
  //#region set price
  setPrice=(e) => {
    try {
      let updateItem={...this.state.updateItem};
      var price=((parseFloat(this.state.updateItem.newCost!==""? this.state.updateItem.newCost:0))/((100-parseFloat(this.state.updateItem.profitMargin!==""? this.state.updateItem.profitMargin:0))/100));
      updateItem["newPrice"]=parseFloat(price);
      this.setState({updateItem});
    } catch(error) {
      console.log(error);
    }
  }

  //#endregion set price
