  changeDiscountType=(e) => {
    const {value}=e.target;
    const {invDiscValue}=this.state.formFields
    let discount=invDiscValue
    if(parseInt(value)===1) {
      discount=0
    } else if(parseInt(value)===2) {
      discount=0
    }
    else {
      discount=0
    }
    this.setState({
      formFields: {
        ...this.state.formFields,
        invDiscMode: parseInt(value),
        invDiscValue: discount
      }

    },() => this.IncDiscChangeHandler())
  }
  changeDiscountSearch=(e) => {
    var value=e.target.value;
    value=value.replace(/,/g,"");
    let mode=parseInt(this.state.formFields.invDiscMode)
    if(mode===1) {
      if((value>0||value=='')&&value<100000000&&value.length<9) {
        this.setState({
          formFields: {
            ...this.state.formFields,
            invDiscValue: value
          }

        })
      }
    } else if(mode===2) {
      if(value<101&&value.length<6) {
        this.setState({
          formFields: {
            ...this.state.formFields,
            invDiscValue: value,
          }

        })
      }
    } else if(value<10000000&&value.length<9) {
      this.setState({
        formFields: {
          ...this.state.formFields,
          invDiscValue: value,
        }

      })

    }

  }

