const mapDispatchToProps=(dispatch) => {
  return {
    getOrderDetail: (orderId) => dispatch(getOrderDetail(orderId)),
    getPurchaseOrderItemList: (data,isLoading,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) =>
    dispatch(getPurchaseOrderItemList(data,isLoading,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    deleteOrderItem: (deleteeRequestParameter,requestParameter,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) => 
    dispatch(deleteOrderItem(deleteeRequestParameter,requestParameter,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin)),
    updatePOItemInline: (updateInlineItemParam,requestParameter) => dispatch(updatePOItemInline(updateInlineItemParam,requestParameter)),
    updatePOItem: (updateItemParam,requestParameter,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) =>
    dispatch(updatePOItem(updateItemParam,requestParameter,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin)),
    completePO: (param) => dispatch(completePO(param)),

    getPurchaseOrderById: (id) => dispatch(getPurchaseOrderById(id)),
    updatePurchaseOrderItem: (data,click) => dispatch(updatePurchaseOrderItem(data,click)),
    getCustomerByAccNo: (data) => dispatch(getCustomerByAccNo(data)),
    POGetCustomerList: (data) => dispatch(POGetCustomerList(data)),
    POGetsupplierList: (data) => dispatch(POGetsupplierList(data)),
    getPOPurchaserList: (data) => dispatch(getPOPurchaserList(data)),
    getPOShippingList: (data) => dispatch(getPOShippingList(data)),
    IncDiscChange: (data) => dispatch(IncDiscChange(data)),
    clearMessage: () => dispatch(clearMessage()),
    changePOSupplier: (data) => dispatch(changePOSupplier(data)),
    setSearchRequestParameter: (data) => dispatch(setSearchRequestParameter(data)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    updateSerialNumber: (data,ItemListParam,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) =>
    dispatch(updateSerialNumber(data,ItemListParam,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin)),
  };
};

const mapStateToProps=(state) => {
  const {purchaseOrderItemList,requestParameter,isLoading,poValues,orderDetail,
    purchaseOrder,deliveryStatus,invoiceDiscount,invoiceStatus,orderStatus,
    supplierInfo,purchesInfo,termInfo,supplierList,poTermList,poCustList,customerDetails,poPurchaserList,
    poShippingList,totalRecord,status,message,searchValue,searchData,searchRequestParameter,SearchTerm
  }=state.PurchaseOrderEditReducer;
  return {
    orderDetail: orderDetail.values,
    requestParameter: requestParameter,
    purchaseOrderItemList: purchaseOrderItemList.values.length>0? purchaseOrderItemList.values:[],
    isLoading: isLoading,
    poValues: poValues.poValues!==undefined? poValues.poValues:{
      podId: 0,
      itemEntries: 0,
      subTotal: 0,
      charges: 0,
      saleTax: 0,
      totalAmount: 0
    },
    purchaseOrder: purchaseOrder,
    deliveryStatus: deliveryStatus,
    invoiceDiscount: invoiceDiscount,
    invoiceStatus: invoiceStatus,
    orderStatus: orderStatus,
    supplierInfo: supplierInfo,
    purchesInfo: purchesInfo,
    termInfo: termInfo,
    supplierList: supplierList,
    poTermList,
    poCustList,
    customerDetails,
    poPurchaserList,
    poShippingList,
    totalRecord: totalRecord,status,message,
    searchValue: searchValue,
    searchData: searchData.values.length>0? searchData.values:[],
    searchRequestParameter: searchRequestParameter,
    SearchTerm: SearchTerm,
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(PurchaseOrderEditContainer);