import React from 'react';
import SubscriptionFailedComponent from '../component/SubscriptionFailedComponent';
import {updateSubscription } from '../state/action/index';
import { connect } from 'react-redux';
class SubscriptionFailedContainer extends React.Component {

   retryHandler=()=>{
       this.props.updateSubscription();
   }
    render() {


        return (
            <SubscriptionFailedComponent
                {...this.props}
                retryHandler={this.retryHandler}
            />

        )

    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateSubscription: () => dispatch(updateSubscription()),
    };
};

const mapStateToProps = (state) => {
    const {isLoading } = state.SubscriptionFailedReducer;
    return {
        isLoading:isLoading

    };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubscriptionFailedContainer);