import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    isLoading: false,
    getCustomList:[],
    CustomDropList:[]
};

export default function ExpandedPropertiesReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_ITEMCUSTOM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ITEMCUSTOM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                getCustomList: action.data.values,
            }
        case types.GET_ITEMCUSTOM_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
        case types.GET_ITEMCUSTOMLIST_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ITEMCUSTOMLIST_SUCCESS:
         
            return {
                ...state,
                isLoading: false,
                CustomDropList: action.data.values,
            }
        case types.GET_ITEMCUSTOMLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }     
        case types.INSERT_ITEMEXT_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.INSERT_ITEMEXT_SUCCESS:
            
            return {
                ...state,
                isLoading: false,
            }
        case types.INSERT_ITEMEXT_FAILED:
            return {
                ...state,
                isLoading: false,
            }    
        case types.UPDATE_ITEMEXT_LIST:

            return {
                ...state,
                isLoading: false,
                getCustomList: action.data,
            }   
            
        default:
            return state;
    }
}