import React from 'react';
import WorkOrderEditComponent from '../component/WorkOrderEditComponent';
import {
  getWorkOrderItemList, setOrderItemListParam,
  deleteOrderItem, getOrderDetail,
  setSearchValue, getSearchList, setSearchData,
  setSearchTerm, insertOrderItem, getCustomers,
  getCustomerDetail, updateWOItemInline, clearMessage,
  applyTax, setChangeCustomerRequestParam, changeCustomer,
  saveComment, duplicateItem, applyDiscount, getTaxList,
  setChangeTaxRequestParam, changeTax, getItemOptionsByLookupCode, setPriceMustBeEntered
} from '../state/actions/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import moment from 'moment';

import { encryptURL, decryptURL } from '../../../Custom'
window.$ = $;
function handleWOColumnFilter(classdata) {
  $("#WOTableColumnFilter").on('click', '.ColumnFilterCheckbox', function () {
    if($('input[name="isChecked"]:checked').length==0)
    {
      swal("","Please select at least one column","error");
      return false;
    }
  });
}
function handleWOITEMGridRowClick(classdata) {
  $("#tblWOOrderItem").on('click', '.WOITEMGridRowClick', function () {
    var dataString = $(this).attr('data');
    classdata.redirectItemEditComponent(dataString);
  });
}
function handleWOITEMGridDeleteIcon(classdata) {
  $("#tblWOOrderItem").on('click', '.WOITEMGridDeleteIcon', function () {
    var dataId = $(this).attr('data');
    var dataItemId = $(this).attr('data-itemid');
    classdata.deleteOrderItemHandler(dataId, dataItemId);
  });
}
function ScanItemContiner() {
  $(".itemscanshow").click(function() {
    $("#GroupInfoWrap").hide();
    $("#ItemScanWrap").show();
  });
  $(".closeitemscan-wrap").click(function() {
    $("#ItemScanWrap").hide();
    $("#GroupInfoWrap").show();
  });
}
function handleWOITEMGridEditIcon(classdata) {
  $("#tblWOOrderItem").on('click', '.WOITEMGridEditIcon', function () {
    var dataString = $(this).attr('data');
    classdata.openEditModel(dataString);
  });
}
function handleWOITEMGridInlineCHKOnChange(classdata) {
  $("#tblWOOrderItem").on('click', '.WOITEMGridCHK', function () {
    var dataString = $(this).attr('data');
    classdata.inlineCHKChangeHandler(dataString);
  });
}

function handleWOITEMGridInlineEditOnChange(classdata) {
  $("#tblWOOrderItem").on('change', '.WOITEMGridInlineEdit', function () {

    var dataItemId = parseInt($(this).attr('data-itemid') !== "" ? $(this).attr('data-itemid') : 0);
    var dataFieldName = $(this).attr('data-fieldname');
    var e = { target: { value: $(this)[0].value, maxLength: $(this)[0].maxLength } };
    classdata.inlineEditChangeHandler(e, dataItemId, dataFieldName);
  });
}
function handleWOITEMGridInlineEditOnBlur(classdata) {
  $("#tblWOOrderItem").on('blur', '.WOITEMGridInlineEdit', function () {
    var dataItemId = parseInt($(this).attr('data-itemid') !== "" ? $(this).attr('data-itemid') : 0);
    var dataFieldName = $(this).attr('data-fieldname');
    var e = { target: { value: $(this)[0].value, maxLength: $(this)[0].maxLength } };
    classdata.inlineEditBlurHandler(e, dataItemId, dataFieldName);
  });
}

const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
function debounce(func, wait, immediate) {
  var timeout;

  return function executedFunction() {
    var context = this;
    var args = arguments;

    var later = function () {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };

    var callNow = immediate && !timeout;

    clearTimeout(timeout);

    timeout = setTimeout(later, wait);

    if (callNow) func.apply(context, args);
  };
};
class WorkOrderEditContainer extends React.Component {

  state = {
    
    isScanItem: false,
    isShowWOInformation: false,
    isShowWODatatable: false,
    isInlineChanged: false,
    isInlineChangedCallOnce: "",
    isOpenLookUpCustomerModel: false,
    isApplyTax: false,
    isOpenCommentModel: false,
    commentModelReqParam: {
      woid: 0,
      comment: "",
      ref: "",
      dueDate:"",
      isSetTime:false
    },
    isOpenEditModel: false,
    editItemParam: {
      woid: 0,
      woEntryID: 0,
      code: "",
      itemId: 0,
      newPrice: 0,
      quantity: 0,
      description: "",
      taxable: true,
      price: 0,
      committed: 0,
      available: 0,
      priceMustBeEntered: true,
      quantityEntryNotAllowed: true,
      itemNotDiscountable: false,
      priceSource: 0,
      IsTaxApplied: false,
      taxId: 0,
      isPriceChangebyCashier: false
    },
    isOpenDiscountModel: false,
    discountPer: 0,
    isOpenTaxModel: false,
    isOpenPriceModel: false,
    insertItemParam: {
      woId: 0,
      userId: 0,
      price: 0,
      itemLookupCode: "",
      quantity: 1,
      isNewlyCreated: false,
      oldQuantity: 0,
    },
    isOpenSearchBox: false,

    isSetCode:true,
    isCode: true,
    isName:true,
    isSetName:true,
    isSetPrice:true,
    isPrice: true,
    isSetExtPrice:true,
    isExtPrice: true,
    isAction:true,
    isOnOrder:true,
    isTax:true,
    isSetAction:true,
    isSetOnOrder:true,
    isSetTax:true,
    isOnHandQuantity:true,
    isSetOnHandQuantity:true,
    isSetTime:false
	
  }

  componentDidMount() {
    
    this.checkUserRoleValidation();
    this.props.getOrderDetail(decryptURL(this.props.match.params.orderId));
    let orderItemListParam = { ...this.props.orderItemListParam };
    orderItemListParam["woId"] = parseInt(decryptURL(this.props.match.params.orderId));
    this.props.setOrderItemListParam(orderItemListParam);
    this.getWorkOrderItemList();
    window.addEventListener('scroll', this.handleScroll);
    handleWOITEMGridRowClick(this);
    handleWOITEMGridInlineCHKOnChange(this);
    handleWOITEMGridInlineEditOnChange(this);
    handleWOITEMGridInlineEditOnBlur(this);
    handleWOITEMGridDeleteIcon(this);
    window.addEventListener('mouseup', this.handleMouseUp);
    handleWOITEMGridEditIcon(this);
    ScanItemContiner();
    handleWOColumnFilter(this);
    try {
      this.clearMessageIntervalID = setInterval(async () => {
        this.props.clearMessage();
      }, 10 * 1000);
    } catch (e) {
      console.log(e);
    }
  }
  getWorkOrderItemList = () => {
    let orderItemListParam = { ...this.props.orderItemListParam };
    orderItemListParam["woId"] = parseInt(decryptURL(this.props.match.params.orderId));
    const result = this.props.getWorkOrderItemList(orderItemListParam, false, this.state.isSetCode,
      this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity).then(() => {
      var taxableList = this.props.orderItemList.filter((item) => item.taxable === false);
      if (taxableList.length > 0)
        this.setState({ isApplyTax: false });
      else
        this.setState({ isApplyTax: true });

    });
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    $('#tblWOOrderItem').DataTable().destroy();
    $('#tblWOLookUpCustomers').DataTable().destroy();
    $('#tblWOTax').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
    window.addEventListener('mouseup', this.handleMouseUp);
  }
  //#region check user role validation 
  //#region handle mouse up event to close autocomplete
  handleMouseUp = (e) => {
    this.closeAutoComplete(e);
  }
  //#endregion handle mouse up event to close autocomplete
  checkUserRoleValidation = () => {
    try {
      const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
      if (userDetails.role !== process.env.REACT_APP_UserRole_Manager && userDetails.role !== process.env.REACT_APP_UserRole_Cashier) {
        // swal({
        //   title: "Warning",
        //   text: "Please get permission to view Work orders.",
        //   icon: "warning",
        //   button: "Ok"
        // }).then(() => this.props.history.goBack());
        this.props.history.push("/NotFound");
      }
      else {
        this.setState({ isRender: true });
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  //#region scroll event to on demand loading
  handleScroll = scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt = (window.innerHeight + document.documentElement.scrollTop).toFixed(0);
      var offsetHeight = (document.documentElement.offsetHeight).toFixed(0);
      if (scrollHeigt === offsetHeight || (parseInt(scrollHeigt)+1).toString() === offsetHeight || (parseInt(scrollHeigt)-1).toString() === offsetHeight) {
        if (this.props.totalRecord !== this.props.orderItemList.length) {
          let orderItemListParam = { ...this.props.orderItemListParam };
          let pagesize = this.props.orderItemListParam.pageSize + 50;
          orderItemListParam["woId"] = parseInt(decryptURL(this.props.match.params.orderId));
          orderItemListParam["pageSize"] = pagesize;
          this.props.setOrderItemListParam(orderItemListParam);
          this.props.getWorkOrderItemList(orderItemListParam, false, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
        }
      }
    } catch (error) {
      console.log(error);

    }
  }, 0);

  //#endregion scroll event to on demand loading

  //#region  on row click redirect
  redirectItemEditComponent = (itemId) => {
    try {

      if (userDetails.role !== process.env.REACT_APP_UserRole_Manager) {
        swal({
          title: "warning",
          text: types.ADDEDIT_PERMISSION_MESSAGE,
          icon: "warning",
          button: "Ok"
        })

      }
      else {
        window.open("/ItemEdit/" + encryptURL(itemId) + "/" + this.props.match.params.orderId + "/" + encryptURL("wo"), '_blank');
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion  on row click redirect
  //#region setIsApplyTax
  setIsApplyTax = () => {
    this.setState({ isApplyTax: !this.state.isApplyTax }, () => {
      let applyTaxRequestParameter = {
        woid: parseInt(decryptURL(this.props.match.params.orderId)),
        isTax: this.state.isApplyTax
      };
      this.props.applyTax(applyTaxRequestParameter, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
      var chk = document.getElementsByClassName("WOITEMGridCHK");
      for (var i = 0; i <= chk.length; i++) {
        if (chk[i])
          chk[i].checked = this.state.isApplyTax;
      }
    });
  }
  //#endregion setIsApplyTax

 


  //#region inline chk change handler

  inlineCHKChangeHandler = (id) => {
    var orderItemList = this.props.orderItemList;
    var elementsIndex = this.props.orderItemList.findIndex(element => element.id === parseInt(id));
    orderItemList[elementsIndex] = { ...orderItemList[elementsIndex], taxable: !orderItemList[elementsIndex]["taxable"] };
    if (document.getElementsByClassName("wo-" + id)[1])
      document.getElementsByClassName("wo-" + id)[1].checked = orderItemList[elementsIndex]["taxable"];
    let updateInlineItemParam = {
      woid: parseInt(decryptURL(this.props.match.params.orderId)),
      woEntryID: parseInt(id),
      itemId: parseInt(orderItemList[elementsIndex]["itemID"]),
      quantity: parseInt(orderItemList[elementsIndex]["quantityOnOrder"]),
      newPrice: parseFloat(orderItemList[elementsIndex]["price"].toString()),
      taxable: orderItemList[elementsIndex]["taxable"],
      IsTaxApplied: true,
      isPriceChangebyCashier: false
    }
    var taxableList = orderItemList.filter((item) => item.taxable === false);
    if (taxableList.length > 0)
      this.setState({ isApplyTax: false });
    else
      this.setState({ isApplyTax: true });
    this.props.updateWOItemInline(updateInlineItemParam, this.props.requestParameter, false,
      this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
  }
  //#endregion inline chk change handler
  //#region inline  change handler
  inlineEditChangeHandler = (e, id, name) => {
    try {

      if (name === "extPrice")
        e.target.value = e.target.value.replace(/,/g, "");
      if (this.state.isInlineChangedCallOnce !== e.target.value || e.target.value === "") {

        var value = e.target.value;
        if (name === "extPrice")
          value = value.replace(/,/g, "");
        this.setState({ isInlineChangedCallOnce: value });
        if (value.length > e.target.maxLength) {
          value = value.substring(0, e.target.maxLength);
        }
        var isInlineChanged = false;
        var orderItemList = this.props.orderItemList;
        var elementsIndex = this.props.orderItemList.findIndex(element => element.id === id);

        if (orderItemList[elementsIndex][name].toString() !== value)
          isInlineChanged = true;

        if (name === "quantityOnOrder") {
          if (parseInt(value) < 0)
            value = 0;
          orderItemList[elementsIndex] = { ...orderItemList[elementsIndex], quantityOnOrder: value, extPrice: parseFloat(value !== "" ? value : 0) * parseFloat(orderItemList[elementsIndex]["price"]) }
          if (document.getElementsByClassName("woExtPrice-" + id)[1])
            document.getElementsByClassName("woExtPrice-" + id)[1].value = orderItemList[elementsIndex]["extPrice"];
        }
        else {
          if (parseInt(value) < 0)
            value = 0;
          orderItemList[elementsIndex] = { ...orderItemList[elementsIndex], extPrice: value, price: parseFloat(value !== "" ? value : 0) / parseFloat(orderItemList[elementsIndex]["quantityOnOrder"]) }

          if (document.getElementsByClassName("woPrice-" + id)[1])
            document.getElementsByClassName("woPrice-" + id)[1].innerHTML = '$' + orderItemList[elementsIndex]["price"].toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
        }
        this.setState({ isInlineChanged, orderItemList });
      }


    } catch (error) {
      console.log(error);
    }
  }
  //#endregion inline  change handler
  //#region inline  update handler
  inlineEditBlurHandler = (e, Id, name) => {
    try {

      var value = 0;
      var targetValue = e.target.value;
      if (name === "extPrice") {
        name = "ext price";
        targetValue = targetValue.replace(/,/g, "");
      }
      value = targetValue !== "" ? parseInt(targetValue) : 0;
      if (name === "quantityOnOrder")
        name = "quantity on order";
      if (value <= 0) {
        swal({
          title: "Error",
          text: "Please enter the " + name,
          icon: "error",
          button: "Ok",
        })
        this.setState({ isInlineChangedCallOnce: "" });
      }
      else if (this.state.isInlineChanged && targetValue !== "") {
        var orderItemList = this.props.orderItemList;
        var elementsIndex = this.props.orderItemList.findIndex(element => element.id === Id);
        var isPriceChangebyCashier = false;
        if (name === "extPrice") {
          if (orderItemList[elementsIndex]["fullPrice"] !== orderItemList[elementsIndex]["price"]) {
            isPriceChangebyCashier = true;
            orderItemList[elementsIndex] = { ...orderItemList[elementsIndex], priceSource: 10 }

          }
        }
        let updateInlineItemParam = {
          woid: parseInt(decryptURL(this.props.match.params.orderId)),
          woEntryID: parseInt(Id),
          itemId: parseInt(orderItemList[elementsIndex]["itemID"]),
          quantity: parseInt(orderItemList[elementsIndex]["quantityOnOrder"]),
          newPrice: parseFloat(orderItemList[elementsIndex]["price"].toString().replace(/,/g, "")),
          taxable: orderItemList[elementsIndex]["taxable"],
          IsTaxApplied: false,
          isPriceChangebyCashier: isPriceChangebyCashier
        }

        this.props.updateWOItemInline(updateInlineItemParam, this.props.requestParameter, false,
          this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
        this.setState({ isInlineChanged: false, isInlineChangedCallOnce: "" });
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion inline  update handler

  //#region delete order item
  deleteOrderItemHandler = (id, itemId) => {
    try {
      swal({
        title: types.DELETE_TITLE,
        text: types.DELETE_MESSAGE,
        icon: "warning",
        buttons: {
          confirm: true,
          cancel: true
        },
        dangerMode: true,
      })
        .then((willDelete) => {
          if (willDelete) {
            let deleteRequestParameter = {
              woId: parseInt(decryptURL(this.props.match.params.orderId)),
              woEntryID: parseInt(id),
              itemId: parseInt(itemId)

            };

            this.props.deleteOrderItem(deleteRequestParameter, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
            var orderItemListData = this.props.orderItemList.filter(element => element.id === parseInt(id));
            var searchData = this.props.searchData;
            var elementsIndex = this.props.searchData.findIndex(element => element.value === orderItemListData[0].code + " - " + orderItemListData[0].description);
            searchData[elementsIndex] = { ...searchData[elementsIndex], itemChecked: false };
          }
        });

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion delete order item

  //#region search handler
  searchHandler = (record, cb) => {
    try {
      this.setState({ isOpenSearchBox: true });
      let getdata;
      record = record.trim();
      if (record !== "") {
        let searchRequestParameter = {
          searchText: record,
          woId: parseInt(decryptURL(this.props.match.params.orderId))
        };
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata = [];
        this.props.setSearchData(getdata);
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion search handler

  //#region search change handler
  onSearchChangeHandler = (value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value, (items) => {
        this.props.setSearchData(items);
      })
    } catch (error) {
      console.log(error);
    }
  }
  setSearchTerm = debounce((searchTerm, cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm, cb);
    } catch (error) {
      console.log(error);
    }
  }, 1000)
  //#endregion search change handler 
  //#region open search box
  openSearchBox = () => {
    this.setState({ isOpenSearchBox: true });
  }
  //#endregion open search box
  //#region close search box
  closeSearchBox = () => {
    this.setState({ isOpenSearchBox: false })
  }


 //#region filter checkbox onchange function
 setCodeCheckbox = () => {
  this.setState({ isCode: !this.state.isCode }, () => {
  this.setState({ isSetCode: this.state.isCode });
  });
 
}

setNameCheckbox = () => {
  this.setState({ isName: !this.state.isName }, () => {
  this.setState({ isSetName: this.state.isName });
  });
 
}

setPriceCheckbox = () => {
  this.setState({ isPrice: !this.state.isPrice }, () => {
  this.setState({ isSetPrice: this.state.isPrice });
  });
  
}

setExtPriceCheckbox = () => {
  this.setState({ isExtPrice: !this.state.isExtPrice }, () => {
  this.setState({ isSetExtPrice: this.state.isExtPrice });
  });
  
}


setTaxCheckbox = () => {
  this.setState({ isTax: !this.state.isTax }, () => {
  this.setState({ isSetTax: this.state.isTax });
  });
 
}

setOnOrderCheckbox = () => {
  
  this.setState({ isOnOrder: !this.state.isOnOrder }, () => {
  this.setState({ isSetOnOrder: this.state.isOnOrder });
  });
 
}



setOnHandQuantityCheckbox = () => {
  
  this.setState({ isOnHandQuantity: !this.state.isOnHandQuantity }, () => {
  this.setState({ isSetOnHandQuantity: this.state.isOnHandQuantity });
  });
  
}


//#endregion filter checkbox onchange function

  //#region close search box
  //#region search checkbox handler
  onSearchCheckBoxHandler = (value) => {
    try {
      var searchData = this.props.searchData;
      var elementsIndex = this.props.searchData.findIndex(element => element.value === value);
      searchData[elementsIndex] = { ...searchData[elementsIndex], itemChecked: true };
      let itemLookupCode = value.split(" - ")[0];
      var quantity = 1;
      let insertItemParam = {
        woId: parseInt(decryptURL(this.props.match.params.orderId)),
        userId: parseInt(userDetails.userId),
        price: 0,
        itemLookupCode: itemLookupCode,
        quantity: 1,
        isNewlyCreated: false,
        oldQuantity: quantity,
      };
      this.setState({ insertItemParam, isOpenSearchBox: false });
      var optionParam = {
        itemLookupCode: itemLookupCode
      };
      const result = this.props.getItemOptionsByLookupCode(optionParam).then(() => {
        if (this.props.priceMustBeEntered) {
          var itemAlredyExist = this.props.orderItemList.filter(element => element.code === itemLookupCode)
          if (itemAlredyExist.length <= 0) {
            this.togglePriceModel();
            this.props.setPriceMustBeEntered(false);
          }
          else
            this.props.insertOrderItem(insertItemParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
        }
        else
          this.props.insertOrderItem(insertItemParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
      });

      return result;

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion search checkbox handler
  closeAutoComplete = (e) => {
    try {
      var ele = document.getElementById("workOrderItemSearchMenu");
      var targetId = "";
      var targetClass = "";
      if (e.target.id !== null)
        targetId = e.target.id;
      if (e.target.className !== null)
        targetClass = e.target.className.split(" ")[0];
        if(ele!=null)
        {
          if (ele.id !== targetId // if the target of the click isn't the container...
          && ele.innerHTML.length > 0 && targetClass !== "workOrderItemSearchControl") // ... nor a descendant of the container
          {
            this.setState({ isOpenSearchBox: false });
          }
        }
    
    } catch (error) {
      console.log(error);
    }
  }
  closeScanItem = () => {
    this.setState({
      isScanItem: false,
    })
  }

  //#region toggle work order model
  toggleLookUpCustomerModel = () => this.setState({ isOpenLookUpCustomerModel: !this.state.isOpenLookUpCustomerModel });
  //#endregion toggle work order model
  //#region open work order
  openLookUpCustomerModel = () => {
    this.props.getCustomers(this.props.customerRequestParam, true);
    this.toggleLookUpCustomerModel();
  }
  //#endregion open work order
  //#region  customer check box handler
  customerCheckBoxHandler = (e, id) => {
    try {
      var isChecked = e.target.checked;
      var cbs = document.getElementsByClassName("customerCHK");
      for (var i = 0; i < cbs.length; i++) {
        cbs[i].checked = false;
      }
      if (isChecked) {
        document.getElementById(e.target.name).checked = true;
        let changeCustomerRequestParam = {
          woid: parseInt(decryptURL(this.props.match.params.orderId)),
          customerID: parseInt(id)
        };
        this.props.setChangeCustomerRequestParam(changeCustomerRequestParam);
      }
      else {
        let changeCustomerRequestParam = {
          orderId: 0,
          customerId: 0
        };
        this.props.setChangeCustomerRequestParam(changeCustomerRequestParam);
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion  customer check box handler
  //#region changeCustomer
  changeCustomer = () => {
    try {
      if (this.props.changeCustomerRequestParam.customerID <= 0) {
        swal({
          title: "Error",
          text: types.REQUIRED_CUSTOMER_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else {
        if (this.props.customerDetails.id !== this.props.changeCustomerRequestParam.customerID) {
          swal({
            title: types.DELETE_TITLE,
            text: "Do you really want to change the customer?",
            icon: "warning",
            buttons: {
              confirm: true,
              cancel: true
            },
            dangerMode: true,
          })
            .then((willDelete) => {
              if (willDelete) {

                this.props.changeCustomer(this.props.changeCustomerRequestParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
              }
            });
        }
        // else
        //   this.props.changeCustomer(this.props.changeCustomerRequestParam, this.props.orderItemListParam);
        this.toggleLookUpCustomerModel();
      }
    } catch (error) {
      console.log(error);
    }


  }
  //#endregion changeCustomer
  //#region toggele comment model
  toggleCommentModel = () => {
    this.setState({ isOpenCommentModel: !this.state.isOpenCommentModel }, () => {
      setTimeout(() => {
        if (!!document.getElementById("ref"))
          document.getElementById("ref").focus();
      }, 500)
    });
  }
  //#endregion toggele comment model
  //#region open comment model
  openCommentModel = () => {
    try {
      let commentModelReqParam = { ...this.state.commentModelReqParam };
      commentModelReqParam["woid"] = parseInt(decryptURL(this.props.match.params.orderId));
      commentModelReqParam["ref"] = this.props.orderDetail.ref;
      commentModelReqParam["comment"] = this.props.orderDetail.comment;
      this.setState({ commentModelReqParam }, () => { this.toggleCommentModel(); });
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion open comment model
  //#region saveComment
  saveComment = () => {
  
    try {
      var showMessage = false;
      if (this.state.commentModelReqParam.ref !== this.props.orderDetail.ref || this.state.commentModelReqParam.comment !== this.props.orderDetail.comment)
        showMessage = true;
      let commentModelReqParam=this.state.commentModelReqParam;
      let myCurrentDate = new Date()

      let dueDate= moment(this.props.woValues.dueDate).format("YYYY-MM-DD[T]HH:mm:ss");
      commentModelReqParam["dueDate"]= dueDate;
      commentModelReqParam["isSetTime"]=this.state.isSetTime;
      //commentModelReqParam["dueDate"]= moment(this.props.woValues.dueDate).format('YYYY-MM-DD') +'T'+ myCurrentDate.getHours()+':'+ myCurrentDate.getMinutes()+':'+ myCurrentDate.getSeconds()
      this.props.saveComment(this.state.commentModelReqParam, showMessage);
      this.toggleCommentModel();
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion saveComment
  //#region commentModelInputChangeHandler
  commentModelInputChangeHandler = (e) => {
    try {
      var value = e.target.value;
      if (e.target.maxLength >= 0) {
        if (value.length > e.target.maxLength) {
          value = value.substring(0, e.target.maxLength);
        }
      }
      let commentModelReqParam = { ...this.state.commentModelReqParam };
      commentModelReqParam[e.target.name] = value;
      this.setState({ commentModelReqParam });

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion commentModelInputChangeHandler
  //#region toggle edit model
  toggleEditModel = () => {
    this.setState({ isOpenEditModel: !this.state.isOpenEditModel }, () => {
      setTimeout(() => {
        if (!!document.getElementById("quantity") && !this.state.editItemParam.quantityEntryNotAllowed)
          document.getElementById("quantity").focus();
        else if (!!document.getElementById("newPrice") && !this.state.editItemParam.itemNotDiscountable) {
          document.getElementById("newPrice").focus();
        }
        else if (!!document.getElementById("description"))
          document.getElementById("description").focus();
      }, 500)
    });
  }
  //#endregion toggle edit model
  //#region open edit model 
  openEditModel = (id) => {
    try {
      let editItemParam = { ...this.state.editItemParam };
      var orderItemList = this.props.orderItemList;
      var elementsIndex = this.props.orderItemList.findIndex(element => element.id === parseInt(id));
      editItemParam["woid"] = parseInt(decryptURL(this.props.match.params.orderId));
      editItemParam["woEntryID"] = parseInt(id);
      editItemParam["itemId"] = parseInt(orderItemList[elementsIndex]["itemID"]);
      editItemParam["code"] = orderItemList[elementsIndex]["code"];
      editItemParam["price"] = parseFloat(orderItemList[elementsIndex]["fullPrice"]).toFixed(2);
      editItemParam["newPrice"] = parseFloat(orderItemList[elementsIndex]["price"]);
      editItemParam["quantity"] = parseInt(orderItemList[elementsIndex]["quantityOnOrder"]);
      editItemParam["priceSource"] = parseInt(orderItemList[elementsIndex]["priceSource"]);
      editItemParam["available"] = orderItemList[elementsIndex]["availableQuantity"];
      editItemParam["committed"] = orderItemList[elementsIndex]["quantityCommitted"];
      editItemParam["description"] = orderItemList[elementsIndex]["description"];
      editItemParam["taxable"] = orderItemList[elementsIndex]["taxable"];
      editItemParam["priceMustBeEntered"] = orderItemList[elementsIndex]["priceMustBeEntered"];
      editItemParam["quantityEntryNotAllowed"] = orderItemList[elementsIndex]["quantityEntryNotAllowed"];
      editItemParam["itemNotDiscountable"] = orderItemList[elementsIndex]["itemNotDiscountable"];
      editItemParam["taxId"] = orderItemList[elementsIndex]["taxID"];
      editItemParam["isPriceChangebyCashier"] = false;

      this.setState({ editItemParam }, () => {
        let changeTaxRequestParam = {
          woid: parseInt(decryptURL(this.props.match.params.orderId)),
          woEntryID: this.state.editItemParam.woEntryID,
          itemId: this.state.editItemParam.itemId,
          taxId: parseInt(orderItemList[elementsIndex]["taxID"])
        };
        this.props.setChangeTaxRequestParam(changeTaxRequestParam);
        this.toggleEditModel();
      });
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion open edit model
  //#region update edit model handler
  updateEditModelHandler = () => {
    try {

      if (this.state.editItemParam.newPrice <= 0) {
        swal({
          title: "Error",
          text: "Please enter the price.",
          icon: "error",
          button: "Ok",
        })

      }
      else if (this.state.editItemParam.quantity <= 0) {
        swal({
          title: "Error",
          text: "Please enter the quantity.",
          icon: "error",
          button: "Ok",
        })

      }
      else {

        this.props.updateWOItemInline(this.state.editItemParam, this.props.orderItemListParam, true,
          this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
        this.toggleEditModel();
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion update edit model handler
  //#region change edit model handler
  changeEditModelInputHandler = (e) => {
    try {
      var value = e.target.value.trim();
      let editItemParam = { ...this.state.editItemParam };
      if (e.target.name === "newPrice")
        value = value.replace(/,/g, "");

      if (e.target.maxLength >= 0) {
        if (value.length > e.target.maxLength) {
          value = value.substring(0, e.target.maxLength);
        }
      }
      if (e.target.name !== "description") {
        if (value < 0)
          value = 0;
      }
      if (e.target.name === "quantity") {
        if (value > 0)
          value = parseInt(value);
      }

      if (e.target.name === "newPrice") {
        value = value.replace(/,/g, "");
        if (value > 0)
          value = parseFloat(value);
      }


      editItemParam[e.target.name] = value;
      if (e.target.name === "newPrice") {
        if (editItemParam["price"] !== editItemParam["newPrice"]) {
          editItemParam["isPriceChangebyCashier"] = true;
          editItemParam["priceSource"] = 10;
        }
      }
      this.setState({ editItemParam });
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion change edit model handler
  //#region Duplicate item
  duplicateItem = () => {
    try {
      this.props.duplicateItem(this.state.editItemParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
      this.toggleEditModel();
    } catch (error) {
      console.log(error);
    }

  }
  //#endregion Duplicate item
  //#region toggele discount model
  toggleDiscountModel = () => {
    this.setState({ isOpenDiscountModel: !this.state.isOpenDiscountModel })
  }
  //#endregion toggele discount model
  //#region toggle price model
  togglePriceModel = () => {
    this.setState({ isOpenPriceModel: !this.state.isOpenPriceModel })
  }
  //#endregion toggle price model
  //#region price model change handler
  priceModelInputChangeHandler = (e) => {
    try {
      let insertItemParam = { ...this.state.insertItemParam }
      var value = e.target.value.trim();
      value = value.replace(/,/g, "");
      if (value > 0)
        value = parseFloat(value)
      if (e.target.maxLength >= 0) {
        if (value.length > e.target.maxLength) {
          value = value.substring(0, e.target.maxLength);
        }
      }
      if (value < 0)
        value = 0

      insertItemParam["price"] = value;
      this.setState({ insertItemParam });
    } catch (error) {
      console.log(error);
    }

  }
  //#endregion price model change handler
  //#region insert item from price model
  insertItem = () => {
    if (this.state.insertItemParam.price <= 0)
      swal({
        title: "Error",
        text: "Please enter the price.",
        icon: "error",
        button: "Ok",
      })
    else {
      this.props.insertOrderItem(this.state.insertItemParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
      this.togglePriceModel();
    }
  }
  //#endregion insert item from price model
  //#region open discount model
  openDiscountModel = () => {
    try {
      this.toggleDiscountModel();
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion open discount model
  //#region apply discount
  applyDiscount = () => {
    try {
      if (this.state.discountPer > 0) {
        let discountModelReqParam = {
          woid: this.state.editItemParam.woid,
          woEntryID: this.state.editItemParam.woEntryID,
          discountPer: this.state.discountPer
        }
        this.props.applyDiscount(discountModelReqParam);
        this.toggleDiscountModel();
      }
      else
        swal({
          title: "Error",
          text: "Please enter the discount.",
          icon: "error",
          button: "Ok",
        })
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion apply discount
  //#region discountModelInputChangeHandler
  discountModelInputChangeHandler = (e) => {
    try {
      var value = e.target.value.trim();
      if (e.target.maxLength >= 0) {
        if (value.length > e.target.maxLength) {
          value = value.substring(0, e.target.maxLength);
        }
      }
      if (value < 0)
        value = 0
      if (value > 0)
        value = parseInt(value)
      this.setState({ discountPer: value });
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion discountModelInputChangeHandler
  //#region toggle Tax model
  toggleTaxModel = () => this.setState({ isOpenTaxModel: !this.state.isOpenTaxModel });
  //#endregion toggle Tax model
  //#region open tax
  openTaxModel = () => {
    this.props.getTaxList();
    this.toggleTaxModel();
  }
  //#endregion open tax
  //#region  tax check box handler
  taxCheckBoxHandler = (e, id) => {
    try {
      var isChecked = e.target.checked;
      var cbs = document.getElementsByClassName("taxCHK");
      for (var i = 0; i < cbs.length; i++) {
        cbs[i].checked = false;
      }
      if (isChecked) {
        document.getElementById(e.target.name).checked = true;
        let changeTaxRequestParam = {
          woid: parseInt(decryptURL(this.props.match.params.orderId)),
          woEntryID: this.state.editItemParam.woEntryID,
          itemId: this.state.editItemParam.itemId,
          taxId: parseInt(id)
        };
        this.props.setChangeTaxRequestParam(changeTaxRequestParam);
      }
      else {
        let changeTaxRequestParam = {
          woid: 0,
          woEntryID: 0,
          itemId: 0,
          taxId: 0
        };
        this.props.setChangeTaxRequestParam(changeTaxRequestParam);
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion  tax check box handler
  //#region changeTax
  changeTax = () => {
    try {

      if (this.props.changeTaxRequestParam.taxId <= 0) {
        swal({
          title: "Error",
          text: "Please select tax.",
          icon: "error",
          button: "Ok",
        })
      }
      else {
        if (this.props.changeTaxRequestParam.taxId !== this.state.editItemParam.taxId) {
          const result = this.props.changeTax(this.props.changeTaxRequestParam, this.props.orderItemListParam, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,
            this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity).then(() => {
            let changeTaxRequestParam = {
              woid: 0,
              woEntryID: 0,
              itemId: 0,
              taxId: 0
            };
            this.props.setChangeTaxRequestParam(changeTaxRequestParam);
            this.toggleTaxModel();
            this.toggleEditModel();
          });
        }
        else {
          this.toggleTaxModel();
          this.toggleEditModel();
        }

      }
    } catch (error) {
      console.log(error);
    }


  }
  //#endregion changeTax

  showScanItem = () => {
    this.setState({
      isScanItem: true,
    })
  }

  closeScanItem = () => {
    this.setState({
      isScanItem: false,
    })
  }
  
  changeDateHandler=(date,name) => {
    
    var datestr = (new Date(date)).toUTCString();
    let woValues=this.props.woValues;
    let myCurrentDate = new Date();
    let isSetTime=this.state.isSetTime;
    woValues[name]=date!==""&&date!==null? moment(date).format('YYYY-MM-DD') +' '+ myCurrentDate.getHours()+':'+ myCurrentDate.getMinutes()+':'+ myCurrentDate.getSeconds():''
    this.setState({
      woValues,isSetTime:true
    });
  };
  //#region sort event  handler
  sortHandler = (sortColumnName) => {
    try {
      var sortdirection = "";
      switch (sortColumnName) {
        case "code":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "code" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "description":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "description" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "price":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "price" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "extPrice":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "extPrice" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "taxable":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "taxable" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        case "quantityOnOrder":
          sortdirection = this.props.orderItemListParam.sortColumnName !== "quantityOnOrder" ? "asc" : this.props.orderItemListParam.sortDirection === "asc" ? "desc" : "asc";
          break;
        default:
          break;
      }
      var data = {
        woId: this.props.orderItemListParam.woId,
        searchText: this.props.orderItemListParam.searchText,
        pageNo: this.props.orderItemListParam.pageNo,
        pageSize: this.props.orderItemListParam.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection

      };
      var orderItemListParam = { ...this.props.orderItemListParam };
      orderItemListParam["sortColumnName"] = sortColumnName;
      orderItemListParam["sortDirection"] = sortdirection;
      this.props.setOrderItemListParam(orderItemListParam);
      this.props.getWorkOrderItemList(data, false, this.state.isSetCode,this.state.isSetName,this.state.isSetPrice,this.state.isSetExtPrice,this.state.isSetOnOrder,this.state.isSetTax,this.state.isSetOnHandQuantity);
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion sort event handler

  //#region toggleWOInformation
  toggleWOInformation=() => {this.setState({isShowWOInformation: !this.state.isShowWOInformation})}
  toggleWODatatable=() => {this.setState({isShowWODatatable: !this.state.isShowWODatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowWODatatable: false})}

  //#endregion toggleWOInformation
  render() {
   
    const { customerDetails, orderItemList,isSetCode } = this.props;
    return (
      <WorkOrderEditComponent
        {...this.props}
        userDetails={userDetails}
        isShowWOInformation={this.state.isShowWOInformation}
        isShowWODatatable={this.state.isShowWODatatable}
        toggleWOInformation={this.toggleWOInformation}
        toggleWODatatable={this.toggleWODatatable}
        showScanItem={this.showScanItem}
        isScanItem={this.state.isScanItem}
        inlineEditBlurHandler={this.inlineEditBlurHandler}
        inlineEditChangeHandler={this.inlineEditChangeHandler}
        redirectItemEditComponent={this.redirectItemEditComponent}
        deleteOrderItemHandler={this.deleteOrderItemHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        closeScanItem={this.closeScanItem}
        onSearchCheckBoxHandler={this.onSearchCheckBoxHandler}
        isOpenLookUpCustomerModel={this.state.isOpenLookUpCustomerModel}
        toggleLookUpCustomerModel={this.toggleLookUpCustomerModel}
        openLookUpCustomerModel={this.openLookUpCustomerModel}
        customerCheckBoxHandler={this.customerCheckBoxHandler}
        getWorkOrderItemList={this.getWorkOrderItemList}
        editItemParam={this.state.editItemParam}
        isApplyTax={this.state.isApplyTax}
        setIsApplyTax={this.setIsApplyTax}
        changeCustomer={this.changeCustomer}
        isOpenCommentModel={this.state.isOpenCommentModel}
        toggleCommentModel={this.toggleCommentModel}
        openCommentModel={this.openCommentModel}
        saveComment={this.saveComment}
        commentModelReqParam={this.state.commentModelReqParam}
        commentModelInputChangeHandler={this.commentModelInputChangeHandler}
        isOpenEditModel={this.state.isOpenEditModel}
        toggleEditModel={this.toggleEditModel}
        updateEditModelHandler={this.updateEditModelHandler}
        changeEditModelInputHandler={this.changeEditModelInputHandler}
        duplicateItem={this.duplicateItem}
        discountPer={this.state.discountPer}
        isOpenDiscountModel={this.state.isOpenDiscountModel}
        toggleDiscountModel={this.toggleDiscountModel}
        openDiscountModel={this.openDiscountModel}
        applyDiscount={this.applyDiscount}
        discountModelInputChangeHandler={this.discountModelInputChangeHandler}
        isOpenTaxModel={this.state.isOpenTaxModel}
        toggleTaxModel={this.toggleTaxModel}
        openTaxModel={this.openTaxModel}
        taxCheckBoxHandler={this.taxCheckBoxHandler}
        changeTax={this.changeTax}
        isOpenPriceModel={this.state.isOpenPriceModel}
        togglePriceModel={this.togglePriceModel}
        priceModelInputChangeHandler={this.priceModelInputChangeHandler}
        insertItemParam={this.state.insertItemParam}
        insertItem={this.insertItem}
        isOpenSearchBox={this.state.isOpenSearchBox}
        closeSearchBox={this.closeSearchBox}
        openSearchBox={this.openSearchBox}
        closeAutoComplete={this.closeAutoComplete}
        sortHandler={this.sortHandler}

        setCodeCheckbox={this.setCodeCheckbox}
        isCode={this.state.isCode}
        isSetCode={this.state.isSetCode}
        isName={this.state.isName}
        isSetName={this.state.isSetName}
        setNameCheckbox={this.setNameCheckbox}
        isSetPrice={this.state.isSetPrice}
        isSetExtPrice={this.state.isSetExtPrice}
        isPrice={this.state.isPrice}
        isExtPrice={this.state.isExtPrice}
        setExtPriceCheckbox={this.setExtPriceCheckbox}
        setPriceCheckbox={this.setPriceCheckbox}
        setOnOrderCheckbox={this.setOnOrderCheckbox}
        setTaxCheckbox={this.setTaxCheckbox}
       
        isSetOnOrder={this.state.isSetOnOrder}
        isSetAction={this.state.isSetAction}
        isSetTax={this.state.isSetTax}

        isOnOrder={this.state.isOnOrder}
        isAction={this.state.isAction}
        isTax={this.state.isTax}

        isOnHandQuantity={this.state.isOnHandQuantity}
        isSetOnHandQuantity={this.state.isSetOnHandQuantity}
        setOnHandQuantityCheckbox={this.setOnHandQuantityCheckbox}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
        changeDateHandler={this.changeDateHandler}

      />
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getWorkOrderItemList: (data, isLoading,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(getWorkOrderItemList(data, isLoading,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    getOrderDetail: (orderId) => dispatch(getOrderDetail(orderId)),
    setOrderItemListParam: (data) => dispatch(setOrderItemListParam(data)),
    updateWOItemInline: (updateInlineItemParam, requestParameter, isGetItem,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(updateWOItemInline(updateInlineItemParam, requestParameter, isGetItem,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    deleteOrderItem: (deleteeRequestParameter, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(deleteOrderItem(deleteeRequestParameter, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    insertOrderItem: (insertItemParam, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(insertOrderItem(insertItemParam, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    getCustomers: (data, isLoading) => dispatch(getCustomers(data, isLoading)),
    getCustomerDetail: (data) => dispatch(getCustomerDetail(data)),
    clearMessage: () => dispatch(clearMessage()),
    applyTax: (applyTaxRequestParameter, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(applyTax(applyTaxRequestParameter, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    setChangeCustomerRequestParam: (data) => dispatch(setChangeCustomerRequestParam(data)),
    changeCustomer: (data, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(changeCustomer(data, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    saveComment: (commentModelReqParam, showMessage) => dispatch(saveComment(commentModelReqParam, showMessage)),
    duplicateItem: (updateInlineItemParam, requestParameter,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(duplicateItem(updateInlineItemParam, requestParameter,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    applyDiscount: (discountModelReqParam) => dispatch(applyDiscount(discountModelReqParam)),
    getTaxList: () => dispatch(getTaxList()),
    setChangeTaxRequestParam: (data) => dispatch(setChangeTaxRequestParam(data)),
    changeTax: (data, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity) => dispatch(changeTax(data, orderItemListParam,isSetCode,isSetName,isSetPrice,isSetExtPrice,isSetOnOrder,isSetTax,isSetOnHandQuantity)),
    getItemOptionsByLookupCode: (data) => dispatch(getItemOptionsByLookupCode(data)),
    setPriceMustBeEntered: (data) => dispatch(setPriceMustBeEntered(data)),
  };
};

const mapStateToProps = (state) => {
  const { isLoading, orderItemList, orderItemListParam, customerDetails,
    orderDetail, searchValue, searchData, SearchTerm, customerRequestParam,
    customers, totalRecord, woValues, status, message, changeCustomerRequestParam,
    changeTaxRequestParam, taxList, priceMustBeEntered } = state.WorkOrderEditReducer;

  return {
    orderItemList: orderItemList.values.length > 0 ? orderItemList.values : [],
    orderItemListParam: orderItemListParam,
    orderDetail: orderDetail.values,
    isLoading: isLoading,
    searchValue: searchValue,
    searchData: searchData.values.length > 0 ? searchData.values : [],
    SearchTerm: SearchTerm,
    customerRequestParam: customerRequestParam,
    customers: customers.values.length > 0 ? customers.values : [],
    totalRecord: totalRecord,
    customerDetails: customerDetails,
    woValues: woValues.woValues !== undefined ? woValues.woValues : {
      woid: 0,
      subTotal: 0,
      totalTax: 0,
      totalAmount: 0,
      itemEntries: 0,
      discount: 0,
      dueDate: "",
      prvDeposite: 0,
      addlDeposite: 0,
      balance: 0
    }, status, message,
    changeCustomerRequestParam: changeCustomerRequestParam,
    taxList: taxList.values.length > 0 ? taxList.values : [],
    changeTaxRequestParam: changeTaxRequestParam,
    priceMustBeEntered
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(WorkOrderEditContainer);