import React from 'react';
import NumberFormat from 'react-number-format';
export default function SubstitutesComponent(props) {
    const { itemSubstitutes } = props;
    return (
        <div>
            <div className="card-body">
                <div className="row">

                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblSubstitutes" className="table w-100" >
                                <thead>
                                    <tr>
                                        <th className="plus-sort"></th>
                                        <th>Item lookup code</th>
                                        <th>Description</th>
                                        <th>PRICE</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {itemSubstitutes.map((item, index) =>
                                        <tr key={"tblItemSubstitutes-" + index}>
                                            <td className="details-control plus-sort"></td>
                                            <td>{item.itemLookupCode}</td>
                                            <td>{item.description}</td>
                                            <td><NumberFormat value={item.price} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}