import React, { useState } from 'react';

class Td extends React.Component {  
  

     handleEdit = (e) => {
      
        if (e.ctrlKey)
        {
            this.props.redirectItemEditComponent(this.props.itemId);
        }
        else
        {
            this.props.setViewAll(this.props.setmodevalue,'view')
            this.props.value.mode='edit';
            this.forceUpdate();
        }
       
    };

     handleOnBlur = (e) => {
      
        this.props.changeEvent(this.props.rowIndex, this.props.cellPropName, e.target.value);
      
       
    };
   




    handleSelectAll = (e) => {
    
    if (e.ctrlKey)
    this.props.redirectItemEditComponent(this.props.itemId);
    };
   render()
   {
        if (this.props.value.mode === 'edit')
        return (
        (this.props.type=="text"?
        <td><input type={this.props.type} onBlur={(e)=>this.handleOnBlur(e)}  onClick={(e)=>this.handleSelectAll(e)}   defaultValue={this.props.value.value} /></td>:

        <td>
            <select className="floating-select " tabIndex="2" onBlur={(e)=>this.handleOnBlur(e)} defaultValue={this.props.value.value}  >
            <option key="I" value="">Select</option> 
            {this.props.optionList.length<=0? <option key="C" value="">None</option>:''}
            {this.props.optionList.map((optionList) => <option key={optionList.id} value={optionList.id}>{optionList[this.props.valueName]}</option>)}
            </select>
        </td>
        ))

        if (this.props.value.mode === 'view')
        return (<td onClick={(e)=>this.handleEdit(e)} >{this.props.value.value}</td>);

        return (<td onClick={(e)=>this.handleEdit(e)}  >{this.props.value.value}</td>);
   }
   

}
export default Td;