import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        poId: 0,
        pageNo: 0,
        pageSize: 50,
        sortColumnName: "",
        sortDirection: "",
        searchText: "",
    },
    totalRecord: 0,
    orderDetail: {},
    deliveryStatus: [],
    invoiceDiscount: [],
    invoiceStatus: [],
    orderStatus: [],
    supplierList: [],
    poTermList: [],
    purchaseOrder: {},
    supplierInfo: {},
    termInfo: {},
    purchesInfo: {},
    poValues: {
        podId: 0,
        itemEntries: 0,
        subTotal: 0,
        charges: 0,
        saleTax: 0,
        totalAmount: 0
    },
    purchaseOrderItemList: [],
    isLoading: false,
    poCustList: [],
    poPurchaserList: [],
    customerDetails: {},
    poShippingList: [],
    status: "",
    message: "",
    searchData: [],
    searchRequestParameter: {
        searchText: "",
        poId: 0,
    },
    searchValue: "",
    searchTerm: "",
};

export default function PurchaseOrderEditReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_PURCHASEORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_PURCHASEORDERITEM_SUCCESS:
            return {
                ...state,
                purchaseOrderItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                poValues: action.data,
                isLoading: false,

            }
        case types.GET_PURCHASEORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERITEM_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.DELETE_PURCHASEORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_PURCHASEORDERITEM_SUCCESS:
            return {
                ...state,
                poValues: action.data,
                isLoading: false,
            }
        case types.DELETE_PURCHASEORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.UPDATE_POITEMINLINE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_POITEMINLINE_SUCCESS:
            return {
                ...state,
                poValues: action.data,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_POITEMINLINE_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.UPDATE_POITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_POITEM_SUCCESS:
            return {
                ...state,
                poValues: action.data,
                isLoading: false,
            }
        case types.UPDATE_POITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMPLETE_PO_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.COMPLETE_PO_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMPLETE_PO_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_POORDERDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_POORDERDETAIL_SUCCESS:
            return {
                ...state,
                orderDetail: action.data,
                isLoading: false,
            }
        case types.GET_POORDERDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CHANGE_PODISCOUNT_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CHANGE_PODISCOUNT_SUCCESS:
            return {
                ...state,
                poValues: action.data,
                isLoading: false,
            }
        case types.CHANGE_PODISCOUNT_FAILED:
            return {
                ...state,
                isLoading: false,
            }



        case types.GET_PURCHASEORDERBYID_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_PURCHASEORDERBYID_SUCCESS:
            return {
                ...state,
                purchaseOrder: action.data,
                isLoading: false,
            }
        case types.GET_PURCHASEORDERBYID_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_DELIVERYSTATUS_SUCCESS:
            return {
                ...state,
                isLoading: false,
                deliveryStatus: action.data
            }
        case types.GET_INVOICEDISCOUNT_SUCCESS:
            return {
                ...state,
                isLoading: false,
                invoiceDiscount: action.data
            }
        case types.GET_INVOICESTATUS_SUCCESS:
            return {
                ...state,
                isLoading: false,
                invoiceStatus: action.data

            }
        case types.GET_ORDERSTATUS_SUCCESS:
            return {
                ...state,
                isLoading: false,
                orderStatus: action.data

            }
        case types.GET_SUPPILERINFO_SUCCESS:
            return {
                ...state,
                isLoading: false,
                supplierInfo: action.data

            }
        case types.GET_PURCHESINFO_SUCCESS:
            return {
                ...state,
                isLoading: false,
                purchesInfo: action.data

            }
        case types.GET_TERMINFO_SUCCESS:
            return {
                ...state,
                isLoading: false,
                termInfo: action.data

            }
        case types.GET_SUPPLIER_LIST_SUCCESS:
            return {
                ...state,
                isLoading: false,
                supplierList: action.data

            }
        case types.GET_PO_TERM_LIST_SUCCESS:
            return {
                ...state,
                poTermList: action.data,
            }
        case types.GET_PO_CUST_LIST_SUCCESS:
            return {
                ...state,
                poCustList: action.data,
            }
        case types.GET_CUST_BY_ID_SUCCESS:
            return {
                ...state,
                customerDetails: action.data,
            }
        case types.GET_PO_PURCHASER_LIST_SUCCESS:
            return {
                ...state,
                poPurchaserList: action.data,
            }
        case types.GET_PO_SHIPPING_LIST_SUCCESS:
            return {
                ...state,
                poShippingList: action.data,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.CHANGE_POSUPPLIER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CHANGE_POSUPPLIER_SUCCESS:
            return {
                ...state,
                poValues: action.data,
                isLoading: false,
            }
        case types.CHANGE_POSUPPLIER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_PURCHASEORDERS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_PURCHASEORDERSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_PURCHASEORDERSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_PURCHASEORDERSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.UPDATE_SERIALNUMBER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_SERIALNUMBER_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_SERIALNUMBER_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}