import React from 'react';
import DashboardComponent from '../component/DashboardComponent';
import {
  getOrderList, setRequestParameter,
  getSearchList, setSearchRequestParameter,
  setSearchValue, setSearchData,
  setSearchTerm, deleteOrder, createOrder, checkOrderExist
} from '../state/actions/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import { toShowHideFilterBox, encryptURL } from '../../../Custom'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
function debounce(func, wait, immediate) {
  var timeout;

  return function executedFunction() {
    var context = this;
    var args = arguments;

    var later = function () {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };

    var callNow = immediate && !timeout;

    clearTimeout(timeout);

    timeout = setTimeout(later, wait);

    if (callNow) func.apply(context, args);
  };
};
class DashboardContainer extends React.Component {
  state = {
    isRender: false
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
   
    //filter box handler method 
    toShowHideFilterBox();
    let requestParameter = { ...this.props.requestParameter };
    requestParameter["userRole"] = userDetails.role;
    if (userDetails.role === process.env.REACT_APP_UserRole_Cashier) {
      var filtersData = this.props.requestParameter.filterText !== "" ? this.props.requestParameter.filterText.split(",") : [];
      filtersData = filtersData.filter((item) => item.toLowerCase() !== "purchase order");
      requestParameter["filterText"] = filtersData.toString();

    }
    this.props.setRequestParameter(requestParameter);
    this.props.getOrderList(requestParameter, true);
    window.addEventListener('scroll', this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);

  }
  //#region check user role validation 
  checkUserRoleValidation = () => {
    try {
      
      const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
      if (userDetails.role !== process.env.REACT_APP_UserRole_Manager && userDetails.role !== process.env.REACT_APP_UserRole_Cashier  && userDetails.role !== process.env.REACT_APP_UserRole_CentralManager) {

        this.props.history.push("/NotFound");
      }
      else {
        this.setState({ isRender: true });
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion check user role validation

  //#region scroll event to on demand loading
  handleScroll = scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt = (window.innerHeight + document.documentElement.scrollTop).toFixed(0);
      var offsetHeight = (document.documentElement.offsetHeight).toFixed(0);
      if (scrollHeigt === offsetHeight || (parseInt(scrollHeigt)+1).toString() === offsetHeight || (parseInt(scrollHeigt)-1).toString() === offsetHeight) {
        let pagesize = this.props.requestParameter.pageSize + 50;
        let requestParameter = { ...this.props.requestParameter };
        requestParameter["pageSize"] = pagesize;
        this.props.setRequestParameter(requestParameter);
        this.props.getOrderList(requestParameter, false);
      }
    } catch (error) {
      console.log(error);

    }
  }, 0);

  //#endregion scroll event to on demand loading
  //#region filter change handle method
  onFilterChangedHandler = (e) => {
    try {

      var filtersData = this.props.requestParameter.filterText !== "" ? this.props.requestParameter.filterText.split(",") : [];
      var requestParameter = { ...this.props.requestParameter };
      if (e.target.checked)
        filtersData.push(e.target.name);
      else
        filtersData = filtersData.filter((item) => item.toLowerCase() !== e.target.name.toLowerCase());
      if (userDetails.role === process.env.REACT_APP_UserRole_Cashier)
        filtersData = filtersData.filter((item) => item.toLowerCase() !== "purchase order");
      requestParameter["filterText"] = filtersData.toString();
      this.props.setRequestParameter(requestParameter);
      this.props.getOrderList(requestParameter, true);
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion filter change handle method
  //#region on row click handler
  onRowClickHandler = (id, label) => {
    var redirectPage = "";
    if (label === "order list")
      redirectPage = "/Order/";
    else if (label === "purchase order")
      redirectPage = "/PurchaseOrderEdit/";
    else if (label === "transfer out")
      redirectPage = "/TransferOutEdit/";
    else
      redirectPage = "/WorkOrderEdit/";
    window.open(redirectPage + encryptURL(id), '_blank');
  }
  //#endregion on row click handler
  //#region create order
  createOrderHandler = () => {
    let createOrderRequestParameter = {
      userId: parseInt(userDetails.userId)
    }
    this.props.createOrder(createOrderRequestParameter);
  }
  //#endregion create order
  //#region search handler
  searchHandler = (record, cb) => {
    try {

      let getdata;
      record = record.trim();
      //this.props.setSearchValue(record);
      if (record !== "" || this.props.requestParameter.searchText !== "") {

        let requestParameter = { ...this.props.requestParameter };
        requestParameter["searchText"] = record;
        if (userDetails.role === process.env.REACT_APP_UserRole_Cashier) {
          var filtersData = this.props.requestParameter.filterText !== "" ? this.props.requestParameter.filterText.split(",") : [];
          filtersData = filtersData.filter((item) => item.toLowerCase() !== "purchase order");
          requestParameter["filterText"] = filtersData.toString();
        }
        this.props.setRequestParameter(requestParameter);
        this.props.getOrderList(requestParameter, true);
      }
      if (record !== "") {
        let searchRequestParameter = { ...this.props.searchRequestParameter };
        searchRequestParameter["searchText"] = record;
        searchRequestParameter["filterText"] = this.props.requestParameter.filterText;
        if (userDetails.role === process.env.REACT_APP_UserRole_Cashier) {
          var filtersData = this.props.searchRequestParameter.filterText !== "" ? this.props.searchRequestParameter.filterText.split(",") : [];
          filtersData = filtersData.filter((item) => item.toLowerCase() !== "purchase order");
          searchRequestParameter["filterText"] = filtersData.toString();

        }
        this.props.setSearchRequestParameter(searchRequestParameter);
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata = [];
        this.props.setSearchData(getdata);
      }
    } catch (error) {
      console.log(error);
    }
  }
  //#endregion search handler
  //#region search change handler
  onSearchChangeHandler = (value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value, (items) => {
        this.props.setSearchData(items);
      })
    } catch (error) {
      console.log(error);
    }
  }
  setSearchTerm = debounce((searchTerm, cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm, cb);
    } catch (error) {
      console.log(error);
    }
  }, 1000)
  //#endregion search change handler
  //#region delete order
  deleteOrderHandler = (id) => {
    try {
      const result = this.props.checkOrderExist(parseInt(id)).then(() => {
        if (this.props.isOrderExist) {

          swal({
            title: "Are you sure?",
            text: "Once deleted, you will not be able to recover this data!",
            icon: "warning",
            buttons: {
              confirm: true,
              cancel: true
            },
            dangerMode: true,
          })
            .then((willDelete) => {
              if (willDelete) {
                let deleteRequestParameter = {
                  orderId: parseInt(id)
                };

                this.props.deleteOrder(deleteRequestParameter, this.props.requestParameter);

              }
            });
        }
        else {
          swal({
            title: "This order is no more exist!",
            text: 'Might be it is converted into purchase order or work order.',
            icon: 'error',
            button: "Ok",
          }).then(() => {
            this.props.history.push("/Dashboard");
          });
        }
      })


    } catch (error) {
      console.log(error);
    }
  }

  render() {
    // const { searchData } = this.props;   
    return (
      <DashboardComponent
        {...this.props}
        onFilterChangedHandler={this.onFilterChangedHandler}
        onRowClickHandler={this.onRowClickHandler}
        searchHandler={this.searchHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        deleteOrderHandler={this.deleteOrderHandler}
        createOrderHandler={this.createOrderHandler}
        userDetails={userDetails}
        isRender={this.state.isRender}
      />
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getOrderList: (data, isLoading) => dispatch(getOrderList(data, isLoading)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    setSearchRequestParameter: (data) => dispatch(setSearchRequestParameter(data)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    deleteOrder: (deleteeRequestParameter, requestParameter) => dispatch(deleteOrder(deleteeRequestParameter, requestParameter)),
    createOrder: (createOrderRequestParameter) => dispatch(createOrder(createOrderRequestParameter)),
    checkOrderExist: (data) => dispatch(checkOrderExist(data)),
  };
};

const mapStateToProps = (state) => {
  const { isOrderExist, orderList, requestParameter, isLoading, searchValue, searchData, searchRequestParameter, SearchTerm } = state.DashboardReducer;
  return {
    requestParameter: requestParameter,
    orderList: orderList.values,
    isLoading: isLoading,
    searchValue: searchValue,
    searchData: searchData.values.length > 0 ? searchData.values : [],
    searchRequestParameter: searchRequestParameter,
    SearchTerm: SearchTerm,
    isOrderExist
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(DashboardContainer);