import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet';
import {history} from "../../../history";


export default function PastInventoryCountComponent(props) {

	const {isRender,isLoading,pastInventoryList,
		pastInventoryListParam,userDetails,status,message,isShowPastInventoryDatatable,
		isUserName,isDate,isFile,isSetUserName,isSetDate,isSetFile}=props;

	return (
		<div>
			<Helmet><title>Past Inventory Counts| Modus Go!</title></Helmet>

			{isLoading? <Loading />:""}
			{isRender?
				<div>
					<div className="sticky-filter-datatable" onClick={props.togglePastInventoryDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>
					<div className={`datatable-filter${isShowPastInventoryDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.togglePastInventoryDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
						</div>
						<div className="datatable-head-list">
							<div className="datatable-list-title text-darkblue font-weight-bold">
								Past inventory
											</div>
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isUserName" name="isUserName" onChange={props.setUserNameCheckbox} checked={isUserName} />
										<label className="custom-control-label font-weight-bold" htmlFor="isUserName">USER NAME</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDate" name="isDate" onChange={props.setDateCheckbox} checked={isDate} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDate">DATE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isFile" name="isFile" onChange={props.setFileCheckbox} checked={isFile} />
										<label className="custom-control-label font-weight-bold" htmlFor="isFile">FILE</label>
									</div>
								</li>
							</ul>
						</div>
					</div>
					<div className="main-content-area order-space mobile-bottom-space " id="test" onClick={props.toggleHideOrderDatatable}>
						<div className="container-fluid">
							<div className="row">
								<div className="col position-relative px-0">
									<div className="order-list-wrap py-0">
										<h2 className="text-left text-xl-center heading-text d-none d-md-block">Past inventory counts</h2>
									</div>
								</div>
							</div>
						</div>

						<div className="container-fluid d-block d-md-none">
							<div className="row">

								<div className="col p-0 mobile-header-info">
									<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
									<p className="text-darkblue font-weight-bold mobile-title-headline">Past inventory counts</p>
								</div>
							</div>
						</div>



						<div className="container-fluid mt-3">
							<div className="row">
								<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
							</div>
						</div>
						<div className="container-fluid bg-white header-shadow mt-1 mb-150">
							<div className="row ">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblPastInventory" className="table w-100 new-order-list wo-collasped" >
											<thead>
												<tr>
													<th className="plus-sort"></th>

													<th style={{display: isSetUserName? '':'none'}}>USER NAME</th>

													<th style={{display: isSetDate? '':'none'}}>DATE</th>

													<th style={{display: isSetFile? '':'none'}}>FILE</th>

													<th className=""><span className="d-md-none">Download</span></th>

												</tr>
											</thead>
											<tbody>
												{pastInventoryList.map((itemList,index) =>
													<tr key={itemList.id}>
														<td className="details-control plus-sort" tabIndex="-1"></td>

														<td style={{display: isSetUserName? '':'none'}} className="default-cursor">{itemList.userName}</td>

														<td style={{display: isSetDate? '':'none'}} className="default-cursor">{itemList.date}</td>

														<td style={{display: isSetFile? '':'none'}} className="default-cursor">{itemList.fileName}</td>

														<td><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del PastInventoryGridDownloadIcon" data={itemList.pathName}><i className="zmdi zmdi-download "></i></button></td>

													</tr>
												)}
											</tbody>
										</table>
									</div>

								</div>

							</div>
						</div>
					</div>

				</div>
				:""}


		</div>
	)
}