import React from 'react';

export default function OptionsComponent(props) {
    const { itemOptions } = props;
    return (
        <div>
            <div className="card-body row">
                <div className="col-12 padding-space">
                    <fieldset>
                        <legend> Options  </legend>
                        <div className="row">
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check1" name="options-check1" checked={itemOptions.priceMustBeEntered} disabled />
                                    <label className="custom-control-label" htmlFor="options-check1">Must enter price at the POS.</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check2" name="options-check2" checked={itemOptions.foodStampable} disabled />
                                    <label className="custom-control-label" htmlFor="options-check2">Accept food stamps for this item.</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check3" name="options-check3" checked={itemOptions.quantityEntryNotAllowed} disabled />
                                    <label className="custom-control-label" htmlFor="options-check3">Quantity entry at POS not allowed.</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check4" name="options-check4" checked={itemOptions.itemNotDiscountable} disabled />
                                    <label className="custom-control-label" htmlFor="options-check4">Item is not discountable at the POS.</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check5" name="options-check5" checked={itemOptions.webItem} disabled />
                                    <label className="custom-control-label" htmlFor="options-check5">Item is available on the website.</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="options-check6" name="options-check6" checked={itemOptions.inactive} disabled />
                                    <label className="custom-control-label" htmlFor="options-check6">Item is inactive</label>
                                </div>
                            </div>
                        </div>
                    </fieldset>
                </div>
                <div className="col-12 mt-3 padding-space">
                    <fieldset>
                        <legend> Block sales </legend>
                        <div className="row">
                            <div className="col-12">
                                <div className="custom-control custom-checkbox mb-2 ml-2">
                                    <input type="checkbox" className="custom-control-input" id="option-radio1" name="option-radio1" checked={itemOptions.blockSalesType === 0 ? false : true} disabled />
                                    <label className="custom-control-label" htmlFor="option-radio1">Block the sale for this item.</label>
                                </div>

                            </div>
                            <div className="col-12">
                                <div className="floating-label my-2">
                                    <input className="floating-input" type="text" placeholder=" " value={itemOptions.blockSalesReason} disabled />
                                    <span className="highlight"></span>
                                    <label>Reason</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="option-radio2" name="optionsCustomRadio" className="custom-control-input" checked={itemOptions.blockSalesAfterDate !== null || itemOptions.blockSalesBeforeDate !== null ? true : false} disabled />
                                    <label className="custom-control-label" htmlFor="option-radio2">The Block is in effect for a specific period</label>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 d-flex align-items-center">
                                <div className="custom-control custom-checkbox mb-2 ml-2">
                                    <input type="checkbox" className="custom-control-input" id="optionsCHKstartdate1" name="optionsCHK" checked={itemOptions.blockSalesAfterDate !== null ? true : false} disabled />
                                    <label className="custom-control-label" htmlFor="start-date-chk"></label>
                                </div>
                                <div className="floating-label mt-3">
                                    <input className="floating-input" type="text" placeholder=" " value={itemOptions.blockSalesAfterDate !== null ? itemOptions.blockSalesAfterDate : ""} disabled />
                                    <span className="highlight"></span>
                                    <label>Block after</label>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 d-flex align-items-center">
                                <div className="custom-control custom-checkbox mb-2 ml-2">
                                    <input type="checkbox" className="custom-control-input" id="optionsCHKstartdate2" name="optionsCHK" checked={itemOptions.blockSalesBeforeDate !== null ? true : false} disabled />
                                    <label className="custom-control-label" htmlFor="start-date-chk"></label>
                                </div>
                                <div className="floating-label mt-3">
                                    <input className="floating-input" type="text" placeholder=" " value={itemOptions.blockSalesBeforeDate !== null ? itemOptions.blockSalesBeforeDate : ""} disabled />
                                    <span className="highlight"></span>
                                    <label>Block before</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="option-radio3" name="optionsCustomRadio" className="custom-control-input" checked={itemOptions.blockSalesScheduleID > 0 ? true : false} disabled />
                                    <label className="custom-control-label" htmlFor="option-radio3">The block is in effect according to a schedule</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="floating-label mt-3">
                                    <select className="floating-select" value={itemOptions.blockSalesScheduleIDName} disabled>
                                        <option value={itemOptions.blockSalesScheduleIDName}>{itemOptions.blockSalesScheduleIDName}</option>
                                    </select>
                                    <span className="highlight"></span>
                                    <label>Schedule</label>
                                </div>
                            </div>
                        </div>
                    </fieldset>
                </div>
            </div>
        </div>
    )
}