import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
import NumberFormat from 'react-number-format';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import Autocomplete from 'react-autocomplete';
import DatePicker from "react-datepicker";
import moment from 'moment';
export default function WorkOrderComponent(props) {
	const {isLoading,workOrderList,searchValue,searchData,requestParameter,isRender,isOpenCustomerModel,customers,isShowWOCDatatable,apiNameChecked,apiDisplayCnt,successResult,isfilterHideShow,
		isSucessDiv,apiNameList,isAPINameDiv,isWoNumber,formFields,isAccount,isCustomer,isDate,changeDateHandler,isTotal,isComment,isRef,isSetWoNumber,isSetAccount,isSetCustomer,isSetDate,isSetTotal,isSetComment,isSetRef,checkboxOnchange,isCloseOrderDiv }=props;

	const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";

	return (
		<div>
			<Helmet>
				<title>Work order | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
				<div className={`sticky-filter-datatable ${isfilterHideShow}`} onClick={props.toggleWOCDatatable}>
					<img className="" src="/assets/images/filter-icon.svg" alt="" />
				</div>
				<div className={`datatable-filter${isShowWOCDatatable? " open":""}`}>
					<div className="close-datalist-popup" onClick={props.toggleWOCDatatable}>
						<i className="zmdi zmdi-close-circle"></i>
					</div>
					<div className="datatable-head-list">
						<div className="datatable-list-title text-darkblue font-weight-bold">
							WO table
						</div>
						<ul>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isWoNumber" name="isWoNumber" onChange={props.setWoNumberCheckbox} checked={isWoNumber} />
									<label className="custom-control-label font-weight-bold" htmlFor="isWoNumber">WO NUMBER</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isAccount" name="isAccount" onChange={props.setAccountCheckbox} checked={isAccount} />
									<label className="custom-control-label font-weight-bold" htmlFor="isAccount">ACCOUNT</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCustomer" name="isCustomer" onChange={props.setCustomerCheckbox} checked={isCustomer} />
									<label className="custom-control-label font-weight-bold" htmlFor="isCustomer">CUSTOMER</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDate" name="isDate" onChange={props.setDateCheckbox} checked={isDate} />
									<label className="custom-control-label font-weight-bold" htmlFor="isDate">DATE</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isTotal" name="isTotal" onChange={props.setTotalCheckbox} checked={isTotal} />
									<label className="custom-control-label font-weight-bold" htmlFor="isTotal">TOTAL</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isComment" name="isComment" onChange={props.setCommentCheckbox} checked={isComment} />
									<label className="custom-control-label font-weight-bold" htmlFor="isComment">COMMENT</label>
								</div>
							</li>
							<li>
								<div className="custom-control custom-checkbox">
									<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isRef" name="isRef" onChange={props.setRefCheckbox} checked={isRef} />
									<label className="custom-control-label font-weight-bold" htmlFor="isRef">REF</label>
								</div>
							</li>
						</ul>
					</div>
				</div>
				

				{isRender?

					<div className="main-content-area mobile-bottom-space" onClick={props.toggleHideOrderDatatable}>
						<div className="add-item-btn-mobile d-md-none" onClick={props.openCustomerModel}>
							<i className="material-icons">add</i>
						</div>

						{isSucessDiv?
						<div>
							{successResult.map((apiChecked) =>
								
							<div className="container-fluid bg-white header-shadow mt-4">
								<div className="row my-3">
									<div className="col-12 ">
										<div className="row p-3">
											<div className="col-md-2 col-12">
											<h6><b>{apiChecked.apiName}</b></h6>
											</div>
											<div className="col-md-8 col-12">
											<h6>{apiChecked.responseMsg!=null? <div  className="value-red" >: {apiChecked.responseMsg}</div>:""}</h6>

											</div>
											
										</div>


									
										{apiChecked.orderResponseList!=null && apiChecked.orderResponseList.length>0?
										<div className="table-responsive">
											<table id="tblResponse" className="table w-100" >
												<thead>
													<tr>
														<th style={{width: "2%"}} className="plus-sort" ></th>
														<th >{apiChecked.apiName} Order No.</th>
														{apiChecked.apiName=="ReserveBar"?"":<th >Purchase Order No.</th>}
														<th >Work Order No.</th>
													</tr>
												</thead>
											
												<tbody>
												{apiChecked.orderResponseList.map((data) =>
													<tr>
													<td style={{width: "2%"}} className="details-control plus-sort"></td>

													<td>{data.apiOrderNo}</td>
													{apiChecked.apiName=="ReserveBar"?"":
													<td className={data.purchaseOrderNo==null?"":data.purchaseOrderNo.toLowerCase()==="failed"? "value-red":""}>{data.purchaseOrderNo}</td>}
													<td  className={data.workOrderNo==null?"":data.workOrderNo.toLowerCase()==="failed"? "value-red":""}>{data.workOrderNo}</td>
													</tr>
													)}
												</tbody>
												
											</table>
										</div>
										:""}
									</div>
								</div>
							</div>
							)}
							<button type="button" className="btn rounded-pill btn-primary mr-1 closeapiname-wrap" onClick={props.CancelResponse}>Ok</button>

						</div>

						:""}
					{!isSucessDiv?
					<div>
						{isAPINameDiv || isCloseOrderDiv ?
						<div className="bg-white header-shadow mt-3 p-3 mb-3">
							<label>Please select API Name :</label>
							<div className="row pt-3">
								
									{apiNameList.map((data) =>
										(data.isDisplay?
									
											<div className="col-md-6 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox ml-2 my-2">
														<input type="checkbox" className="custom-control-input " id={data.id} name={data.id} onChange={checkboxOnchange} />
														<label className="custom-control-label mr-4" htmlFor={data.id}>{data.apiName}</label>
													</div>
													<div className={`floating-label w-100 ${data.apiName.toLowerCase()=="bigcommerce"?"":"d-none"}`}>
														<DatePicker className="floating-input" id="fromDate"
															onChange={(e) => changeDateHandler(e,'fromDate')}
															placeholderText="mm/dd/yyyy"
															dateFormat="MM/DD/YYYY"
															value={moment(formFields.fromDate).format('MM/DD/YYYY')=='Invalid date'? '':moment(formFields.fromDate).format('MM/DD/YYYY')}
															autocomplete="off"
														/>
														<span className="highlight"></span>
														<label>From date</label>
													</div>
													<div className={`floating-label w-100 ${data.apiName.toLowerCase()=="reservebar"?"":"d-none"}`}>
														<DatePicker className="floating-input" id="reservebarfromDate"
															onChange={(e) => changeDateHandler(e,'reservebarfromDate')}
															placeholderText="mm/dd/yyyy"
															dateFormat="MM/DD/YYYY"
															value={moment(formFields.reservebarfromDate).format('MM/DD/YYYY')=='Invalid date'? '':moment(formFields.reservebarfromDate).format('MM/DD/YYYY')}
															autocomplete="off"
														/>
														<span className="highlight"></span>
														<label>From date</label>
													</div>
												</div>
											</div>
										
										
										:"")
										)}
								
								<div className="itemscanbottom mt-2">
									<button className="btn rounded-pill btn-primary mr-1" onClick={props.checkOnlineOrCloseWO} type="submit">{isAPINameDiv?"Check Online":"Close API Order"}</button>
									<button type="button" className="btn rounded-pill btn-primary mr-1 closeapiname-wrap" onClick={props.CancelAPIName}>Cancel</button></div>
							</div>
						</div>
						:""}

						{!isAPINameDiv && !isCloseOrderDiv?
						<div className="container-fluid d-none d-md-block">
							<div className="row">
								<div className="col position-relative px-0">
								<button className="position-absolute  btn btn-primary rounded-pill" type="button" onClick={props.openCustomerModel}>Create new WO</button>
								{userDetails.isThirdPartyAPI && apiDisplayCnt.length>0?
									<div>
										<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.SelectAPIName}>Check Online WO</button>

										<button className="d-none position-absolute right210-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.SelectCloseOrder}>Close API Order</button>
									</div>:""}
									<h2 className="text-left text-xl-center heading-text">Work order</h2>
								</div>
							</div>
						</div>
						:""}
						
						<div className="container-fluid d-block d-md-none">
							<div className="row">
								{/* <div className="col p-0">
									<p className="text-darkblue font-weight-bold">Work order</p>
								</div> */}
								<div className="col p-0 mobile-header-info">
									<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
									<p className="text-darkblue font-weight-bold mobile-title-headline">Work order</p>
								</div>
							</div>
						</div>

						<div className="container-fluid mt-3">
							<div className="row">
								<div className="col p-0">
									<div className="searchbox-wrap header-shadow">
										<Autocomplete
											inputProps={{id: 'workorderlist-autocomplete'}}
											wrapperStyle={{position: 'relative'}}
											value={searchValue}
											items={searchData}
											getItemValue={(item) => item.key}
											inputProps={{placeholder: 'Work order number or account or customer or date or total or comment or ref'}}
											onSelect={record => props.onSearchChangeHandler(record)}
											onChange={(event,value) => props.onSearchChangeHandler(value)}
											renderMenu={children => (
												<div className="search-menu">
													{children}
												</div>
											)}
											renderItem={(item,isHighlighted) => (
												<div
													key={item.key}
												>{item.value}</div>
											)}
										/>
										<div className="icon-box rounded-circle">
											<i className="material-icons">search</i>
										</div>

									</div>
								</div>
							</div>
						</div>

						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblWorkOrder" className="table w-100" >
											<thead>
												<tr>
													<th style={{width: "2%"}} className="plus-sort" ></th>

													<th style={{display: isSetWoNumber? '':'none'}} className="issetwonumber" className={requestParameter.sortColumnName==="id"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("id")}>WO NUMBER</th>

													<th style={{display: isSetAccount? '':'none'}} className="issetaccount" className={requestParameter.sortColumnName==="accountNumber"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("accountNumber")}>ACCOUNT</th>

													<th style={{display: isSetCustomer? '':'none'}} className="issetcustomer" className={requestParameter.sortColumnName==="customer"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("customer")}>CUSTOMER</th>

													<th style={{display: isSetDate? '':'none'}} className="issettotal" className={requestParameter.sortColumnName==="date"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("date")}>DATE</th>

													<th style={{display: isSetTotal? '':'none'}} className="issettotal" className={requestParameter.sortColumnName==="total"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("total")}>TOTAL</th>

													<th style={{display: isSetComment? '':'none'}} className="issetcomment" className={requestParameter.sortColumnName==="comment"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("comment")}>COMMENT</th>

													<th style={{display: isSetRef? '':'none'}} className="issettotal" className={requestParameter.sortColumnName==="ref"? requestParameter.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("ref")}>REF</th>

												</tr>
											</thead>
											<tbody>
												{workOrderList.map((data) =>
													<tr key={data.id}>
														<td style={{width: "2%"}} className="details-control plus-sort"></td>

														<td className="issetwonumber" style={{display: isSetWoNumber? '':'none'}} onClick={() => props.onEditHandler(data.id)}>{data.id}</td>

														<td className="issetaccount" style={{display: isSetAccount? '':'none'}}   ><div className="WOGridRowClick" data={data.id}>{data.accountNumber}</div></td>

														<td className="issetcustomer" style={{display: isSetCustomer? '':'none'}} ><div className="WOGridRowClick" data={data.id}>{data.customer}</div></td>

														<td className="issettotal" style={{display: isSetDate? '':'none'}} className="issettotal"><div className="WOGridRowClick" data={data.id}>{data.date}</div></td>

														<td className="issettotal" style={{display: isSetTotal? '':'none'}} ><div className="WOGridRowClick" data={data.id}>
															<NumberFormat value={data.total} displayType={'text'} fixedDecimalScale={true} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} />
														</div></td>

														<td className="issetcomment" style={{display: isSetComment? '':'none'}} onClick={() => {if(data.comment==="") props.onEditHandler(data.id)}} ><div className="WOGridRowClick" data={data.id}>{data.comment}</div></td>

														<td className="issettotal" style={{display: isSetRef? '':'none'}} onClick={() => {if(data.ref==="") props.onEditHandler(data.id)}}><div className="WOGridRowClick" data={data.id}>{data.ref}</div></td>

													</tr>)}
											</tbody>
										</table>
									</div>

								</div>
							</div>
						</div>
						</div>
						:""}
					</div>
					:""}
			</div>
			{/* <! Modal customers> */}
			<Modal backdrop="static" isOpen={isOpenCustomerModel} toggle={props.toggleCustomerModel} centered={true} size="lg">
				<ModalHeader toggle={props.toggleCustomerModel}>Create WO</ModalHeader>
				<ModalBody>
					<div className="row">
						<div className="col-12 p-1">
							<div className="table-responsive">
								<table id="tblWOCustomersList" className="table w-100 new-order-list" >
									<thead>
										<tr>
											<th></th>
											<th>ACCOUNT NUMBER</th>
											<th>NAME</th>
											<th>COMPANY</th>

										</tr>
									</thead>
									<tbody>
										{customers.map((customer) =>
											<tr key={customer.id}>
												<td> <div className="custom-control custom-checkbox " key={customer.id}><input type="checkbox" className="custom-control-input WOcustomerCHK" onChange={(e) => props.customerCheckBoxHandler(e,customer.id)} id={customer.id} name={customer.id} />
													<label className="custom-control-label" htmlFor={customer.id}></label></div></td>
												<td>{customer.accountNumber}</td>
												<td>{customer.firstName} {customer.lastName}</td>
												<td>{customer.company}</td>

											</tr>
										)}
									</tbody>
								</table>
							</div>
						</div>
					</div>

				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.createWOHandler} >Create WO</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleCustomerModel}>Cancel</button>

				</ModalFooter>
			</Modal>

		</div>
	)
}