import * as types from "../../../../constants/AppConstants";
import { get, post } from "../../../../service/index";
import swal from 'sweetalert';
import { history } from '../../../../history';
import AuthenticateService from '../../../../Services/AuthenticateServices';
import { encryptURL, decryptURL } from '../../../../Custom'
const obj = new AuthenticateService();
//#region to get order list
export function getOrderList(data, isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_ORDERS_REQUEST,
            isLoading: isLoading,
        });
        let url = 'order/orderList';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_ORDERS_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_ORDERS_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get order list
//#region set request parameter
export function setRequestParameter(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_ORDERS_REQUESTPARAMETER,
            data: data,

        });
    }
}
//#endregion set request parameter
