//#region delete order
export function deleteOrder(deleteRequestParameter, requestParameter) {
    return (dispatch) => {
        dispatch({
            type: types.DELETE_ORDER_REQUEST,
        });
        let url = 'order/DeleteOrder';
        return post(url, deleteRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getOrderList(requestParameter, true));
                    dispatch({
                        type: types.DELETE_ORDER_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.DELETE_ORDER_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion delete order
//#region create order
export function createOrder(createOrderRequestParameter) {
  
    return (dispatch) => {
        dispatch({
            type: types.CREATE_ORDER_REQUEST,
        });
        let url = 'order/createOrder';
        return post(url, createOrderRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    history.push("/Order/" + encryptURL(payload.orderId));
                    dispatch({
                        type: types.CREATE_ORDER_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.CREATE_ORDER_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion create order
export function checkOrderExist(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_ORDEREXIST_REQUEST
        });
        let url = 'order/CheckOrderExist/' + data;

        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {

                    dispatch({
                        type: types.GET_ORDEREXIST_SUCCESS,
                        data: response.payload,
                    });
                }
                else {
                    dispatch({
                        type: types.GET_ORDEREXIST_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
