import React from 'react';
import AdditionalSettingsComponent from '../component/AdditionalSettingsComponent';
import { setSuccessDialogue,clearMessage,ResetAllItemSupllierList,GetAllAPINameMasterList,
  UpdateIsDisplayAPI
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class AdditionalSettingsContainer extends React.Component {
  state={
    selectALL:false
  }
 
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.props.GetAllAPINameMasterList(true);
    window.addEventListener('scroll',this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    clearInterval(this.clearMessageIntervalID);
  }
    //#region check user role validation 
    checkUserRoleValidation=() => {
      try {
        const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
        if(userDetails.role!==process.env.REACT_APP_UserRole_Admin) {
          this.props.history.push("/NotFound");
        }
      } catch(error) {
        console.log(error);
      }
    }
    ResetALL=()=>{
      this.props.ResetAllItemSupllierList(true);
    }

   
    setIsDisplayCheckbox= (e) => {
      let id = 0;
      if(e.target.name=="selectAll")
      {
        id=-1;
        this.setState({selectALL:e.target.checked})
      }
      else
      {
        id = e.target.name;
      }

      let paramValue={
        apiId:parseInt(id),
        isDisplay:e.target.checked
      }
      this.props.UpdateIsDisplayAPI(paramValue,true)
    }

  render() {
  
    return (
      <AdditionalSettingsComponent
       {...this.props}
       ResetALL={this.ResetALL}
       setIsDisplayCheckbox={this.setIsDisplayCheckbox}
       selectALL={this.state.selectALL}
      
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    clearMessage: () => dispatch(clearMessage()),
    ResetAllItemSupllierList:(isLoading)=>dispatch(ResetAllItemSupllierList(isLoading)),
    GetAllAPINameMasterList:(isLoading)=>dispatch(GetAllAPINameMasterList(isLoading)),
    UpdateIsDisplayAPI:(data,isLoading)=>dispatch(UpdateIsDisplayAPI(data,isLoading))
  };
};

const mapStateToProps=(state) => {

  const {updatestatus, updatemessage, isSuccessDialogue,isLoading,allAPINameList}=state.AdditionalSettingsReducer;
  return {
    updatestatus, updatemessage, isSuccessDialogue,isLoading,allAPINameList
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(AdditionalSettingsContainer);