import React from 'react';
import ConfigureStoreComponent from '../component/ConfigureStoreComponent';
import {clearMessage,UpdateConfigureStore,getConfigureStoreList
} from '../state/actions/index';
import {connect} from 'react-redux';
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-fixedheader';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
window.$=$;

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class ConfigureStoreContainer extends React.Component {
  state={
  formFields:{
    centralServerURL:"",
    isEnableCentralMode:false
  },
  centralServerURLDisable:true
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.props.getConfigureStoreList(true)
    window.addEventListener('scroll',this.handleScroll);
   
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    clearInterval(this.clearMessageIntervalID);
  }
    //#region check user role validation 
    checkUserRoleValidation=() => {
      try {
        const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
        if(userDetails.role!==process.env.REACT_APP_UserRole_Admin) {
          this.props.history.push("/NotFound");
        }
      } catch(error) {
        console.log(error);
      }
    }
    //#endregion check user role validation
    inputChangeHandler= (e) => {
      try {
        var value = e.target.value;
        let getFormField = this.props.getFormField;
        var disableServerURL=false;
        if(e.target.name=="isHQConnected")
        {
          getFormField[e.target.name] =e.target.checked;
          if(e.target.checked==false)
          {
            getFormField["serverURL"] = "";
            getFormField["storeID"] = 0;
            disableServerURL=true;
          }
        }
        else
        {
          getFormField[e.target.name] = value;
          disableServerURL=false;
        }
        this.setState({
          getFormField,centralServerURLDisable:disableServerURL
        });
      } catch (error) {
        console.log(error);
  
      }
    }

    saveCofigStore=() => {

        if(this.props.getFormField.isHQConnected && this.props.getFormField.serverURL=="")
        {
          swal("","Please Enter Server URL","error")
        }
        else
        {
          let createConfigParam={
            centralMode:this.props.getFormField.isHQConnected,
            serverURL:this.props.getFormField.serverURL,
            storeID:parseInt(this.props.getFormField.storeID),
          }
          
          this.props.UpdateConfigureStore(createConfigParam,true)
        }
       
    }
  render() {
    // const { searchData } = this.props;   
    

    return (
      <ConfigureStoreComponent
       {...this.props}
       saveCofigStore={this.saveCofigStore}
       inputChangeHandler={this.inputChangeHandler}
       formFields={this.state.formFields}
       centralServerURLDisable={this.state.centralServerURLDisable}

      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
   // setSuccessDialogue: (data, updatestatus, updatemessage) => dispatch(setSuccessDialogue(data, updatestatus, updatemessage)),
    clearMessage: () => dispatch(clearMessage()),
    UpdateConfigureStore:(data,isLoading)=>dispatch(UpdateConfigureStore(data,isLoading)),
    getConfigureStoreList:(isLoading)=>dispatch(getConfigureStoreList(isLoading))
  };
};

const mapStateToProps=(state) => {
  const {updatestatus, updatemessage,getFormField}=state.ConfigureStoreReducer;
  return {
    updatestatus, updatemessage,getFormField
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(ConfigureStoreContainer);