import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        searchText: "",
        filterText: "Order List,Purchase Order,Work Order,Transfer Out",
        pageNo: 0,
        pageSize: 50,
        userRole: ""
    },
    orderList: [],
    searchData: [],
    searchRequestParameter: {
        searchText: "",
        filterText: "Order List,Purchase Order,Work Order,Transfer Out",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    isOrderExist: true
};

export default function DashboardReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_ORDERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ORDERS_SUCCESS:
            return {
                ...state,
                orderList: action.data,
                isLoading: false,
                isOrderDelete: false,
            }
        case types.GET_ORDERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDERS_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.SET_ORDERS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_ORDERS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_ORDERS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_ORDERS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_ORDERSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ORDERSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_ORDERSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_ORDER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_ORDER_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_ORDER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CREATE_ORDER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CREATE_ORDER_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.CREATE_ORDER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ORDEREXIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ORDEREXIST_SUCCESS:
            return {
                ...state,
                isOrderExist: action.data.result,
                isLoading: false,
            }
        case types.GET_ORDEREXIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        default:
            return state;
    }
}