import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    pastInventoryList: [],
    totalRecord: 0,
    pastInventoryListParam: {
        userRole:'',
        pageNo: 0,
        pageSize: 50
    },
    isLoading: false,
    status: "",
    message: ""
};

export default function PastInventoryCountReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_INVENTORYITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYITEM_SUCCESS:
            return {
                ...state,
                pastInventoryList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_INVENTORYITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                pastInventoryListParam: action.data,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        default:
            return state;
    }
}