import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        sortColumnName: "lastTime",
        sortDirection: "desc",
        pageNo: 0,
        pageSize: 50,
        searchText: ""
    },
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status: "",
    message: "",
    isModified: false
};

export default function NewItemListReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_NEWITEMLIST_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_NEWITEMLIST_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_NEWITEMLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                isModified: action.data.values !== undefined || action.data.values !== null ? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_NEWITEMLIST_NEWITEMLISTLISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.SET_NEWITEMLISTS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_NEWITEMLISTS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_NEWITEMLISTS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_NEWITEMLISTS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_NEWITEMLISTSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_NEWITEMLISTSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_NEWITEMLISTSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}