import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import $ from 'jquery';
import AuthenticateService from '../../../../Services/AuthenticateServices';


const obj = new AuthenticateService();

//#region get api list
export function getAPINameList(data, isLoading) {
 
    return (dispatch) => {
        dispatch({
            type: types.GET_APINAME_REQUEST,
            isLoading: isLoading
        });
        let url = 'APIColumnMapping/get/GetAPINameList';
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    $('#tblColumnMapping').DataTable().destroy();
                    /* Logic */
                    dispatch({
                        type: types.GET_APINAME_SUCCESS,
                        data: response.payload,
                    });
                  
                    $('#tblColumnMapping').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })
                } else {
                    dispatch({
                        type: types.GET_APINAME_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}


export function insertUpdateAPIColumnMapping(data,dataParam) {

    return (dispatch) => {
                    dispatch({
                        type: types.INSERT_APICOLUMN_REQUEST,
                    });
                    let url = 'APIColumnMapping/CreateUpdateAPIColumnMapping';
                    return post(url, data)
                        .then((response) => {   
                            if (response.payload && response.payload.status === "Success") {
                                dispatch(getAPIColumnMappingList(dataParam));
                                dispatch({
                                    type: types.INSERT_APICOLUMN_SUCCESS,
                                    data: response.payload,
                                });

                                dispatch(setSuccessDialogue(true, response.payload.status, response.payload.message));


                            } 
                            else if(response.payload.apiColumnMappingId==-1)
                            {
                                swal({
                                    title: response.payload.status,
                                    text: "Duplicate records found!",
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }  
                            else if(response.payload.apiColumnMappingId==-2)
                            {
                                swal({
                                    title: response.payload.status,
                                    text: "API Column Name already exist!",
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }  
                            else {
                                dispatch({
                                    type: types.INSERT_APICOLUMN_FAILED,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }
                        })
                   
    }
}

export function getAPIColumnMappingList(data,isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_APICOLUMNMAPPING_REQUEST,
            isLoading:isLoading
        });
        let url = 'APIColumnMapping/get/GetAPIColumnMappingList?APINameMasterID=' + data.apiNameMasterID +'&isColumnMapping='+true;
                return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    $('#tblColumnMapping').DataTable().destroy();
                    /* Logic */
                    dispatch({
                        type: types.GET_APICOLUMNMAPPING_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblColumnMapping').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    });
                    
                    
                } else {
                    dispatch({
                        type: types.GET_APICOLUMNMAPPING_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}


//#region delete 
export function deleteAPIColumnMapping(deleteColumnMappingParam,data) {
    return (dispatch) => {
        dispatch({
            type: types.DELETE_APICOLUMNMAPPING_REQUEST,
        });
        let url = 'APIColumnMapping/DeleteAPIColumnMapping';
        return post(url, deleteColumnMappingParam)
            .then((response) => {
                const { payload } = response;
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getAPIColumnMappingList(data));
                    dispatch({
                        type: types.DELETE_APICOLUMNMAPPING_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });
                    dispatch(setSuccessDialogue(true, response.payload.status, response.payload.message));


                } else {
                    dispatch({
                        type: types.DELETE_APICOLUMNMAPPING_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion delete 



export function setSuccessDialogue(check, status, message) {
    return (dispatch) => {
        dispatch({
            type: types.RESET_SUCCESS_DIALOGUE,
            isSuccessDialogue: check,
            status: status,
            message: message,
        });
    }
}

export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}


