//#region updateTender 
export function UpdateTender(tenderParam) {
   
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_TENDER_REQUEST,
        });
        let url = 'APIColumnMapping/UpdateTender';
        return post(url, tenderParam)
            .then((response) => {
                const { payload } = response;
                if (response.status === 200) {
                    /* Logic */
               
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })

                } else {
                    dispatch({
                        type: types.UPDATE_TENDER_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion updateTender 

