import React, { Component } from 'react';
import './App.css';
import Login from './Login/Login';
import ConfigureDatabase from './ConfigureDatabase/ConfigureDatabase';
import ConfigureSystem from './ConfigureSystem/ConfigureSystem';
import { Router, Route, Switch, Redirect } from 'react-router-dom';
import LayoutRoute from './Layout/LayoutRoute';
import Items from './Items/Items';
import ItemCreate from './Items/ItemCreate/ItemCreate';
import ItemEdit from './Items/ItemEdit/ItemEdit';
import { history } from './history';
import DashboardContainer from './modules/dashboard/container/DashboardContainer';
import ConfigureDatabaseContainer from './modules/admin/configureDatabase/container/ConfigureDatabaseContainer';
import OrderContainer from './modules/order/container/OrderContainer';
import PODSetting from './PODSetting/PODSetting';
import PurchaseOrderContainer from './modules/purchaseOrder/container/PurchaseOrderContainer';
import PurchaseOrderEditContainer from './modules/purchaseOrderEdit/container/PurchaseOrderEditContainer';
import ActivityLogContainer from './modules/activityLog/container/ActivityLogContainer';
import WorkOrderContainer from './modules/workOrder/container/WorkOrderContainer';
import WorkOrderEditContainer from './modules/workOrderEdit/container/WorkOrderEditContainer';
import configureStore from '../src/AppStore.js';
import { Provider } from 'react-redux';
import NotFound from './notFound/NotFound';
import InventoryListContainer from './modules/inventoryList/container/InventoryListContainer';
import InventoryDetailContainer from './modules/inventoryDetail/container/InventoryDetailContainer';
import FinalInventoryContainer from './modules/finalInventory/container/FinalInventoryContainer';
import PastInventoryCountContainer from './modules/pastInventoryCount/container/PastInventoryCountContainer';
import ManageInventoryContainer from './modules/manageInventory/container/ManageInventoryContainer';
import SubscriptionFailedContainer from './modules/subscriptionFailed/container/SubscriptionFailedContainer';
import APIColumnMappingContainer from './modules/APIColumnMapping/container/APIColumnMappingContainer';
import PriceConfigurationContainer from './modules/PriceConfiguration/container/PriceConfigurationContainer';
import UpdateBulkItemContainer from './modules/UpdateBulkItem/container/UpdateBulkItemContainer';
import ConfigureStoreContainer from './modules/ConfigureStore/container/ConfigureStoreContainer';
import ThirdPartyAPIDataSetupContainer from './modules/ThirdPartyAPIDataSetup/container/ThirdPartyAPIDataSetupContainer';
import AdditionalSettingsContainer from './modules/AdditionalSettings/container/AdditionalSettingsContainer';

import TransferOutContainer from './modules/transferOut/container/transferOutContainer';
import TransferOutEditContainer from './modules/transferOutEdit/container/transferOutEditContainer';
import manageItemHistoryContainer from './modules/manageItemHistory/container/manageItemHistoryContainer';
import CentralUserContainer from './modules/centralUser/container/CentralUserContainer';
import ICashOutSettingContainer from './modules/ICashOutSetting/container/ICashOutSettingContainer';

const { store } = configureStore();

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router history={history}>
          <Switch>
            <Route exact path="/">
              <Redirect to="/Login" />
            </Route>
            <Route exact path="/Login" component={Login} />
            <Route exact path="/ConfigureDatabase" component={ConfigureDatabase} />
            <Route exact path="/ConfigureSystem" component={ConfigureSystem} />
            <LayoutRoute exact path="/Items" component={Items} />
            <LayoutRoute exact path="/PODSetting" component={PODSetting} />
            <LayoutRoute exact path={["/ItemCreate/:poId", "/ItemCreate"]} component={ItemCreate} />
            <LayoutRoute exact path={["/ItemEdit/:itemId/:fromId/:from","/ItemEdit/:itemId/:from", "/ItemEdit/:itemId"]} component={ItemEdit} />
            <LayoutRoute exact path="/AdminConfigureDatabase" component={ConfigureDatabaseContainer} />
            <LayoutRoute exact path="/Dashboard" component={DashboardContainer} />
            <LayoutRoute exact path="/Order/:orderId" component={OrderContainer} />
            <LayoutRoute exact path="/PurchaseOrder" component={PurchaseOrderContainer} />
            <LayoutRoute exact path={["/PurchaseOrderEdit/:orderId/:tab", "/PurchaseOrderEdit/:orderId"]} component={PurchaseOrderEditContainer} />
            <LayoutRoute exact path="/WorkOrder" component={WorkOrderContainer} />
            <LayoutRoute exact path="/WorkOrderEdit/:orderId" component={WorkOrderEditContainer} />
            <LayoutRoute exact path="/ActivityLog" component={ActivityLogContainer} />
            <LayoutRoute exact path="/InventoryList" component={InventoryListContainer} />
            <LayoutRoute exact path="/InventoryDetail/:listId" component={InventoryDetailContainer} />
            <LayoutRoute exact path={["/FinalInventory/:inventory","/FinalInventory"]} component={FinalInventoryContainer} /> 
            <LayoutRoute exact path="/PastInventory" component={PastInventoryCountContainer} />  
            <LayoutRoute exact path="/ManageInventory" component={ManageInventoryContainer} />   
            <LayoutRoute exact path="/APIColumnMapping" component={APIColumnMappingContainer} />   
            <LayoutRoute exact path="/PriceConfiguration" component={PriceConfigurationContainer} />   
            <LayoutRoute exact path="/UpdateBulkItem" component={UpdateBulkItemContainer} />   
            <LayoutRoute exact path="/ConfigureStore" component={ConfigureStoreContainer} />   
            <LayoutRoute exact path="/ThirdPartyAPIDataSetup" component={ThirdPartyAPIDataSetupContainer} />   
            <LayoutRoute exact path="/AdditionalSettings" component={AdditionalSettingsContainer} />   
            <LayoutRoute exact path="/TransferOut" component={TransferOutContainer} />   
            <LayoutRoute exact path="/ManageItemHistory" component={manageItemHistoryContainer} />   
            <LayoutRoute exact path="/CentralUser" component={CentralUserContainer} />   
            <LayoutRoute exact path="/ICashOutSettingContainer" component={ICashOutSettingContainer} />   
            <LayoutRoute exact path={["/TransferOutEdit/:orderId/:tab", "/TransferOutEdit/:orderId"]} component={TransferOutEditContainer} />   

            
            <Route exact path="/SubscriptionFailed" component={SubscriptionFailedContainer} />  
            <Route path={['*', '/NotFound']} component={NotFound} />
          </Switch>
        </Router>
      </Provider>
    );
  }
}

export default App;
