import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        pageNo: 0,
        pageSize: 50,
        userId: 0,
        userRole: ""
    },
    InventoryList: [],
    isLoading: false,
    isCombine: false,
    isFinal: false,
    isOpenLogin: false,
    AllInventoryCount:0,
    isInventoryended:false
};

export default function InventoryListReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORY_SUCCESS:
            return {
                ...state,
                InventoryList: action.data,
                AllInventoryCount:(action.data.values) ? action.data.values.reduce((prev,next) => prev + next.totalInvItems,0):0,
                isCombine: action.data.isCombine,
                isFinal: action.data.isFinal,
                isLoading: false,
                isInventoryended: action.data.isInventoryended,
            }
        case types.GET_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_INVENTORY_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.DELETE_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_INVENTORY_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CREATE_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CREATE_INVENTORY_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.CREATE_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMBINE_INVENTORY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.COMBINE_INVENTORY_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.COMBINE_INVENTORY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ISOPEN_LOGIN:
            return {
                ...state,
                isOpenLogin: action.data,
                isLoading: false,
            }
        case types.INVENTORY_LOGIN_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.INVENTORY_LOGIN_SUCCESS:
            return {
                ...state,
                isOpenLogin: false,
                isLoading: false,
            }
        case types.INVENTORY_LOGIN_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}