import React from 'react';
import NumberFormat from 'react-number-format';
import Loading from "../../loading/components/Loading";
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
export default function PurchaseComponent(props) {
    const { itemPurchaseInfo,  poC_ItemSupplierList, } = props.itemPurchase;
    const {isLoading,isOpenQuantityModel,updateItemParam,supplierListState } = props;
    return (
        
        <div>
            {isLoading ? <Loading /> : ""}
            <div className="card-body">
                <div className="row">
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.taxCode : ""} disabled />
                            <span className="highlight"></span>
                            <label>Tax Code</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.taxGroupIDName : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.taxGroupIDName : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.taxGroupIDName : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>Tax</label>
                        </div>
                        <div className="custom-control custom-checkbox ml-2 my-2 absolute-checkbox">
                            <input type="checkbox" className="custom-control-input" id="Vouchers-check2" name="Vouchers-check2" checked={itemPurchaseInfo !== null ? itemPurchaseInfo.taxGroupID >= 0 ? true : false : false} disabled />
                            <label className="custom-control-label" htmlFor="Vouchers-check1"></label>
                        </div>
                    </div>

                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.childReorderName : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.childReorderName : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.childReorderName : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>Child / Kit Reorder</label>
                        </div>
                    </div>


                    <div className="col-12">
                        <div className="custom-control custom-checkbox ml-2 my-2">
                            <input type="checkbox" className="custom-control-input" id="Vouchers-check1" name="Vouchers-check1" checked={itemPurchaseInfo !== null ? itemPurchaseInfo.doNotOrder : false} disabled />
                            <label className="custom-control-label" htmlFor="Vouchers-check1">Do Not Order</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.unitOfMeasureCode : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.unitOfMeasureCode : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.unitOfMeasureCode : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>Unit of Measure</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.unitOfMeasureName : ""} disabled />
                            <span className="highlight"></span>
                            <label>Label</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID1Code : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID1Code : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID1Code : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>UOM Qty 1</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID1Name : ""} disabled />
                            <span className="highlight"></span>
                            <label>Label</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchQtyUOM1 : ""} disabled />
                            <span className="highlight"></span>
                            <label>Quantity</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID2Code : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID2Code : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID2Code : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>UOM Qty 2</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID2Name : ""} disabled />
                            <span className="highlight"></span>
                            <label>Label</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchQtyUOM2 : ""} disabled />
                            <span className="highlight"></span>
                            <label>Quantity</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID3Code : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID3Code : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID3Code : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>UOM Qty 3</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchUOMID3Name : ""} disabled />
                            <span className="highlight"></span>
                            <label>Label</label>
                        </div>
                    </div>
                    <div className="col-sm-4 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchQtyUOM3 : ""} disabled />
                            <span className="highlight"></span>
                            <label>Quantity</label>
                        </div>
                    </div>
                    {/* <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <select className="floating-select" value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchaseUOMCode : ""} disabled>
                                <option value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchaseUOMCode : ""}>{itemPurchaseInfo !== null ? itemPurchaseInfo.purchaseUOMCode : ""}</option>

                            </select>
                            <span className="highlight"></span>
                            <label>Purchase UOM</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemPurchaseInfo !== null ? itemPurchaseInfo.purchaseUOMName : ""} disabled />
                            <span className="highlight"></span>
                            <label>Label</label>
                        </div>
                    </div> */}
                </div>
                <div className="row">
                    <div className="col-12">
                        <span className="heading-text"><b>Suppliers</b></span>
                    </div>
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblPurchaseSuppliers" className="table w-100" >
                                <thead>
                                    <tr>
                                        <th className="plus-sort"></th>
                                        <th>Primary</th>
                                        <th>Code</th>
                                        <th>Name</th>                                       
                                        <th>Currency</th>
                                        <th>Reorder NO.</th>
                                        <th>Min. Order</th>
                                        <th>MPQ</th>
                                        <th>Cost</th>
                                        <th>Local Cost</th>
                                        <th className=""><span className="d-md-none">Edit</span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {supplierListState.map((item, index) =>
                                        <tr key={"tblPurchaseSuppliers-" + index}>
                                            <td className="details-control plus-sort"></td>

                                            <td>
                                                <div className="custom-control custom-checkbox ml-2">
                                                    <input type="checkbox" className="custom-control-input" id="Purchase-check1" name="Purchase-check1" checked={item.primaryChecked} disabled />
                                                    <label className="custom-control-label" htmlFor="Purchase-check1"></label>
                                                </div>
                                            </td>
                                            <td>{item.supplierCode}</td>
                                            <td>{item.supplierName}</td>                                            
                                            <td>{item.currencyCode}</td>
                                            <td>{item.reorderNumber}</td>
                                            <td>{item.minimumOrder}</td>
                                            <td><div id={`supplierMPQ-${index}`} className={`SMPQS supplierMPQ-${index}`}>{item.masterPackQuantity}</div></td>
                                            <td><NumberFormat fixedDecimalScale={true} value={item.cost} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><NumberFormat fixedDecimalScale={true} value={item.localCost} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del PurchaseSuppliersGridEditIcon" data={item.itemID}><i className="zmdi zmdi-edit "></i></button></td>
                                        </tr>
                                    )}

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <span className="heading-text"><b>Suppliers Item Cost</b></span>
                    </div>
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblPurchaseSupplierItems" className="table w-100">
                                <thead>
                                    <tr>
                                        <th className="plus-sort"></th>
                                        <th>Suppliers</th>
                                        <th>Currency</th>
                                        <th>UOM</th>
                                        <th>Min. Qty</th>
                                        <th>Starting Date</th>
                                        <th>Ending Date</th>
                                        <th>Cost</th>
                                        <th>Local Cost</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {poC_ItemSupplierList.map((item, index) =>
                                        <tr key={"tblPurchaseSupplierItems-" + index}>
                                            <td className="details-control plus-sort"></td>

                                            <td>{item.supplierCode}</td>
                                            <td>{item.currencyCode}</td>
                                            <td>{item.uomCode}</td>
                                            <td>{item.minQuantity}</td>
                                            <td>{item.startingDate}</td>
                                            <td>{item.endingDate}</td>
                                            <td><NumberFormat fixedDecimalScale={true} value={item.cost} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                            <td><NumberFormat fixedDecimalScale={true} value={item.localCost} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={'$'} /></td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        {/* <! Modal Quantity> */}
			<Modal backdrop="static" isOpen={isOpenQuantityModel} toggle={props.toggleQuantityModel} centered={true}>
				<ModalHeader toggle={props.toggleQuantityModel}>Update MPQ</ModalHeader>
				<ModalBody>

					<div className="row">
						<div className="col-12  container px-4 db-setting-table">
							<table className="table">
								<thead>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">OLD MPQ</span>
										</td>
										<td className="col-6">
											{updateItemParam.oldmpq}
										</td>
									</tr> 
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Update MPQ</span>
										</td>
										<td className="col-6">
											<input type="number" autoFocus id="newmpq" className="floating-label floating-input" placeholder=" " onChange={(e) => props.quantityModelChangeHandler(e)} maxLength="8" min="0" value={updateItemParam.mpq} />
										</td>
									</tr>
								</thead>
							</table>
						</div>




					</div>


				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.onSaveQuantityModel} >Ok</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleQuantityModel}>Cancel</button>

				</ModalFooter>
			</Modal>


        </div>
    )
}