import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet';
import {history} from "../../../history";
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
export default function ManageInventoryComponent(props) {

	const {isRender,isLoading,inventoryItemList,
		inventoryItemListParam,isOpenQuantityModel,updateItemParam,
		userDetails,status,message,categories,departments,suppliers,isCategoryDisabled,isShowManageInventoryDatatable,
		isSupplier,isCategory,isDepartment,isOnHandQty,isDescription,isCode}=props;

	return (
		
		<div>
			<Helmet><title>Manage Inventory| Modus Go!</title></Helmet>
			<div className="sticky-filter-datatable" onClick={props.toggleManageInventoryDatatable}>
				<img className="" src="/assets/images/filter-icon.svg" alt="" />
			</div>
			<div className={`datatable-filter${isShowManageInventoryDatatable? " open":""}`}>
				<div className="close-datalist-popup" onClick={props.toggleManageInventoryDatatable}>
					<i className="zmdi zmdi-close-circle"></i>
				</div>
				<div className="datatable-head-list">
					<div className="datatable-list-title text-darkblue font-weight-bold">
						Manage inventory table
							</div>
					<ul>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCode" name="isCode" onChange={props.setCodeCheckbox} checked={isCode} />
								<label className="custom-control-label font-weight-bold" htmlFor="isCode">CODE</label>
							</div>
						</li>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDescription" name="isDescription" onChange={props.setDescriptionCheckbox} checked={isDescription} />
								<label className="custom-control-label font-weight-bold" htmlFor="isDescription">DESCRIPTION</label>
							</div>
						</li>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOnHandQty" name="isOnHandQty" onChange={props.setOnHandQtyCheckbox} checked={isOnHandQty} />
								<label className="custom-control-label font-weight-bold" htmlFor="isOnHandQty">QTY ON HAND</label>
							</div>
						</li>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDepartment" name="isDepartment" onChange={props.setDepartmentCheckbox} checked={isDepartment} />
								<label className="custom-control-label font-weight-bold" htmlFor="isDepartment">DEPARTMENT</label>
							</div>
						</li>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCategory" name="isCategory" onChange={props.setCategoryCheckbox} checked={isCategory} />
								<label className="custom-control-label font-weight-bold" htmlFor="isCategory">CATEGORY</label>
							</div>
						</li>
						<li>
							<div className="custom-control custom-checkbox">
								<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isSupplier" name="isSupplier" onChange={props.setSupplierCheckbox} checked={isSupplier} />
								<label className="custom-control-label font-weight-bold" htmlFor="isSupplier">SUPPLIER</label>
							</div>
						</li>
					</ul>
				</div>
			</div>

			{isLoading? <Loading />:""}
			{isRender?
				<div onClick={props.toggleHideOrderDatatable}>
					<div className="main-content-area mobile-bottom-space">

						<div className="container-fluid ">
							<div className="row">
								<div className="col position-relative px-0">

									<div className="order-list-wrap py-0 ">
										<h2 className="text-left text-xl-center heading-text d-none d-md-block">Manage inventory</h2>

									</div>


								</div>
							</div>
						</div>

						<div className="container-fluid d-block d-md-none">
							<div className="row">
								<div className="col p-0 mobile-header-info">
									<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
									<p className="text-darkblue font-weight-bold mobile-title-headline">Manage inventory</p>
								</div>
							</div>
						</div>



						<div className="container-fluid mt-3">
							<div className="row">
								<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
							</div>
						</div>
						<div className="container-fluid mt-3">
							<div className="row">

								<div className="col-lg-12 col-md-12 col-sm-12">
									<div className="row bg-white header-shadow py-3 align-items-center">
										<div className="col-lg-12 col-md-12 col-sm-12">
											<div className="row">
												<div className="col-lg-3 col-md-12 col-12 mt-3 ">
													<div className="floating-label mb-0">
														<select className="floating-select" id="departmentId" name="departmentId" onChange={(e) => props.filterChangeHandler(e)} value={inventoryItemListParam.departmentId} >
															<option key="d" value="0">Select department</option>
															{departments.map((departments) => <option key={departments.id} value={departments.id}>{departments.name}</option>)}

														</select>
														<span className="highlight"></span>
														<label >Department</label>
													</div>
												</div>
												<div className="col-lg-3 col-md-12 col-12 mt-3 ">
													<div className="floating-label mb-0">
														<select className="floating-select" id="categoryId" disabled={isCategoryDisabled} name="categoryId" onChange={(e) => props.filterChangeHandler(e)} value={inventoryItemListParam.categoryId}>
															<option key="C" value="0">None</option>
															{categories.map((categories) => <option key={categories.id} value={categories.id}>{categories.name}</option>)}

														</select>
														<span className="highlight"></span>
														<label >Category</label>
													</div>
												</div>
												<div className="col-lg-3 col-md-12 col-12 mt-3 ">
													<div className="floating-label mb-0">
														<select className="floating-select" id="supplierId" name="supplierId" onChange={(e) => props.filterChangeHandler(e)} value={inventoryItemListParam.supplierId}>
															<option key="s" value="0">Select supplier</option>
															{suppliers.map((suppliers) => <option key={suppliers.id} value={suppliers.id}>{suppliers.supplierName}</option>)}

														</select>
														<span className="highlight"></span>
														<label >Supplier</label>
													</div>
												</div>
												<div className="col-lg-1 col-md-2 col-2 mt-3">
													<div className="icon-box icon-box1 rounded-circle left-0 left-15" onKeyPress={(e) => {
														if(e.key==="Enter")
															props.filterHandler();
													}} tabIndex="0" onClick={props.filterHandler}>
														<i className="material-icons">search</i>
													</div>

												</div>

												<div className="col-lg-2 col-md-10 col-10 mt-3">
													<button className="btn btn-primary rounded-pill text-center mr-2" onMouseDown={e => e.preventDefault()} type="button" onClick={props.clearFilterHandler}>Clear filter</button>
												</div>
											</div>
										</div>
									</div>
								</div>

							</div>
						</div>

						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblManageInventory" className="table w-100 new-order-list wo-collasped" >
											<thead>
												<tr>
													<th className="plus-sort"></th>
													<th style={{display: isCode? '':'none'}} className={inventoryItemListParam.sortColumnName==="itemLookupCode"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("itemLookupCode")}>CODE</th>
													<th style={{display: isDescription? '':'none'}} className={inventoryItemListParam.sortColumnName==="description"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("description")}>Description</th>
													<th style={{display: isOnHandQty? '':'none'}} className={inventoryItemListParam.sortColumnName==="qtyOnHand"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("qtyOnHand")}>Qty On Hand</th>
													<th style={{display: isDepartment? '':'none'}} className={inventoryItemListParam.sortColumnName==="department"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("department")}>Department</th>
													<th style={{display: isCategory? '':'none'}} className={inventoryItemListParam.sortColumnName==="category"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("category")}>Category</th>
													<th style={{display: isSupplier? '':'none'}} className={inventoryItemListParam.sortColumnName==="supplier"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("supplier")}>Supplier</th>
													<th className=""><span className="d-md-none">Edit</span></th>
												</tr>
											</thead>
											<tbody>
												{inventoryItemList.map((itemList,index) =>
													<tr key={"tblManageInventory"+index}>
														<td className="details-control plus-sort" tabIndex="-1"></td>
														<td style={{display: isCode? '':'none'}} className="default-cursor">{itemList.itemLookupCode}</td>
														<td style={{display: isDescription? '':'none'}} className="default-cursor">{itemList.description}</td>
														<td style={{display: isOnHandQty? '':'none'}} className="default-cursor">{itemList.qtyOnHand}</td>
														<td style={{display: isDepartment? '':'none'}} className="default-cursor">{itemList.department}</td>
														<td style={{display: isCategory? '':'none'}} className="default-cursor">{itemList.category}</td>
														<td style={{display: isSupplier? '':'none'}} className="default-cursor">{itemList.supplier}</td>
														<td><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del ManageInventoryGridEditIcon" data={itemList.itemID}><i className="zmdi zmdi-edit "></i></button></td>
													</tr>
												)}
											</tbody>
										</table>

									</div>

								</div>

							</div>
						</div>

					</div>
				</div>

				:""}
			{/* <! Modal Quantity> */}
			<Modal backdrop="static" isOpen={isOpenQuantityModel} toggle={props.toggleQuantityModel} centered={true}>
				<ModalHeader toggle={props.toggleQuantityModel}>Update Qty On Hand</ModalHeader>
				<ModalBody>

					<div className="row">
						<div className="col-12  container px-4 db-setting-table">
							<table className="table">
								<thead>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Item code</span>
										</td>
										<td className="col-6">
											{updateItemParam.itemLookupCode}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Item description</span>
										</td>
										<td className="col-6 ">
											{updateItemParam.description}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Qty On Hand</span>
										</td>
										<td className="col-6">
											{updateItemParam.oldQuantity}
										</td>
									</tr>
									<tr className="row">
										<td className="col-6">
											<span className="db-info font-weight-bold">Update QTY</span>
										</td>
										<td className="col-6">
											<input type="number" autoFocus id="newQuantity" className="floating-label floating-input" placeholder=" " onChange={(e) => props.quantityModelChangeHandler(e)} maxLength="8" min="0" value={updateItemParam.quantity} />
										</td>
									</tr>
								</thead>
							</table>
						</div>




					</div>


				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.onSaveQuantityModel} >Ok</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleQuantityModel}>Cancel</button>

				</ModalFooter>
			</Modal>


		</div>
	)
}