window.$=$;
const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
function scanItemDebounce(func, wait, immediate) {
    var timeout;

    return function executedFunction() {
        var context = this;
        var args = arguments;

        var later = function () {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };

        var callNow = immediate && !timeout;

        clearTimeout(timeout);

        timeout = setTimeout(later, wait);

        if (callNow) func.apply(context, args);
    };
};
class NewScanItemContainer extends React.Component {
    state = {
        itemCode: '',
        quantity: 1,
        showPrice: false,
        price: 0,
        taxId: 0,
        itemNotFound: false,
        showDialogue: false,
        isDisabled: false,
        isCheckItemCodeCall: false,
        showPriceField: false,
        isSuccessDialogue: false,
        isItemChecked: false,
        comboBoxSelection: '',
        isOpenProductModel: false,
        isSupplierFound: true,
        selectedProduct: "",
        status: "",
        message: "",
        IsMPQSelected:false,
        priceScanMustBeEntered: false,
        isOpenCodeModel: false,
        addLookupCodeParam: {
            lookupCode: ""
        },
        isScannerUse: false,
        Pagination: {
            pageNo: 1,
            pageSize: 5,
            sortColumnName: "",
            sortDirection: "",
            searchText: ""
        },
        isOpenModel: false,
        description:"",

        formFields: {
            supplier:""
          },
        oldFormFields: {
        supplier:"",
        isSupplier:false,
        },

    }
    static getDerivedStateFromProps(props, state) {
        return {
            quantity: props['mpqQuantity'] === 0 ? 1 : props['mpqQuantity'],
            showPrice: !props.itemExistPayload ? true : false,
            itemNotFound: !props.itemExistPayload ? true : false,
            isItemChecked: props.isItemChecked,
            isSupplierFound: props.isSupplierFound,
            isSuccessDialogue: props.isSuccessDialogue,
            status: props.status,
            message: props.message,
            priceScanMustBeEntered: props.priceScanMustBeEntered
        }
    }
    
    constructor(props) {
        super(props)
        this.handleScan = this.handleScan.bind(this);
    }
    componentDidMount() {
       
        this.setState({ isOpenModel: false }, () => document.getElementById("itemCode").focus());
        this.props.getTaxList();
        this.props.setpriceScanMustBeEntered(false);
        if (this.state.isScannerUse)
            this.setState({ isDisabled: true });
        this.clearScanMessageIntervalID = setInterval(async () => {
            this.props.clearMessage();
        }, 30 * 1000);
    }
    componentWillUnmount() {
        clearInterval(this.clearScanMessageIntervalID);
        $('#tblLookupCodeList').DataTable().destroy();
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.isSuccessDialogue !== prevProps.isSuccessDialogue) {
            if (this.props.isFromPO)
                this.props.getPurchaseOrderItemList();
            else if (this.props.isFromWO)
                this.props.getWorkOrderItemList();
            else
                this.props.getOrderItemList();
            this.setState({
                itemCode: '',
                //comboBoxSelection: '',
                itemNotFound: false,

            })
            this.props.setQuantity(1)
        }
    }
    handleKeyDown = (e) => {
        //console.log("key :" + e.key);
        // if (this.state.isScannerUse) {         
        // if ((e.key.toLowerCase() === "enter" || e.key.toLowerCase() === "tab") && !this.state.isDisabled) {
        //     this.acceptHandler();

        // }
        if ((e.key.toLowerCase() === "enter") && !this.state.isDisabled) {
            if(this.props.isAskedQuantity)
            {
                document.getElementById("quantity").focus();
                document.getElementById("quantity").select();
    
            }
            else 
            {
                this.acceptHandler();

            }

        }
        //}
    }
    handleQuantityKeyDown = (e) => {              
        if ((e.key.toLowerCase() === "enter") && !this.state.isDisabled) {
            this.acceptHandler();
        }
    }
    handlePaste = () => {
       // console.log("Paste :");
        // if (this.state.isScannerUse) { 
        setTimeout(() => {
            if(this.props.isAskedQuantity)
            {
                document.getElementById("quantity").focus();
                document.getElementById("quantity").select();
    
            }
            else 
            {
                this.acceptHandler();

            }

            //this.acceptHandler();
           
        }, 200);
        //}
    }
    handlechange = (e) => {
       
       // console.log("handleChange :");
      
        const { value } = e.target;
        this.setState({
            itemCode: value.trim(""),
            isItemChecked: false,
            isCheckItemCodeCall: false,
            status: "",
            message: "",
            showPriceField: false
        })
       

        this.props.setSuccessDialogue(false, "", "");
       
        
    }

    getComboBoxValue = e => {    
        const { value } = e.target;
        this.setState({
            comboBoxSelection: value
        })
      
        if (value === 'supplier_mpq') {
            this.setState({IsMPQSelected:true,isSupplier:true});
            this.props.getMPQValue(this.state.itemCode);
        } else {
            this.setState({IsMPQSelected:false,isSupplier:false});
            this.props.resetMPQValue();
        }
        
    }

    setQuantity = (e) => {
        const { value } = e.target;
        if ((value > -1 || value === '')  && value.length<=5 ) {
            this.setState({
                quantity: value,
            })

            if (isNaN(parseInt(value))) {
                this.props.setQuantity(value)
            } else {
                this.props.setQuantity(parseInt(value))
            }
        }
    }

    checkItemCode = (e) => {
        try {
            if(!userDetails.isHQConnected)
            {
            const { value } = e.target;
            if (value) {
                this.setState({ showDialogue: true });
                var poId = 0;
                if (this.props.isFromPO) {
                    const { location } = this.props
                    var id = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
                    poId = parseInt(id);
                }

                let param = {
                    poId: poId,
                    itemLookupCode: value
                }
                const result = this.props.isItemExist(param).then(() => {
                    if (this.state.comboBoxSelection === 'supplier_mpq') {
                        const resultQuantity = this.props.getMPQValue(value).then(() => {
                            if (this.props.isFromWO) {
                                var optionParam = {
                                    itemLookupCode: value
                                };
                                const priceresult = this.props.getItemOptionsByLookupCode(optionParam).then(() => {
                                    var itemAlredyExist = this.props.WorkOrderItemList.filter(element => element.code === value);

                                    if (this.props.priceScanMustBeEntered) {
                                        if (this.props.products.length > 0) {
                                            this.openProductModel();

                                        }
                                        else {
                                            if (itemAlredyExist.length > 0) {
                                                this.setState({ isCheckItemCodeCall: true, priceScanMustBeEntered: false });
                                                this.props.setpriceScanMustBeEntered(false);
                                            }
                                            else
                                                this.setState({ showPriceField: true, showPrice: true, isDisabled: true });
                                        }
                                    }
                                    else {
                                        if (this.props.products.length > 0) {
                                            this.openProductModel();

                                        }
                                        else {
                                            if (this.props.itemExistPayload)
                                                this.setState({ isCheckItemCodeCall: true });
                                        }
                                    }
                                });

                            }
                            else {
                                if (this.props.products.length > 0) {
                                    this.openProductModel();

                                }
                                else {
                                    if (this.props.itemExistPayload)
                                        this.setState({ isCheckItemCodeCall: true });
                                }

                            }

                        });
                    }
                    else {
                        if (this.props.isFromWO) {
                            var optionParam = {
                                itemLookupCode: value
                            };
                            const priceresult1 = this.props.getItemOptionsByLookupCode(optionParam).then(() => {
                                var itemAlredyExist1 = this.props.WorkOrderItemList.filter(element => element.code === value);

                                if (this.props.priceScanMustBeEntered) {
                                    if (this.props.products.length > 0) {
                                        this.openProductModel();

                                    }
                                    else {
                                        if (itemAlredyExist1.length > 0) {

                                            this.setState({ isCheckItemCodeCall: true, priceScanMustBeEntered: false });
                                            this.props.setpriceScanMustBeEntered(false);
                                        }
                                        else
                                            this.setState({ showPriceField: true, showPrice: true, isDisabled: true });
                                    }
                                }
                                else {
                                    if (this.props.products.length > 0) {
                                        this.openProductModel();

                                    }
                                    else {
                                        if (this.props.itemExistPayload)
                                            this.setState({ isCheckItemCodeCall: true });
                                    }
                                }
                            });
                        }
                        else {
                            if (this.props.products.length > 0) {
                                this.openProductModel();

                            }
                            else {

                                if (this.props.itemExistPayload)
                                {
                                    if(this.props.isAskedQuantity)
                                    {
                                        document.getElementById("quantity").focus();
                                        this.setState({ isCheckItemCodeCall: true });
                            
                                    }
                                    else 
                                    {
                                        this.setState({ isCheckItemCodeCall: true });

                                    }
                                }

                                
                            }
                        }
                    }
                });

                return result;


            }

           

        }
        else
        {
            this.setState({itemCode: '' })
         this.props.setSuccessDialogue(true, "failed", "Item not found!");
        }
    }
        catch (error) {
            console.log(error);
        }
    }
    //#region acceptHandler
    acceptHandler = () => {
        if(!userDetails.isHQConnected)
        {

       
            if (this.state.itemCode) {
                if (!this.state.isCheckItemCodeCall) {
                    let target = { target: { value: this.state.itemCode } };
                    const result = this.checkItemCode(target).then((result) => {
    
                        setTimeout(() => {
                            if (this.state.isCheckItemCodeCall)
                                this.accept();
                        }, 200);
    
                    });
                }
                else {
                    this.accept();
                }
            }
        }
        else{
            this.setState({itemCode: '' })
            this.props.setSuccessDialogue(true, "failed", "Item not found!");
        }
        
        document.getElementById("itemCode").focus();
        
    }
    accept = () => {
       
        if (!this.state.isSupplierFound && this.props.isFromPO) {
            swal({
                title: "Are you sure?",
                text: "The selected supplier does not carry item " + this.state.itemCode + ". Continue?",
                icon: "warning",
                buttons: {
                    confirm: true,
                    cancel: true
                },
                dangerMode: true,
            })
                .then((willDelete) => {
                    if (willDelete) {
                        this.setState({ isCheckItemCodeCall: false, selectedProduct: "" ,isOpenModel:false});
                        if (this.props.isFromPO)
                            this.insertPOItem();
                        else if (this.props.isFromWO)
                            this.insertWOItem();
                        else
                            this.saveItemOrder();
                            
                    }
                    else {
                        this.setState({
                            itemCode: '',
                            // comboBoxSelection: '',
                            isSupplierFound: true,
                            isCheckItemCodeCall: false,
                            selectedProduct: "",
                            price: '',
                            taxId: 0,
                            showPrice: false,
                            showPriceField: false,
                            description:'',
                            isOpenModel:false,
                            isSupplier:false

                        });
                        let formFields={...this.state.formFields};
                        formFields["supplier"]="";
                        this.setState({
                          formFields
                        });
                
                        this.props.setQuantity(1);

                    }
                  
                });

        }
        else {
            this.setState({ isCheckItemCodeCall: false, selectedProduct: "",isOpenModel:false });
            if (this.props.isFromPO)
                this.insertPOItem();
            else if (this.props.isFromWO)
                this.insertWOItem();
            else
                this.saveItemOrder();
        }

    }
    //#endregion acceptHandler
    saveItemOrder = () => {
        if (this.state.itemCode) {
            var poId = 0;
            if (this.props.isFromPO) {
                const { location } = this.props
                var poid = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
                poId = parseInt(poid);
            }
            const { location } = this.props
            const id = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1)
            const data = {
                orderId: parseInt(id),
                userId: parseInt(userDetails.userId),
                itemLookupCode: this.state.itemCode,
                quantity: parseInt(this.state.quantity),
                price: parseFloat(this.state.price) || 0,
                taxId: this.state.taxId,
                description:this.state.description,
                mpq: parseInt(this.state.quantity),
                supplierID:this.state.formFields.supplierID,
            }
            this.props.insertOrder(poId, data, this.state.isItemChecked, this.props.products);
        }

        if (this.state.isItemChecked) {
            this.props.setQuantity(1);
            this.setState({
                itemCode: '',
               // comboBoxSelection: '',
                showPriceField: false,
                isCheckItemCodeCall: false,
            })
        }

    }
    //#region insert po item
    insertPOItem = () => {

        if (this.state.itemCode) {
            var poId = 0;
           
            
            if (this.props.isFromPO) {
                const { location } = this.props
                var poid = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
                poId = parseInt(poid);
            }
            const { location } = this.props
            const id = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1)
            const data = {
                podOrderID: parseInt(id),
                itemLookupCode: this.state.itemCode,
                quantity: this.state.quantity,
                price: parseFloat(this.state.price) || 0,
                taxId: this.state.taxId,
                IsMPQSelected:this.props.purchaseOrder.supplierID!=0?true:this.state.IsMPQSelected,
                description: this.state.description,
                supplierID: this.props.purchaseOrder.supplierID
            }

            this.props.insertPOItem(poId, data, this.state.isItemChecked, this.props.products);
        }

        if (this.state.isItemChecked) {
            this.props.setQuantity(1);
            this.setState({
                itemCode: '',
               // comboBoxSelection: '',
                showPriceField: false,
                isCheckItemCodeCall: false,
            })
        }

    }
    //#endregion insert po item
    //#region insert wo item
    insertWOItem = () => {

        if (this.state.itemCode) {
            var poId = 0;
            if (this.props.isFromPO) {
                const { location } = this.props
                var poid = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
                poId = parseInt(poid);
            }
            const { location } = this.props
            const id = decryptURL(this.props.match.params.orderId);//location.pathname.substring(location.pathname.lastIndexOf('/') + 1)
            const data = {
                woId: parseInt(id),
                itemLookupCode: this.state.itemCode,
                quantity: this.state.quantity,
                price: parseFloat(this.state.price) || 0,
                description: this.state.description
            }

            this.props.insertWOItem(poId, data, this.state.isItemChecked, this.props.products);
        }

        if (this.state.isItemChecked) {
            this.props.setQuantity(1);
            this.setState({
                itemCode: '',
                //comboBoxSelection: '',
                showPriceField: false,
                isCheckItemCodeCall: false,
            })
        }

    }
    //#endregion insert wo item

    setPrice = (e) => {
        var value = e.target.value;
        value = value.replace(/,/g, "");
        if (value.length > e.target.maxLength) {
            value = value.substring(0, e.target.maxLength);
        }
        this.setState({ price: value })
        if ((value > 0 && this.state.taxId > 0) || (value > 0 && this.state.priceScanMustBeEntered)) {
            this.setState({
                isDisabled: false,
                isCheckItemCodeCall: true
            })
        } else {
            this.setState({
                isDisabled: true,
                isCheckItemCodeCall: false
            })
        }
    }
    setTax = (e) => {
        var value = e.target.value;
        value = parseInt(value);
        this.setState({ taxId: value });
        if (value > 0 && this.state.price > 0) {
            this.setState({
                isDisabled: false,
                isCheckItemCodeCall: true
            })
        } else {
            this.setState({
                isDisabled: true,
                isCheckItemCodeCall: false
            })
        }
    }

    setDescription = (e) => {
         const { value } = e.target;
         this.setState({
             description: value.trim("")
         })
     }

    createNewEntry = (e) => {
        if(this.state.comboBoxSelection=="")
        {
            this.setState({ isSupplier:false })
        }
        let formFields={...this.state.formFields};
        formFields["supplier"]="";
        this.setState({
          formFields
        });
        this.setState({
            isOpenModel: true,
            showPriceField: true,
            showDialogue: false,
            isDisabled: true,
            price: 0,
            taxId: 0,
            isItemChecked: true,
            isSupplierFound: true,
            isCheckItemCodeCall: true,
        }, () => {
            setTimeout(() => {
                if (!this.state.showPriceField) {
                    document.getElementById("itemCode").blur();
                }
                if (!!document.getElementById("scanPopupPrice"))
                    document.getElementById("scanPopupPrice").focus();
                    document.getElementById("scanPopupPrice").select();
            }, 500)
        });
        //e.preventDefault();
    }

    closeInnerDialogue = () => {
        this.setState({
            showDialogue: false,
            showPriceField: false,
            isDisabled: false,
            itemCode: '',
            itemNotFound: false,
            isItemChecked: false,
            isCheckItemCodeCall: false
            // comboBoxSelection: '',
        });
        this.props.setQuantity(1);
        this.props.setSuccessDialogue(false, "", "");
    }

    closeDialogue = () => {
        this.setState({
            comboBoxSelection: '',
            priceScanMustBeEntered: false,
            showPriceField: false,
            showPrice: false,
            isDisabled:false,
            isSupplier:false
        });
        this.props.closeScanItem();
        this.props.setQuantity(1);
        let formFields={...this.state.formFields};
        formFields["supplier"]="";
        this.setState({
          formFields
        });
        this.props.setSuccessDialogue(false, "", "");
        this.toggleOpenModel();
    }

    clearItemCode = () => {
        this.setState({
            itemCode: '',
            // comboBoxSelection: '',
            showDialogue: false,
            itemNotFound: false,
            isCheckItemCodeCall: false

        });
        this.props.setQuantity(1);
        this.props.setSuccessDialogue(false, "", "");
    }
    handleScan(data) {
        data = data.trim();
        if (data !== "") {
            //console.log("scandata:" + data);
            this.setState({
                itemCode: data,
                isCheckItemCodeCall: false,
                isItemChecked: false,
                status: "",
                message: ""
            }, () => { this.acceptHandler() });
            this.props.setSuccessDialogue(false, "", "");
        }
    }

    handleScanError = (err) => {
        console.error(err)
    }
    //#region toggleProductModel
    openProductModel = () => {
        this.setState({
            isOpenProductModel: true, itemCode: '',
            // comboBoxSelection: '',
            selectedProduct: ''
        });
        this.props.setQuantity(1);
    }
    toggleProductModel = () => {
        if (this.state.isOpenProductModel) {
            //if (this.state.selectedProduct === "") {
            this.setState({
                itemCode: '',
                // comboBoxSelection: '',
                selectedProduct: '',
            });
            this.props.setQuantity(1);

            // }

        }
        this.setState({ isOpenProductModel: !this.state.isOpenProductModel });
    }
    //#endregion toggleProductModel
    //#region  productCheckBoxChangeHandler
    productCheckBoxChangeHandler = (e) => {
        try {
            var isChecked = e.target.checked;
            var cbs = document.getElementsByClassName("productCHK");
            for (var i = 0; i < cbs.length; i++) {
                cbs[i].checked = false;
            }
            if (isChecked) {
                document.getElementById(e.target.name).checked = true;
                this.setState({ selectedProduct: e.target.name });
            }
            else
                this.setState({ selectedProduct: "" });


        } catch (error) {
            console.log(error);
        }
    }
    //#endregion productCheckBoxChangeHandler
    //#region ProductHandler
    ProductHandler = () => {
        if (this.state.selectedProduct !== "") {
            this.setState({ itemCode: this.state.selectedProduct, isCheckItemCodeCall: true, isOpenProductModel: false });

        }
        else
            swal({
                title: "Error",
                text: "Please select the product.",
                icon: "error",
                button: "Ok",
            })

    }
    //#endregion ProductHandler
    //#region toggle code model
    toggleCodeModel = () => {
        let addLookupCodeParam = {
            lookupCode: ""
        };
        this.setState({ isOpenCodeModel: !this.state.isOpenCodeModel, addLookupCodeParam });
    }
    //#endregion codek order model
    //#region open code model
    openCodeModel = () => {
        let Pagination = { ...this.state.Pagination };
        Pagination["searchText"] = "";
        this.setState({
            itemCode: '',
            Pagination
        }, () => { this.props.getLookupCodeList(this.state.Pagination, true); });
        this.props.setlookupCodeSearchValue("");
        this.props.setlookupCodeSearchData([]);
        this.props.setQuantity(1);

        this.toggleCodeModel();

    }
    //#endregion open code model
    //#region  code check box handler
    codeCheckBoxHandler = (e, id) => {
        try {
            var isChecked = e.target.checked;
            var cbs = document.getElementsByClassName("lookupCodeCHK");
            for (var i = 0; i < cbs.length; i++) {
                cbs[i].checked = false;
            }
            if (isChecked) {
                document.getElementById(e.target.name).checked = true;
                let addLookupCodeParam = {
                    lookupCode: id
                };
                this.setState({ addLookupCodeParam });
            }
            else {
                let addLookupCodeParam = {
                    lookupCode: ""
                };
                this.setState({ addLookupCodeParam });
            }

        } catch (error) {
            console.log(error);
        }
    }
    //#endregion  code check box handler
    //#region addLookupCode
    addLookupCode = () => {
        try {
            if (this.state.addLookupCodeParam.lookupCode === "") {
                swal({
                    title: "Error",
                    text: "Please select code.",
                    icon: "error",
                    button: "Ok",
                })
            }
            else {
                this.setState({ itemCode: this.state.addLookupCodeParam.lookupCode }, () => this.toggleCodeModel());

            }
        } catch (error) {
            console.log(error);
        }

    }
    //#endregion addLookupCode
    //#region onScannerCHKChange
    onScannerCHKChange = (e) => {
        if (e.target.checked) {
            this.setState({ isDisabled: true });
            // if (this.state.itemCode.length <= 2) {
            //     this.closeInnerDialogue();
            // }


        }
        else
            this.setState({ isDisabled: false });
        this.setState({ isScannerUse: e.target.checked }, () => { document.getElementById("itemCode").focus(); });
    }
    //#endregion onScannerCHKChange
    //#region search handler
    lookupCodeSearchHandler = (record, cb) => {
        try {
            let getdata;
            record = record.trim();
            if (record !== "" || this.state.Pagination.searchText !== "") {

                let Pagination = { ...this.state.Pagination };
                Pagination["searchText"] = record;
                this.setState({ Pagination }, () => {
                    this.props.getLookupCodeList(this.state.Pagination, true);
                });
            }
            if (record !== "") {
                let searchRequestParameter = {
                    searchText: record,
                    orderId: parseInt(decryptURL(this.props.match.params.orderId))
                };
                this.props.getSearchList(searchRequestParameter);
            }
            else {
                getdata = [];
                this.props.setlookupCodeSearchData(getdata);
            }
        } catch (error) {
            console.log(error);
        }
    }
    //#endregion search handler

    //#region search change handler
    onLookupCodeSearchChangeHandler = (value) => {
        try {
            this.props.setlookupCodeSearchValue(value);
            this.setLookupCodeSearchTerm(value, (items) => {
                this.props.setlookupCodeSearchData(items);
            })
        } catch (error) {
            console.log(error);
        }
    }
    setLookupCodeSearchTerm = scanItemDebounce((searchTerm, cb) => {
        try {
            this.props.setLookupCodeSearchTerm(searchTerm);
            this.lookupCodeSearchHandler(searchTerm, cb);
        } catch (error) {
            console.log(error);
        }
    }, 1000)
    //#endregion search change handler 
    //#region search checkbox handler
    onLookupCodeSearchCheckBoxHandler = (value) => {
        try {
            this.props.setlookupCodeSearchValue(value);
            this.lookupCodeSearchHandler(value);
        } catch (error) {
            console.log(error);
        }
    }
    //#endregion search checkbox handler
    toggleOpenModel = () => {
        this.setState({ isOpenModel: !this.state.isOpenModel });
    }
    //#region pagination click events
    handlePageClick = data => {
        let Pagination = { ...this.state.Pagination };
        let pageno = data.selected;
        Pagination["pageNo"] = pageno + 1;
        this.setState({ Pagination }, () => {
            this.props.getLookupCodeList(this.state.Pagination, true);
        });
    };
    pageSizeChangeHandler = (e) => {
        let Pagination = { ...this.state.Pagination };
        Pagination["pageSize"] = parseInt(e.target.value);
        this.setState({ Pagination }, () => {
            this.props.getLookupCodeList(this.state.Pagination, true);
        });
    };
    //#endregion pagination click events
    createNewEntryKeyPressHandler = (e) => {
        if (e.key === "Enter")
            this.createNewEntry();
    }
    closeInnerDialogueKeyPressHandler = (e) => {
        if (e.key === "Enter")
            this.closeInnerDialogue();
    }
    clearItemCodeKeyPressHandler = (e) => {
        if (e.key === "Enter")
            this.clearItemCode();
    }
   

    inputChangeHandlerSupplier=(e) => {
        let formFields={...this.state.formFields};
        formFields[e.target.name]=e.target.value;
        this.setState({
          formFields
        });
      }
    toggleSupplierModel=() => {
        let data={
          pageNo: 0,
          pageSize: 0,
          searchText: ""
        }
        this.props.GetsupplierList(data);
        this.setState({isOpenSupplierModal: !this.state.isOpenSupplierModal});
      }

      supplierCheckBoxHandler=(e,supp) => {
        var isChecked=e.target.checked;
        var cbs=document.getElementsByClassName("supplierCHK");
        for(var i=0;i<cbs.length;i++) {
          cbs[i].checked=false;
        }
        if(isChecked) {
          document.getElementById(e.target.name).checked=true;
          this.setState({
            supplierInfo: supp
          })
        }
      }

      supplierModalSave=() => {
            this.setState({
                ...this.state,
                formFields: {
                ...this.state.formFields,
                supplierID: this.state.supplierInfo.id,
                supplier: this.state.supplierInfo.code,
                supplierName: this.state.supplierInfo.supplierName,
                },
                isOpenSupplierModal: false
            });
            
      }
      closeItemScan = () => {
        this.setState({
           itemCode:"", showDialogue: false
        });
        this.props.setQuantity(1);
    }
    render() {

        const { taxId, isScannerUse, isOpenCodeModel, priceScanMustBeEntered, isSupplierFound, showPrice, price, itemNotFound, showDialogue, isDisabled, showPriceField, isSuccessDialogue, isItemChecked, comboBoxSelection, status, message } = this.state;
        if (isSuccessDialogue) {
            // swal({
            //     title: 'Success',
            //     text: 'Success',
            //     icon: 'Success'.toLowerCase(),
            //     timer: 'Success'.toLowerCase() !== "error" ? 3000 : 0,
            //     button: 'Success'.toLowerCase() !== "error" ? false : "Ok",
            // })
            this.props.setSuccessDialogue(false, status, message);
        }

        return (
            <NewScanItemComponent
                handlechange={this.handlechange}
                handleKeyDown={this.handleKeyDown}
                handleQuantityKeyDown={this.handleQuantityKeyDown}
                handlePaste={this.handlePaste}
                itemCode={this.state.itemCode}
                checkItemCode={this.checkItemCode}
                getComboBoxValue={this.getComboBoxValue}
                quantity={this.state.quantity}
                setQuantity={this.setQuantity}
                saveItemOrder={this.saveItemOrder}
                closeDialogue={this.closeDialogue}
                showPrice={showPrice}
                setPrice={this.setPrice}
                price={price}
                setTax={this.setTax}
                taxId={taxId}
                itemNotFound={itemNotFound}
                userRole={userDetails.role}
                createNewEntry={this.createNewEntry}
                showDialogue={showDialogue}
                isDisabled={isDisabled}
                closeInnerDialogue={this.closeInnerDialogue}
                showPriceField={showPriceField}
                clearItemCode={this.clearItemCode}
                isItemChecked={isItemChecked}
                handleScanError={this.handleScanError}
                handleScan={this.handleScan}
                comboBoxSelection={comboBoxSelection}
                insertPOItem={this.insertPOItem}
                insertWOItem={this.insertWOItem}
                isFromPO={this.props.isFromPO}
                isFromWO={this.props.isFromWO}
                products={this.props.products}
                isOpenProductModel={this.state.isOpenProductModel}
                toggleProductModel={this.toggleProductModel}
                ProductHandler={this.ProductHandler}
                productCheckBoxChangeHandler={this.productCheckBoxChangeHandler}
                acceptHandler={this.acceptHandler}
                status={status}
                message={message}
                priceScanMustBeEntered={priceScanMustBeEntered}
                isOpenCodeModel={isOpenCodeModel}
                codeList={this.props.codeList}
                toggleCodeModel={this.toggleCodeModel}
                openCodeModel={this.openCodeModel}
                codeCheckBoxHandler={this.codeCheckBoxHandler}
                addLookupCode={this.addLookupCode}
                isScannerUse={isScannerUse}
                onScannerCHKChange={this.onScannerCHKChange}
                taxList={this.props.taxList}
                lookupCodeSearchData={this.props.lookupCodeSearchData}
                lookupCodeSearchValue={this.props.lookupCodeSearchValue}
                onLookupCodeSearchChangeHandler={this.onLookupCodeSearchChangeHandler}
                onLookupCodeSearchCheckBoxHandler={this.onLookupCodeSearchCheckBoxHandler}
                isOpenModel={this.state.isOpenModel}
                toggleOpenModel={this.toggleOpenModel}
                Pagination={this.state.Pagination}
                pageSizeChangeHandler={this.pageSizeChangeHandler}
                handlePageClick={this.handlePageClick}
                totalRecord={this.props.totalRecord}
                totalPage={this.props.totalPage}
                isLoading={this.props.isLoading}
                createNewEntryKeyPressHandler={this.createNewEntryKeyPressHandler}
                closeInnerDialogueKeyPressHandler={this.closeInnerDialogueKeyPressHandler}
                clearItemCodeKeyPressHandler={this.clearItemCodeKeyPressHandler}
                setDescription={this.setDescription}
                isAskedQuantity={this.props.isAskedQuantity}
                onSearchCheckBoxHandler={this.props.onSearchCheckBoxHandler}
                supplierList={this.props.supplierList}
                toggleSupplierModel={this.toggleSupplierModel}
                isOpenSupplierModal={this.state.isOpenSupplierModal}
                supplierCheckBoxHandler={this.supplierCheckBoxHandler}
                inputChangeHandlerSupplier={this.inputChangeHandlerSupplier}
                formFields={this.state.formFields}
                oldFormFields={this.state.oldFormFields}
                supplierModalSave={this.supplierModalSave}
                isSupplier={this.state.isSupplier}
                isFromOrder={this.props.isFromOrder}
                closeItemScan={this.closeItemScan}
            />
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        isItemExist: (data) => dispatch(isItemExist(data)),
        getMPQValue: (data) => dispatch(getMPQValue(data)),
        resetMPQValue: () => dispatch(resetMPQValue()),
        insertOrder: (poId, data, isItemChecked, products) => dispatch(insertOrder(poId, data, isItemChecked, products)),
        insertPOItem: (poId, data, isItemChecked, products) => dispatch(insertPOItem(poId, data, isItemChecked, products)),
        insertWOItem: (poId, data, isItemChecked, products) => dispatch(insertWOItem(poId, data, isItemChecked, products)),
        setSuccessDialogue: (data, status, message) => dispatch(setSuccessDialogue(data, status, message)),
        setQuantity: (data) => dispatch(setQuantity(data)),
        getItemOptionsByLookupCode: (data) => dispatch(getItemOptionsByLookupCode(data)),
        setpriceScanMustBeEntered: (data) => dispatch(setpriceScanMustBeEntered(data)),
        getLookupCodeList: (data, isLoading) => dispatch(getLookupCodeList(data, isLoading)),
        clearMessage: () => dispatch(clearMessage()),
        getTaxList: () => dispatch(getTaxList()),
        setlookupCodeSearchValue: (data) => dispatch(setlookupCodeSearchValue(data)),
        getSearchList: (data) => dispatch(getSearchList(data)),
        setlookupCodeSearchData: (data) => dispatch(setlookupCodeSearchData(data)),
        setLookupCodeSearchTerm: (data) => dispatch(setLookupCodeSearchTerm(data)),
        GetsupplierList: (data) => dispatch(GetsupplierList(data)),
    };
};

const mapStateToProps = (state) => {
   
    const { isLoading, totalRecord, totalPage, lookupCodeSearchValue, lookupCodeSearchData, SearchTerm, taxList, codeList, itemExistPayload, mpqQuantity, isSuccessDialogue, isItemChecked, products, isSupplierFound, status, message, priceScanMustBeEntered,
        supplierList } = state.NewScanItemReducer;
    return {
        itemExistPayload: itemExistPayload,
        products: products.values !== null ? products.values : [],
        mpqQuantity, isSuccessDialogue, isItemChecked, isSupplierFound, status, message, priceScanMustBeEntered,
        codeList: codeList.values.length > 0 ? codeList.values : [],
        taxList: taxList.values.length > 0 ? taxList.values : [],
        lookupCodeSearchValue: lookupCodeSearchValue,
        lookupCodeSearchData: lookupCodeSearchData.values.length > 0 ? lookupCodeSearchData.values : [],
        SearchTerm: SearchTerm,
        totalRecord, totalPage, isLoading,
        supplierList: supplierList,

    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NewScanItemContainer));