import React from 'react';
import InventoryListComponent from '../component/InventoryListComponent';
import {
  getInventoryList, setRequestParameter, deleteInventory,
  createInventory, inventoryLogin, setisOpenLogin, combineInventory
} from '../state/actions/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import { encryptURL } from '../../../Custom'
import scrolldebounce from "lodash.debounce";
import { history } from '../../../history';
import swal from 'sweetalert';
const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
class InventoryListContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isRender: false,
      formFields: {
        password: "",
        description:"",
      },
      AllInventoryCount:0,
      iscreateNewInventory: false
    }
    this.formHandler = this.formHandler.bind(this);
  }

  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    this.setState({ AllInventoryCount: this.props.AllInventoryCount})
    
  }
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);

  }
  componentDidUpdate(prevProps, prevState) {
    if (this.props.isFinal) {
      
      //history.push("/FinalInventory");
    }   
  }

  


  //#region check user role validation 
  checkUserRoleValidation = () => {
    try {
      const userDetails = Cookies.get('userDetails') !== undefined ? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)) : "";
      if (userDetails.role !== process.env.REACT_APP_UserRole_Manager && userDetails.role !== process.env.REACT_APP_UserRole_Cashier) {

        this.props.history.push("/NotFound");
      }
      else {
        let requestParameter = { ...this.props.requestParameter };
        requestParameter["userId"] = parseInt(userDetails.userId);
        requestParameter["userRole"] = userDetails.role;
        this.props.setRequestParameter(requestParameter);

        if (userDetails.role !== process.env.REACT_APP_UserRole_Manager) {
          if (Cookies.get('InventoryUser') !== undefined) {
            
            this.props.setisOpenLogin(false);
            this.props.getInventoryList(requestParameter, true).then(()=>{
              setTimeout(() => {this.setState({ isRender: true })}, 500);
            });
          }
          else {
            this.props.setisOpenLogin(true);
            this.setState({ isRender: true }, () => setTimeout(() => {
              if (!!document.getElementById("password")) document.getElementById("password").focus()
            }, 500));

          }
        }
        else {
          // this.setState({ isRender: true });
          this.props.getInventoryList(requestParameter, true).then(()=>{
            setTimeout(() => {this.setState({ isRender: true })}, 500);
          });
        }
      }

    } catch (error) {
      console.log(error);
    }
  }
  //#endregion check user role validation

  //#region scroll event to on demand loading
  handleScroll = scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt = (window.innerHeight + document.documentElement.scrollTop).toFixed(0);
      var offsetHeight = (document.documentElement.offsetHeight).toFixed(0);
      if (scrollHeigt === offsetHeight || (parseInt(scrollHeigt)+1).toString() === offsetHeight || (parseInt(scrollHeigt)-1).toString() === offsetHeight) {
        let pagesize = this.props.requestParameter.pageSize + 50;
        let requestParameter = { ...this.props.requestParameter };
        requestParameter["pageSize"] = pagesize;
        this.props.setRequestParameter(requestParameter);
        this.props.getInventoryList(requestParameter, false);
      }
    } catch (error) {
      console.log(error);

    }
  }, 0);

  //#endregion scroll event to on demand loading
  //#region on row click handler
  onRowClickHandler = (id) => {
    window.open("/InventoryDetail/" + encryptURL(id), '_blank');
  }
  //#endregion on row click handler
  //#region create Inventory
  createInventoryHandler = (isSave) => {

    if (this.state.formFields.description === "" && isSave) {
      swal({
        title: "Error",
        text: "Please enter description",
        icon: "error",
        button: "Ok",
      })
      return;
    }
    else
    {
      let  createInventoryRequestParameter = {
        userId: parseInt(userDetails.userId),
        description: isSave ? this.state.formFields.description : ""
      }
      this.props.createInventory(createInventoryRequestParameter);

    }
  }
  //#endregion create Inventory
  //#region combine Inventory
  combineInventoryHandler = (IsSingleOrMultiple="M",AllInventoryCount) => {
    if(!this.props.isInventoryended)
    {
      swal({
        text: "Please end inventory for all items!!",
        icon: "warning",
      });
    }
    else
    {
        if(AllInventoryCount > 0)
        {
        swal({
          title: "Are you sure?",
          text: IsSingleOrMultiple=="M"?"Do you want to combine inventory?":"Do you want create final inventory?",
          icon: "warning",
          buttons: {
            confirm: true,
            cancel: true
          },
          dangerMode: true,
        })
          .then((willDelete) => {
            if (willDelete) {
              let combineInventoryRequestParameter = {
                userId: parseInt(userDetails.userId),
                inventoryId:parseInt(this.props.InventoryList[0].inventoryId)
              }
              this.props.combineInventory(combineInventoryRequestParameter);
            }
          });
        }
        else {
          swal({
            text: "No item find inside any inventory!!",
            icon: "warning",
          });
        }
    }
    
    
  }
  //#endregion combine Inventory
  //#region delete Inventory
  deleteInventoryHandler = (id) => {
    try {

      swal({
        title: "Are you sure?",
        text: "Once deleted, you will not be able to recover this data!",
        icon: "warning",
        buttons: {
          confirm: true,
          cancel: true
        },
        dangerMode: true,
      })
        .then((willDelete) => {
          if (willDelete) {
            let deleteRequestParameter = {
              inventoryId: parseInt(id)
            };

            this.props.deleteInventory(deleteRequestParameter, this.props.requestParameter);

          }
        });
    } catch (error) {
      console.log(error);
    }
  }
  //#region all inpute onchange method   
  inputChangeHandler= (e) => {
    try {
      var value = e.target.value;
      let formFields = { ...this.state.formFields };
      formFields[e.target.name] = value;
      this.setState({
        formFields
      });
    } catch (error) {
      console.log(error);

    }
  }
  //#endregion all inpute onchange method
  //#region Post method for login on login button click 
  formHandler = (data) => {
    this.props.inventoryLogin({ ...this.state.formFields, ...data }, this.props.requestParameter);
  }

  //#endregion Post method for login on login button click 
toggleLoginModel=()=>{
  this.props.setisOpenLogin(!this.props.isOpenLogin);
  this.setState({isRender:false})
}

toggleModel = () => this.setState({ iscreateNewInventory: !this.state.iscreateNewInventory, description: "" }, () => {
  setTimeout(() => {
    if (!!document.getElementById("description"))
      document.getElementById("description").focus();
  }, 500)
});
createNewInventoryHandler = () => {
  this.setState({
    iscreateNewInventory: true,
  })
}

  render() {
    // const { searchData } = this.props;   
    return (
      <InventoryListComponent
        {...this.props}
        onRowClickHandler={this.onRowClickHandler}
        deleteInventoryHandler={this.deleteInventoryHandler}
        createInventoryHandler={this.createInventoryHandler}
        combineInventoryHandler={this.combineInventoryHandler}
        userDetails={userDetails}
        isRender={this.state.isRender}
        formFields={this.state.formFields}
        inputChangeHandler={this.inputChangeHandler}
        formHandler={this.formHandler}
        toggleLoginModel={this.toggleLoginModel}
        createNewInventoryHandler={this.createNewInventoryHandler}
        iscreateNewInventory={this.state.iscreateNewInventory}
        toggleModel={this.toggleModel}
      />
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getInventoryList: (data, isLoading) => dispatch(getInventoryList(data, isLoading)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    deleteInventory: (deleteeRequestParameter, requestParameter) => dispatch(deleteInventory(deleteeRequestParameter, requestParameter)),
    createInventory: (createInventoryRequestParameter) => dispatch(createInventory(createInventoryRequestParameter)),
    combineInventory: (combineInventoryRequestParameter) => dispatch(combineInventory(combineInventoryRequestParameter)),
    inventoryLogin: (data, requestParameter) => dispatch(inventoryLogin(data, requestParameter)),
    setisOpenLogin: (data) => dispatch(setisOpenLogin(data))
  };
};

const mapStateToProps = (state) => {
  const { InventoryList, requestParameter, isLoading, isFinal, isCombine, isOpenLogin,AllInventoryCount,isInventoryended } = state.InventoryListReducer;
  return {
    requestParameter: requestParameter,
    InventoryList: InventoryList.values,
    isLoading: isLoading,
    isFinal, isCombine, isOpenLogin,AllInventoryCount,isInventoryended
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(InventoryListContainer);