import React from 'react';
import ManageItemHistoryComponent from '../component/manageItemHistoryComponent';
import {
  getManageItemHistoryList,setManageItemHistoryListParam,
  clearMessage,getCheckItemHistoryList,GetKeyValuePair
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {history} from '../../../history'
import {encryptURL,decryptURL} from '../../../Custom'
window.$=$;
function handleManageItemHistoryGridDownloadIcon(classdata) {
  $("#tblManageItemHistory").on('click','.ManageItemHistoryGridDownloadIcon',function() {
  
    var dataString=$(this).attr('data');
    classdata.fileDownloadHandler(dataString);
  });
}
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
class manageItemHistoryContainer extends React.Component {

  state={
    isInventoryScanItem: false,
    isRender: false,
    isShowManageItemHistoryDatatable: false,
    isUserName:true,
    isDate:true,
    isFile:true,
    isSetUserName:true,
    isSetDate:true,
    isSetFile:true,
    timer:1

  }

  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.getManageItemHistoryList()
    handleManageItemHistoryGridDownloadIcon(this);
    ColumnFilterCheckbox();
    this.succeessMsg();

    window.addEventListener('scroll',this.handleScroll);
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }

  succeessMsg=()=>
  {
    clearInterval(this.Interval)
    this.Interval=setInterval(() => { 
      let timer=this.state.timer;
      if(timer > 0){
        this.props.GetKeyValuePair(true);
        this.setState({
          timer: this.state.timer - 1
        })
      }else{
        clearInterval(this.Interval)}
    },10000)
  }
  //#region check user role validation 
   checkUserRoleValidation=() => {
      try {
        const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
        if(userDetails.role!==process.env.REACT_APP_UserRole_Admin) {
          this.props.history.push("/NotFound");
        }
      } catch(error) {
        console.log(error);
      }
    }
  //#endregion check user role validation
  getManageItemHistoryList=() => {
   
    let ManageItemHistoryListParam={...this.props.ManageItemHistoryListParam};
    ManageItemHistoryListParam["userRole"]=userDetails.role;
    this.props.setManageItemHistoryListParam(ManageItemHistoryListParam);
    this.props.getManageItemHistoryList(ManageItemHistoryListParam,false);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblManageItemHistory').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        if(this.props.totalRecord!==this.props.ManageItemHistoryList.length) {
          let ManageItemHistoryListParam={...this.props.ManageItemHistoryListParam};
          let pagesize=this.props.ManageItemHistoryListParam.pageSize+50;
          ManageItemHistoryListParam["pageSize"]=pagesize;
          this.props.setManageItemHistoryListParam(ManageItemHistoryListParam);
          this.props.getManageItemHistoryList(ManageItemHistoryListParam,false);
        }
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading
  fileDownloadHandler=(file) => {
  
    window.location.href=file;
  }
  toggleManageItemHistoryDatatable=() => {this.setState({isShowManageItemHistoryDatatable: !this.state.isShowManageItemHistoryDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowManageItemHistoryDatatable: false})}

  //#region filter checkbox onchange function
  setUserNameCheckbox = () => {
    this.setState({ isUserName: !this.state.isUserName }, () => {
    this.setState({ isSetUserName: this.state.isUserName });
    });
  }

  setDateCheckbox = () => {
    this.setState({ isDate: !this.state.isDate }, () => {
    this.setState({ isSetDate: this.state.isDate });
    });
  }

  setFileCheckbox = () => {
    this.setState({ isFile: !this.state.isFile }, () => {
    this.setState({ isSetFile: this.state.isFile });
    });
  }

  //#endregion filter checkbox onchange function
  CheckHistory = () => {
  
    let ManageItemHistoryListParam={...this.props.ManageItemHistoryListParam};
    ManageItemHistoryListParam["userRole"]=userDetails.role;
    this.props.setManageItemHistoryListParam(ManageItemHistoryListParam);
    this.props.getCheckItemHistoryList(ManageItemHistoryListParam,true);
    this.setState({
      timer: 1
    })
    this.succeessMsg()
  }
  
  render() {
    //  const {ManageItemHistoryList}=this.props   
    return (
      <ManageItemHistoryComponent
        {...this.props}
        userDetails={userDetails}
        getManageItemHistoryList={this.getManageItemHistoryList}
        isRender={this.state.isRender}
        fileDownloadHandler={this.fileDownloadHandler}
        toggleManageItemHistoryDatatable={this.toggleManageItemHistoryDatatable}
        isShowManageItemHistoryDatatable={this.state.isShowManageItemHistoryDatatable}
        setUserNameCheckbox={this.setUserNameCheckbox}
        setDateCheckbox={this.setDateCheckbox}
        setFileCheckbox={this.setFileCheckbox}
        isUserName={this.state.isUserName}
        isDate={this.state.isDate}
        isFile={this.state.isFile}
        isSetUserName={this.state.isSetUserName}
        isSetDate={this.state.isSetDate}
        isSetFile={this.state.isSetFile}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
        CheckHistory={this.CheckHistory}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getManageItemHistoryList: (data,isLoading) => dispatch(getManageItemHistoryList(data,isLoading)),
    setManageItemHistoryListParam: (data) => dispatch(setManageItemHistoryListParam(data)),
    clearMessage: () => dispatch(clearMessage()),
    getCheckItemHistoryList: (data,isLoading) => dispatch(getCheckItemHistoryList(data,isLoading)),
    GetKeyValuePair:(isLoading)=>dispatch(GetKeyValuePair(isLoading)),

  };
};

const mapStateToProps=(state) => {
  const {isLoading,ManageItemHistoryList,ManageItemHistoryListParam,
    totalRecord,status,message,responseStatusProgress,responseStatusComplete}=state.manageItemHistoryReducer;
  return {
    ManageItemHistoryList: ManageItemHistoryList.values.length>0? ManageItemHistoryList.values:[],
    ManageItemHistoryListParam: ManageItemHistoryListParam,
    isLoading: isLoading,
    totalRecord: totalRecord,status,message,responseStatusProgress,responseStatusComplete


  };
};

export default connect(mapStateToProps,mapDispatchToProps)(manageItemHistoryContainer);