//#region to get order item list
export function gettransferOutItemList(data, isLoading,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) {
  
    return (dispatch) => {

        dispatch({
            type: types.GET_TRANSFEROUTITEM_REQUEST,
            isLoading: isLoading,
        });
        let url = 'order/POItemsList';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tbltransferOutItem').DataTable().destroy();
                    dispatch({
                        type: types.GET_TRANSFEROUTITEM_SUCCESS,
                        data: response.payload,
                    });
                    $('#tbltransferOutItem').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        "initComplete": function( settings, json ) {
                           
                            if(!LookupCode)
                            {
                                $(".itemLookupCode").hide()
                            }
                            if(!SerialNumber)
                            {
                                $(".serialNumber").hide()
                            }
                           
                            if(!QtyOnHand)
                            {
                                $(".itemQtyOnHand").hide()
                            }
                            if(!Description)
                            {
                                $(".description").hide()
                            }

                            if(!Cost)
                            {
                                $(".cost").hide()
                            }
                            if(!Price)
                            {
                                $(".price").hide()
                            }
                           
                            if(!OrderNumber)
                            {
                                $(".orderNumber").hide()
                            }
                            if(!LastWeekQty)
                            {
                                $(".lastWeekQuantity").hide()
                            }
                            if(!LastMonthQty)
                            {
                                $(".lastMonthQuantity").hide()
                            }
                            if(!QtyOnOrder)
                            {
                                $(".qtyOnOrder").hide()
                            }
                            else
                            {
                                $(".qtyOnOrder").show()
                            }
                            if(!ExtendedCost)
                            {
                                $(".extendedCost").hide()
                            }
                            if(!ProfitMargin)
                            {
                                $(".profitMargin").hide()
                            }
                            else
                            {
                                $(".profitMargin").show()

                            }
                           
                          },
                    });

                } else {
                    dispatch({
                        type: types.GET_TRANSFEROUTITEM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get order item list
