//#region delete order item
export function deleteOrderItem(deleteRequestParameter, requestParameter,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) {
    return (dispatch) => {
        dispatch({
            type: types.DELETE_TRANSFEROUTITEM_REQUEST,
        });
        let url = 'order/DeletePoItem';
        return post(url, deleteRequestParameter)
            .then((response) => {

                const { payload } = response;
              
                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(gettransferOutItemList(requestParameter, true,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin));
                    dispatch({
                        type: types.DELETE_TRANSFEROUTITEM_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.DELETE_TRANSFEROUTITEM_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion delete order item
