//#region complete po handler
export function completeTO(param) {
    return (dispatch) => {
        dispatch({
            type: types.COMPLETE_TO_REQUEST,
        });
        let url = 'order/CompletePO';
        return post(url, param)
            .then((response) => {
                const { payload } = response;
              

                if (response.status === 200) {
                    /* Logic */

                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        button: "Ok",
                    }).then(() => {
                        if (payload.status.toLowerCase() !== "error")
                            history.push("/TransferOut");
                    });

                    dispatch({
                        type: types.COMPLETE_PO_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.COMPLETE_PO_FAILED,
                        data: response.payload,
                    });

                }

            })
    }
}
//#endregion complete po handler

//#region get PO order  detail
export function getOrderDetail(orderId) {
    return (dispatch) => {
        dispatch({
            type: types.GET_POORDERDETAIL_REQUEST,

        });
        let url = 'order/getPurchaseOrderById/' + orderId;
        return get(url)
            .then((response) => {
                const { payload } = response;
              

                if (response.status === 200) {
                    if (payload.code === "E08" || payload.code === "E03")
                        history.push("/transferOut");
                    dispatch({
                        type: types.GET_POORDERDETAIL_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.GET_POORDERDETAIL_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get PO order detail

