export function gettransferOutById(purchaseId) {
    return (dispatch) => {
        dispatch({
            type: types.GET_TRANSFEROUTBYID_REQUEST,
        });
        let url = `order/getpurchaseOrderById/${purchaseId}`;

        return get(url)
            .then((response) => {
                const { payload } = response;
              
                // console.log('response', response)
                if (response.status === 200) {
                    dispatch({
                        type: types.GET_TRANSFEROUTBYID_SUCCESS,
                        data: response.payload.values,
                    });
                    dispatch(getDeliveryStatus())
                    dispatch(getInvoiceStatus())
                    dispatch(getInvoiceDiscount())
                    dispatch(getOrderStatus())
                    dispatch(getSupplierInfo(response.payload.values.supplierID))
                    dispatch(getPurchesInfo(response.payload.values.purchaserID))
                    dispatch(getTermInfo(response.payload.values.payTermID))
                    // dispatch(getShippingMethodInfo(response.payload.values.supplierID))
                    dispatch(POGetsupplierList({
                        "searchText": "",
                        "pageNo": 0,
                        "pageSize": 0

                    }))
                    dispatch(POTermList({
                        "searchText": "",
                        "pageNo": 0,
                        "pageSize": 0
                    }))
                    dispatch(TOGetCustomerList({
                        "searchText": "",
                        "pageNo": 0,
                        "pageSize": 0
                    }))
                    dispatch(getPOPurchaserList({
                        "searchText": "",
                        "pageNo": 0,
                        "pageSize": 0
                    }))
                    dispatch(getPOShippingList({
                        "searchText": "",
                        "pageNo": 0,
                        "pageSize": 0
                    }))

                    // if (response.payload.values && response.payload.values.locationCode &&
                    //     response.payload.values.locationCode.length) {
                    //     dispatch(getCustomerByAccNo(response.payload.values.locationCode))
                    // }

                    if (response.payload.values && response.payload.values.locationType == 1) {
                        dispatch(getCustomerByAccNo(response.payload.values.locationID))
                    }
                } else {
                    dispatch({
                        type: types.GET_TRANSFEROUTBYID_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

