export function updatetransferOutItem(data, click) {
    return (dispatch) => {
        let url = 'order/updatePurchaseOrder';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    // dispatch({
                    //     type: types.PO_UPDATE_SUCCESS,
                    //     data: response.payload.values,
                    // });
                    const { payload } = response;
                    if (click === 'save') {
                        swal({
                            title: payload.status,
                            text: payload.message,
                            icon: payload.status.toLowerCase(),
                            timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                            button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                        })
                    }
                }
            })
    }
}
