export function updateSerialNumber(data, itemListParam,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_SERIALNUMBER_REQUEST,
        });
        let url = 'order/UpdatePoItemSrNo';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(gettransferOutItemList(itemListParam, true,SerialNumber,LookupCode,Description,Cost,Price,OrderNumber,QtyOnHand,LastWeekQty,LastMonthQty,QtyOnOrder,ExtendedCost,ProfitMargin));
                    dispatch({
                        type: types.UPDATE_SERIALNUMBER_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_SERIALNUMBER_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
