import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {Multiselect} from 'multiselect-react-dropdown';
import {history} from "../../../history";
import Autocomplete from 'react-autocomplete';
import NumberFormat from 'react-number-format';
import Td from '../../../Table/Td'
export default function UpdateBulkItemComponent(props) {
	const {isLoading,searchData,isCategoryDisabled,filterField,onCheckboxChange,departments,itemDataValue,categories,itemListParam,updatemessage,updatestatus,searchValue,fieldName,formField,checkFormField,isCategoryDisabledDiv,sizeInputOnchange,dummy,
		allcategories,selectStoreHandler,storelist,removeStoreHandler,SearchItemLookupCode,itemtype,setPricingPrice,valueInputOnchange,searchPlaceholder,itemFilterColumnList,UpdateValue,isShowUBIDatatable,suppliers,isItemDetail,onSetAllCheckboxChange,SetFilter,isUpdateItemData,ItemTax,itemCodeInputOnchange}=props;
	
		var styleOn = {
			backgroundColor: '#ccc'
		};


	return (

		<div>
			<Helmet>
				<title>Update Bulk Item | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
			<div className="sticky-filter-datatable" onClick={props.toggleUBIDatatable}>
					<img className="" src="/assets/images/filter-icon.svg" alt="" />
				</div>
				<div className={`datatable-filter${isShowUBIDatatable? " open":""}`}>
					<div className="close-datalist-popup" onClick={props.toggleHideOrderDatatable}>
						<i className="zmdi zmdi-close-circle"></i>
					</div>
					<div className="datatable-head-list">
						<div className="datatable-list-title text-darkblue font-weight-bold">
							Item table
					</div>
						<ul>
						<li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isName" name="isName" onChange={props.setNameCheckbox} checked={filterField.isSetName} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isName">NAME</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCode" name="isCode" onChange={props.setCodeCheckbox} checked={filterField.isSetCode} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isCode">CODE</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isType" name="isType" onChange={props.setTypeCheckbox} checked={filterField.isSetType} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isType">TYPE</label>
                </div>
              </li>

              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDepartment" name="isDepartment" onChange={props.setDepartmentCheckbox} checked={filterField.isSetDepartment} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isDepartment">DEPARTMENT</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCategory" name="isCategory" onChange={props.setCategoryCheckbox} checked={filterField.isSetCategory} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isCategory">CATEGORY</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isAvailable" name="isAvailable" onChange={props.setAvailableCheckbox} checked={filterField.isSetAvailable} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isAvailable">AVAILABLE</label>
                </div>
              </li>
              <li>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isPrice" name="isPrice" onChange={props.setPriceCheckbox} checked={filterField.isSetPrice} />
                  <label className="custom-control-label font-weight-bold" htmlFor="isPrice">PRICE</label>
                </div>
              </li>


						</ul>
					</div>
				</div>
					<div className="main-content-area mobile-bottom-space">
					    <div className="container-fluid d-none d-md-block">

							<div className="row">
						    	<div className="col position-relative px-0">
								<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={(e) => UpdateValue(e)} >Update Value</button>

									<h2 className="text-left text-xl-center heading-text">Update Bulk Item</h2>

								</div>
							</div>

						</div>
					
						<div className={!isUpdateItemData?"container-fluid mt-3":"d-none container-fluid mt-3"}>
						<div className="row">
							<div className="col-lg-12 col-md-12 col-sm-12">
								<div className="row bg-white header-shadow py-3 align-items-center">
							
									<div className="col-lg-12 col-md-12 col-sm-12">
										
										<div className="row">
											<div className="col-lg-3 col-md-12 col-12 mt-3">
												<div className="floating-label mb-0">
													<Multiselect className="floating-select"
														options={departments}
														displayValue="name"
														selectedValues={itemListParam.departmentIds}
														onSelect={props.selectDepartmentHandler}
														onRemove={props.removeDepartmentHandler}
														placeholder="Select department"
														name="departmentIds"
														id="departmentIds"
														avoidHighlightFirstOption={true}
														showCheckbox={true}
														hidePlaceholder={true}
														closeOnSelect={false}
													/>

													<span className="highlight"></span>
													<label >Department</label>
												</div>
											</div>
											<div className="col-lg-3 col-md-12 col-12 mt-3">
												<div className="floating-label mb-0">
													<Multiselect className="floating-select"
														options={categories}
														displayValue="name"
														selectedValues={itemListParam.categoryIds}
														onSelect={props.selectCategoryHandler}
														onRemove={props.removeCategoryHandler}
														placeholder="None"
														name="categoryIds"
														id="categoryIds"
														avoidHighlightFirstOption={true}
														showCheckbox={true}
														hidePlaceholder={true}
														closeOnSelect={false}
														disable={isCategoryDisabled}
													/>
													<span className="highlight"></span>
													<label >Category</label>
												</div>
											</div>
											<div className="col-lg-3 col-md-12 col-12 mt-3">
												<div className="floating-label mb-0">
													<Multiselect className="floating-select"
														options={suppliers}
														displayValue="supplierName"
														selectedValues={itemListParam.supplierIds}
														onSelect={props.selectSupplierHandler}
														onRemove={props.removeSupplierHandler}
														placeholder="Select supplier"
														name="supplierIds"
														id="supplierIds"
														avoidHighlightFirstOption={true}
														showCheckbox={true}
														hidePlaceholder={true}
														closeOnSelect={false}
													/>
													<span className="highlight"></span>
													<label >Supplier</label>
												</div>
											</div>
											<div className="col-lg-3 col-md-12 col-12 mt-3">
												<div className="floating-label w-100">
													<input className="floating-input" placeholder=" " value={formField.size} rows="1" id="size" name="size" onKeyDown={(e) => sizeInputOnchange(e)}></input>
													<span className="highlight"></span>
													<label >Size</label>
												</div>	
											</div>
										</div>
										<div className="row">
										<div className="col-lg-6 col-md-12 col-12 mt-3">

											<div className="floating-label mb-0">
												<Multiselect className="floating-select"
												options={storelist}
												displayValue="code"
												//	selectedValues={this.state.selectedStore.storeIds}
												onSelect={selectStoreHandler}
												onRemove={removeStoreHandler}
												placeholder="Select Store"
												name="storeIds"
												id="storeIds"
												avoidHighlightFirstOption={true}
												showCheckbox={true}
												hidePlaceholder={true}
												closeOnSelect={false}
												/>
												<span className="highlight"></span>
												<label >Store</label>
											</div>
										</div>
										</div>
									</div>
										
								</div>
							
							</div>

						</div>
					
					</div>
					<div className={isUpdateItemData?"container-fluid mt-3":"d-none container-fluid mt-3"}>
						<div className="row">
							<div className="col-lg-12 col-md-12 col-sm-12">
								<div className="row bg-white header-shadow py-3 align-items-center">
									<div className="container pt-2 ml-1 mb-2">
										<div className="row">
											<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
										</div>
									</div>
								    <div className="col-lg-12 col-md-12 col-sm-12">
								       <div className="row">
									    	<div className="col-md-4 col-12">
												<input type="text" className="floating-input" placeholder="Item Lookup Code"  id="itemLookupCode" name="itemLookupCode" onChange={(e) => itemCodeInputOnchange(e)} value={SearchItemLookupCode}/>
														
												<div className="icon-box rounded-circle" type="button" onClick={() => props.searchItemCode(SearchItemLookupCode)} >
													<i className="material-icons">search</i>
												</div>
											</div>
										</div>
									</div>
									<div className="col-lg-12 col-md-12 col-sm-12 mt-3">
									

									<div className="row">
										<div className="col-md-4 col-12">
											<div className="d-flex align-items-center">
												<div className="custom-control custom-checkbox">
													<input type="checkbox" className="custom-control-input" id="description" name="description" onChange={onSetAllCheckboxChange} value={formField.description}   checked={checkFormField.description}/>
													<label className="custom-control-label font-weight-bold" htmlFor="description"></label>
													</div>
													<div className="floating-label w-100">
														<textarea className="floating-input" placeholder=" " rows="1" id="description" name="description" value={formField.description} onChange={(e) => valueInputOnchange(e)}></textarea>
														<span className="highlight"></span>
														<label >Description</label>
													</div>	
											</div>
											
										</div>
										<div className="col-md-4 col-12">
											<div className="d-flex align-items-center">
												<div className="custom-control custom-checkbox">
													<input type="checkbox" className="custom-control-input" id="weight" name="weight" onChange={onSetAllCheckboxChange} value={formField.weight}  checked={checkFormField.weight} />
													<label className="custom-control-label font-weight-bold" htmlFor="weight"></label>
													</div>
													<div className="floating-label w-100">
													<input  className="floating-input" type="number" step=".01" maxLength="8" placeholder=" " value={formField.weight} onChange={(e) => valueInputOnchange(e)} id="weight" name="weight"/>
														<span className="highlight"></span>
														<label >Weight</label>
													</div>	
											</div>
											
										</div>
										<div className="col-md-4 col-12">
											<div className="d-flex align-items-center">
												<div className="custom-control custom-checkbox">
													<input type="checkbox" className="custom-control-input" id="departmentId" onChange={onSetAllCheckboxChange} checked={checkFormField.departmentId} value={formField.departmentId} name="departmentId"  />
													<label className="custom-control-label font-weight-bold" htmlFor="departmentId"></label>
													</div>
													<div className="floating-label w-100">
														<select className="floating-select" tabIndex="9" id="departmentId" name="departmentId" onChange={(e) => valueInputOnchange(e)} defaultValue={formField.departmentId} value={formField.departmentId}>
															<option key="D" value="">Select department</option>
															{departments.map((departments) => <option key={departments.id} value={departments.id} >{departments.name}</option>)}
														</select>
														<span className="highlight"></span>
														<label>Departments</label>
													</div>	
											</div>
											
										</div>
										</div>
										<div className="row">
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="notes"  name="notes" onChange={onSetAllCheckboxChange} value={formField.notes} checked={checkFormField.notes} />
														<label className="custom-control-label font-weight-bold" htmlFor="notes"></label>
														</div>
														<div className="floating-label w-100">
															<textarea className="floating-input" placeholder=" " value={formField.notes} rows="1" id="notes" name="notes" onChange={(e) => valueInputOnchange(e)}></textarea>
															<span className="highlight"></span>
															<label >Notes</label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="categoryID" name="categoryID" value={formField.categoryID} onChange={onSetAllCheckboxChange} checked={checkFormField.categoryID} />
														<label className="custom-control-label font-weight-bold" htmlFor="categoryID"></label>
														</div>
														<div className="floating-label w-100">
															<select className="floating-select" tabIndex="10"  id="categoryID" name="categoryID" defaultValue={formField.categoryID} onChange={(e) => valueInputOnchange(e)} value={formField.categoryID}>
															<option key="0" value="">Select Category</option>
															{allcategories.length<=0? <option key="C" value="">None</option>:''}
															{allcategories.map((allcategories) => <option key={allcategories.id} value={allcategories.id}>{allcategories.name}</option>)}
															</select>
															<span className="highlight"></span>
															<label>Categories</label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="itemType" name="itemType" value={formField.itemType} onChange={onSetAllCheckboxChange}  checked={checkFormField.itemType}/>
														<label className="custom-control-label font-weight-bold" htmlFor="itemType"></label>
														</div>
														<div className="floating-label w-100">
														<select className="floating-select" tabIndex="2" id="itemType" name="itemType" defaultValue={formField.itemType} value={formField.itemType} onChange={(e) => valueInputOnchange(e)}>
															<option key="I" value="">Select item type</option> 
															{itemtype.map((itemtypes) => <option key={itemtypes.id} value={itemtypes.id}>{itemtypes.itemTypeName}</option>)}
															</select>
															<span className="highlight"></span>
															<label>Item type</label>
														</div>	
												</div>
											</div>
										</div>
										<div className="row">
										   <div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="taxId" name="taxId" onChange={onSetAllCheckboxChange} value={formField.taxId} checked={checkFormField.taxId}/>
														<label className="custom-control-label font-weight-bold" htmlFor="taxId"></label>
														</div>
														<div className="floating-label w-100">
														<select className="floating-select" tabIndex="2" id="taxId" name="taxId" onChange={(e) => valueInputOnchange(e)} defaultValue={formField.taxId} value={formField.taxId}>
															<option key="I" value="">Select Tax</option> 
															{ItemTax.map((ItemTax) => <option key={ItemTax.id} value={ItemTax.id}>{ItemTax.name}</option>)}
															</select>
															<span className="highlight"></span>
															<label>Tax</label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="cost" name="cost" onChange={onSetAllCheckboxChange} value={formField.cost} checked={checkFormField.cost}/>
														<label className="custom-control-label font-weight-bold" htmlFor="cost"></label>
														</div>
														<div className="floating-label w-100">
															<span className="dolar">{!!formField.cost&&"$"}</span>
															<NumberFormat  thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemCost" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="4" maxLength="11" min="0" id="cost" name="cost" onChange={(e) => valueInputOnchange(e)} value={formField.cost} />
															<span className="highlight"></span>
															<label>Cost </label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="price" name="price" onChange={onSetAllCheckboxChange} value={formField.price} checked={checkFormField.price}/>
														<label className="custom-control-label font-weight-bold" htmlFor="price"></label>
														</div>
														<div className="floating-label w-100">
															<span className="dolar">{!!formField.price&&"$"}</span>
															<NumberFormat  thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="price" name="price" onChange={(e) => valueInputOnchange(e)} value={formField.price} />
															<span className="highlight"></span>
															<label>Price</label>
														</div>	
												</div>
											</div>

										</div>
										<div className="row">
									    	<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" disabled={true} className="custom-control-input" id="isGM" name="isGM"  />
														<label className="custom-control-label font-weight-bold" htmlFor="isGM"></label>
														</div>
														<div className="floating-label w-100">
														<NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="5" min="0" value={formField.pricingProfitMargin} id="pricingProfitMargin" name="pricingProfitMargin" onBlur={(e) => setPricingPrice(e)} onChange={(e) => valueInputOnchange(e)}  />
															<span className="highlight"></span>
															<label>GM %</label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="masterPackQuantity" name="masterPackQuantity" onChange={onSetAllCheckboxChange} value={formField.masterPackQuantity} checked={checkFormField.masterPackQuantity}/>
														<label className="custom-control-label font-weight-bold" htmlFor="masterPackQuantity"></label>
														</div>
														<div className="floating-label w-100">
														<input  className="floating-input" type="number" step=".01" maxLength="8" placeholder=" " id="masterPackQuantity" name="masterPackQuantity"  value={formField.masterPackQuantity} onChange={(e) => valueInputOnchange(e)}/>
														<span className="highlight"></span>
														<label>MPQ</label>
														</div>	
												</div>
											</div>
											<div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="supplierName" name="supplierName"  onChange={onSetAllCheckboxChange} checked={checkFormField.supplierName} value={formField.supplierName}/>
														<label className="custom-control-label font-weight-bold" htmlFor="supplierName"></label>
														</div>
														<div className="floating-label w-100">
														<input  className="floating-input" type="text" id="supplierName" name="supplierName"  value={formField.supplierName} onChange={(e) => valueInputOnchange(e)}/>
														<span className="highlight"></span>
														<label>Supplier(Prime)</label>
														</div>	
												</div>
											</div>
										</div>
										<div className="row">
										    <div className="col-md-4 col-12">
												<div className="d-flex align-items-center">
													<div className="custom-control custom-checkbox">
														<input type="checkbox" className="custom-control-input" id="reorderNumber" name="reorderNumber"  onChange={onSetAllCheckboxChange} checked={checkFormField.reorderNumber} value={formField.reorderNumber}/>
														<label className="custom-control-label font-weight-bold" htmlFor="reorderNumber"></label>
														</div>
														<div className="floating-label w-100">
														<input  className="floating-input" type="text" id="reorderNumber" name="reorderNumber"  value={formField.reorderNumber} onChange={(e) => valueInputOnchange(e)}/>
														<span className="highlight"></span>
														<label>Reorder #</label>
														</div>	
												</div>
											</div>
										</div>
										<div className="row">
											<div className="itemscanbottom mt-2">
												<button className="btn rounded-pill btn-primary mr-1"  type="submit"  onClick={props.SetAll}>Set All</button>
												<button type="button" className="btn rounded-pill btn-primary mr-1 closeitemscan-wrap" onClick={props.cancelSetAll}>Cancel</button>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					{isItemDetail && !isUpdateItemData?
					<div className="container-fluid mt-3">
										<div className="row">
											<div className="col p-0">
												<div className="searchbox-wrap header-shadow">
												<Autocomplete
													inputProps={{id: 'states-autocomplete'}}
													wrapperStyle={{position: 'relative'}}
													value={searchValue}
													items={searchData}
													getItemValue={(item) => item.key}
													inputProps={{placeholder: searchPlaceholder}}
													onSelect={record => props.onSearchChangeHandler(record,"select")}
														onChange={(event,value) => props.onSearchChangeHandler(value,"onchange")}
														renderMenu={children => (
															<div className="search-menu">
																{children}
															</div>
														)}
														renderItem={(item,isHighlighted) => (
															<div
																key={item.key}
															>{item.value}</div>
														)}
												/>


												<div className="icon-box rounded-circle" type="button"  onClick={props.searchFilter} >
													<i className="material-icons">search</i>
												</div>

												</div>
											</div>
										</div>
									</div>
									:""}
									{isItemDetail ?
									<div className="container-fluid bg-white header-shadow mt-4">
										
										<div className="row my-3">
										<div className="col-12 pt-2">
											<span className="heading-text"><b>Item records</b></span>
										</div>
										<div className="col-12 p-0">
											<div className="table-responsive">

											<table id="tblItemList" className="table nowrap w-100" >
												<thead>
												<tr>
												<th className="plus-sort"></th>
												<th className="d-none">Select</th>

												<th style={{display: filterField.isSetName? '':'none'}} className={itemDataValue.sortColumnName==="description"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("description")}>NAME</th>

												<th style={{display: filterField.isSetCode? '':'none'}} className={itemDataValue.sortColumnName==="code"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("code")}>CODE</th>

												<th style={{display: filterField.isSetType? '':'none'}} className={itemDataValue.sortColumnName==="Type"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("Type")}>TYPE</th>

												<th style={{display: filterField.isSetDepartment? '':'none'}} className={itemDataValue.sortColumnName==="department"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("department")}>DEPARTMENT</th>

												<th style={{display: filterField.isSetCategory? '':'none'}} className={itemDataValue.sortColumnName==="category"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("category")}>CATEGORY</th>

												<th style={{display: filterField.isSetAvailable? '':'none'}} className={itemDataValue.sortColumnName==="availableQuantity"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("availableQuantity")}>AVAILABLE</th>

												<th style={{display: filterField.isSetPrice? '':'none'}} className={itemDataValue.sortColumnName==="price"? itemDataValue.sortDirection==="asc"? "sort-top":"sort-bottom":"sort-both"} onClick={() => props.sortHandler("price")}>PRICE</th>

													
												</tr>
												</thead>
												<tbody>

												{itemDataValue.map((rowdata,index) =>

													<tr key={rowdata.itemId} style={rowdata.IsSelected ? styleOn : null}>
													<td className="details-control plus-sort"></td>
													
													<td className="d-none"><input id={rowdata.itemId} type="checkbox"  value={rowdata.IsSelected} defaultChecked={rowdata.IsSelected} />
													</td>
													<Td  value={rowdata.description} rowIndex={rowdata.itemId} cellPropName="description"  type="text" style={{display: filterField.isSetName? '':'none'}} setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="description")} changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)} redirectItemEditComponent={(itemId) => props.redirectItemEditComponent(rowdata.itemId)}></Td>

													<Td  style={{display: filterField.isSetCode? '':'none'}} value={rowdata.code}  type="text" setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="code")} rowIndex={rowdata.itemId} cellPropName="itemLookupCode"  changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)}  redirectItemEditComponent={(itemId) => props.redirectItemEditComponent(rowdata.itemId)}></Td>

													<Td style={{display: filterField.isSetType? '':'none'}} value={rowdata.itemTypeName} type="select" setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="itemTypeName")} rowIndex={rowdata.itemId} optionList={itemtype} valueName="itemTypeName" cellPropName="itemtype" onChange={(e) => valueInputOnchange(e)} changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)} /*onClick={() => props.redirectItemEditComponent(rowdata.itemId)}*/></Td>
													<Td style={{display: filterField.isSetDepartment? '':'none'}} value={rowdata.departmentName}   type="select" rowIndex={rowdata.itemId} setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="departmentName")} optionList={departments} valueName="name" cellPropName="departmentId" onChange={(e) => valueInputOnchange(e)} changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)} 
													 /*  onClick={() => props.redirectItemEditComponent(rowdata.itemId)}*/></Td>
													<Td style={{display: filterField.isSetCategory? '':'none'}} value={rowdata.categoryName} type="select" rowIndex={rowdata.itemId} optionList={allcategories} setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="categoryName")} valueName="name" cellPropName="categoryID" onChange={(e) => valueInputOnchange(e)} changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)}   /*onClick={() => props.redirectItemEditComponent(rowdata.itemId)}*/></Td>
													<td style={{display: filterField.isSetAvailable? '':'none'}} onClick={() => props.redirectItemEditComponent(rowdata.itemId)}>{rowdata.availableQuantity}</td>
													<Td style={{display: filterField.isSetPrice? '':'none'}}  type="text" value={rowdata.price} rowIndex={rowdata.itemId} cellPropName="price" setViewAll={(setmodevalue)=>props.setViewAll(setmodevalue="price")}  changeEvent={(rowIndex, cellPropName, value) => props.changeValue(rowIndex, cellPropName, value)} redirectItemEditComponent={(itemId) => props.redirectItemEditComponent(rowdata.itemId)}>{rowdata.price}</Td>
													</tr>
												)}
												</tbody>
											</table>
											</div>
										</div>
										</div>
									</div>:
									<div className="bg-white header-shadow mt-3 p-3 mb-3">
										<div class="row">
											{itemFilterColumnList.map((data) =>
												<div className="col-2">
													<div className="custom-control custom-checkbox ml-2 my-2">
														<input type="checkbox" className="custom-control-input " id={data.id} name={data.id} checked={data.isChecked} onChange={onCheckboxChange}/>
														<label className="custom-control-label " htmlFor={data.id}>{data.value}</label>
													</div>
												</div>
												)}
										</div>
										<div className="row">
											<div className="itemscanbottom mt-2">
												<button className="btn rounded-pill btn-primary mr-1"  type="submit"  onClick={props.SetFilter}>Set Filter</button>
												<button type="button" className="btn rounded-pill btn-primary mr-1 closeitemscan-wrap" onClick={props.cancelSetFilter}>Cancel</button>
											</div>
										</div>
									</div>
									}
				    </div>
			</div>
		</div>
	)
}