import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    iCashOutList:[]
 
};

export default function ICashOutSettingReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
      
       
        case types.GET_ICASHOUT_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ICASHOUT_SUCCESS:
            return {
                ...state,
                iCashOutList: action.data.values,
                isLoading: false,
            }
        case types.GET_ICASHOUT_FAILED:
            return {
                ...state,
                isLoading: false,
            }  
        case types.UPDATE_CODEORALIAS_LIST:
debugger;
            return {
                ...state,
                isLoading: false,
                iCashOutList: action.data,
            }   
        case types.INSERT_ICASHOUT_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.INSERT_ICASHOUT_SUCCESS:
            
            return {
                ...state,
                isLoading: false,
            }
        case types.INSERT_ICASHOUT_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
            
        case types.GET_POSTITEMAPI_REQUEST:
            debugger
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_POSTITEMAPI_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_POSTITEMAPI_FAILED:
            return {
                ...state,
                isLoading: false,
            }      
        default:
            return state;
    }
}