import React from "react";
import {viewPassword} from '../../../../Custom';
import Loading from "../../../loading/components/Loading";
import {useForm} from "react-hook-form";
import {Helmet} from 'react-helmet';
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
import 'react-accessible-accordion/dist/fancy-example.css';
import ConfigureStoreContainer from '../../../ConfigureStore/container/ConfigureStoreContainer';
import ThirdPartyAPIDataSetupContainer from '../../../ThirdPartyAPIDataSetup/container/ThirdPartyAPIDataSetupContainer';
import AdditionalSettingsContainer from '../../../AdditionalSettings/container/AdditionalSettingsContainer';
import CentralUserContainer from '../../../centralUser/container/CentralUserContainer';
import ICashOutSettingContainer from '../../../ICashOutSetting/container/ICashOutSettingContainer';

export default function ConfigureDatabaseComponent(props) {

    const {userDetails,isLoading,formFieldsOldValue,inputChangeHandler,inputStoreChangeHandler,storeFormFields,storeFormFieldsOldValue,centralServerURLDisable,getFormField,
        rmhBigcommerceHandler,getalltax,SaveSendRHMorAPIDetails,valueInputOnchange,formFields,isShowTax,storeerrors,
        inputChangeHandlerStore,inputChangeHandlerBigC,apiBigCom}=props;

    const {register,errors,handleSubmit}=useForm()

    const onSubmit=(data) => {
        props.formHandler({...data});
    };
   
    
    return (
        <div>
            <Helmet>
                <title>Settings | Modus Go!</title>
            </Helmet>
            {isLoading? <Loading />:""}
            <div>

                <section className="po-bg superadmin-bg">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="bg-heading">
                                <h2 className="head-white">Database settings</h2>
                            </div>
                        </div>
                    </div>

                </section>
                <div className="main-content-area">
                    <div className="container po-bottom">
                        <div className="row">
                            <div className="col-sm-12">
                                <div className="user-profile-box">
                                    <img src="/assets/images/user_avatar.svg" alt="" />
                                </div>
                                <div className="db-username">{userDetails.name}</div>
                            </div>

                            <div className="col-sm-12">
                                <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                   Central Database Configuration

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
                                            <div className="row">
                                                <div className="col-md-12 p-0">
                                                    <form onSubmit={handleSubmit(onSubmit)}>
                                                        <div className="col-12 padding-md-0">
                                                            <div className="bg-white header-shadow container px-4 db-setting-table">
                                                                <table className="table">
                                                                    <thead>
                                                                        <tr className="row">
                                                                        <input type="text" autoFocus className="floating-input floating-input-label db-input"
                                                                                    ref={register({required: true,maxLength: 100})}
                                                                                    name="xmlnode"
                                                                                    defaultValue="authStringCentral"
                                                                                    hidden
                                                                                />
                                                                            <td className="col-5 col-sm-3 col-md-2">
                                                                                <span className="db-info font-weight-bold">Server name</span>
                                                                            </td>
                                                                            <td className="col-7 col-sm-9 col-md-10">
                                                                                <input type="text" autoFocus className="floating-input floating-input-label db-input"
                                                                                    ref={register({required: true,maxLength: 100})}
                                                                                    name="serverName"
                                                                                    defaultValue={formFields.serverName}
                                                                                    onChange={(e) => inputChangeHandler(e)}
                                                                                />
                                                                                <span className="db-info">{formFieldsOldValue.serverName!==""? "Old server name: "+formFieldsOldValue.serverName:""}</span>
                                                                                <span className="d-block text-danger db-error">{errors.serverName&&"Please enter the server name / IP."}{errors.serverName&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                            </td>
                                                                        </tr>
                                                                        <tr className="row">
                                                                            <td className="col-5 col-sm-3 col-md-2">
                                                                                <span className="db-info font-weight-bold">User name</span>

                                                                            </td>
                                                                            <td className="col-7 col-sm-9 col-md-10">
                                                                                <input type="text" className="floating-input floating-input-label db-input"
                                                                                    ref={register({required: true,maxLength: 50})}
                                                                                    name="username"
                                                                                    defaultValue={formFields.username}
                                                                                    onChange={(e) => inputChangeHandler(e)}
                                                                                />
                                                                                <span className="db-info">{formFieldsOldValue.username!==""? "Old user name: "+formFieldsOldValue.username:""}
                                                                                </span>
                                                                                <span className="d-block text-danger db-error">{errors.username&&"Please enter the username."}{errors.username&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                            </td>
                                                                        </tr>
                                                                        <tr className="row">
                                                                            <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Password</span></td>
                                                                            <td className="col-7 col-sm-9 col-md-10">
                                                                                <div className="db-password">
                                                                                    <input type="password" className="floating-input floating-input-label db-input"
                                                                                        ref={register({required: true,maxLength: 50})}
                                                                                        id="password"
                                                                                        name="password"
                                                                                        defaultValue={formFields.password}
                                                                                        onChange={(e) => inputChangeHandler(e)}
                                                                                    />
                                                                                    <span className="eye-icon"><i id="pass-status" className="zmdi zmdi-eye-off zmdi-hc-fw" onClick={()=>viewPassword("")}></i></span>
                                                                                    <span className="d-block text-danger db-error">{errors.password&&"Please enter the password."}{errors.password&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                                </div>
                                                                            </td>
                                                                        </tr>
                                                                        <tr className="row">
                                                                            <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Port</span>
                                                                            </td>
                                                                            <td className="col-7 col-sm-9 col-md-10">
                                                                                <input type="text" className="floating-input floating-input-label db-input"
                                                                                    ref={register({required: false,maxLength: 10})}
                                                                                    name="port"
                                                                                    defaultValue={formFields.port}
                                                                                    onChange={(e) => inputChangeHandler(e)}
                                                                                />
                                                                                <span className="db-info">{formFieldsOldValue.port!==""? "Old port: "+formFieldsOldValue.port:""}</span>
                                                                            </td>
                                                                        </tr>
                                                                        <tr className="row">
                                                                            <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Database name</span>
                                                                            </td>
                                                                            <td className="col-7 col-sm-9 col-md-10">
                                                                                <input type="text" className="floating-input floating-input-label db-input"
                                                                                    ref={register({required: true,maxLength: 50})}
                                                                                    name="databaseName"
                                                                                    defaultValue={formFields.databaseName}
                                                                                    onChange={(e) => inputChangeHandler(e)}
                                                                                />
                                                                                <span className="db-info">{formFieldsOldValue.databaseName!==""? "Old database name: "+formFieldsOldValue.databaseName:""}</span>
                                                                                <span className="d-block text-danger db-error">{errors.databaseName&&"Please enter the database name."}{errors.databaseName&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                            </td>
                                                                        </tr>
                                                                    </thead>
                                                                </table>
                                                            </div>

                                                        </div>
                                                        <div className="col-12">
                                                            <div className="justify-content-end d-flex">
                                                                <button className="btn rounded-pill btn-primary mr-3" type="submit">Save</button>
                                                            </div>
                                                        </div>
                                                    </form>

                                                </div>

                                            </div>

                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>



                                <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                   Store Database Configuration

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
                                            <div className="row">
                                           
                                                <div className="col-md-12 p-0">
                                                    
                                                    <div className="col-12 padding-md-0">
                                                        <div className="bg-white header-shadow container px-4 db-setting-table">
                                                            <table className="table">
                                                                <thead>
                                                                    <tr className="row">
                                                                      
                                                                        <td className="col-5 col-sm-3 col-md-2">
                                                                            <span className="db-info font-weight-bold">Server name</span>
                                                                        </td>
                                                                        <td className="col-7 col-sm-9 col-md-10">
                                                                            <input type="text" autoFocus className="floating-input floating-input-label db-input"
                                                                              
                                                                                name="storeserverName"
                                                                                defaultValue={storeFormFields.serverName}
                                                                                onChange={(e) => inputStoreChangeHandler(e)}
                                                                            />
                                                                            <span className="d-block text-danger db-error">{(storeerrors && !storeFormFields.serverName)&&"Please enter the server name / IP."}{(storeerrors && !storeFormFields.serverName)&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                        </td>
                                                                    </tr>
                                                                    <tr className="row">
                                                                        <td className="col-5 col-sm-3 col-md-2">
                                                                            <span className="db-info font-weight-bold">User name</span>

                                                                        </td>
                                                                        <td className="col-7 col-sm-9 col-md-10">
                                                                            <input type="text" className="floating-input floating-input-label db-input"
                                                                              
                                                                                name="storeusername"
                                                                                defaultValue={storeFormFields.username}
                                                                                onChange={(e) => inputStoreChangeHandler(e)}
                                                                            />
                                                                            
                                                                            <span className="d-block text-danger db-error">{(storeerrors && !storeFormFields.username)&&"Please enter the username."}{(storeerrors && !storeFormFields.username)&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                        </td>
                                                                    </tr>
                                                                    <tr className="row">
                                                                        <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Password</span></td>
                                                                        <td className="col-7 col-sm-9 col-md-10">
                                                                            <div className="db-password">
                                                                                <input type="password" className="floating-input floating-input-label db-input"
                                                                                    id="storepassword"
                                                                                    name="storepassword"
                                                                                    defaultValue={storeFormFields.password}
                                                                                    onChange={(e) => inputStoreChangeHandler(e)}
                                                                                />
                                                                                <span className="eye-icon"><i id="pass-status" className="zmdi zmdi-eye-off zmdi-hc-fw" onClick={()=>viewPassword("storepassword")}></i></span>
                                                                                <span className="d-block text-danger db-error">{(storeerrors && !storeFormFields.password)&&"Please enter the Password."}{(storeerrors && !storeFormFields.password)&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                            </div>
                                                                        </td>
                                                                    </tr>
                                                                    <tr className="row">
                                                                        <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Port</span>
                                                                        </td>
                                                                        <td className="col-7 col-sm-9 col-md-10">
                                                                            <input type="text" className="floating-input floating-input-label db-input"
                                                                                name="storeport"
                                                                                defaultValue={storeFormFields.port}
                                                                                onChange={(e) => inputStoreChangeHandler(e)}
                                                                            />
                                                                        </td>
                                                                    </tr>
                                                                    <tr className="row">
                                                                        <td className="col-5 col-sm-3 col-md-2"><span className="db-info font-weight-bold">Database name</span>
                                                                        </td>
                                                                        <td className="col-7 col-sm-9 col-md-10">
                                                                            <input type="text" className="floating-input floating-input-label db-input"
                                                                                name="storedatabaseName"
                                                                                defaultValue={storeFormFields.databaseName}
                                                                                onChange={(e) => inputStoreChangeHandler(e)}
                                                                            />
                                                                            <span className="d-block text-danger db-error">{(storeerrors && !storeFormFields.databaseName)&&"Please enter the database name."}{(storeerrors && !storeFormFields.databaseName)&&<i className="zmdi zmdi-alert-triangle float-right"></i>}</span>
                                                                        </td>
                                                                    </tr>
                                                                </thead>
                                                            </table>
                                                        </div>

                                                    </div>
                                                    <div className="col-12">
                                                        <div className="justify-content-end d-flex">
                                                            <button className="btn rounded-pill btn-primary mr-3"  onClick={props.storeformHandler}>Save</button>
                                                        </div>
                                                    </div>
                                                

                                            </div>
                                         
                                            </div>

                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>

                                <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                    Central user management

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
							               
                                           <CentralUserContainer/>

                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>

                                <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                    Multi store management

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
							                <ConfigureStoreContainer  />
                                           

                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>
                                {apiBigCom!=undefined?

                                    ( apiBigCom.length>0 ?
                                        <Accordion className="accordion" allowZeroExpanded={true}>
                                            <AccordionItem className="card">

                                                <AccordionItemHeading className="card-header">
                                                    <AccordionItemButton>
                                                        <div className="col-md-12 p-0">
                                                            BigCommerce API settings

                                                        </div>
                                                    </AccordionItemButton>
                                                </AccordionItemHeading >
                                                <AccordionItemPanel>
                                            
                                                <ThirdPartyAPIDataSetupContainer/>
                                                
                                                </AccordionItemPanel>
                                            </AccordionItem>
                                        </Accordion>
                                        :"")
                                        :""
                                }
                               <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                   ICashOut API settings

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
                                    
                                        <ICashOutSettingContainer/>
                                        
                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>
                                <Accordion className="accordion" allowZeroExpanded={true}>
                                    <AccordionItem className="card">

                                        <AccordionItemHeading className="card-header">
                                            <AccordionItemButton>
                                                <div className="col-md-12 p-0">
                                                   Additional settings

                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading >
                                        <AccordionItemPanel>
                                         <AdditionalSettingsContainer/>
                                        </AccordionItemPanel>
                                    </AccordionItem>
                                </Accordion>
                            </div>


                        </div>
                    </div>
                </div>

            </div>

        </div>
    )
}