//#region search handler
searchHandler=(record,event,cb) => {
  try {
    let getdata;
    record=record.trim();

    if(record!==""||this.props.itemListParam.searchText!=="") {
      let itemListParam={...this.props.itemListParam};
      if(event=="select")
      {
        itemListParam["FilterQueryStr"]="";
      }
     
      itemListParam["searchText"]=record;
      this.props.setitemListParam(itemListParam);
      this.props.getAllItems(itemListParam,true);
    }
    if(record!=="") {
      let searchRequestParameter={...this.props.searchRequestParameter};
      searchRequestParameter["itemVal"]=record;
      this.props.setSearchRequestParameter(searchRequestParameter);
      this.props.getSearchList(searchRequestParameter,true);
    }
    else {
      getdata=[];
      this.props.setSearchData(getdata);
    }
  } catch(error) {
    console.log(error);
  }
}

