handleScroll=scrolldebounce(() => {
  try {

    // Checks that the page has scrolled to the bottom
    var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
    var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
    if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
      let pagesize=this.props.itemListParam.pageSize+50;
      let data={
        pageNo: this.props.itemListParam.pageNo,
        pageSize: pagesize,
        sortColumnName: this.props.itemListParam.sortColumnName,
        sortDirection: this.props.itemListParam.sortDirection,
        searchText: this.props.itemListParam.searchText,
        departmentIds: this.props.itemListParam.departmentIds,
        categoryIds: this.props.itemListParam.categoryIds,
        supplierIds: this.props.itemListParam.supplierIds,
        FilterQueryStr:this.props.itemListParam.FilterQueryStr,
        size:this.props.itemListParam.size
      };
      let itemListParam={...this.props.itemListParam};
      itemListParam["pageSize"]=pagesize;
      this.props.setitemListParam(itemListParam);
      this.props.getAllItems(data,false);
    }
  } catch(error) {
    console.log(error);
  
  }
},0);

