SetAll=() => {
  let itemListParam={...this.props.itemListParam};
  let itemDataValue=this.props.itemDataValue;
  let lenitemDataValue =itemDataValue.filter(x=>x.IsSelected==true)

  let checkboxValue=this.state.checkboxValue;
  let validate=checkboxValue.filter(x=>x.value=="")
  let validateCategory=checkboxValue.filter(x=>x.name=="departmentId")
  let catList=this.props.allcategories;
  let formField=this.props.formField;
  let categoryID=checkboxValue.filter(x=>x.name=="categoryID")
  if(validateCategory.length==1 && categoryID.length==0)
  {
    checkboxValue.push({name : "categoryID",value:"0"});
    this.setState({checkboxValue})
  }


  if(checkboxValue.length==0)
  {
    swal("","Please Select Value","error")
  }
  else if(validate.length>0)
  {
    swal("","Please Enter Selected Value","error")
  }
 
  else if(lenitemDataValue.length==0 && itemListParam.departmentIds.length==0 && itemListParam.categoryIds.length==0 && itemListParam.supplierIds.length==0 && itemListParam.searchText=="")
  {
    
        swal({
          title: "No filter applied!!",
          text: "Do you want to update the selected column with all Item?",
          icon: "warning",
          buttons: {
            confirm: 'Yes',
            cancel: 'No'
          },
          dangerMode: true,
        })
          .then((willUpdate) => {
            
            if(willUpdate) {
            
              let ItemDataParam={
                UpdateItemField:checkboxValue,
                UpdateBulkItem:[-1],
                IsAllItem:true
              }
              this.clearEnterValue();
              this.props.UpdateBulkItem(ItemDataParam,this.props.itemListParam,true)
              this.props.setSearchValue("");
            }
            else{
              
            } 
          })
          
    }
    else
    {

      swal({
        title: "Are you sure!!",
        text: "Do you want to update item the selected column?",
        icon: "warning",
        buttons: {
          confirm: 'Yes',
          cancel: 'No'
        },
        dangerMode: true,
      })
        .then((willUpdate) => {
          
          if(willUpdate) {
            let itemDetailsList =lenitemDataValue
            let ItemId=itemDetailsList.map(x=>x.itemId)
            let ItemDataParam={
              UpdateItemField:checkboxValue,
              UpdateBulkItem:ItemId,
              IsAllItem:false
            }
            this.props.UpdateBulkItem(ItemDataParam,itemListParam,true)
            let allItemListParam={
              sortColumnName: "",
              sortDirection: "desc",
              pageNo: 0,
              pageSize: 50,
              searchText: "",
              departmentIds: [],
              categoryIds: [],
              supplierIds:[],
              FilterQueryStr:"A.ItemLookupCode,AL.Alias,A.Description,A.ExtendedDescription"
            }
            this.props.setitemListParam(allItemListParam);
            this.clearEnterValue();
            this.props.setSearchValue("");
          }
          else{
            
          } 
        })


      
    }
  

  
  //this.setState({isItemDetail: true})
}
