import React from 'react';
import DiscountsComponent from '../component/DiscountsComponent';
import { } from '../state/action/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { withRouter } from 'react-router-dom';

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class DiscountsContainer extends React.Component {
    state = {
        priceList: []
    }
    componentDidMount() {
        try {

            var list = [];
            var object = {
                quantity: "0",
                price: "0.00",
                priceA: "0.00",
                priceB: "0.00",
                priceC: "0.00"
            }
            if (this.props.itemDiscount === null || this.props.itemDiscount.selectedDiscount !== 3) {
                list = [object, object, object, object];
            }
            else {
                if (this.props.itemDiscount.quantity1 !== undefined) {
                    list.push({
                        quantity: this.props.itemDiscount.quantity1,
                        price: this.props.itemDiscount.price1.toFixed(2),
                        priceA: this.props.itemDiscount.price1A.toFixed(2),
                        priceB: this.props.itemDiscount.price1B.toFixed(2),
                        priceC: this.props.itemDiscount.price1C.toFixed(2)
                    });
                }
                if (this.props.itemDiscount.quantity2 !== undefined) {
                    list.push({
                        quantity: this.props.itemDiscount.quantity2,
                        price: this.props.itemDiscount.price2.toFixed(2),
                        priceA: this.props.itemDiscount.price2A.toFixed(2),
                        priceB: this.props.itemDiscount.price2B.toFixed(2),
                        priceC: this.props.itemDiscount.price2C.toFixed(2)
                    });
                }
                if (this.props.itemDiscount.quantity3 !== undefined) {
                    list.push({
                        quantity: this.props.itemDiscount.quantity3,
                        price: this.props.itemDiscount.price3.toFixed(2),
                        priceA: this.props.itemDiscount.price3A.toFixed(2),
                        priceB: this.props.itemDiscount.price3B.toFixed(2),
                        priceC: this.props.itemDiscount.price3C.toFixed(2)
                    });
                }
                if (this.props.itemDiscount.quantity4 !== undefined) {
                    list.push({
                        quantity: this.props.itemDiscount.quantity4,
                        price: this.props.itemDiscount.price4.toFixed(2),
                        priceA: this.props.itemDiscount.price4A.toFixed(2),
                        priceB: this.props.itemDiscount.price4B.toFixed(2),
                        priceC: this.props.itemDiscount.price4C.toFixed(2)
                    });
                }

            }
            //$('#tblDiscountPriceList').DataTable().destroy();

            this.setState({ priceList: list }, () => {
                $('#tblDiscountPriceList').DataTable({
                    "paging": false,
                    "ordering": false,
                    "info": false,
                    "searching": false,
                    "responsive": true,
                    "retrieve": true,
                    "oLanguage": {
                        "sEmptyTable": "No data found"
                    },
                    fixedHeader: true
                })
            });

        } catch (error) {
            console.log(error);
        }
    }
    componentWillUnmount() {
        $('#tblDiscountPriceList').DataTable().destroy();
    }
    render() {

        return (
            <DiscountsComponent
                {...this.props}
                priceList={this.state.priceList}
            />
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {

    };
};

const mapStateToProps = (state) => {
    const { } = state.DiscountsReducer;
    return {


    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(DiscountsContainer));