import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { history } from '../../../../history'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
//#region get order item list
export function getPastInventoryList(data, isLoading) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_INVENTORYITEM_REQUEST,
            isLoading: isLoading
        });
        let url = 'Inventory/ListPastInventory';
        return post(url, data)
            .then((response) => {
                const { payload } = response;                          
              
                if (response.status === 200) {
                    /* Logic */                  
                    $('#tblPastInventory').DataTable().destroy();
                    dispatch({
                        type: types.GET_INVENTORYITEM_SUCCESS,
                        data: response.payload,                       
                    });
                    $('#tblPastInventory').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_INVENTORYITEM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
//#region set order item list param
export function setPastInventoryListParam(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM,
            data: data,


        });
    }
}
//#endregion set order item list param
export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}
