import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'
import {history} from '../../../history';
export default function ICashOutSettingComponent(props) {
	const {isLoading,iCashOutList,PostItem}=props;
	return (
		<>
		<div>
		<Helmet>
			<title>API Column Mapping | Modus Go!</title>
		</Helmet>
		{isLoading? <Loading />:""}

		<div className="container-fluid d-none d-md-block">
            <div className="row align-items-center justify-content-between">
             
              <div className="order-3 order-xl-3 col-xl-12 col-lg-12 col-md-6 justify-content-lg-end d-flex p-0 mb-2 pull-right">
			    <button className="mr-3 btn btn-primary rounded-pill" type="button"  onClick={(e) => PostItem(e)}>Post Item</button>

				<button className="btn btn-primary rounded-pill" type="button" onClick={() => props.SaveSettings()} >Save Settings</button>

              </div>
            </div>
          </div>
	
		</div>
			
		<div className="container bg-white header-shadow mt-1 mb-150">
			<div className="row ">
				<div className="col-12 p-0">
					<div className="table-responsive">
						<table id="tblInventoryItem" className="table w-100 new-order-list wo-collasped" >
							<thead>
								<tr>
									<th className="plus-sort"></th>
									<th>Department</th>
									<th>Select</th>
								
								</tr>
							</thead>
							<tbody>

								{iCashOutList.map((itemList,index) =>
									<tr key={itemList.id}>
										<td className={`details-control plus-sort`} tabIndex="-1"></td>

										<td  className="default-cursor ">{itemList.name}</td>

										<td  className="default-cursor ">
											
													<div class="row">
														<div className="col-sm-5 col-12">
															<div className="custom-control custom-radio ml-2 my-2">
																<input type="radio"  id={`code_${itemList.id}`}  checked={itemList.itemCode_Alias_id==1?true:""}  name={`codeAlias_${itemList.id}`} className="custom-control-input" onChange={(e) => props.codeALiasHandler(e,itemList.name)} />
																<label className="custom-control-label" htmlFor={`code_${itemList.id}`}>Item Lookup Code</label>
															</div>
														</div>
														<div className="col-sm-5 col-12">
															<div className="custom-control custom-radio ml-2 my-2">
																<input type="radio"  id={`Alias_${itemList.id}`}  checked={itemList.itemCode_Alias_id==2?true:""} name={`codeAlias_${itemList.id}`} className="custom-control-input"  onChange={(e) => props.codeALiasHandler(e,itemList.name)}/>
																<label className="custom-control-label" htmlFor={`Alias_${itemList.id}`}>Alias</label>
															</div>
														</div>
													</div>
											
										</td>

									</tr>
								)}
							</tbody>
						</table>
					</div>

				</div>

			</div>
		</div>						
		
			

	</>
	)
}