import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    isLoading: false,
};

export default function SubscriptionFailedReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.UPDATE_SUBSCRIPTION_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_SUBSCRIPTION_SUCCESS:
            return {
                ...state,
                purchaseOrderList: action.data,
                isLoading: false,

            }
        case types.UPDATE_SUBSCRIPTION_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        default:
            return state;
    }
}