import * as types from "../../../../../constants/AppConstants";

const INITIAL_STATE = {
    formFields: {
        serverName: "",
        username: "",
        password: "",
        port: "",
        databaseName: "",
        xmlnode:"authStringCentral"

    },

    formFieldsOldValue: {
        serverName: "",
        username: "",
        password: "",
        port: "",
        databaseName: ""
    },
  
    isLoading: false,
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    getalltax:[],
    status: "",
    message: "",
    
    getFormField:[],
    apiBigCom:[]
};

export default function ConfigureDatabaseReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_DATABASE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_DATABASE_SUCCESS:
            
            return {
                ...state,
                formFields: action.data,
               // storeFormFields:action.data,
                isLoading: false,
            }
        case types.SET_DATABASE_VALUES:
            return {
                ...state,
                formFieldsOldValue: {
                    ...state.formFieldsOldValue,
                    ...action.data
                },
                isLoading: false,
            }
        case types.GET_DATABASE_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SAVE_DATABASE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.SAVE_DATABASE_SUCCESS:
            return {
                ...state,
                isLoading: false,
                formFieldsOldValue: INITIAL_STATE.formFieldsOldValue,
                formFields: action.data,
            }
        case types.SAVE_DATABASE_FAILED:
            return {
                ...state,
                formFieldsOldValue: INITIAL_STATE.formFieldsOldValue,
                isLoading: false,
            }
            case types.RESET_SUCCESS_DIALOGUE:

                return {
                    ...state,
                    isSuccessDialogue: action.isSuccessDialogue,  
                    updatestatus: action.status !== undefined ? action.status : "",
                    updatemessage: action.message !== undefined ? action.message : "",
                    
                }
            case types.CLEAR_PO_MESSAGE:
                return {
                    ...state,
                    status: "",
                    message: "",
                    updatestatus:"",
                    updatemessage:""
                }
            case types.GETALLPRODUCT_REQUEST:
                return {
                    ...state,
                    isLoading: true,
                }
            case types.GETALLPRODUCT_SUCCESS:
                return {
                    ...state,
                    isLoading: false,
                }
            case types.GETALLPRODUCT_FAILED:
                return {
                    ...state,
                    isLoading: false,
                }
            case types.GETALLTAX_BIGCOM_REQUEST:
                return {
                    ...state,
                    isLoading: true,
                }
            case types.GETALLTAX_BIGCOM_SUCCESS:
                return {
                  
                    ...state,
                    getalltax: action.data,
                    isLoading: false,
                }
            case types.GETALLTAX_BIGCOM_FAILED:
                return {
                    ...state,
                    isLoading: false,
                }
                case types.INSERT_STORECONFIG_REQUEST:
                    return {
                        ...state,
                        isLoading: action.isLoading,
                    }
                case types.INSERT_STORECONFIG_SUCCESS:
                    return {
                        ...state,
                        isLoading: false,
                    }
                case types.INSERT_STORECONFIG_FAILED:
                    return {
                        ...state,
                        isLoading: false,
                    } 
                case types.GET_CONFIGURESTORE_REQUEST:
                    return {
                        ...state,
                        isLoading: action.isLoading,
                    }
                case types.GET_CONFIGURESTORE_SUCCESS:
                    return {
                        ...state,
                        isLoading: false,
                        getFormField: action.data.values,
                    }
                case types.GET_CONFIGURESTORE_FAILED:
                    return {
                        ...state,
                        isLoading: false,
                    } 
                case types.GET_CONFIGAPINAME_REQUEST:
                    return {
                        ...state,
                        isLoading: action.isLoading,
                    }
                case types.GET_CONFIGAPINAME_SUCCESS:
                 
                    return {
                        ...state,
                        apiBigCom: action.data.values.filter(x=>x.apiName=="BigCommerce"),
                        isLoading: false,
                    }
                case types.GET_CONFIGAPINAME_FAILED:
                    return {
                        ...state,
                        isLoading: false,
                    }
                       
               
        default:
            return state;
    }
}