import * as types from "../../../../constants/AppConstants";
import { post } from "../../../../service/index";
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
//#region to get order list
export function gettransferOutList(data, isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_TRANSFEROUTS_REQUEST,
            isLoading: isLoading,
        });
        let url = 'order/transferOutList';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tbltransferOut').DataTable().destroy();
                    dispatch({
                        type: types.GET_TRANSFEROUTS_SUCCESS,
                        data: response.payload,
                    });
                    $('#tbltransferOut').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_TRANSFEROUTS_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get order list
//#region set request parameter
export function setRequestParameter(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_TRANSFEROUTS_REQUESTPARAMETER,
            data: data,

        });
    }
}
//#endregion set request parameter
