//#region to get search list
export function getSearchList(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_TRANSFEROUTSEARCH_REQUEST,
        });
        let url = 'order/searchTRANSFEROUTByValue';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_TRANSFEROUTSEARCH_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_TRANSFEROUTSEARCH_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
