import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'

export default function AdditionalSettingsComponent(props) {
	const {isLoading,allAPINameList,setIsDisplayCheckbox,updatestatus,updatemessage,selectALL}=props;
	return (
		<div>
			<Helmet>
				<title>Additional Settings | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
				<div className="bg-white header-shadow px-4 py-2">
					<div className="row pt-2 align-items-center">
						<div className="col-sm-12">
							<p class="text-muted py-2">Reset primary supplier based on the lower cost and new one</p>
						</div>
						<div className="col-12 text-md-right py-2">
							<button className="d-inline-block btn btn-primary rounded-pill" type="button" onClick={() => props.ResetALL()}>Reset Now</button>
						</div>
					</div>
				</div>
		    	<br/>
				<div className="bg-white header-shadow px-4 py-2">
					<div className="row mb-4">
						<div className="col-12 activity-title-block">
							<h4 className="text-left text-xl-center heading-text mt-2">API Running Status</h4>

						</div>
					</div>
					<div className="container pt-3 ml-1">
						<div className="row">
							<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
						</div>
					</div>
					<div className="datatable-head-list">
							
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input" id="selectAll" name="selectAll" onChange={setIsDisplayCheckbox}  checked={selectALL} />
										<label className="custom-control-label text-darkblue font-weight-bold" htmlFor="selectAll">Select All</label>
									</div>
								</li>
								{allAPINameList.map((list) =>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input" id={list.id+`_`+list.apiName} name={list.id+`_`+list.apiName} onChange={setIsDisplayCheckbox}  checked={list.isDisplay} />
										<label className="custom-control-label font-weight-bold" htmlFor={list.id+`_`+list.apiName}>{list.apiName}</label>
									</div>
								</li>
								)}
							</ul>
						</div>
				</div>
			</div>
		</div>
	)
}