import React from 'react';
import BarcodeReader from 'react-barcode-reader';
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
import NumberFormat from 'react-number-format';
import Autocomplete from 'react-autocomplete';
import ReactPaginate from 'react-paginate';

import Loading from "../../loading/components/Loading";
export default function NewInventoryScanItemComponent(props) {
    const {isLoading,totalRecord,totalPage,isOpenModel,itemCode,setScreenLock,quantity,description,isScannerUse,lookupCodeSearchValue,lookupCodeSearchData,codeList,isOpenCodeModel,screenLock,isDisabled,showDescription,status,message,isOpenProductModel,products,
        isOpenSupplierModal,toggleSupplierModel,supplierModalSave,supplierList,supplierCheckBoxHandler,formFields,isScreenLock,inputChangeHandlerSupplier,isSupplier,comboBoxSelection,isFromInventory}=props;
    return (

        <div className="night-mode-css-override">
            {isLoading? <Loading />:""}

            <div id="InventoryDetailItemScanWrap" className="bg-white header-shadow  position-relative mt-3 p-3 mb-3">
                <div className="row">
                    <div className="ml-3 col-12 floating-label">
                        <h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " id="itemCode"
                                value={itemCode}
                                onKeyDown={props.handleKeyDown}
                                onChange={props.handlechange}
                                onPaste={props.handlePaste}
                                onBlur={(e) => {if(!showDescription) props.checkItemCode(e)}}
                            />
                            <span className="highlight"></span>
                            <label>Enter code</label>
                            <span className="barcode-img">
                                <img src="/assets/images/barcode.svg" className="img-fluid" />
                            </span>
                        </div>
                    </div>
                    <div className="col-md-6 col-12">
                        <div className="floating-label">
                            <input inputMode="numeric" className="floating-input" maxLength="5" min="0" id="quantity" name="quanity" type="number" pattern="[0-9]*" placeholder=" " value={quantity}
                                onChange={props.setQuantity}
                                onKeyDown={props.handleQuantityKeyDown} />
                            <span className="highlight"></span>
                            <label>Quantity</label>
                        </div>
                    </div>
                    <div className='col-12 fix-position'>
                    <div className="custom-control custom-checkbox">

                        <input type="checkbox" className="custom-control-input " id="isScreenLock" name="isScreenLock" onChange={setScreenLock} checked={isScreenLock} />
                        <label className="custom-control-label font-weight-bold" htmlFor="isScreenLock">Screen Lock</label>

                    </div>
                    </div>
                    
                    <div className="itemscanbottom mt-2">
                      
                        <button className="btn rounded-pill btn-primary mr-1" id="btnScanPopupAccept" type="submit" onClick={props.acceptHandler} disabled={isScreenLock}>Accept</button>
                        <button className="btn rounded-pill btn-primary mr-1" type="button" onClick={props.openCodeModel} disabled={isScannerUse && isScreenLock} >Lookup code</button>
                        <button className="btn rounded-pill btn-primary mr-1" type="button" onClick={props.resetHandler}   >Reset</button>
                        <button type="button" className="btn rounded-pill btn-primary mr-1 closeitemscan-wrap" onMouseDown={props.closeItemScan}>Done</button></div>
                    </div>
            </div>


            <Modal backdrop="static" isOpen={isOpenModel} centered={true} id="scanItemPopup" >
                <ModalHeader toggle={props.closeDialogue}>Item name</ModalHeader>
                <ModalBody>

                    <div className="row">
                        <div className="ml-3 col-12 floating-label">
                            <h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
                        </div>
                    </div>

                    <div className="row">
                        <div className="ml-3 col-12 floating-label">
                            {showDescription? <h6 className="value-green">New item found.</h6>:""}
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <div className="floating-label">
                                <input className="floating-input" type="text" placeholder="" id="itemCode"
                                    value={itemCode}
                                    onKeyDown={props.handleKeyDown}
                                    onChange={props.handlechange}
                                    onPaste={props.handlePaste}
                                    onBlur={(e) => {if(!showDescription) props.checkItemCode(e)}}
                                    disabled={showDescription}
                                />
                                <span className="highlight"></span>
                                <label>Enter code</label>
                                <span className="barcode-img">
                                    <img src="/assets/images/barcode.svg" className="img-fluid" />
                                </span>
                            </div>
                        </div>

                        <div className="col-12">
                            <div className="floating-label">

                                <input className="floating-input" type="text" placeholder=" " value={description}
                                    onChange={props.setDescription} maxLength="30"
                                />

                                <span className="highlight"></span>
                                <label>Description</label>
                            </div>
                        </div>


                        <div className="col-12">
                            <div className="floating-label">
                                <select className="floating-select" onChange={props.getComboBoxValue} value={comboBoxSelection}>
                                    <option value="">Select</option>
                                    <option value="supplier_mpq">Supplier's MPQ</option>

                                </select>
                                <span className="highlight"></span>
                                <label>Combo code</label>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="floating-label">
                                <input inputMode="numeric" className="floating-input" maxLength="5" min="0" id="quantity" name="quanity" type="number" pattern="[0-9]*" placeholder=" " value={quantity}
                                    onChange={props.setQuantity}
                                    onKeyDown={props.handleQuantityKeyDown} />
                                <span className="highlight"></span>
                                <label>Quantity</label>
                            </div>
                        </div>
                        {(isSupplier&&isFromInventory)?
                            <div className="col-12">
                                <div className="floating-label">
                                    <input className="floating-input" type="text" placeholder=" " maxLength="70" id="supplier" name="supplier" onChange={(e) => inputChangeHandlerSupplier(e)} value={formFields.supplier} />
                                    <span className="highlight"></span>
                                    <label>Supplier </label>
                                    <div className="icon-box rounded-circle modal-search" onClick={toggleSupplierModel}>
                                        <i className="zmdi zmdi-search"></i>
                                    </div>
                                </div>
                            </div>
                            :""}
                    </div>
                </ModalBody>
                <ModalFooter>
                    <button
                        className="btn rounded-pill btn-primary" id="btnScanPopupAccept"
                        type="submit"
                        onClick={props.acceptHandler}

                        disabled={isDisabled} >Accept</button>
                    <button className="btn rounded-pill btn-primary" type="button"
                        onClick={props.resetHandler}
                    >Reset</button>
                    <button type="button" className="btn rounded-pill btn-primary" data-dismiss="modal"
                        onClick={props.closeDialogue}>Done</button>
                </ModalFooter>
            </Modal>



            {/* <! Modal combine product> */}
            <Modal backdrop="static" isOpen={isOpenProductModel} toggle={props.toggleProductModel} centered={true}>
                <ModalHeader toggle={props.toggleProductModel}>Select product</ModalHeader>
                <ModalBody>

                    <div className="row">
                        <div className="col-12">
                            <ul className="setting-checkbox">
                                {products.length>0? products.map((product,index) =>
                                    <li key={"scanProduct-"+index}>
                                        <div className="custom-control custom-checkbox">
                                            <input type="checkbox" className="custom-control-input productCHK" defaultChecked={false} onChange={(e) => props.productCheckBoxChangeHandler(e)} id={product.itemLookupCode} name={product.itemLookupCode} />
                                            <label className="custom-control-label" htmlFor={product.itemLookupCode}><b className="bold">{product.itemLookupCode}</b>{product.description!==""? " - "+product.description:""}</label>
                                        </div>
                                    </li>
                                ):<li><div className="custom-control custom-checkbox">No data found</div></li>}

                            </ul>
                        </div>
                    </div>


                </ModalBody>
                <ModalFooter>
                    {products.length>0? <button className="btn rounded-pill btn-primary" type="button" onClick={props.ProductHandler} >Ok</button>:""}
                    <button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleProductModel}>Cancel</button>

                </ModalFooter>
            </Modal>

            <Modal backdrop="static" isOpen={isOpenSupplierModal} toggle={toggleSupplierModel} centered={true} size="lg">
                <ModalHeader toggle={toggleSupplierModel}>Supplier</ModalHeader>
                <ModalBody>
                    <div className="row">
                        <div className="col-12 p-1">
                            {supplierList.length>0? (<div className="table-responsive">
                                <table id="tblWOSupplier-PO" className="table w-100 new-order-list" >
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>ID</th>
                                            <th>CODE</th>
                                            <th>SUPPLIER NAME</th>

                                        </tr>
                                    </thead>
                                    <tbody>
                                        {supplierList.map((supp) =>
                                            <tr key={supp.id}>
                                                <td> <div className="custom-control custom-checkbox "
                                                    key={supp.id}>
                                                    <input type="checkbox" className="custom-control-input supplierCHK" onChange={(e) => supplierCheckBoxHandler(e,supp)} id={supp.id} name={supp.id} />
                                                    <label className="custom-control-label" htmlFor={supp.id}></label></div></td>
                                                <td>{supp.id}</td>
                                                <td>{supp.code}</td>
                                                <td>{supp.supplierName}</td>
                                            </tr>
                                        )}
                                    </tbody>
                                </table>
                            </div>)
                                :<div className="custom-control custom-checkbox">No data found</div>
                            }
                        </div>
                    </div>
                </ModalBody>
                <ModalFooter>
                    {supplierList.length>0? <button className="btn rounded-pill btn-primary" type="button" onClick={supplierModalSave} >OK</button>:''}
                    <button type="button" className="btn rounded-pill btn-primary" onClick={toggleSupplierModel}>Cancel</button>

                </ModalFooter>
            </Modal>

            <Modal backdrop="static" isOpen={isOpenCodeModel} toggle={props.toggleCodeModel} centered={true} size="lg" id="codeModel" >
                <ModalHeader toggle={props.toggleCodeModel}>Select item</ModalHeader>
                <ModalBody >
                    <div className="row">
                        <div className="col">
                            <div className="searchbox-wrap header-shadow lookupcode">
                                <Autocomplete
                                    wrapperStyle={{position: 'relative'}}
                                    value={lookupCodeSearchValue}
                                    items={lookupCodeSearchData}
                                    onSelect={(record) => props.onLookupCodeSearchCheckBoxHandler(record)}
                                    getItemValue={(item) => item.keys}
                                    inputProps={{id: 'lookupitemsearchautocomplete',placeholder: 'item code, description, alias, {department/category} or combination'}}
                                    onChange={(event,value) => props.onLookupCodeSearchChangeHandler(value)}
                                    renderMenu={children => (
                                        <div className="search-menu" id="lookupItemSearchMenu">
                                            {children}
                                        </div>
                                    )}
                                    renderItem={(item,isHighlighted) => (
                                        <div className="lookupItemSearchControl" key={item.keys}
                                        >{item.value}</div>
                                    )}
                                />

                                <div className="icon-box rounded-circle">
                                    <i className="material-icons">search</i>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12 p-1">
                            <div className="table-responsive">
                                <table id="tblLookupCodeList" className="table w-100 new-order-list" >
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>CODE</th>
                                            <th>NAME</th>


                                        </tr>
                                    </thead>
                                    <tbody>
                                        {codeList.map((code) =>
                                            <tr key={code.itemID}>
                                                <td> <div className="custom-control custom-checkbox " key={"div-"+code.itemID}><input type="checkbox" className="custom-control-input lookupCodeCHK" onChange={(e) => props.codeCheckBoxHandler(e,code.itemLookupCode)} id={code.itemLookupCode} name={code.itemLookupCode} />
                                                    <label className="custom-control-label" htmlFor={code.itemLookupCode}></label></div></td>
                                                <td>{code.itemLookupCode}</td>
                                                <td>{code.description}</td>


                                            </tr>
                                        )}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-3">
                        <div className="col-12 dataTables_wrapper">
                            <ReactPaginate
                                breakLabel={'...'}
                                pageCount={totalPage}
                                containerClassName={'pagination float-right lookupPagination'}
                                pageClassName={'page-item'}
                                pageLinkClassName={'page-link'}
                                previousClassName={'page-item btnPrevious'}
                                previousLinkClassName={'page-link'}
                                nextClassName={totalRecord===0? "page-item btnNext disabled":"page-item btnNext"}
                                nextLinkClassName={'page-link'}
                                breakClassName={'page-item'}
                                breakLinkClassName={'page-link'}
                                onPageChange={props.handlePageClick}
                                activeClassName={'active'}
                            />
                        </div>
                    </div>
                </ModalBody>
                <ModalFooter>
                    <button className="btn rounded-pill btn-primary" type="button" onClick={props.addLookupCode} >Ok</button>
                    <button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleCodeModel}>Cancel</button>

                </ModalFooter>
            </Modal>
        </div>
    )
}