import React from "react";
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet';
import {history} from "../../../history";
import InventoryScanItemContainer from '../../inventoryScanItem/container/InventoryScanItemContainer';
import NewInventoryScanItemContainer from '../../newInventoryScanItem/container/NewInventoryScanItemContainer';
export default function InventoryDetailComponent(props) {

	const {isRender,isLoading,inventoryItemList,isInventoryScanItem,
		inventoryDetail,closeInventoryScanItem,inventoryItemListParam,isScreenLock,screenLock,
		
		getInventoryItemList,userDetails,status,message,isModified,description,updatedValue,scanItem,inputChangeHandler,updatemessage,updatestatus,isShowInventoryDetailDatatable,
		isItemCode,isQuantity,isDescription,isLastTime,isSetItemCode,isSetQuantity,isSetDescription,isSetLastTime,isSetOnHandQuantity,isOnHandQuantity,onFileChange}=props;


	return (
		<div>
			<Helmet><title>{inventoryDetail!==undefined&&inventoryDetail!==null&&inventoryDetail.name!==null? inventoryDetail.name:""} | Modus Go!</title></Helmet>

			{isLoading? <Loading />:""}
			{isRender?
				<div>
					<div className="sticky-filter-datatable" onClick={props.toggleInventoryDetailDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>
					<div className={`datatable-filter${isShowInventoryDetailDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.toggleInventoryDetailDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
						</div>
						<div className="datatable-head-list">
							<div className="datatable-list-title text-darkblue font-weight-bold">
								Inventory table
							</div>
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isItemCode" name="isItemCode" onChange={props.setItemCodeCheckbox} checked={isItemCode} />
										<label className="custom-control-label font-weight-bold" htmlFor="isItemCode">ITEM CODE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isQuantity" name="isQuantity" onChange={props.setQuantityCheckbox} checked={isQuantity} />
										<label className="custom-control-label font-weight-bold" htmlFor="isQuantity">QUANTITY</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDescription" name="isDescription" onChange={props.setDescriptionCheckbox} checked={isDescription} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDescription">DESCRIPTION</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isLastTime" name="isLastTime" onChange={props.setLastTimeCheckbox} checked={isLastTime} />
										<label className="custom-control-label font-weight-bold" htmlFor="isLastTime">LAST TIME</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOnHandQuantity" name="isOnHandQuantity" onChange={props.setOnHandQuantityCheckbox} checked={isOnHandQuantity} />
										<label className="custom-control-label font-weight-bold" htmlFor="isOnHandQuantity">QTY ON HAND</label>
									</div>
								</li>
							</ul>
						</div>
					</div>
					<div className="main-content-area order-space mobile-bottom-space " id="test" onClick={props.toggleHideOrderDatatable}>
						<div className="container">
							<div className="row">
								<div className="col edit-title-wrap">
									<div className="edit-title mb-2 p-0 mobile-header-info">
										<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
										<h3>Inventory details<span>{inventoryDetail!==undefined&&inventoryDetail!==null? " - "+inventoryDetail.name:""} </span></h3>

									</div>
									<div className="mb-2">
									<input type="file" onChange={(e) => onFileChange(e)}  id="excelUploadFile" name="excelUploadFile"/>
									</div>

									<div id="buttonId" className="order-header">

										<div className="custom-checkbox top-head-btn">
											<button className="btn btn-primary rounded-pill ml-2 "   disabled={screenLock} type="button" onClick={() => props.uploadInventoryHandler()}>Upload</button>
											
											<button className="btn btn-primary rounded-pill ml-2"  disabled={screenLock} type="button" onMouseDown={e => e.preventDefault()} onClick={() => history.push("/InventoryList")}>Back</button>
											
											<button className={inventoryDetail!==undefined&&inventoryDetail!==null&&inventoryDetail.isInventoryended?"d-none":"btn btn-primary rounded-pill ml-2" } type="button" onClick={() => props.endInventoryHandler()}  disabled={screenLock}>End Inventory</button>
									       
												{inventoryDetail!==undefined&&inventoryDetail!==null&&!inventoryDetail.isInventoryended? 
											<button className="itemscanshow btn btn-primary rounded-pill ml-2" onClick={()=>scanItem()} type="button">Scan item</button>
											:""	}
										</div>
									</div>

								</div>
							</div>
						</div>


						<div id="DivDescriptionID" className="container bg-white header-shadow mt-2">
							<div className="container pt-3">
								<div className="row">
									<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
								</div>
							</div>
							<div className="row">
								<div className="col-10 col-sm-11 mt-3">
									<div className="floating-label">
										<textarea className="floating-input" tabIndex="3" placeholder=" " onChange={(e) => inputChangeHandler(e)} defaultValue={description} id="description" name="description"></textarea>
										<span className="highlight"></span>
										<label>Description</label>
									</div>
								</div>
								<div className="col-2 col-sm-1 mt-5 action-flex">
									<button type="button" tabIndex="0" className="WOITEMGridEditIcon round-icon-wrap border-blue" onClick={() => props.updateInventoryHandler()} ><i className="zmdi zmdi-save blue-icon"></i></button>
								</div>
							</div>
						</div>
						<div className="container p-0">
							<NewInventoryScanItemContainer isScreenLock={isScreenLock}  setScreenLock={()=>props.setScreenLock()}  isFromFinal={false} getInventoryItemList={getInventoryItemList} closeInventoryScanItem={closeInventoryScanItem} isFromInventory={true} />
						</div>

						<div className="container mt-3">
							<div className="row">
								<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
							</div>
						</div>
						<div className="container bg-white header-shadow mt-1 mb-150">
							<div className="row ">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblInventoryItem" className="table w-100 new-order-list wo-collasped" >
											<thead>
												<tr>
													<th className="plus-sort"></th>

													<th style={{display: isSetItemCode? '':'none'}} className={inventoryItemListParam.sortColumnName==="itemLookupCode"? inventoryItemListParam.sortDirection==="asc"? "sort-top itemLookupCode":"sort-bottom itemLookupCode":"sort-both itemLookupCode"} onClick={() => props.sortHandler("itemLookupCode")}>ITEM CODE</th>

													<th style={{display: isSetQuantity? '':'none'}} className={inventoryItemListParam.sortColumnName==="quantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top quantity":"sort-bottom quantity":"sort-both quantity"} onClick={() => props.sortHandler("description")}>QUANTITY</th>

													<th style={{display: isSetDescription? '':'none'}} className={inventoryItemListParam.sortColumnName==="description"? inventoryItemListParam.sortDirection==="asc"? "sort-top description":"sort-bottom description":"sort-both description"} onClick={() => props.sortHandler("description")}>Description</th>

													<th style={{display: isSetLastTime? '':'none'}} className={inventoryItemListParam.sortColumnName==="lastTime"? inventoryItemListParam.sortDirection==="asc"? "sort-top":"sort-bottom lastTime":"sort-both lastTime"} onClick={() => props.sortHandler("lastTime")}>Last Time</th>

													<th style={{display: isSetOnHandQuantity? '':'none'}} className={inventoryItemListParam.sortColumnName==="onHandQuantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top onHandQuantity":"sort-bottom onHandQuantity":"sort-both onHandQuantity"} onClick={() => props.sortHandler("onHandQuantity")}>Qty On Hand</th>

													<th className="">{isModified? <span className="d-md-none">Delete</span>:''}</th>

												</tr>
											</thead>
											<tbody>

												{inventoryItemList.map((itemList,index) =>
													<tr key={itemList.id} className={index==0?updatedValue:""}>
														<td className={`details-control plus-sort`} tabIndex="-1"></td>

														<td style={{display: isSetItemCode? '':'none'}} className="default-cursor itemLookupCode">{itemList.itemLookupCode}</td>

														<td style={{display: isSetQuantity? '':'none'}} className="default-cursor quantity">{itemList.quantity}</td>

														<td style={{display: isSetDescription? '':'none'}} className="default-cursor description">{itemList.description}</td>

														<td style={{display: isSetLastTime? '':'none'}} className="default-cursor lastTime">{itemList.lastTime}</td>

														<td style={{display: isSetOnHandQuantity? '':'none'}} className="default-cursor onHandQuantity">{itemList.onhandQantity}</td>

														<td>{isModified? <button disabled={isModified? false:true} type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del InventoryItemGridDeleteIcon" data={itemList.itemId}><i className="zmdi zmdi-delete "></i></button>:''}</td>
													</tr>
												)}
											</tbody>
										</table>
									</div>

								</div>

							</div>
						</div>
					</div>

				</div>
				:""}
			{isInventoryScanItem? <InventoryScanItemContainer isFromFinal={false} getInventoryItemList={getInventoryItemList} closeInventoryScanItem={closeInventoryScanItem} />:''}

		</div>
	)
}