import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import $ from 'jquery';
import AuthenticateService from '../../../../Services/AuthenticateServices';


const obj = new AuthenticateService();


export function setSuccessDialogue(check, status, message) {
    return (dispatch) => {
        dispatch({
            type: types.RESET_SUCCESS_DIALOGUE,
            isSuccessDialogue: check,
            status: status,
            message: message,
        });
    }
}

export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}



export function ResetAllItemSupllierList(isLoading) {
    return (dispatch) => {
        dispatch({
            type: types.GET_RESETALLSUPPLIER_REQUEST,
            isLoading: isLoading
        });
        let url = 'Setting/ResetAllItemSupllier';
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    /* Logic */
                    dispatch({
                        type: types.GET_RESETALLSUPPLIER_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_RESETALLSUPPLIER_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}


export function GetAllAPINameMasterList(isLoading) {
 
    return (dispatch) => {
        dispatch({
            type: types.GET_ALLAPINAME_REQUEST,
            isLoading: isLoading
        });
        let url = 'APIColumnMapping/get/GetAllAPINameMasterList';
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_ALLAPINAME_SUCCESS,
                        data: response.payload,
                    });
                  
                } else {
                    dispatch({
                        type: types.GET_ALLAPINAME_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}






//#region UpdateIsDisplayAPI 
export function UpdateIsDisplayAPI(Param,isLoading) {
   
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_ISDISPLAYAPI_REQUEST,
            isLoading:isLoading
        });
        let url = 'APIColumnMapping/UpdateIsDisplayAPI';
        return post(url, Param)
            .then((response) => {
                const { payload } = response;
                if (response.status === 200) {
                    /* Logic */
               
                    dispatch(GetAllAPINameMasterList(true));
                    dispatch({
                        type: types.UPDATE_ISDISPLAYAPI_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_ISDISPLAYAPI_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion UpdateIsDisplayAPI 
