import React,{Component} from 'react';
import './Pricing.css'
import CommonServices from '../../Services/CommonServices'
import ItemsServices from '../../Services/ItemsServices'
import swal from 'sweetalert';
import {FormWithConstraints} from 'react-form-with-constraints'
import {showLoader,hideLoader} from '../../Custom';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from 'moment';
import NumberFormat from 'react-number-format';

class Pricing extends Component {

    constructor(props) {
        super(props);
        this.state={
            Schedules: [],
            PricingformFields: {
                itemId: 0,
                itemPricingPrice: "",
                itemPricingCost: "",
                priceA: "",
                priceB: "",
                priceC: "",
                msrp: "",
                priceLowerBound: "",
                priceUpperBound: "",
                salePrice: "",
                saleStartDate: "",
                saleEndDate: "",
                saleScheduleID: "",
                buydownPrice: "",
                buydownQuantity: "",
                pricingProfitMargin: "",
                saleType: 0,
                ColumnPriceName:"",
            },
            formFieldsOldValue: {
                itemPricingPrice: "",
                itemPricingCost: "",
                priceA: "",
                priceB: "",
                priceC: "",
                msrp: "",
                priceLowerBound: "",
                priceUpperBound: "",
                salePrice: "",
                saleStartDate: "",
                saleEndDate: "",
                saleScheduleID: "",
                buydownPrice: "",
                buydownQuantity: "",
                pricingProfitMargin: ""
                ,priceD: "",
                 priceE: "",
            },
            priceList: [
               
                { id: "priceD",value:"Price Level D"  , isChecked: false},
                { id: "priceE",value:"Price Level E"  , isChecked: false},
                { id: "priceF",value:"Price Level F"  , isChecked: false},
                { id: "priceG",value:"Price Level G"  , isChecked: false},
                { id: "priceH",value:"Price Level H"  , isChecked: false},
                { id: "priceI",value:"Price Level I"  , isChecked: false},
                { id: "priceJ",value:"Price Level J"  , isChecked: false},
                { id: "priceK",value:"Price Level K"  , isChecked: false},
                { id: "priceL",value:"Price Level L"  , isChecked: false},
                { id: "priceM",value:"Price Level M"  , isChecked: false},
                { id: "priceN",value:"Price Level N"  , isChecked: false},
                { id: "priceO",value:"Price Level O"  , isChecked: false},
                { id: "priceP",value:"Price Level P"  , isChecked: false},
                { id: "priceQ",value:"Price Level Q"  , isChecked: false},
                { id: "priceR",value:"Price Level R"  , isChecked: false},
                { id: "priceS",value:"Price Level S"  , isChecked: false},
                { id: "priceT",value:"Price Level T"  , isChecked: false},
                { id: "priceU",value:"Price Level U"  , isChecked: false},
                { id: "priceV",value:"Price Level V"  , isChecked: false},
                { id: "priceW",value:"Price Level W"  , isChecked: false},
                { id: "priceX",value:"Price Level X"  , isChecked: false},
                { id: "priceY",value:"Price Level Y"  , isChecked: false},
                { id: "priceZ",value:"Price Level Z"  , isChecked: false}
              ]
        }

    }

    componentDidMount() {
        showLoader();
        this.getSchedules();
        this.BindPricingformFields();
        // Auto Save Draft after 2 min
        try {
            this.intervalID=setInterval(async () => {
                this.formHandler(false,'interval')
            },120*1000);
        } catch(e) {
            console.log(e);
        }
        hideLoader();
    }
    componentWillUnmount() {
        clearInterval(this.intervalID);
    }
    //#region  get Schedules to bind dropdown
    getSchedules=async () => {
        try {
            var Obj=new CommonServices();
            await Obj.GetAllSchedules().then((Schedules) => {
                if(!!Schedules.values)
                    this.setState({
                        Schedules: Schedules.values
                    });
            });
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#endregion get Schedules to bind dropdown
    //#region bind item pricing
    BindPricingformFields=() => {
        try {
            let priceList=this.state.priceList;
            if(!!this.props.itemPricing) {
                var PricingformFields={
                    itemId: this.props.itemId,
                    itemPricingPrice: this.props.itemPricing.price.toFixed(2),
                    itemPricingCost: this.props.itemPricing.cost.toFixed(4),
                    priceA: this.props.itemPricing.priceA.toFixed(2),
                    priceB: this.props.itemPricing.priceB.toFixed(2),
                    priceC: this.props.itemPricing.priceC.toFixed(2),
                    msrp: this.props.itemPricing.msrp.toFixed(2),
                    priceLowerBound: this.props.itemPricing.priceLowerBound.toFixed(2),
                    priceUpperBound: this.props.itemPricing.priceUpperBound.toFixed(2),
                    salePrice: this.props.itemPricing.salePrice.toFixed(2),
                    saleStartDate: this.props.itemPricing.saleStartDate===null? '':new Date(this.props.itemPricing.saleStartDate),
                    saleEndDate: this.props.itemPricing.saleEndDate===null? '':new Date(this.props.itemPricing.saleEndDate),
                    saleScheduleID: this.props.itemPricing.saleScheduleID,
                    buydownPrice: this.props.itemPricing.buydownPrice.toFixed(2),
                    buydownQuantity: this.props.itemPricing.buydownQuantity.toFixed(2),
                    pricingProfitMargin: "",
                    saleType: this.props.itemPricing.saleType,
                }
                for ( let i = 0; i < priceList.length; i++) {
                    PricingformFields[priceList[i].id] = this.props.itemPricing[priceList[i].id].toFixed(2);
                }

                this.setState({PricingformFields},() => {
                    if(this.state.PricingformFields.saleType>0)
                        document.getElementById("sale-chk").checked=true;
                    this.inputEnableDisableHandler();
                    this.setpricingProfitMargin("");
                });
                let ItemBigcommerceDetail=this.props.ItemBigcommerceDetail;
                ItemBigcommerceDetail["sale_price"]=parseFloat(PricingformFields.salePrice);
                this.setState({ItemBigcommerceDetail:ItemBigcommerceDetail});
            }
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#endregion  bind item pricing
    //#region all controll onChange handle method  
    inputChangeHandler(e) {
        try {
            var value=e.target.value;
            let priceList=this.state.priceList;
            if(e.target.name==="itemPricingPrice"||e.target.name==="itemPricingCost"||e.target.name==="pricingProfitMargin"||e.target.name==="priceA"||e.target.name==="priceB"||e.target.name==="priceC"||e.target.name==="msrp"||e.target.name==="salePrice")
                value=value.replace(/,/g,"");
            //#region displayed old value
            let formFieldsOldValue={...this.state.formFieldsOldValue};
            let itemPricingProps={...this.props.itemPricing};
            var itemPricingPropsField="";
            switch(e.target.name) {
                case 'itemPricingPrice':
                    itemPricingPropsField="price";
                    break;
                case 'itemPricingCost':
                    itemPricingPropsField="cost";
                    break;
                case 'salePrice':
                    let ItemBigcommerceDetail=this.props.ItemBigcommerceDetail;
                    ItemBigcommerceDetail["sale_price"]=parseFloat(value);
                    this.setState({ItemBigcommerceDetail:ItemBigcommerceDetail})
                    break;
                default:
                    itemPricingPropsField=e.target.name;
                    break;
            }

            for ( let i = 0; i < priceList.length; i++) {
                if(e.target.name==priceList[i].id)
                value=value.replace(/,/g,"");
            }
              
            if(itemPricingProps[itemPricingPropsField]!==undefined) {
                if(itemPricingProps[itemPricingPropsField].toString()!==value) {
                    if(itemPricingPropsField==="saleScheduleID")
                        itemPricingPropsField="saleScheduleName";
                    formFieldsOldValue[e.target.name]=itemPricingProps[itemPricingPropsField]!==""&&itemPricingProps[itemPricingPropsField]!==null? itemPricingProps[itemPricingPropsField]:"";
                    this.props.setcurrentPODChanged("Pricing");
                }
                else
                    formFieldsOldValue[e.target.name]="";
            }
            //#endregion displayed old value

            if(value.length>e.target.maxLength) {
                value=value.substring(0,e.target.maxLength);
            }
            if(e.target.name!=="saleStartDate"&&e.target.name!=="saleEndDate"&&value!=="")
                value=parseFloat(value);
            this.form.validateFields(e.target);
            let PricingformFields={...this.state.PricingformFields};
            PricingformFields[e.target.name]=value;
            this.setState({
                PricingformFields,formFieldsOldValue
            });
            if(e.target.name==="itemPricingPrice") {
                this.props.setitemCommonPrice(value);
                this.props.setitemCommonPriceOldValue(formFieldsOldValue.itemPricingPrice);
            }
            if(e.target.name==="itemPricingCost") {
                this.props.setitemCommonCost(value);
                this.props.setitemCommonCostOldValue(formFieldsOldValue.itemPricingCost);
            }
            if(e.target.name==="pricingProfitMargin") {
                this.props.setitemCommonProfit(value);
                this.props.setitemCommonProfitOldValue(formFieldsOldValue.profitMargin);
            }
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#endregion all controll onChange handle method
    //#region clear form fields old values
    clearFormFieldsOldValues=() => {
        var formFieldsOldValue={
            itemPricingPrice: "",
            itemPricingCost: "",
            priceA: "",
            priceB: "",
            priceC: "",
            msrp: "",
            priceLowerBound: "",
            priceUpperBound: "",
            salePrice: "",
            saleStartDate: "",
            saleEndDate: "",
            saleScheduleID: "",
            buydownPrice: "",
            buydownQuantity: "",
            pricingProfitMargin: "",
            priceD:"",
            priceE:""
        }
        this.setState({formFieldsOldValue: formFieldsOldValue});
    }
    //#endregion clear form fields old values
    setpricingProfitMargin(e) {
        try {
            let PricingformFields={...this.state.PricingformFields};
            var pricingProfitMargin=0.00;
            if(this.state.PricingformFields.itemPricingPrice!=="") {
                pricingProfitMargin=(((parseFloat(this.state.PricingformFields.itemPricingPrice!==""? this.state.PricingformFields.itemPricingPrice:0)-parseFloat(this.state.PricingformFields.itemPricingCost!==""? this.state.PricingformFields.itemPricingCost:0))*100)/parseFloat(this.state.PricingformFields.itemPricingPrice!==""? this.state.PricingformFields.itemPricingPrice:0)).toFixed(2);
                pricingProfitMargin=pricingProfitMargin==="NaN"? 0:pricingProfitMargin;
            }
            PricingformFields["pricingProfitMargin"]=pricingProfitMargin;
            this.setState({
                PricingformFields
            });
            this.props.setitemCommonProfit(pricingProfitMargin);
            // this.props.setitemCommonProfitOldValue(pricingProfitMargin);  
            //#region displayed old value
            let formFieldsOldValue={...this.state.formFieldsOldValue};
            let itemPricingProps={...this.props.itemPricing};
            var oldProfitMargin=(((parseFloat(itemPricingProps.price!==""? itemPricingProps.price:0)-parseFloat(itemPricingProps.cost!==""? itemPricingProps.cost:0))*100)/parseFloat(itemPricingProps.price!==""? itemPricingProps.price:0)).toFixed(2);
            oldProfitMargin=oldProfitMargin==="NaN"? 0:oldProfitMargin;
            if(oldProfitMargin!==pricingProfitMargin) {

                formFieldsOldValue["pricingProfitMargin"]=oldProfitMargin!==""? oldProfitMargin:"";
                this.props.setitemCommonProfitOldValue(oldProfitMargin);
            }
            else {
                formFieldsOldValue["pricingProfitMargin"]="";
                this.props.setitemCommonProfitOldValue("");
            }
            this.setState({
                formFieldsOldValue
            });

            //#endregion displayed old value
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#region  enable or disable inpute fields
    //#region set price
    setPricingPrice=(e) => {
        try {
            let PricingformFields={...this.state.PricingformFields};
            var price=((parseFloat(this.state.PricingformFields.itemPricingCost!==""? this.state.PricingformFields.itemPricingCost:0))/((100-parseFloat(this.state.PricingformFields.pricingProfitMargin!==""? this.state.PricingformFields.pricingProfitMargin:0))/100)).toFixed(2);
            PricingformFields["itemPricingPrice"]=price;
            this.props.setitemCommonPrice(price);
            this.setState({PricingformFields});

        } catch(error) {
            console.log(error);
        }
    }

    //#endregion set price
    inputEnableDisableHandler=() => {
        try {
            let PricingformFields={...this.state.PricingformFields};
            //this.props.setcurrentPODChanged("Pricing");
            if(document.getElementById("sale-chk").checked) {

                document.getElementById("salePrice").disabled=false;
                document.getElementById("customRadio1").disabled=false;
                document.getElementById("customRadio2").disabled=false;
                if(document.getElementById("customRadio1").checked) {
                    PricingformFields["saleType"]=1;
                    //document.getElementById("saleStartDate").disabled = false;
                    // document.getElementById("saleStartDatediv").className = "floating-label my-2";
                    // document.getElementById("saleEndDate").disabled = false;
                    // document.getElementById("saleEndDatediv").className = "floating-label my-2";
                    document.getElementById("saleScheduleID").disabled=true;
                    document.getElementById("start-date-chk").disabled=false;
                    document.getElementById("end-date-chk").disabled=false;
                    if(this.state.PricingformFields.saleStartDate!==null&&this.state.PricingformFields.saleStartDate!=="")
                        document.getElementById("start-date-chk").checked=true;
                    else
                        document.getElementById("start-date-chk").checked=false;

                    if(this.state.PricingformFields.saleEndDate!==null&&this.state.PricingformFields.saleEndDate!=="")
                        document.getElementById("end-date-chk").checked=true;
                    else
                        document.getElementById("end-date-chk").checked=false;
                    if(document.getElementById("start-date-chk").checked) {
                        document.getElementById("saleStartDate").disabled=false;
                        document.getElementById("saleStartDatediv").className="floating-label my-2";
                    }
                    else {
                        document.getElementById("saleStartDate").disabled=true;
                        document.getElementById("saleStartDatediv").className="datepickerdisabled";
                    }
                    if(document.getElementById("end-date-chk").checked) {
                        document.getElementById("saleEndDate").disabled=false;
                        document.getElementById("saleEndDatediv").className="floating-label my-2";
                    }
                    else {
                        document.getElementById("saleEndDate").disabled=true;
                        document.getElementById("saleEndDatediv").className="datepickerdisabled";
                    }

                }
                else if(document.getElementById("customRadio2").checked) {
                    PricingformFields["saleType"]=2;
                    document.getElementById("saleStartDate").disabled=true;
                    document.getElementById("saleStartDatediv").className="datepickerdisabled";
                    document.getElementById("saleEndDate").disabled=true;
                    document.getElementById("saleEndDatediv").className="datepickerdisabled";
                    document.getElementById("start-date-chk").disabled=true;
                    document.getElementById("end-date-chk").disabled=true;
                    document.getElementById("saleScheduleID").disabled=false;
                }
                else {
                    PricingformFields["saleType"]=0;
                    document.getElementById("saleStartDate").disabled=true;
                    document.getElementById("saleStartDatediv").className="datepickerdisabled";
                    document.getElementById("saleEndDate").disabled=true;
                    document.getElementById("saleEndDatediv").className="datepickerdisabled";
                    document.getElementById("saleScheduleID").disabled=true;
                    document.getElementById("start-date-chk").disabled=true;
                    document.getElementById("end-date-chk").disabled=true;
                }

            }
            else {
                PricingformFields["saleType"]=0;
                document.getElementById("salePrice").disabled=true;
                document.getElementById("customRadio1").disabled=true;
                document.getElementById("saleStartDate").disabled=true;
                document.getElementById("saleStartDatediv").className="datepickerdisabled";
                document.getElementById("saleEndDate").disabled=true;
                document.getElementById("saleEndDatediv").className="datepickerdisabled";
                document.getElementById("customRadio2").disabled=true;
                document.getElementById("saleScheduleID").disabled=true;
                document.getElementById("start-date-chk").disabled=true;
                document.getElementById("end-date-chk").disabled=true;
            }
            this.setState({PricingformFields});
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }




    //#endregion  enable or disable inpute fields
    //#region post form data to save
    formHandler=async (showMessage,click) => {
        try {
            if(showMessage)
                showLoader();
            var PricingformFields={...this.state.PricingformFields};
            PricingformFields.itemPricingPrice=parseFloat(PricingformFields.itemPricingPrice!==""? PricingformFields.itemPricingPrice:"0");
            PricingformFields.itemPricingCost=parseFloat(PricingformFields.itemPricingCost!==""? PricingformFields.itemPricingCost:"0");
            PricingformFields.priceA=parseFloat(PricingformFields.priceA!==""? PricingformFields.priceA:"0");
            PricingformFields.priceB=parseFloat(PricingformFields.priceB!==""? PricingformFields.priceB:"0");
            PricingformFields.priceC=parseFloat(PricingformFields.priceC!==""? PricingformFields.priceC:"0");
            PricingformFields.msrp=parseFloat(PricingformFields.msrp!==""? PricingformFields.msrp:"0");
            PricingformFields.priceLowerBound=parseFloat(PricingformFields.priceLowerBound!==""? PricingformFields.priceLowerBound:"0.00");
            PricingformFields.priceUpperBound=parseFloat(PricingformFields.priceUpperBound!==""? PricingformFields.priceUpperBound:"0.00");
            PricingformFields.salePrice=parseFloat(PricingformFields.salePrice!==""? PricingformFields.salePrice:"0");
            PricingformFields.saleScheduleID=parseFloat(PricingformFields.saleScheduleID!==""? PricingformFields.saleScheduleID:"0");
            PricingformFields.buydownPrice=parseFloat(PricingformFields.buydownPrice!==""? PricingformFields.buydownPrice:"0.00");
            PricingformFields.buydownQuantity=parseFloat(PricingformFields.buydownQuantity!==""? PricingformFields.buydownQuantity:"0.00");
            PricingformFields.pricingProfitMargin=parseFloat(PricingformFields.pricingProfitMargin!==""? PricingformFields.pricingProfitMargin:"0");
            PricingformFields.saleStartDate=PricingformFields.saleStartDate!==""&&PricingformFields.saleStartDate!==null? moment(PricingformFields.saleStartDate).format('YYYY-MM-DD'):'';
            PricingformFields.saleEndDate=PricingformFields.saleEndDate!==""&&PricingformFields.saleEndDate!==null? moment(PricingformFields.saleEndDate).format('YYYY-MM-DD'):'';
            var str="";
            for (var i = 3; i < 26; i++) {
                let price="price"+(i+10).toString(36).toUpperCase();
                str = str + ',' +price+"="+ parseFloat(PricingformFields[price]);
            }
            str=str.substring(1);
            PricingformFields.ColumnPriceName=str;
            this.form.validateFields();
            if(this.form.isValid()) {
                var Obj=new ItemsServices();
                await Obj.updateItemPricing(PricingformFields).then((response) => {

                    if(click==='saveClick') {
                        if(showMessage||response.status.toLowerCase()==="error")
                            swal({
                                title: response.status,
                                text: response.message,
                                icon: response.status.toLowerCase(),
                                timer: response.status.toLowerCase()!=="error"? 3000:"",
                                button: response.status.toLowerCase()!=="error"? false:"Ok",
                            })
                        if(response.status.toLowerCase()!=="error") {
                            this.clearFormFieldsOldValues();
                            this.props.setCODNULL();
                        }
                        //   this.props.history.push("/ItemEdit/"+response.itemId);
                        hideLoader();

                    }

                }).catch(function(error) {
                    hideLoader();
                    console.log(error);
                    //Perform action based on error
                });
            }
            hideLoader();
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#endregion post form data to save
    startDateChangeHandler=date => {
        try {
            if(document.getElementById("saleStartDate").disabled===false) {
                let formFieldsOldValue={...this.state.formFieldsOldValue};
                let itemPricingProps={...this.props.itemPricing};
                var PricingformFields={...this.state.PricingformFields}
                if(itemPricingProps["saleStartDate"]!==undefined) {
                    if(itemPricingProps["saleStartDate"]!==moment(date).format('MM/DD/yyyy')) {

                        formFieldsOldValue["saleStartDate"]=itemPricingProps["saleStartDate"]!==""&&itemPricingProps["saleStartDate"]!==null? itemPricingProps["saleStartDate"]:"";
                        this.props.setcurrentPODChanged("Pricing");
                    }
                    else
                        formFieldsOldValue["saleStartDate"]="";
                }
                PricingformFields.saleStartDate=date;
                this.setState({PricingformFields,formFieldsOldValue});

            }

        } catch(error) {
            console.log(error);
            hideLoader();
        }

    };
    handleDateChangeRaw=(e) => {
        e.preventDefault();
    }

    endDateChangeHandler=date => {
        try {
            if(document.getElementById("saleEndDate").disabled===false) {

                let formFieldsOldValue={...this.state.formFieldsOldValue};
                let itemPricingProps={...this.props.itemPricing};
                var PricingformFields={...this.state.PricingformFields}
                if(itemPricingProps["saleEndDate"]!==undefined) {
                    if(itemPricingProps["saleEndDate"]!==moment(date).format('MM/DD/yyyy')) {

                        formFieldsOldValue["saleEndDate"]=itemPricingProps["saleEndDate"]!==""&&itemPricingProps["saleEndDate"]!==null? itemPricingProps["saleEndDate"]:"";
                        this.props.setcurrentPODChanged("Pricing");
                    }
                    else
                        formFieldsOldValue["saleEndDate"]="";
                }
                PricingformFields.saleEndDate=date;
                this.setState({PricingformFields,formFieldsOldValue});

            }
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    };
    startDateCHKHandler=() => {
        try {
            let formFieldsOldValue={...this.state.formFieldsOldValue};
            let itemPricingProps={...this.props.itemPricing};
            var PricingformFields={...this.state.PricingformFields}
            var date=null;
            if(document.getElementById("start-date-chk").checked) {
                document.getElementById("saleStartDate").disabled=false;
                document.getElementById("saleStartDatediv").className="floating-label my-2";
                PricingformFields.saleStartDate=new Date();
                date=new Date();
            }
            else {
                document.getElementById("saleStartDate").disabled=true;
                document.getElementById("saleStartDatediv").className="datepickerdisabled";
                PricingformFields.saleStartDate=null;
            }
            if(itemPricingProps["saleStartDate"]!==undefined) {
                if(itemPricingProps["saleStartDate"]!==moment(date).format('MM/DD/yyyy')) {

                    formFieldsOldValue["saleStartDate"]=itemPricingProps["saleStartDate"]!==""&&itemPricingProps["saleStartDate"]!==null? itemPricingProps["saleStartDate"]:"";
                    this.props.setcurrentPODChanged("Pricing");
                }
                else
                    formFieldsOldValue["saleStartDate"]="";
            }

            this.setState({PricingformFields,formFieldsOldValue});
        } catch(error) {
            console.log(error);
            hideLoader();
        }

    }
    endDateCHKHandler=() => {
        try {
            let formFieldsOldValue={...this.state.formFieldsOldValue};
            let itemPricingProps={...this.props.itemPricing};
            var PricingformFields={...this.state.PricingformFields}
            var date=null;

            if(document.getElementById("end-date-chk").checked) {
                document.getElementById("saleEndDate").disabled=false;
                document.getElementById("saleEndDatediv").className="floating-label my-2";
                PricingformFields.saleEndDate=new Date();
                date=new Date();
            }
            else {
                document.getElementById("saleEndDate").disabled=true;
                document.getElementById("saleEndDatediv").className="datepickerdisabled";
                PricingformFields.saleEndDate=null;
            }
            if(itemPricingProps["saleEndDate"]!==undefined) {
                if(itemPricingProps["saleEndDate"]!==moment(date).format('MM/DD/yyyy')) {

                    formFieldsOldValue["saleEndDate"]=itemPricingProps["saleEndDate"]!==""&&itemPricingProps["saleEndDate"]!==null? itemPricingProps["saleEndDate"]:"";
                    this.props.setcurrentPODChanged("Pricing");
                }
                else
                    formFieldsOldValue["saleEndDate"]="";
            }

            this.setState({PricingformFields,formFieldsOldValue});
        } catch(error) {
            console.log(error);
            hideLoader();
        }
    }
    //#region render html  
    render() {
        return (
            <div>
                <div className="card-body row">
                    <FormWithConstraints ref={form => this.form=form} noValidate >
                        <div className="col-12 padding-space">
                            <fieldset>

                                <legend> General pricing </legend>
                                <div className="row">
                                    <div className="col-6">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.props.itemCommonPrice&&"$"}</span>
                                            {/* <input type="number" step=".01" maxLength="8" placeholder=" " onBlur={(e) => this.setpricingProfitMargin(e)} className="floating-input pl-3 clsitemPrice" id="itemPricingPrice" name="itemPricingPrice" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonPrice} /> */}
                                            <NumberFormat autoFocus thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemCost" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="itemPricingPrice" name="itemPricingPrice" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonPrice} />
                                            <span className="highlight"></span>
                                            <label>Price {this.props.itemCommonPriceOldValue!==""?
                                                // " : " + this.props.itemCommonPriceOldValue 
                                                <NumberFormat value={this.props.itemCommonPriceOldValue} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                    <div className="col-6">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.props.itemCommonCost&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" onBlur={(e) => this.setpricingProfitMargin(e)} className="floating-input pl-3 clsitemCost" id="itemPricingCost" name="itemPricingCost" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonCost} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3 clsitemCost" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="4" maxLength="11" min="0" id="itemPricingCost" name="itemPricingCost" onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonCost} />
                                            <span className="highlight"></span>
                                            <label>Cost {this.props.itemCommonCostOldValue!==""?
                                                // " : " + this.props.itemCommonCostOldValue
                                                <NumberFormat value={this.props.itemCommonCostOldValue} displayType={'text'} decimalScale="4" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label d-flex">
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="5" className="floating-input" id="pricingProfitMargin" name="pricingProfitMargin" onBlur={(e) => this.setPricingPrice(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonProfit} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="5" min="0" id="pricingProfitMargin" name="pricingProfitMargin" onBlur={(e) => this.setPricingPrice(e)} onChange={(e) => this.inputChangeHandler(e)} value={this.props.itemCommonProfit} />
                                            <span className="dolar per-right">%</span>
                                            <span className="highlight"></span>
                                            <label>Profit margin {this.props.itemCommonProfitOldValue!==""?
                                                // " : " + this.props.itemCommonProfitOldValue 
                                                <NumberFormat value={this.props.itemCommonProfitOldValue} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': '} suffix={'%'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.priceA&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceA" name="priceA" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceA} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="priceA" name="priceA" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceA} />
                                            <span className="highlight"></span>
                                            <label>Price level A {this.state.formFieldsOldValue.priceA!==""?
                                                // " : " + this.state.formFieldsOldValue.priceA 
                                                <NumberFormat value={this.state.formFieldsOldValue.priceA} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label d-flex">
                                            <span className="dolar">{!!this.state.PricingformFields.priceB&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceB" name="priceB" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceB} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="priceB" name="priceB" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceB} />
                                            <span className="highlight"></span>
                                            <label>Price level B {this.state.formFieldsOldValue.priceB!==""?
                                                // " : " + this.state.formFieldsOldValue.priceB 
                                                <NumberFormat value={this.state.formFieldsOldValue.priceB} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>

                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.priceC&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceC" name="priceC" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceC} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="priceC" name="priceC" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceC} />
                                            <span className="highlight"></span>
                                            <label>Price level C {this.state.formFieldsOldValue.priceC!==""?
                                                //  " : " + this.state.formFieldsOldValue.priceC
                                                <NumberFormat value={this.state.formFieldsOldValue.priceC} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>


                                    {this.state.priceList.map((priceList) =>
                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields[priceList.id]&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceC" name="priceC" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceC} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id={priceList.id} name={priceList.id} onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields[priceList.id]} />
                                            <span className="highlight"></span>
                                            <label>{priceList.value}
                                            {this.state.formFieldsOldValue[priceList.id]!==""?
                                                //  " : " + this.state.formFieldsOldValue.priceC
                                                <NumberFormat value={this.state.formFieldsOldValue[priceList.id]} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""} </label>
                                        </div>
                                    </div>
                                    )}

                                    <div className="col-sm-6 col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.msrp&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="msrp" name="msrp" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.msrp} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" type="text" pattern="[0-9]*" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="msrp" name="msrp" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.msrp} />
                                            <span className="highlight"></span>
                                            <label>MSRP {this.state.formFieldsOldValue.msrp!==""?
                                                // " : " + this.state.formFieldsOldValue.msrp 
                                                <NumberFormat value={this.state.formFieldsOldValue.msrp} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>
                        </div>
                        <div className="col-12 mt-3 padding-space">
                            <fieldset>
                                <legend> Sales pricing </legend>
                                <div className="row">
                                    <div className="col-12">
                                        <div className="custom-control custom-checkbox mb-2 ml-2">
                                            <input type="checkbox" className="custom-control-input" id="sale-chk" name="sale-chk" onChange={this.inputEnableDisableHandler} defaultChecked={this.state.PricingformFields.saleType>0? true:false} />
                                            <label className="custom-control-label" htmlFor="sale-chk">This item on sale</label>
                                        </div>

                                    </div>
                                    <div className="col-12">
                                        <div className="floating-label my-2">
                                            <span className={`dolar ${this.state.PricingformFields.saleType===0? " disabled-text":""}`}>{!!this.state.PricingformFields.salePrice&&"$"}</span>
                                            {/* <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="salePrice" name="salePrice" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.salePrice} /> */}
                                            <NumberFormat thousandSeparator={true} thousandsGroupStyle="lakh" className="floating-input pl-3" inputMode="numeric" decimalScale="2" maxLength="8" min="0" id="salePrice" name="salePrice" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.salePrice} />
                                            <span className="highlight"></span>
                                            <label>Sale price {this.state.formFieldsOldValue.salePrice!==""?
                                                //  " : " + this.state.formFieldsOldValue.salePrice 
                                                <NumberFormat value={this.state.formFieldsOldValue.salePrice} displayType={'text'} decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" prefix={': $'} />
                                                :""}</label>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="custom-control custom-radio ml-2 my-2">
                                            <input type="radio" id="customRadio1" name="customRadio" className="custom-control-input" onChange={this.inputEnableDisableHandler} checked={this.state.PricingformFields.saleType===1? true:false} />
                                            <label className="custom-control-label" htmlFor="customRadio1">This sale is in effect for a special period</label>
                                        </div>


                                    </div>
                                    <div className="col-12 col-sm-6 d-flex align-items-center">
                                        <div className="custom-control custom-checkbox mb-2 ml-2">
                                            <input type="checkbox" className="custom-control-input" id="start-date-chk" name="start-date-chk" onChange={this.startDateCHKHandler} />
                                            <label className="custom-control-label" htmlFor="start-date-chk"></label>
                                        </div>
                                        <div className="my-2 " id="saleStartDatediv">

                                            <DatePicker className="floating-input" id="saleStartDate"
                                                disabledKeyboardNavigation
                                                onFocus={e => e.target.blur()}
                                                onChange={this.startDateChangeHandler}
                                                placeholderText="mm/dd/yyyy"
                                                selected={this.state.PricingformFields.saleStartDate}
                                                autocomplete="off"
                                            />
                                            <span className="highlight"></span>
                                            <label>Start Date {this.state.formFieldsOldValue.saleStartDate!==""? " : "+this.state.formFieldsOldValue.saleStartDate:""}</label>
                                        </div>

                                    </div>
                                    <div className="col-12 col-sm-6 d-flex align-items-center">
                                        <div className="custom-control custom-checkbox mb-2 ml-2">
                                            <input type="checkbox" className="custom-control-input" id="end-date-chk" name="end-date-chk" onChange={this.endDateCHKHandler} />
                                            <label className="custom-control-label" htmlFor="end-date-chk"></label>
                                        </div>
                                        <div className="floating-label my-2" id="saleEndDatediv">
                                            <DatePicker className="floating-input" id="saleEndDate"
                                                disabledKeyboardNavigation
                                                onFocus={e => e.target.blur()}
                                                placeholderText="mm/dd/yyyy"
                                                selected={this.state.PricingformFields.saleEndDate}
                                                onChange={this.endDateChangeHandler}
                                                autocomplete="off"
                                            />
                                            <span className="highlight"></span>
                                            <label>End Date {this.state.formFieldsOldValue.saleEndDate!==""? " : "+this.state.formFieldsOldValue.saleEndDate:""}</label>

                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="custom-control custom-radio ml-2 my-2">
                                            <input type="radio" id="customRadio2" name="customRadio" className="custom-control-input" onChange={this.inputEnableDisableHandler} checked={this.state.PricingformFields.saleType===2? true:false} />
                                            <label className="custom-control-label" htmlFor="customRadio2">This sale is in effect according to a schedule</label>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="floating-label my-2">
                                            <select className="floating-select" id="saleScheduleID" name="saleScheduleID" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.saleScheduleID}>
                                                <option key="s" value="">None</option>
                                                {this.state.Schedules.map((schedule) => <option key={schedule.id} value={schedule.id}>{schedule.name}</option>)}
                                            </select>
                                            <span className="highlight"></span>
                                            <label>Schedule {this.state.formFieldsOldValue.saleScheduleID!==""? " : "+this.state.formFieldsOldValue.saleScheduleID:""}</label>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>
                        </div>
                        <div className="col-12 mt-3 padding-space">
                            <fieldset>
                                <legend> Price bounds </legend>
                                <div className="row">
                                    <div className="col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.priceLowerBound&&"$"}</span><input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceLowerBound" name="priceLowerBound" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceLowerBound} />
                                            <span className="highlight"></span>
                                            <label>Lower bound {this.state.formFieldsOldValue.priceLowerBound!==""? " : "+this.state.formFieldsOldValue.priceLowerBound:""}</label>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.priceUpperBound&&"$"}</span><input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="priceUpperBound" name="priceUpperBound" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.priceUpperBound} />
                                            <span className="highlight"></span>
                                            <label>Upper bound {this.state.formFieldsOldValue.priceUpperBound!==""? " : "+this.state.formFieldsOldValue.priceUpperBound:""}</label>
                                        </div>
                                    </div>


                                </div>
                            </fieldset>
                        </div>
                        <div className="col-12 mt-3 padding-space">
                            <fieldset>
                                <legend> Buydown pricing </legend>
                                <div className="row">
                                    <div className="col-12">
                                        <div className="floating-label">
                                            <span className="dolar">{!!this.state.PricingformFields.buydownPrice&&"$"}</span><input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3" id="buydownPrice" name="buydownPrice" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.buydownPrice} />
                                            <span className="highlight"></span>
                                            <label>Buydown price {this.state.formFieldsOldValue.buydownPrice!==""? " : "+this.state.formFieldsOldValue.buydownPrice:""}</label>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="floating-label">
                                            <input type="number" step=".01" placeholder=" " maxLength="8" className="floating-input pl-3c" id="buydownQuantity" name="buydownQuantity" onChange={(e) => this.inputChangeHandler(e)} value={this.state.PricingformFields.buydownQuantity} />
                                            <span className="highlight"></span>
                                            <label>Buydown quantity {this.state.formFieldsOldValue.buydownQuantity!==""? " : "+this.state.formFieldsOldValue.buydownQuantity:""}</label>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>
                        </div>

                    </FormWithConstraints>
                </div>

            </div>


        );
    }
}
//#endregion render html
export default Pricing;