//#region insert order item
export function insertOrderItem(insertItemParam, orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity) {

    return (dispatch) => {
        dispatch({
            type: types.INSERT_ORDERITEM_REQUEST,
        });
        let url = 'order/insertOrderItem';
        return post(url, insertItemParam)
            .then((response) => {

                const { payload } = response;
              
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getOrderItemList(orderItemListParam, true,ItemName,ItemCode,Quantity,OnHandQuantity));
                    dispatch({
                        type: types.INSERT_ORDERITEM_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.INSERT_ORDERITEM_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
