//#region get Existing order  list
export function getExistingOrder(orderId) {
    return (dispatch) => {
        dispatch({
            type: types.GET_EXISTINGORDER_REQUEST,

        });
        let url = 'order/getExistingOrder/' + orderId;
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblCombineOrderItem').DataTable().destroy();
                    dispatch({
                        type: types.GET_EXISTINGORDER_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblCombineOrderItem').DataTable({
                        "paging": true,
                        "ordering": false,
                        "info": false,
                        "searching": true,
                        "responsive": true,
                        "retrieve": true,


                        "oLanguage": {
                            "sEmptyTable": "No data found",
                            "sZeroRecords": "No data found"
                        }
                    })
                } else {
                    dispatch({
                        type: types.GET_EXISTINGORDER_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get Existing order list
//#region combine order
export function combineOrder(combineOrderRequestParam, orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity) {
    return (dispatch) => {
        dispatch({
            type: types.COMBINE_ORDER_REQUEST,
        });

        let url = 'order/CombineOrder';
        return post(url, combineOrderRequestParam)
            .then((response) => {

                const { payload } = response;
              

                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 200) {
                    /* Logic */
                    combineOrderRequestParam = {
                        orderId: 0,
                        userId: 0,
                        orderIds: ""
                    };
                    dispatch(getOrderItemList(orderItemListParam, true,ItemName,ItemCode,Quantity,OnHandQuantity));
                    dispatch({
                        type: types.COMBINE_ORDER_SUCCESS,
                        data: response.payload,
                        combineOrderRequestParam: combineOrderRequestParam,
                    });

                } else {
                    dispatch({
                        type: types.COMBINE_ORDER_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion combine order
//#region get  order  detail
export function getOrderDetail(orderId) {
    return (dispatch) => {
        dispatch({
            type: types.GET_ORDERDETAIL_REQUEST,

        });
        let url = 'order/getOrderById/' + orderId;
        return get(url)
            .then((response) => {
                const { payload } = response;
              

                if (response.status === 200) {
                    /* Logic */
                    if (payload.code === "E08" || payload.code === "E03" || payload.code === "E05")
                        history.push("/Dashboard");
                    dispatch({
                        type: types.GET_ORDERDETAIL_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.GET_ORDERDETAIL_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
