//#region generate wo handler
export function generateWO(data, listName) {
    return (dispatch) => {
        dispatch({
            type: types.GENERATE_WO_REQUEST,
        });
        let url = 'order/GenerateWO';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */

                    swal({
                        title: payload.status,
                        text: payload.status.toLowerCase() !== "error" ? listName + " is successfully converted into work order with name " + payload.workOrderId : payload.message,
                        icon: payload.status.toLowerCase(),
                        button: "Ok",
                    }).then(() => {
                        if (payload.status.toLowerCase() !== "error")
                            history.push("/Dashboard");
                    });

                    dispatch({
                        type: types.GENERATE_WO_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.GENERATE_WO_FAILED,
                        data: response.payload,
                    });

                }

            })
    }
}
//#endregion generate wo handler
