import React from 'react';
import SerialComponent from '../component/SerialComponent';
import { } from '../state/action/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { withRouter } from 'react-router-dom';

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class SerialContainer extends React.Component {
    state = {
        checkedSerialNumber: 1
    }
    componentDidMount() {
        if (this.props.itemSerial.length > 0) {
            var checkedSerialNumber = this.props.itemSerial[0].serialNumberCount;
            this.setState({ checkedSerialNumber });
        }
        $('#tblSerial').DataTable({
            "paging": false,
            "ordering": false,
            "info": false,
            "searching": false,
            "responsive": true,
            "retrieve": true,
            "oLanguage": {
                "sEmptyTable": "No data found"
            },
            fixedHeader: true
        });

    }
    componentWillUnmount() {
        $('#tblSerial').DataTable().destroy();
    }

    render() {
        return (
            <SerialComponent
                {...this.props}
                checkedSerialNumber={this.state.checkedSerialNumber}
            />
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {

    };
};

const mapStateToProps = (state) => {
    const { } = state.SerialReducer;
    return {


    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SerialContainer));