import * as types from "../../../../constants/AppConstants";
import { post } from "../../../../service/index";
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import AuthenticateService from '../../../../Services/AuthenticateServices';
import swal from 'sweetalert';

const obj = new AuthenticateService();
//#region to get order list
export function getPurchaseOrderList(data, isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.GET_PURCHASEORDERS_REQUEST,
            isLoading: isLoading,
        });
        let url = 'order/purchaseOrderList';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    $('#tblPurchaseOrder').DataTable().destroy();
                    dispatch({
                        type: types.GET_PURCHASEORDERS_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblPurchaseOrder').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_PURCHASEORDERS_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get order list
