//#region to get search list
export function getSearchList(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_PURCHASEORDERSEARCH_REQUEST,
        });
        let url = 'order/searchPurchaseOrderByValue';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_PURCHASEORDERSEARCH_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_PURCHASEORDERSEARCH_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
