export function setpurchaseOrderList(data,isChecked) {

    return (dispatch) => {
        dispatch({
            type: types.SET_PURCHASEORDERSLIST,
            data: data,
            isChecked:isChecked

        });
    }
}
export function deleteBulkPO(data,isLoading,poData) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_DELETEBULKPO_REQUEST,
            isLoading:isLoading
        });
        let url = 'Order/DeleteBulkPO';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    
                    dispatch(getPurchaseOrderList(poData,isLoading));
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    /* Logic */
                    dispatch({
                        type: types.GET_DELETEBULKPO_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.GET_DELETEBULKPO_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

