import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        sortColumnName: "",
        sortDirection: "",
        pageNo: 0,
        pageSize: 50,       
        departmentId: 0,
        categoryId: 0,
        supplierId: 0

    },
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status: "",
    message: "",
    isModified: false,
    departments: [],
    categories: [],
    suppliers: []
};

export default function ManageInventoryReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_NONESCANNEDITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_NONESCANNEDITEM_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_NONESCANNEDITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                isModified: action.data.values !== undefined || action.data.values !== null ? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_NONESCANNEDITEM_NONESCANNEDITEMLISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.SET_WORKORDERS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_WORKORDERS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_WORKORDERSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_WORKORDERSEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_WORKORDERSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_DEPARTMENTS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_DEPARTMENTS_SUCCESS:

            return {
                ...state,
                departments: action.data,
                isLoading: false,
            }
        case types.GET_DEPARTMENTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_CATEGORIES_SUCCESS:

            return {
                ...state,
                categories: action.data,
                isLoading: false,
            }
        case types.GET_CATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_SUPPLIERS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_SUPPLIERS_SUCCESS:

            return {
                ...state,
                suppliers: action.data,
                isLoading: false,
            }
        case types.GET_SUPPLIERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.UPDATE_MANAGEQUANTITY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_MANAGEQUANTITY_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_MANAGEQUANTITY_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}