import React from 'react';

export default function VoucherComponent(props) {
    const { itemVoucher } = props;
    return (
        <div>
            <div className="card-body row">
                <div className="col-12">
                    <fieldset>
                        <legend> Options </legend>
                        <div className="row">
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="Vouchers-check1" name="Vouchers-check1" checked={itemVoucher.itemCannotBeSold} disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-check1">Item can not be sold</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="Vouchers-check2" name="Vouchers-check2" checked={itemVoucher.itemCannotBeRet} disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-check2">Item can not be returned</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="Vouchers-check3" name="Vouchers-check3" disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-check3">Voucher can be reused</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-checkbox ml-2 my-2">
                                    <input type="checkbox" className="custom-control-input" id="Vouchers-check4" name="Vouchers-check4" checked={itemVoucher.isGlobalvoucher} disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-check4">Global</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="floating-label">
                                    <input className="floating-input" type="text" placeholder=" " value={itemVoucher.tenderIDName} disabled />
                                    <span className="highlight"></span>
                                    <label className="mt-1">Linked Tender</label>
                                </div>
                            </div>
                        </div>
                    </fieldset>
                </div>
                <div className="col-12 mt-3">
                    <fieldset>
                        <legend> Numbering </legend>
                        <div className="row">

                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="Vouchers-radio1" name="voucherCustomRadio" className="custom-control-input" checked={!itemVoucher.isAutogenerated} disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-radio1">Cashier entered</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="custom-control custom-radio ml-2 my-2">
                                    <input type="radio" id="Vouchers-radio2" name="voucherCustomRadio" className="custom-control-input" checked={itemVoucher.isAutogenerated} disabled />
                                    <label className="custom-control-label" htmlFor="Vouchers-radio2">Auto generated</label>
                                </div>
                            </div>
                            <div className="col-12">
                                <p className="item-edit-info">
                                    Make voucher numbers recognizable by using a voucher number format.  To require numbers of a certain length.
                                    type # for each digit in the number  (ex. #### gives 0001. etc.) You can also type specific characters where desired
                                    (ABC001 - ### gives ABC001-001. etc.)
                                                       </p>
                            </div>

                            <div className="col-sm-6 col-12">
                                <div className="floating-label">
                                    <input className="floating-input" type="text" placeholder=" " value={itemVoucher.numberFormat} disabled />
                                    <span className="highlight"></span>
                                    <label className="mt-1">Number Format</label>

                                </div>
                            </div>
                            <div className="col-sm-6 col-12">
                                <div className="floating-label">
                                    <input className="floating-input" type="text" placeholder=" " value={itemVoucher.numberFormat !== "" && itemVoucher.numberFormat !== null ? itemVoucher.numberFormat.replace(/#/g, "0") : ""} disabled />
                                    <span className="highlight"></span>
                                    <label className="mt-1">Sample</label>
                                </div>
                            </div>
                        </div>
                    </fieldset>
                </div>

            </div>
        </div>
    )
}