import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    status: "",
    message: "",
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    getFormField:[]
};

export default function ConfigureStoreReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
     
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:""
            }
            case types.INSERT_STORECONFIG_REQUEST:
                return {
                    ...state,
                    isLoading: action.isLoading,
                }
            case types.INSERT_STORECONFIG_SUCCESS:
                return {
                    ...state,
                    isLoading: false,
                }
            case types.INSERT_STORECONFIG_FAILED:
                return {
                    ...state,
                    isLoading: false,
                } 
            case types.GET_CONFIGURESTORE_REQUEST:
                return {
                    ...state,
                    isLoading: action.isLoading,
                }
            case types.GET_CONFIGURESTORE_SUCCESS:
                return {
                    ...state,
                    isLoading: false,
                    getFormField: action.data.values,
                }
            case types.GET_CONFIGURESTORE_FAILED:
                return {
                    ...state,
                    isLoading: false,
                } 
                        
        default:
            return state;
    }
}