import React from 'react';
import WorkOrderComponent from '../component/WorkOrderComponent';
import {
  getWorkOrderList,setRequestParameter,
  getSearchList,setSearchRequestParameter,
  setSearchValue,setSearchData,
  setSearchTerm,createWO,
  setCreateWORequestParam,
  getCustomers,
  checkOnlineWorkOrder,
  closeAPIWorkOrder,
  getAPINameList,
  setResponseParameter
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {encryptURL,decryptURL} from '../../../Custom';
import moment from 'moment';
window.$=$;
function handleWOGridRowClick(classdata) {
  $("#tblWorkOrder").on('click','.WOGridRowClick',function() {
    var dataString=$(this).attr('data');
    classdata.onEditHandler(dataString);
  });
}
function debounce(func,wait,immediate) {
  var timeout;

  return function executedFunction() {
    var context=this;
    var args=arguments;

    var later=function() {
      timeout=null;
      if(!immediate) func.apply(context,args);
    };

    var callNow=immediate&&!timeout;

    clearTimeout(timeout);

    timeout=setTimeout(later,wait);

    if(callNow) func.apply(context,args);
  };
};

function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
class WorkOrderContainer extends React.Component {
  state={
    isRender: false,
    isOpenCustomerModel: false,
    isShowWOCDatatable: false,
    isWoNumber:true,
    isAccount:true,
    isCustomer:true,
    isDate:true,
    isTotal:true,
    isComment:true,
    isRef:true,
    isSetWoNumber:true,
    isSetAccount:true,
    isSetCustomer:true,
    isSetDate:true,
    isSetTotal:true,
    isSetComment:true,
    isSetRef:true,
    isAPINameDiv:false,
    apiNameChecked:[],
    isSucessDiv:false,
    isCloseOrderDiv:false,
    isfilterHideShow:"",
    formFields:{
      fromDate:null,
      reservebarfromDate:null
    }
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.props.getWorkOrderList(this.props.requestParameter,true);
    this.props.getAPINameList([],true);
    window.addEventListener('scroll',this.handleScroll);
    handleWOGridRowClick(this);
    ColumnFilterCheckbox();
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblWorkOrder').DataTable().destroy();
    $('#tblWOCustomersList').DataTable().destroy();
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager&&userDetails.role!==process.env.REACT_APP_UserRole_Cashier) {
        //   swal({
        //     title: "Warning",
        //     text: "Please get permission to view Work orders.",
        //     icon: "warning",
        //     button: "Ok"
        //   }).then(() => this.props.history.goBack());
        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        let pagesize=this.props.requestParameter.pageSize+50;
        let requestParameter={...this.props.requestParameter};
        requestParameter["pageSize"]=pagesize;
        this.props.setRequestParameter(requestParameter);
        this.props.getWorkOrderList(requestParameter,false);
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading

  //#region search handler
  searchHandler=(record,cb) => {
    try {
      let getdata;
      record=record.trim();
      // this.props.setSearchValue(record);

      if(record!==""||this.props.requestParameter.searchText!=="") {
        let requestParameter={...this.props.requestParameter};
        requestParameter["searchText"]=record;
        this.props.setRequestParameter(requestParameter);
        this.props.getWorkOrderList(requestParameter,true);
      }
      if(record!=="") {
        let searchRequestParameter={...this.props.searchRequestParameter};
        searchRequestParameter["searchText"]=record;
        this.props.setSearchRequestParameter(searchRequestParameter);
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata=[];
        this.props.setSearchData(getdata);
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion search handler
  //#region search change handler
  onSearchChangeHandler=(value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value,(items) => {
        this.props.setSearchData(items);
      })
    } catch(error) {
      console.log(error);
    }
  }
  setSearchTerm=debounce((searchTerm,cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm,cb);
    } catch(error) {
      console.log(error);
    }
  },1000)
  //#endregion search change handler

  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";

      switch(sortColumnName) {
        case "id":
          sortdirection=this.props.requestParameter.sortColumnName!=="id"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "accountNumber":
          sortdirection=this.props.requestParameter.sortColumnName!=="accountNumber"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "customer":
          sortdirection=this.props.requestParameter.sortColumnName!=="customer"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "date":
          sortdirection=this.props.requestParameter.sortColumnName!=="date"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "total":
          sortdirection=this.props.requestParameter.sortColumnName!=="total"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "comment":
          sortdirection=this.props.requestParameter.sortColumnName!=="comment"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        case "ref":
          sortdirection=this.props.requestParameter.sortColumnName!=="ref"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";

          break;
        default:
          break;
      }
      var data={
        pageNo: this.props.requestParameter.pageNo,
        pageSize: this.props.requestParameter.pageSize,
        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        searchText: this.props.requestParameter.searchText
      };
      var requestParameter={...this.props.requestParameter};
      requestParameter["sortColumnName"]=sortColumnName;
      requestParameter["sortDirection"]=sortdirection;
      this.props.setRequestParameter(requestParameter);
      this.props.getWorkOrderList(data,false);
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler
  //#region edit handler
  onEditHandler=(id) => {
    window.open("/WorkOrderEdit/"+encryptURL(id),'_blank');
  }
  //#endregion edit handler
  //#region toggle work order model
  toggleCustomerModel=() => this.setState({isOpenCustomerModel: !this.state.isOpenCustomerModel});
  toggleWOCDatatable=() => {this.setState({isShowWOCDatatable: !this.state.isShowWOCDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowWOCDatatable: false})}

  //#endregion toggle work order model
  //#region open work order
  openCustomerModel=() => {
    this.props.getCustomers(this.props.customerRequestParam,true);
    this.toggleCustomerModel();
  }
  //#endregion open work order
  //#region  customer check box handler
  customerCheckBoxHandler=(e,id) => {
    try {
      var isChecked=e.target.checked;
      var cbs=document.getElementsByClassName("WOcustomerCHK");
      for(var i=0;i<cbs.length;i++) {
        cbs[i].checked=false;
      }
      if(isChecked) {
        document.getElementById(e.target.name).checked=true;
        let createWORequestParam={
          customerId: parseInt(id)
        };
        this.props.setCreateWORequestParam(createWORequestParam);
      }
      else {
        let createWORequestParam={
          orderId: 0,
          customerId: 0
        };
        this.props.setCreateWORequestParam(createWORequestParam);
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion  customer check box handler
  //#region generate wo handler
  createWOHandler=() => {
    try {
      if(this.props.createWORequestParam.customerId<=0) {
        swal({
          title: "Error",
          text: types.REQUIRED_CUSTOMER_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else {
        this.props.createWO(this.props.createWORequestParam);
        this.toggleCustomerModel();
      }
    } catch(error) {
      console.log(error);
    }

  }
  //#endregion generate wo handler

  //#region filter checkbox onchange function
  setWoNumberCheckbox = () => {
    this.setState({ isWoNumber: !this.state.isWoNumber }, () => {
    this.setState({ isSetWoNumber: this.state.isWoNumber });
    });
  }

  setAccountCheckbox = () => {
    this.setState({ isAccount: !this.state.isAccount }, () => {
    this.setState({ isSetAccount: this.state.isAccount });
    });
  }

  setCustomerCheckbox = () => {
    this.setState({ isCustomer: !this.state.isCustomer }, () => {
    this.setState({ isSetCustomer: this.state.isCustomer });
    });
  }

  setDateCheckbox = () => {
    this.setState({ isDate: !this.state.isDate }, () => {
    this.setState({ isSetDate: this.state.isDate });
    });
  }


  setTotalCheckbox = () => {
    this.setState({ isTotal: !this.state.isTotal }, () => {
    this.setState({ isSetTotal: this.state.isTotal });
    });
  }

  setCommentCheckbox = () => {
    this.setState({ isComment: !this.state.isComment }, () => {
    this.setState({ isSetComment: this.state.isComment });
    });
  }

  setRefCheckbox = () => {
    this.setState({ isRef: !this.state.isRef }, () => {
    this.setState({ isSetRef: this.state.isRef });
    });
  }

  //#endregion filter checkbox onchange function
  checkOnlineOrCloseWO=() => {
    
    var apiNameList=this.props.apiNameList;
    var apiNameListChecked= apiNameList.filter(x=>x.isChecked==true).map(x=>x.apiName).join(',');
    var apiNameChecked= apiNameList.filter(x=>x.isChecked==true);
    var isfilterHideShow="d-none";
    this.setState({apiNameChecked,isfilterHideShow})
    if(apiNameChecked.length==0)
    {
      swal("","Please Select API Name!","error")
    }
    else if(this.state.formFields.fromDate==undefined && apiNameListChecked.toLocaleLowerCase().includes("bigcommerce"))
    {
      swal("","Please Select From date!","error")

    }
    else
    {
      let checkOnlineWORequestParam={
        apiNameString:apiNameListChecked,
        isOpenOrder:true,
        reservebarfromDate:this.state.formFields.reservebarfromDate,
        fromDate:this.state.formFields.fromDate
      };
      if(this.state.isAPINameDiv)
      this.props.checkOnlineWorkOrder(checkOnlineWORequestParam,this.props.requestParameter,true);
      if(this.state.isCloseOrderDiv)
      this.props.closeAPIWorkOrder(checkOnlineWORequestParam,this.props.requestParameter,true);
    }

  }

  closeAPIOrder=() => {
    let closeAPIOrderRequestParam={
      senderCompanyId: parseInt(127409),
      subscription_key:"4BD74035-AFE8-46FA-B11C-B22794462604",
      status:"Ready to ship"
    };
    this.props.closeAPIWorkOrder(closeAPIOrderRequestParam);
  }

  SelectAPIName=()=>{
    
    let apiNameList = this.props.apiNameList;
    let formFields = this.state.formFields;

    apiNameList.forEach(apiNameList => {
      apiNameList["isChecked"] = false
      })
    formFields.fromDate = null;
    this.setState({apiNameList: apiNameList,isAPINameDiv:true,formFields})

  }
  CancelAPIName=()=>{
    this.setState({isAPINameDiv:false,isCloseOrderDiv:false})
  }
  CancelResponse=()=>{
    this.setState({isAPINameDiv:false,isCloseOrderDiv:false,isfilterHideShow:""})
    this.props.setResponseParameter(false)
  }
  checkboxOnchange=(e)=>{
    const value = e.target.name;
 
    let apiNameList = this.props.apiNameList
    apiNameList.forEach(apiNameList => {
      if (apiNameList.id === parseInt(e.target.name))
      apiNameList["isChecked"] =  e.target.checked
      })
    this.setState({apiNameList: apiNameList})
  }
  SelectCloseOrder=()=>{
    
    let apiNameList = this.props.apiNameList
    apiNameList.forEach(apiNameList => {
      apiNameList["isChecked"] = false
      })
    this.setState({apiNameList: apiNameList,isCloseOrderDiv:true})

  }
  changeDateHandler=(date,name) => {
    
    let formFields={...this.state.formFields};
    formFields[name]=date!==""&&date!==null? moment(date).format('YYYY-MM-DD'):'';
    this.setState({
      formFields
    });
  };
  render() {
    // const { searchData } = this.props;   
    return (
      <WorkOrderComponent
        {...this.props}
        searchHandler={this.searchHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        sortHandler={this.sortHandler}
        onEditHandler={this.onEditHandler}
        isRender={this.state.isRender}
        isOpenCustomerModel={this.state.isOpenCustomerModel}
        toggleCustomerModel={this.toggleCustomerModel}
        openCustomerModel={this.openCustomerModel}
        customerCheckBoxHandler={this.customerCheckBoxHandler}
        createWOHandler={this.createWOHandler}
        toggleWOCDatatable={this.toggleWOCDatatable}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
        isShowWOCDatatable={this.state.isShowWOCDatatable}

        setWoNumberCheckbox={this.setWoNumberCheckbox}
        setAccountCheckbox={this.setAccountCheckbox}
        setCustomerCheckbox={this.setCustomerCheckbox}
        setDateCheckbox={this.setDateCheckbox}
        setTotalCheckbox={this.setTotalCheckbox}
        setCommentCheckbox={this.setCommentCheckbox}
        setRefCheckbox={this.setRefCheckbox}
       

        isWoNumber={this.state.isWoNumber}
        isAccount={this.state.isAccount}
        isCustomer={this.state.isCustomer}
        isDate={this.state.isDate}
        isTotal={this.state.isTotal}
        isComment={this.state.isComment}
        isRef={this.state.isRef}
        isSetWoNumber={this.state.isSetWoNumber}
        isSetAccount={this.state.isSetAccount}
        isSetCustomer={this.state.isSetCustomer}
        isSetDate={this.state.isSetDate}
        isSetTotal={this.state.isSetTotal}
        isSetComment={this.state.isSetComment}
        isSetRef={this.state.isSetRef}
        checkOnlineOrCloseWO={this.checkOnlineOrCloseWO}
        closeAPIOrder={this.closeAPIOrder}
        SelectAPIName={this.SelectAPIName}
        CancelAPIName={this.CancelAPIName}
        isAPINameDiv={this.state.isAPINameDiv}
        checkboxOnchange={this.checkboxOnchange}
        apiNameList={this.props.apiNameList}
        apiNameChecked={this.state.apiNameChecked}
        CancelResponse={this.CancelResponse}
        SelectCloseOrder={this.SelectCloseOrder}
        isCloseOrderDiv={this.state.isCloseOrderDiv}
        isfilterHideShow={this.state.isfilterHideShow}
        changeDateHandler={this.changeDateHandler}
        formFields={this.state.formFields}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getWorkOrderList: (data,isLoading) => dispatch(getWorkOrderList(data,isLoading)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    setSearchRequestParameter: (data) => dispatch(setSearchRequestParameter(data)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    createWO: (data) => dispatch(createWO(data)),
    setCreateWORequestParam: (data) => dispatch(setCreateWORequestParam(data)),
    getCustomers: (data,isLoading) => dispatch(getCustomers(data,isLoading)),
    checkOnlineWorkOrder: (data,requestParameter,isLoading) => dispatch(checkOnlineWorkOrder(data,requestParameter,isLoading)),
    closeAPIWorkOrder: (data,requestParameter,isLoading) => dispatch(closeAPIWorkOrder(data,requestParameter,isLoading)),
    getAPINameList:(data,isLoading)=>dispatch(getAPINameList(data,isLoading)),
    setResponseParameter:(data)=>dispatch(setResponseParameter(data))
  };
};

const mapStateToProps=(state) => {
  const {workOrderList,requestParameter,isLoading,searchValue,searchData,searchRequestParameter,SearchTerm,customerRequestParam,customers,createWORequestParam,apiNameList,successResult,
    isSucessDiv,apiDisplayCnt}=state.WorkOrderReducer;
  return {
    requestParameter: requestParameter,
    workOrderList: workOrderList.values.length>0? workOrderList.values:[],
    isLoading: isLoading,
    searchValue: searchValue,
    searchData: searchData.values.length>0? searchData.values:[],
    searchRequestParameter: searchRequestParameter,
    SearchTerm: SearchTerm,
    customerRequestParam: customerRequestParam,
    customers: customers.values.length>0? customers.values:[],
    createWORequestParam: createWORequestParam,
    apiNameList,apiDisplayCnt,
    successResult:successResult!=null?successResult:[],
    isSucessDiv
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(WorkOrderContainer);