import * as types from "../../../../constants/AppConstants";
import { get, post } from "../../../../service/index";
import swal from 'sweetalert';
import { history } from '../../../../history';
import AuthenticateService from '../../../../Services/AuthenticateServices';
import { encryptURL, decryptURL } from '../../../../Custom'
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
const obj = new AuthenticateService();
//#region to get Inventory list
export function getInventoryList(data, isLoading) {
   
    return (dispatch) => {
        dispatch({
            type: types.GET_INVENTORY_REQUEST,
            isLoading: isLoading,
        });
        let url = 'Inventory/InventoryList';
        
        return post(url, data)
            .then((response) => {
                const { payload } = response;                
              
                
                if (response.status === 200) {
                   // if (payload.code === "E08" || payload.code === "E03" || payload.code === "E05")
                    //history.push("/InventoryList");
                    if (!!payload.isFinal && payload.isFinal)
                     history.push("/FinalInventory/" + encryptURL(payload.values[0].inventoryId));
                    /* Logic */
                    dispatch({
                        type: types.GET_INVENTORY_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_INVENTORY_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get Inventory list
//#region set request parameter
export function setRequestParameter(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_INVENTORY_REQUESTPARAMETER,
            data: data,

        });
    }
}
//#endregion set request parameter
//#region delete Inventory
export function deleteInventory(deleteRequestParameter, requestParameter) {
    return (dispatch) => {
        dispatch({
            type: types.DELETE_INVENTORY_REQUEST,
        });
        let url = 'Inventory/DeleteInventory';
        return post(url, deleteRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                swal({
                    title: payload.status,
                    text: payload.message,
                    icon: payload.status.toLowerCase(),
                    timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getInventoryList(requestParameter, true));
                    dispatch({
                        type: types.DELETE_INVENTORY_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.DELETE_INVENTORY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion delete Inventory
//#region create Inventory
export function createInventory(createInventoryRequestParameter) {
    return (dispatch) => {
        dispatch({
            type: types.CREATE_INVENTORY_REQUEST,
        });
        let url = 'Inventory/CreateInventory';
        return post(url, createInventoryRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    history.push("/InventoryDetail/" + encryptURL(payload.inventoryId));
                    dispatch({
                        type: types.CREATE_INVENTORY_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.CREATE_INVENTORY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion create inventory
//#region combine Inventory
export function combineInventory(combineInventoryRequestParameter) {
  
    return (dispatch) => {
        dispatch({
            type: types.COMBINE_INVENTORY_REQUEST,
        });
        let url = 'Inventory/CombineInventory';
        
        return post(url, combineInventoryRequestParameter)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    history.push("/FinalInventory/" + encryptURL(combineInventoryRequestParameter.inventoryId), '_blank');
                    dispatch({
                        type: types.COMBINE_INVENTORY_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.COMBINE_INVENTORY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion combine Inventory
//#region to  Inventory LOGIN
export function inventoryLogin(data,requestParameter) {

    return (dispatch) => {
        dispatch({
            type: types.INVENTORY_LOGIN_REQUEST
        });
        let url = 'Inventory/InventoryLogin';

        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (payload.status.toLowerCase() === "error")
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                if (response.status === 200) {
                    /* Logic */
                    if (payload.status.toLowerCase() === "success") {
                        let InventoryUser = {
                            userId: payload.values.userId,
                            name: payload.values.name,
                            role: payload.values.role
                        };

                        Cookies.set('InventoryUser', CryptoJS.AES.encrypt(JSON.stringify(InventoryUser), process.env.REACT_APP_CryptojsSecretKey).toString());
                        dispatch(getInventoryList(requestParameter, true));
                        dispatch({
                            type: types.INVENTORY_LOGIN_SUCCESS,
                            data: response.payload,
                        });
                    }
                    else {
                        dispatch({
                            type: types.INVENTORY_LOGIN_FAILED,
                            data: response.payload,
                        });
                    }
                } else {
                    dispatch({
                        type: types.INVENTORY_LOGIN_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to  Inventory LOGIN
//#region set isOpenLogin
export function setisOpenLogin(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_ISOPEN_LOGIN,
            data: data,

        });
    }
}
