import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    requestParameter: {
        searchUserId: "",
        searchDate: null,
        sortCol: "",
        sortDir: "",
        pageNo: 0,
        pageSize: 50

    },
    activityLogList: [],
    userList: [],
    isLoading: false,   
};

export default function ActivityLogReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_ACTIVITYLOG_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ACTIVITYLOG_SUCCESS:
            return {
                ...state,
                activityLogList: action.data,              
                isLoading: false,

            }
        case types.GET_ACTIVITYLOG_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ACTIVITYLOG_REQUESTPARAMETER:
            return {
                ...state,
                requestParameter: action.data,
                isLoading: false,
            }
        case types.CLEAR_ACTIVITYLOG_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CLEAR_ACTIVITYLOG_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_ACTIVITYLOG_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_USERLIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_USERLIST_SUCCESS:
            return {
                ...state,
                userList: action.data,
                isLoading: false,

            }
        case types.GET_USERLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}