window.$=$;
//a little function to help us with reordering the result
const reorder=(list,startIndex,endIndex) => {
  try {
    const result=Array.from(list);
    const [removed]=result.splice(startIndex,1);
    result.splice(endIndex,0,removed);

    return result;
  } catch(error) {
    console.log(error);
    hideLoader();
  }
};

/**
 * Moves an item from one list to another list.
 */
const move=(source,destination,droppableSource,droppableDestination) => {
  try {
    const sourceClone=Array.from(source);
    const destClone=Array.from(destination);
    const [removed]=sourceClone.splice(droppableSource.index,1);

    destClone.splice(droppableDestination.index,0,removed);

    const result={};
    result[droppableSource.droppableId]=sourceClone;
    result[droppableDestination.droppableId]=destClone;

    return result;
  } catch(error) {
    console.log(error);
    hideLoader();
  }
};
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";

class ItemEdit extends Component {

  constructor(props) {
    super(props);
    this.state={
      isUserCanView: false,
      itemId: 0,
      fromId: 0,
      from: "",
      openItemId: 0,
      itemLookupCode: "",
      openItemState: false,
      isDragDisabled: true,
      currentPOD: "",
      itemGeneral: [],
      itemPricing: [],
      itemAliasDetail: [],
      itemInventoryDetail: [],
      preExpanded: [],
      isRender: false,
      PODsSetting: {
        userPodSetting: []
      },
      PODsLeft: [],
      PODsRight: [],
      itemCommonCost: "",
      itemCommonPrice: "",
      itemCommonProfit: "",
      itemCommonCostOldValue: "",
      itemCommonPriceOldValue: "",
      itemCommonProfitOldValue: "",
      disabledPODs: {
        General: false,
        Pricing: false,
        Inventory: false,
        Alias: false,
        Substitutes: false,
        Special: false,
        Serial: false,
        Kit: false,
        Options: false,
        Purchase: false,
        Voucher: false,
        Tare_Weights: false,
        ExtendedProperties: false,
        Discount: false,
        Commission: false
      },
      itemCommission: [],
      itemOptions: [],
      itemSpecial: [],
      itemVoucher: [],
      itemKit: [],
      itemSerial: [],
      itemSubstitutes: [],
      itemDiscount: [],
      itemPurchase: [],
      itemTareWeight: [],
      itemExtendedProperty: [],
      isHQConnected:false,
      itemOnlineSetting:[],
      apiBigCom:[],
      storelist:[],
      selectedStore:{
        storeIds:[]
      }
    }
    this.setcurrentPODChanged=this.setcurrentPODChanged.bind(this);
  }

  id2List={
    droppableLeft: 'PODsLeft',
    droppableRight: 'PODsRight'
  };

  getList=id => this.state[this.id2List[id]];
  //#region  handle POD drag and drop
  onDragEnd=async (result) => {
    try {
      const {source,destination}=result;
      const dragId=result.draggableId;
      // dropped outside the list
      if(!destination) {
        return;
      }
      // save index and position setting
      let PODsSetting=this.state.PODsSetting;
      //#region changed existing pod orderno 
      const destposition=destination.droppableId==="droppableRight"? "right":"left";
      PODsSetting.userPodSetting.map(setting => {
        if(setting.orderNo>=destination.index&&setting.position===destposition)
          setting.orderNo=setting.orderNo+1;
        return;
      });
      if(source.droppableId!==destination.droppableId) {
        var sourceposition=source.droppableId==="droppableRight"? "right":"left";
        PODsSetting.userPodSetting.map(setting => {
          if(setting.position===sourceposition&&setting.orderNo>source.index)
            setting.orderNo=setting.orderNo>0? setting.orderNo-1:setting.orderNo;
        });
      }
      //#region changed existing pod orderno 
      //#region change current dragged pod order no and position
      var elementsIndex=this.state.PODsSetting.userPodSetting.findIndex(element => element.podId===parseInt(result.draggableId))
      PODsSetting["userPodSetting"][elementsIndex]={...PODsSetting["userPodSetting"][elementsIndex],position: destination.droppableId==="droppableRight"? "right":"left",orderNo: destination.index}
      //#region change current dragged pod order no and position
      this.setState({
        PODsSetting
      })
      await this.savePodDataToDB(source.droppableId,dragId);
      if(source.droppableId===destination.droppableId) {
        const PODsLeft=reorder(
          this.getList(source.droppableId),
          source.index,
          destination.index
        );

        let state={PODsLeft};

        if(source.droppableId==='droppableRight') {
          state={PODsRight: PODsLeft};
        }

        this.setState(state);
      } else {

        const result=move(
          this.getList(source.droppableId),
          this.getList(destination.droppableId),
          source,
          destination
        );

        this.setState({
          PODsLeft: result.droppableLeft!==undefined? result.droppableLeft:this.getList("droppableLeft"),
          PODsRight: result.droppableRight!==undefined? result.droppableRight:this.getList("droppableRight")
        });
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  };
  //#endregion  handle POD drag and drop
  savePodDataToDB=(droppableId,draggableId) => {
    try {
      if(this.state.currentPOD!=="") {
        if(this.state.currentPOD==="General")
          this.childItemGeneral.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Aliases")
          this.childAlias.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Pricing")
          this.childPricing.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Inventory")
          this.childInventory.formHandler(false,'saveClick');
        //this.getItemById(decryptURL(this.props.match.params.itemId));
        //this.setState({currentPOD:""});
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    showLoader();
    if(this.state.isUserCanView) {
      this.getStoreList();
      this.getAPINameList();

      if(this.props.match.params.from!==undefined) {
        this.setState({fromId: this.props.match.params.fromId,from: decryptURL(this.props.match.params.from)});
      }
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role.toLowerCase()=="central manager")
      {
        this.GetPodSettingbyUserId(-1);
      }
      else
      {
        this.GetPodSettingbyUserId(userDetails.userId);
      }
     
      this.setState({isHQConnected:userDetails.isHQConnected})
     
    }
  }
  //#region hide/show PODs
  setDisabledPODs=(type) => {
    let disabledPODs={...this.state.disabledPODs};

    switch(type) {
      case "Standard":
        disabledPODs["Serial"]=true;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=false;
        break;
      case "Serialized":
        disabledPODs["Serial"]=false;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=false;
        break;
      case "Kit":
        disabledPODs["Serial"]=true;
        disabledPODs["Kit"]=false;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=false;
        break;
      case "Gasoline":
        disabledPODs["Serial"]=true;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=false;
        break;
      case "Weighed":
        disabledPODs["Serial"]=true;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=false;
        disabledPODs["Purchase"]=false;
        break;
      case "NonInventory":
        disabledPODs["Serial"]=true;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=true;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=true;
        break;
      case "Voucher":
        disabledPODs["Serial"]=false;
        disabledPODs["Kit"]=true;
        disabledPODs["Voucher"]=false;
        disabledPODs["Tare_Weights"]=true;
        disabledPODs["Purchase"]=false;
        break;
      default:
        break;
    }
    this.setState({disabledPODs});

  }
  //#endregion hide/show PODs

  //#region get user POD data
  GetPodSettingbyUserId=async (userId) => {
    try {
      var Obj=new SettingServices();
      await Obj.GetPodSettingbyUserId(userId).then((poddata) => {
        
        if(!!poddata.values) {
          var PODsLeft=[];
          var PODsRight=[];
          var preExpanded=[];
          var PODsSetting=this.state.PODsSetting;
          var PODsSettingdata=[];
          var currentPOD="";
          const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
          if(this.state.apiBigCom.length==0){
            poddata.values=poddata.values.filter(x=>x.podName.toLowerCase()!="bigcommerce management")
          }
         
          poddata.values.map((userpoddata,index) => {
            if(userpoddata.isExpand)
              preExpanded.push(userpoddata.podId);
            //currentPOD=userpoddata.podName;
            PODsSettingdata.push({
              userId: parseInt(userId),
              podId: userpoddata.podId,
              position: userpoddata.position,
              orderNo: userpoddata.orderNo,
              isExpand: userpoddata.isExpand
            });
            if(userpoddata.position==="left")
              PODsLeft.push({
                userId: userId,
                podId: userpoddata.podId,
                podName: userpoddata.podName,
                position: userpoddata.position,
                orderNo: userpoddata.orderNo,
                isExpand: userpoddata.isExpand,
                content: this.renderComponent(userpoddata.podName)
              });
            if(userpoddata.position==="right")
              PODsRight.push({
                userId: userId,
                podId: userpoddata.podId,
                podName: userpoddata.podName,
                position: userpoddata.position,
                orderNo: userpoddata.orderNo,
                isExpand: userpoddata.isExpand,
                content: this.renderComponent(userpoddata.podName)
              });
          });
          PODsSetting["userPodSetting"]=PODsSettingdata;
          this.setState({PODsLeft: PODsLeft,PODsRight: PODsRight,PODsSetting: PODsSetting,preExpanded: preExpanded,currentPOD: currentPOD},() => {
            this.getItemById(decryptURL(this.props.match.params.itemId));
          });
        }
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get user POD data
  //#region save settings
  insertOrUpdateUserPodsetting=async (PODsSetting) => {
    try {
      showLoader();

      var Obj=new SettingServices();
      await Obj.insertOrUpdateUserPodsetting(PODsSetting).then((response) => {

        swal({
          title: response.status,
          text: response.message,
          icon: response.status.toLowerCase(),
          timer: response.status.toLowerCase()!=="error"? 3000:0,
          button: response.status.toLowerCase()!=="error"? false:"Ok",
        })
        this.setIsDragEnableDisable();
        hideLoader();
      }).catch(function(error) {
        hideLoader();
        console.log(error);
        //Perform action based on error
      });

      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion save settings
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager && userDetails.role!==process.env.REACT_APP_UserRole_CentralManager) {
        swal({
          title: "warning",
          text: "You do not have permission to access item details.",
          icon: "warning",
          button: "Ok"
        }).then(() => {
          if(window.location.pathname==='/Items')
            this.props.history.push("/Items")
          else {
            if(userDetails.role!==process.env.REACT_APP_UserRole_Admin)
              this.props.history.push("/Dashboard");
            else
              this.props.history.push("/AdminConfigureDatabase");
          }
        });
      }
      else {
        this.setState({isUserCanView: true});
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion check user role validation
  //#region get item by id
  getItemById=async (itemId) => {
    try {
  
      //showLoader();
      var Obj=new ItemsServices();
      await Obj.GetItemById(itemId).then((item) => {
        if(!!item.values) {

          this.setState({
            itemId: item.values.itemGeneral.itemId,
            itemLookupCode: item.values.itemGeneral.itemLookupCode,
            itemGeneral: item.values.itemGeneral,
            itemAliasDetail: item.values.itemAliasDetail,
            itemPricing: item.values.itemPricing,
            itemInventoryDetail: item.values.itemInventoryDetail,
            isRender: true,
            itemCommonCost: item.values.itemGeneral.cost.toFixed(4),
            itemCommonPrice: item.values.itemGeneral.price.toFixed(2),
            itemCommonProfit: (((parseFloat(item.values.itemGeneral.price!==""? item.values.itemGeneral.price:0)-parseFloat(item.values.itemGeneral.cost!==""? item.values.itemGeneral.cost:0))*100)/parseFloat(item.values.itemGeneral.price!==""? item.values.itemGeneral.price:0)).toFixed(2),
            itemCommonCostOldValue: "",
            itemCommonPriceOldValue: "",
            itemCommonProfitOldValue: "",
            itemCommission: item.values.itemCommission,
            itemOptions: item.values.itemOptions,
            itemSpecial: item.values.itemSpecial,
            itemVoucher: item.values.itemVoucher,
            itemKit: item.values.itemKit!==null? item.values.itemKit:[],
            itemSerial: item.values.itemSerial!==null? item.values.itemSerial:[],
            itemSubstitutes: item.values.itemSubstitutes!==null? item.values.itemSubstitutes:[],
            itemDiscount: item.values.itemDiscount,
            itemPurchase: item.values.itemPurchase,
            itemTareWeight: item.values.itemTareWeight,
            itemExtendedProperty: item.values.itemExtendedProperty,
            itemOnlineSetting: item.values.itemOnlineSetting,
            ItemBigcommerceDetail: item.values.itemBigcommerceDetail.data,
            ItemCustomFieldsDetail: item.values.itemCustomFieldsDetail,
            ItemBulkPricingDetail: item.values.itemBulkPricingDetail,
            itemImage: item.values.itemImage

          });


          this.setDisabledPODs(item.values.itemGeneral.itemTypeName);

        }
        if(item.status==="error")
          swal({
            title: item.status,
            text: item.message,
            icon: item.status,
            button: "Ok"
          })

      });
      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  //#endregion get item by id
  accordionHandler=(podIds) => {
    try {

      $.each($.fn.dataTable.tables(true),function() {
        $(this).DataTable()
          .columns.adjust()
          .responsive.recalc();
        if($(this).DataTable().row('.parent').length) {
          $('td.details-control',$(this).DataTable().row('.parent').node()).click();

        }
      });
      let opnID=this.state.openItemId;
      if(document.getElementById("accordion__panel-"+opnID).hidden!==this.state.openItemState) {
        if(document.getElementById("accordion__panel-"+opnID).hidden===true) {
          document.getElementById("accordion__panel-"+opnID).hidden=false;
          document.getElementById("accordion__panel-"+opnID).ariaHidden=false;
          document.getElementById("accordion__heading-"+opnID).ariaExpanded=true;
        }
        else {

          document.getElementById("accordion__panel-"+opnID).hidden=true;
          document.getElementById("accordion__panel-"+opnID).ariaHidden=true;
          document.getElementById("accordion__heading-"+opnID).ariaExpanded=false;
        }
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  accordionClick=(e) => {
    try {


      let PODsSetting=this.state.PODsSetting;
      var elementsIndex=this.state.PODsSetting.userPodSetting.findIndex(element => element.podId===parseInt(e.currentTarget.id))
      if(e.target.ariaExpanded==="false")
        PODsSetting["userPodSetting"][elementsIndex]={...PODsSetting["userPodSetting"][elementsIndex],isExpand: true}
      else
        PODsSetting["userPodSetting"][elementsIndex]={...PODsSetting["userPodSetting"][elementsIndex],isExpand: false}
      this.setState({PODsSetting});
      var openItemId=e.target.parentElement["id"];
      this.setState({openItemId: openItemId})

      if(document.getElementById("accordion__panel-"+openItemId).hidden===true) {
        this.setState({openItemState: false});
        // this.state.openItemState =false;
      }
      else {
        this.setState({openItemState: true});
        // this.state.openItemState =true;
      }
      if(this.state.currentPOD!=="") {
        if(this.state.currentPOD==="General")
          this.childItemGeneral.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Aliases")
          this.childAlias.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Pricing")
          this.childPricing.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Inventory")
          this.childInventory.formHandler(false,'saveClick');
        // this.getItemById(decryptURL(this.props.match.params.itemId));
        // this.setState({currentPOD:""});
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }

  checkBoxHandler=() => {
    try {
      let PODsSetting=this.state.PODsSetting;

      if(document.getElementById("customCheck").checked) {
        PODsSetting.userPodSetting.map(userPodSetting => {userPodSetting.isExpand=true});
        for(var i=0;i<this.state.PODsSetting.userPodSetting.length;i++) {
          let opnID=this.state.PODsSetting.userPodSetting[i].podId;
          if(!!document.getElementById("accordion__panel-"+opnID)&&document.getElementById("accordion__panel-"+opnID).hidden===true) {
            document.getElementById("accordion__panel-"+opnID).hidden=false;
            document.getElementById("accordion__panel-"+opnID).ariaHidden=false;
            document.getElementById("accordion__heading-"+opnID).ariaExpanded=true;
          }
        }
      }
      else {
        PODsSetting.userPodSetting.map(userPodSetting => {userPodSetting.isExpand=false});
        for(var j=0;j<this.state.PODsSetting.userPodSetting.length;j++) {


          let openId=this.state.PODsSetting.userPodSetting[j].podId;
          if(!!document.getElementById("accordion__panel-"+openId)&&document.getElementById("accordion__panel-"+openId).hidden===false) {
            document.getElementById("accordion__panel-"+openId).hidden=true;
            document.getElementById("accordion__panel-"+openId).ariaHidden=true;
            document.getElementById("accordion__heading-"+openId).ariaExpanded=false;
          }
        }
      }
      this.setState({
        PODsSetting
      });

      this.forceUpdate();
    } catch(error) {
      console.log(error);
      hideLoader();
    }

  }
  renderComponent=(component) => {
    try {

      switch(component) {
        
        case "General":
          return <ItemGeneral
            setCODNULL={this.setCODNULL}
            setitemCommonProfit={this.setitemCommonProfit}
            setitemCommonCost={this.setitemCommonCost}
            itemCommonCost={this.state.itemCommonCost}
            setitemCommonPrice={this.setitemCommonPrice}
            itemCommonPrice={this.state.itemCommonPrice}
            itemCommonProfit={this.state.itemCommonProfit}
            itemCommonCostOldValue={this.state.itemCommonCostOldValue}
            itemCommonPriceOldValue={this.state.itemCommonPriceOldValue}
            itemCommonProfitOldValue={this.state.itemCommonProfitOldValue}
            setitemCommonCostOldValue={this.setitemCommonCostOldValue}
            setitemCommonPriceOldValue={this.setitemCommonPriceOldValue}
            setitemCommonProfitOldValue={this.setitemCommonProfitOldValue}
            itemId={this.state.itemId}
            setcurrentPODChanged={this.setcurrentPODChanged}
            itemGeneral={this.state.itemGeneral}
            ref={instance => {this.childItemGeneral=instance;}}
            setDisabledPODs={this.setDisabledPODs}
            isHQConnected={this.state.isHQConnected}
          />

        case "Aliases":
          return <Aliases setCODNULL={this.setCODNULL}  isHQConnected={this.state.isHQConnected} itemId={this.state.itemId} setcurrentPODChanged={this.setcurrentPODChanged} itemLookupCode={this.state.itemGeneral.itemLookupCode} itemAliasDetail={this.state.itemAliasDetail} ref={instance => {this.childAlias=instance;}} />

        case "Pricing":
          return <Pricing
            setCODNULL={this.setCODNULL}
            setitemCommonProfit={this.setitemCommonProfit}
            setitemCommonCost={this.setitemCommonCost}
            itemCommonCost={this.state.itemCommonCost}
            setitemCommonPrice={this.setitemCommonPrice}
            itemCommonPrice={this.state.itemCommonPrice}
            itemCommonProfit={this.state.itemCommonProfit}
            itemCommonCostOldValue={this.state.itemCommonCostOldValue}
            itemCommonPriceOldValue={this.state.itemCommonPriceOldValue}
            itemCommonProfitOldValue={this.state.itemCommonProfitOldValue}
            setitemCommonCostOldValue={this.setitemCommonCostOldValue}
            setitemCommonPriceOldValue={this.setitemCommonPriceOldValue}
            setitemCommonProfitOldValue={this.setitemCommonProfitOldValue}
            itemId={this.state.itemId}
            setcurrentPODChanged={this.setcurrentPODChanged}
            itemPricing={this.state.itemPricing}
            ref={instance => {this.childPricing=instance;}}
            ItemBigcommerceDetail={this.state.ItemBigcommerceDetail}
             />

        case "Inventory":
          return <Inventory setCODNULL={this.setCODNULL} itemId={this.state.itemId} setcurrentPODChanged={this.setcurrentPODChanged} itemInventoryDetail={this.state.itemInventoryDetail} ref={instance => {this.childInventory=instance;}}  isHQConnected={this.state.isHQConnected}/>
        case "Commission":
          return <CommissionContainer itemCommission={this.state.itemCommission} />
        case "Options":
          return <OptionsContainer itemOptions={this.state.itemOptions} />
        case "Special":
          return <SpecialContainer itemSpecial={this.state.itemSpecial} />
        case "Voucher":
          return <VoucherContainer itemVoucher={this.state.itemVoucher} />
        case "Kit":
          return <KitContainer itemKit={this.state.itemKit} itemId={this.state.itemId} />
        case "Serial":
          return <SerialContainer itemSerial={this.state.itemSerial} />
        case "Substitutes":
          return <SubstitutesContainer itemSubstitutes={this.state.itemSubstitutes} />
        case "Tare Weights":
          return <TareWeightsContainer itemTareWeight={this.state.itemTareWeight} />
        case "Discounts":
          return <DiscountsContainer itemDiscount={this.state.itemDiscount} />
        case "Purchase":
          return <PurchaseContainer itemPurchase={this.state.itemPurchase} />
        case "Extended Properties":
          return <ExpandedPropertiesContainer
           itemExtendedProperty={this.state.itemExtendedProperty} 
           ref={instance => {this.childItemExt=instance;}} 
           setcurrentPODChanged={this.setcurrentPODChanged}
           itemId={this.state.itemId} />
      
        case "Online Item Settings":
            return <OnlineItemSettings 
            itemOnlineSetting={this.state.itemOnlineSetting} 
            ref={instance => {this.childOnlineSetting=instance;}} 
            setcurrentPODChanged={this.setcurrentPODChanged}
            itemId={this.state.itemId} 
            ItemBigcommerceDetail={this.state.ItemBigcommerceDetail}
            />
        case "BigCommerce Management":
          return <BigCommerceManagement 
          setcurrentPODChanged={this.setcurrentPODChanged}
          ref={instance => {this.childBigcommerceManagement=instance;}} 
          itemId={this.state.itemId} 
          itemGeneral={this.state.itemGeneral}
          ItemBigcommerceDetail={this.state.ItemBigcommerceDetail}
          ItemCustomFieldsDetail={this.state.ItemCustomFieldsDetail}
          ItemBulkPricingDetail={this.state.ItemBulkPricingDetail}
          itemOnlineSetting={this.state.itemOnlineSetting}
          itemSerial={this.state.itemSerial}
          itemImage={this.state.itemImage}
          />
        case "Store Cost":
          return <StoreCost 
        
          ref={instance => {this.childOnlineSetting=instance;}} 
          setcurrentPODChanged={this.setcurrentPODChanged}
          itemId={this.state.itemId} 
          />  
        
        case "Store Prices":
          return <StorePrice 
        
          ref={instance => {this.childOnlineSetting=instance;}} 
          setcurrentPODChanged={this.setcurrentPODChanged}
          itemId={this.state.itemId} 
          />    
        case "Store Sales Prices":
          return <StoreSalesPrice 
        
          ref={instance => {this.childOnlineSetting=instance;}} 
          setcurrentPODChanged={this.setcurrentPODChanged}
          itemId={this.state.itemId} 
          />   
        case "Store Tax":
          return <StoreTax 
        
          ref={instance => {this.childOnlineSetting=instance;}} 
          setcurrentPODChanged={this.setcurrentPODChanged}
          itemId={this.state.itemId} 
          />   
        case "Store Quantity":
          return <StoreQuantity 
        
          ref={instance => {this.childOnlineSetting=instance;}} 
          setcurrentPODChanged={this.setcurrentPODChanged}
          itemId={this.state.itemId} 
          />      
        default:
        return <h6>Coming soon...</h6>;

      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  savecurrentPOD=() => {
    try {
      
      showLoader();
      if(this.state.currentPOD!=="") {
        if(this.state.currentPOD==="General")
          this.childItemGeneral.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="Aliases")
          this.childAlias.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="Pricing")
          this.childPricing.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="Inventory")
          this.childInventory.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="OnlineItemSettings")
          this.childOnlineSetting.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="BigCommerce Management")
          this.childBigcommerceManagement.formHandler(true,'saveClick');
        else if(this.state.currentPOD==="StoreList")
          this.storeformHandler(true,'saveClick');
        else if(this.state.currentPOD==="Extended Properties")
        {
          document.getElementById("ItemExtSave_ID").click();

        }
        //this.getItemById(decryptURL(this.props.match.params.itemId));
        //this.setState({currentPOD:""});
      }
      hideLoader();
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  setCODNULL=() => {
    this.getItemById(decryptURL(this.props.match.params.itemId));
    this.setState({currentPOD: ""});
  }
  accordionFocusin=(e) => {
    try {
      let currentPOD=document.getElementById("accordion__heading-"+e.currentTarget.id).innerText;

      if(this.state.currentPOD!==""&&this.state.currentPOD!==currentPOD) {
        if(this.state.currentPOD==="General") {
          this.childItemGeneral.formHandler(false,'saveClick');
        }
        else if(this.state.currentPOD==="Aliases")
          this.childAlias.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Pricing")
          this.childPricing.formHandler(false,'saveClick');
        else if(this.state.currentPOD==="Inventory")
          this.childInventory.formHandler(false,'saveClick');

        //   this.getItemById(decryptURL(this.props.match.params.itemId));
        //   this.setState({currentPOD:""});
      }
    } catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  setIsDragEnableDisable=() => {
    this.setState({isDragDisabled: !this.state.isDragDisabled});
  }
  setcurrentPODChanged=(pod) => {
    this.setState({currentPOD: pod});
  }
  setitemCommonPrice=(price) => {
    this.setState({itemCommonPrice: price});
  }
  setitemCommonCost=(cost) => {
    this.setState({itemCommonCost: cost});
  }
  setitemCommonProfit=(profit) => {
    this.setState({itemCommonProfit: profit});
  }

  setitemCommonCostOldValue=(cost) => {
    this.setState({itemCommonCostOldValue: cost});
  }
  setitemCommonPriceOldValue=(price) => {
    this.setState({itemCommonPriceOldValue: price});
  }
  setitemCommonProfitOldValue=(profitmargin) => {
    this.setState({itemCommonProfitOldValue: profitmargin});
  }
  getAPINameList=async () => {
    try {
     
      var Obj=new CommonServices();
      await Obj.getAPINameList().then((apiNameList) => {
        if(!!apiNameList.values)
        {
          let apiBigCom=apiNameList.values.filter(x=>x.isDisplay==true && x.apiName.toLowerCase()=="bigcommerce")

          this.setState({
            apiBigCom:apiBigCom
          });
        } 
      });
    }
     catch(error) {
      console.log(error);
      hideLoader();
    }
  }
  getStoreList=async () => {
    try {
       
      var Obj=new CommonServices();
      await Obj.GetStoreList().then((storelist) => {
        if(!!storelist.values)
          this.setState({
            storelist: storelist.values
          });
      });
    } catch(error) {
      console.log(error);
      hideLoader();
    }
}

selectStoreHandler=(selectedList,selectedItem) => {
  
  var value=selectedItem.code;
  var selectedStore=this.state.selectedStore;
  selectedStore["storeIds"].push(value);
  this.setState({
    selectedStore: selectedStore
  });
  this.setcurrentPODChanged("StoreList");

}

removeStoreHandler=(selectedList,removedItem) => {
  var value=removedItem.code;
  var selectedStore=this.state.selectedStore;
  var elementsIndex=selectedStore["storeIds"].findIndex(element => element===value);
  selectedStore["storeIds"].splice(elementsIndex,1);
  this.setState({
    selectedStore: selectedStore
  });
}
storeformHandler=async (shoeMessage,click) => {
  try {
      var formFieldsFilter=this.state.selectedStore.storeIds.join(',');
      var str="";
      // for (var i = 0; i < formFieldsFilter.length; i++) {
          
      //     let storecode=formFieldsFilter[i].code
      //     if(str.includes(storecode)==false)
      //     {
      //       str = str + ',' +storecode;
      //     }
          
      // }
      // str=str.substring(1);

      let UpdateStore={
        itemId:this.state.itemId,
        storeName:formFieldsFilter,
        insertUpdate:"update",
        tableName:"Item"
      }
      if(shoeMessage)
          showLoader();
     // this.form.validateFields();
     // if(this.form.isValid()) {
          var Obj=new ItemsServices();
          await Obj.UpdateStore(UpdateStore).then((response) => {
              if(click==='saveClick') {
                  if(shoeMessage||response.status.toLowerCase()==="error")
                      swal({
                          title: response.status,
                          text: response.message,
                          icon: response.status.toLowerCase(),
                          timer: response.status.toLowerCase()!=="error"? 3000:0,
                          button: response.status.toLowerCase()!=="error"? false:"Ok",
                      })
                  hideLoader();
                  if(response.status.toLowerCase()!=="error") {
                      this.clearFormFieldsOldValues();
                      this.props.setCODNULL();
                  }
              }

          }).catch(function(error) {
              hideLoader();
              console.log(error);
              //Perform action based on error
          });
      //}
      hideLoader();
  } catch(error) {
      console.log(error);
      hideLoader();
  }
}
  render() {
    return (


      <div>
        {this.state.isRender&&
          <div>
            <Helmet>
              <title>{this.state.itemGeneral.itemLookupCode}-{this.state.itemGeneral.description} | Modus Go!</title>
            </Helmet>
            <div className="main-content-area mobile-bottom-space">
              <div className="container-fluid">
                <div className="row">
                  <div className="col-12 pb-2 edit-info-fixed padding-space">
                    <div className="edit-title-wrap">
                      <div className="edit-title d-none d-md-block">
                        <h3>Item code: <span>{this.state.itemGeneral.itemLookupCode}</span></h3>
                        <h3>Description: <span>{this.state.itemGeneral.description}</span></h3>
                       
                      </div>
                      <div className="col p-0 mobile-header-info">
                        <img className="mobile-logo-icon pl--9" src="/assets/images/logo-icon.png" alt="" />
                        {/* <p className="text-darkblue font-weight-bold mobile-title-headline">POD Settings</p> */}
                        <div className="items-header">
                          <p>Item code: <span>{this.state.itemGeneral.itemLookupCode}</span></p>
                          <p>Description: <span>{this.state.itemGeneral.description}</span></p>
                        </div>
                      </div>
                      {userDetails.role===process.env.REACT_APP_UserRole_CentralManager?
                       <div className='col-md-4 d-none'>
                       <div className="floating-label mb-0">
                         <Multiselect className="floating-select"
                           options={this.state.storelist}
                           displayValue="code"
                         //	selectedValues={this.state.selectedStore.storeIds}
                           onSelect={this.selectStoreHandler}
                           onRemove={this.removeStoreHandler}
                           placeholder="Select Store"
                           name="storeIds"
                           id="storeIds"
                           avoidHighlightFirstOption={true}
                           showCheckbox={true}
                           hidePlaceholder={true}
                           closeOnSelect={false}
                         />
                         <span className="highlight"></span>
                         <label >Store</label>
                       </div>
                     </div>
                     :""}
                     
                      <div className="custom-control custom-checkbox edit-top-head-btn mb-1">
                        <div className="mr-5 d-pod-option">
                          <input type="checkbox" className="custom-control-input" id="isDragDisabled" onChange={this.setIsDragEnableDisable} name="isDragDisabled" checked={!this.state.isDragDisabled} />
                          <label className="custom-control-label font-weight-bold" htmlFor="isDragDisabled">Is drag enable</label>
                        </div>
                        <div>
                          <input type="checkbox" className="custom-control-input" id="customCheck" onChange={this.checkBoxHandler} name="example1" />
                          <label className="custom-control-label font-weight-bold" htmlFor="customCheck">All tabs open</label>
                        </div>

                      </div>

                    </div>
                    <div className="custom-checkbox edit-top-head-btn">
                      {this.state.from==="FinalInventory"? <button type="button" onClick={() => history.push("/FinalInventory"+"/"+encryptURL("New item list"))} className="mr-1 ml-1 btn btn-primary rounded-pill">Back</button>:""}
                      {this.state.from==="po"? <button type="button" onClick={() => history.push("/PurchaseOrderEdit/"+this.state.fromId+"/"+encryptURL("poItem"))} className="mr-1 ml-1 btn btn-primary rounded-pill">Back</button>:""}
                      {this.state.from==="order"? <button type="button" onClick={() => history.push("/Order/"+this.state.fromId)} className="mr-1 ml-1 btn btn-primary rounded-pill">Back</button>:""}
                      {this.state.from==="wo"? <button type="button" onClick={() => history.push("/WorkOrderEdit/"+this.state.fromId)} className="mr-1 ml-1 btn btn-primary rounded-pill">Back</button>:""}
                      <button type="button" onClick={() => this.insertOrUpdateUserPodsetting(this.state.PODsSetting)} onMouseDown={e => e.preventDefault()} className="mr-1 btn btn-primary rounded-pill d-pod-option">Save Settings</button>
                      <button type="button" onClick={this.savecurrentPOD} className="mr-1 btn btn-primary rounded-pill" disabled={this.state.currentPOD!==""? false:true}>Save</button>
                      <button type="button" onClick={() => history.push("/Items")} className="mx-1 btn btn-primary rounded-pill">Cancel</button>
                    </div>
                  </div>
                </div>
                <div className="row mt-90">
                  <DragDropContext onDragEnd={this.onDragEnd}>
                    <div className="col-md-12 col-lg-6 padding-md-0">
                      <Droppable droppableId="droppableLeft">
                        {(provided,snapshot) => (
                          <Accordion className="accordion" onChange={(e) => this.accordionHandler(e)} allowZeroExpanded={true} allowMultipleExpanded={true} preExpanded={this.state.preExpanded}>
                            <div className="pod-height" ref={provided.innerRef}>
                              {this.state.PODsLeft.map((item,index) => (
                                !this.state.disabledPODs[item.podName.replace(" ","_")]?
                                  <Draggable isDragDisabled={this.state.isDragDisabled}
                                    key={item.podId}
                                    draggableId={item.podId.toString()}
                                    index={index}>
                                    {(provided,snapshot) => (
                                      <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}>
                                        <div >

                                          <AccordionItem className="card" id={item.podId} uuid={item.podId} onClick={(e) => this.accordionFocusin(e)}  >

                                            <AccordionItemHeading id={item.podId} className="card-header" onClick={(e) => this.accordionClick(e)}>
                                              <AccordionItemButton>
                                                {item.podName}
                                              </AccordionItemButton>
                                            </AccordionItemHeading >
                                            <AccordionItemPanel>
                                              {this.renderComponent(item.podName)}

                                            </AccordionItemPanel>
                                          </AccordionItem>

                                        </div>
                                      </div>
                                    )}
                                  </Draggable>:""
                              ))}
                              {provided.placeholder}
                            </div>
                          </Accordion>
                        )}
                      </Droppable>
                    </div>
                    <div className="col-md-12 col-lg-6 padding-md-0">
                      <Droppable droppableId="droppableRight">
                        {(provided,snapshot) => (
                          <Accordion className="accordion" onChange={(e) => this.accordionHandler(e)} allowZeroExpanded={true} allowMultipleExpanded={true} preExpanded={this.state.preExpanded}>
                            <div className="pod-height" ref={provided.innerRef}>
                              {this.state.PODsRight.map((item,index) => (
                                !this.state.disabledPODs[item.podName]?
                                  <Draggable isDragDisabled={this.state.isDragDisabled}
                                    key={item.podId}
                                    draggableId={item.podId.toString()}
                                    index={index}>
                                    {(provided,snapshot) => (
                                      <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                      >
                                        <AccordionItem className="card" id={item.podId} uuid={item.podId} onClick={(e) => this.accordionFocusin(e)}>
                                          <AccordionItemHeading id={item.podId} className="card-header" onClick={(e) => this.accordionClick(e)}>
                                            <AccordionItemButton>
                                              {item.podName}
                                            </AccordionItemButton>
                                          </AccordionItemHeading>
                                          <AccordionItemPanel>
                                            {this.renderComponent(item.podName)}
                                          </AccordionItemPanel>
                                        </AccordionItem>
                                      </div>
                                    )}
                                  </Draggable>:""
                              ))}
                              {provided.placeholder}
                            </div>
                          </Accordion>

                        )}
                      </Droppable>
                    </div>
                  </DragDropContext>


                </div>
              </div>
            </div>
          </div>
        }
      </div>


    );
  }
}
export default ItemEdit;
