import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    departments: [],
    categories: [],
    isCategoryDisabled: true,
    dropdownListParam: {
        departmentIds: [],
        categoryIds: [],
    },
    isSuccessDialogue:false,
    updatemessage:"",
    updatestatus:"",
    isLoading: false,
    priceconfig:[],
    priceName:[]
};

export default function PriceConfigurationReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_FINALDEPARTMENTS_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALDEPARTMENTS_SUCCESS:

            return {
                ...state,
                departments: action.data,
                isLoading: false,
            }
        case types.GET_FINALDEPARTMENTS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_FINALCATEGORIES_SUCCESS:

            return {
                ...state,
                categories: action.data,
                isLoading: false,
            }
        case types.GET_FINALCATEGORIES_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        case types.SET_IS_CATEGORYDISABLED:

            return {
                ...state,
                isCategoryDisabled: action.data,
                isLoading: false,
            }
        case types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM:
            return {
                ...state,
                dropdownListParam: action.data,
                isLoading: false,
            }
        case types.INSERT_PRICECONFIG_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.INSERT_PRICECONFIG_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.INSERT_PRICECONFIG_FAILED:
            return {
                ...state,
                isLoading: false,
            } 
            
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatestatus: action.status !== undefined ? action.status : "",
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
                updatestatus:"",
                updatemessage:""
            }
        case types.GET_PRICECONFIG_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_PRICECONFIG_SUCCESS:

            return {
                ...state,
                priceconfig: action.data.values,
                priceName:action.data.values,
                isLoading: false,
            }
        case types.GET_PRICECONFIG_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_APINAME_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_APINAME_SUCCESS:
            return {
                ...state,
                apiNameList: action.data,
                isLoading: false,
            }
        case types.GET_APINAME_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        default:
            return state;
    }
}