import AuthenticateService from '../../../../Services/AuthenticateServices';
import * as types from "../../../../constants/AppConstants";
import { post } from "../../../../service/index";
import { history } from '../../../../history'
import swal from 'sweetalert';
const obj = new AuthenticateService();
export function updateSubscription() {

    return (dispatch) => {
        dispatch({
            type: types.UPDATE_SUBSCRIPTION_REQUEST,
        });
        let url = 'Login/RetrySchedulerJob';

        return post(url)
            .then((response) => {
                const { payload } = response;
                if (payload.status.toLowerCase() === "error")
                    swal({
                        title: payload.status,
                        text: payload.message,
                        icon: payload.status.toLowerCase(),
                        timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                if (response.status === 200) {
                    /* Logic */
                    if (payload.code === "000")
                        history.push("/Dashboard");
                    dispatch({
                        type: types.UPDATE_SUBSCRIPTION_SUCCESS,
                        data: response.payload,
                    });


                } else {
                    dispatch({
                        type: types.UPDATE_SUBSCRIPTION_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
