import React from "react";
import Loading from "../../loading/components/Loading";
import {history} from "../../../history";
import Autocomplete from 'react-autocomplete';
import {Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';
import {Helmet} from 'react-helmet';
export default function DeltaComponent(props) {

	const {isRender,isLoading,inventoryItemList,
		inventoryItemListParam,searchValue,searchData,
		userDetails,status,message,isOpenQuantityModel,updateRequestParam,isShowFinalInventoryDatatable,
		isItemCode,isDiffNumber,isDescription,isDepartment,isCategory,isLastTime,isDiffPercentage,isSetDiffPercentage,
		isSetItemCode,isSetDiffNumber,isSetDescription,isSetDepartment,isSetCategory,isSetLastTime,
		isSetOnHandQuantity,isOnHandQuantity,filterColumn } = props;

	return (
		<div>


			{isLoading? <Loading />:""}
			{isRender?
				<div>
				<Helmet><title>Delta| Modus Go!</title></Helmet>
					<div className="sticky-filter-datatable" onClick={props.toggleFinalInventoryDatatable}>
						<img className="" src="/assets/images/filter-icon.svg" alt="" />
					</div>

					<div className={`datatable-filter${isShowFinalInventoryDatatable? " open":""}`}>
						<div className="close-datalist-popup" onClick={props.toggleFinalInventoryDatatable}>
							<i className="zmdi zmdi-close-circle"></i>
						</div>
						<div className="datatable-head-list">
							<div className="datatable-list-title text-darkblue font-weight-bold">
								Delta table
							</div>
							<ul>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDiffPercentage" name="isDiffPercentage" onChange={props.setDiffPercentageCheckbox} checked={isDiffPercentage} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDiffPercentage">DIFF. PERCENTAGE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDiffNumber" name="isDiffNumber" onChange={props.setDiffNumberCheckbox} checked={isDiffNumber} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDiffNumber">DIFF. NUMBER</label>
									</div>
								</li>

								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isItemCode" name="isItemCode" onChange={props.setItemCodeCheckbox} checked={isItemCode} />
										<label className="custom-control-label font-weight-bold" htmlFor="isItemCode">ITEM CODE</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDescription" name="isDescription" onChange={props.setDescriptionCheckbox} checked={isDescription} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDescription">DESCRIPTION</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isDepartment" name="isDepartment" onChange={props.setDepartmentCheckbox} checked={isDepartment} />
										<label className="custom-control-label font-weight-bold" htmlFor="isDepartment">DEPARTMENT</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isCategory" name="isCategory" onChange={props.setCategoryCheckbox} checked={isCategory} />
										<label className="custom-control-label font-weight-bold" htmlFor="isCategory">CATEGORY</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isLastTime" name="isLastTime" onChange={props.setLastTimeCheckbox} checked={isLastTime} />
										<label className="custom-control-label font-weight-bold" htmlFor="isLastTime">LAST TIME</label>
									</div>
								</li>
								<li>
									<div className="custom-control custom-checkbox">
										<input type="checkbox" className="custom-control-input ColumnFilterCheckbox" id="isOnHandQuantity" name="isOnHandQuantity" onChange={props.setOnHandQuantityCheckbox} checked={isOnHandQuantity} />
										<label className="custom-control-label font-weight-bold" htmlFor="isOnHandQuantity">QTY ON HAND</label>
									</div>
								</li>

							</ul>
						</div>
					</div>
					<div  onClick={props.toggleHideOrderDatatable}>

					<div className="container-fluid mt-3">
						<div className="row">
							<h6 className={status.toLowerCase()==="success"? "value-green":"value-red"}>{message}</h6>
						</div>
					</div>

					<div className="container-fluid mt-3">
						<div className="row">
							<div className="col p-0">
								<div className="searchbox-wrap header-shadow">
									<Autocomplete
										inputProps={{id: 'inventoryItemList-autocomplete'}}
										wrapperStyle={{position: 'relative'}}
										value={searchValue}
										items={searchData}
										getItemValue={(item) => item.key}
										inputProps={{placeholder: 'Item lookup code or description'}}
										onSelect={record => props.onSearchChangeHandler(record)}
										onChange={(event,value) => props.onSearchChangeHandler(value)}
										renderMenu={children => (
											<div className="search-menu">
												{children}
											</div>
										)}
										renderItem={(item,isHighlighted) => (
											<div
												key={item.key}
											>{item.value}</div>
										)}
									/>
									<div className="icon-box rounded-circle">
										<i className="material-icons">search</i>
									</div>

								</div>
							</div>
						</div>
					</div>
					<div className="container-fluid bg-white header-shadow mt-4">
						<div className="row my-3">
							<div className="col-12 p-0">
								<div className="table-responsive">
									<table id="tblDelta" className="table w-100 new-order-list wo-collasped" >
										<thead>
											<tr>
												<th className="plus-sort"></th>
												
												<th style={{display: filterColumn.isSetDiffPercentage ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "diffInPercentage" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetDiffPercentage" : "sort-bottom isSetDiffPercentage" : "sort-both isSetDiffPercentage"} onClick={() => props.sortHandler("diffInPercentage")}>Diff. Percentage</th>
											
												<th style={{display: filterColumn.isSetDiffNumber ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "diffInNumber" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetDiffNumber" : "sort-bottom isSetDiffNumber" : "sort-both isSetDiffNumber"} onClick={() => props.sortHandler("diffInNumber")}>Diff. Number</th>
											
												<th style={{display: filterColumn.isSetItemCode ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "itemLookupCode" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetItemCode" : "sort-bottom isSetItemCode" : "sort-both isSetItemCode"} onClick={() => props.sortHandler("itemLookupCode")}>ITEM CODE</th>
											
												<th style={{display: filterColumn.isSetDescription ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "description" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetDescription" : "sort-bottom isSetDescription" : "sort-both isSetDescription"} onClick={() => props.sortHandler("description")}>Description</th>
												
												<th style={{display: filterColumn.isSetDepartment ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "department" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetDepartment" : "sort-bottom isSetDepartment" : "sort-both isSetDepartment"} onClick={() => props.sortHandler("department")}>Department</th>
											
												<th style={{display: filterColumn.isSetCategory ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "category" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetCategory" : "sort-bottom isSetCategory" : "sort-both isSetCategory"} onClick={() => props.sortHandler("category")}>Category</th>
											
												<th style={{display: filterColumn.isSetLastTime ? '' : 'none' }} className={inventoryItemListParam.sortColumnName === "lastTime" ? inventoryItemListParam.sortDirection === "asc" ? "sort-top isSetLastTime" : "sort-bottom isSetLastTime" : "sort-both isSetLastTime"} onClick={() => props.sortHandler("lastTime")}>Last Time</th>
											
												<th style={{display: filterColumn.isSetOnHandQuantity ? '' : 'none' }} className={inventoryItemListParam.sortColumnName==="onHandQuantity"? inventoryItemListParam.sortDirection==="asc"? "sort-top isSetOnHandQuantity":"sort-bottom isSetOnHandQuantity":"sort-both isSetOnHandQuantity"} onClick={() => props.sortHandler("onHandQuantity")}>Qty On Hand</th>
												
												<th className=""><span className="d-md-none">Edit</span></th>
											</tr>
										</thead>
										<tbody>
											{inventoryItemList.map((itemList,index) =>
												<tr key={"tblDelta"+index}>
													<td className="details-control plus-sort" tabIndex="-1"></td>
													
													<td style={{display: filterColumn.isSetDiffPercentage ? '' : 'none' }} className="default-cursor"><div className={itemList.diffInPercentage < 0 ? "default-cursor value-red" : "default-cursor value-green"}>{itemList.diffInPercentage} %</div></td>
													
													<td style={{display: filterColumn.isSetDiffNumber ? '' : 'none' }} className="default-cursor isSetDiffPercentage"><div className={itemList.diffInNumber < 0 ? "default-cursor value-red" : "default-cursor value-green"}>{itemList.diffInNumber}</div></td>
													
													<td style={{display: filterColumn.isSetItemCode ? '' : 'none' }} className="default-cursor isSetDiffNumber">{itemList.itemLookupCode}</td>
													
													<td style={{display: filterColumn.isSetDescription ? '' : 'none' }} className="default-cursor isSetDescription">{itemList.description}</td>													
												
													<td style={{display: filterColumn.isSetDepartment ? '' : 'none' }} className="default-cursor isSetDepartment">{itemList.department}</td>
												
													<td style={{display: filterColumn.isSetCategory ? '' : 'none' }} className="default-cursor isSetCategory">{itemList.category}</td>
													
													<td style={{display: filterColumn.isSetLastTime ? '' : 'none' }} className="default-cursor isSetLastTime">{itemList.lastTime}</td>
													
													<td style={{display: filterColumn.isSetOnHandQuantity ? '' : 'none' }} className="default-cursor isSetOnHandQuantity">{itemList.onhandQuantity}</td>
													
													<td><button type="button" tabIndex="0" className="round-icon-wrap border-blue po-item-del DeltaGridEditIcon" data={itemList.itemId} data-code={itemList.itemLookupCode}><i className="zmdi zmdi-edit "></i></button></td>
												</tr>
											)}
										</tbody>
									</table>

								</div>

							</div>

						</div>
					</div>
                   </div>

				</div>

				:""}

			{/* <!-- order Quantity --> */}
			<Modal backdrop="static" isOpen={isOpenQuantityModel} toggle={props.toggleQuantityModel} centered={true}>
				<ModalHeader toggle={props.toggleQuantityModel}>Update quantity for : {updateRequestParam.code}</ModalHeader>
				<ModalBody>
					<div className="row">
						<div className="col-12">
							<div className="floating-label  d-flex">
								<input type="number" className="floating-input" id="quantity" name="quantity" placeholder=" " onChange={(e) => props.quantityModelInputChangeHandler(e)} value={updateRequestParam.quantity} />
								<span className="highlight"></span>
								<label>Quantity </label>

							</div>
						</div>
					</div>
				</ModalBody>
				<ModalFooter>
					<button className="btn rounded-pill btn-primary" type="button" onClick={props.updateQuantityHandler} >Ok</button>
					<button type="button" className="btn rounded-pill btn-primary" onClick={props.toggleQuantityModel}>Cancel</button>

				</ModalFooter>
			</Modal>
			{/* <!-- order Quantity --> */}
		</div>
	)
}