import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import $ from 'jquery';
import AuthenticateService from '../../../../Services/AuthenticateServices';

export function setSuccessDialogue(check, status, message) {
    return (dispatch) => {
        dispatch({
            type: types.RESET_SUCCESS_DIALOGUE,
            isSuccessDialogue: check,
            status: status,
            message: message,
        });
    }
}

export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}


export function UpdateConfigureStore(data,isLoading) {
    return (dispatch) => {
        
                    dispatch({
                        type: types.INSERT_STORECONFIG_REQUEST,
                        isLoading: isLoading,
                    });
                    let url = 'ConfigureStore/UpdateConfigureStore';
                  
                    return post(url, data)
                        .then((response) => {                            
                            if (response.payload && response.payload.status === "Success") {
                                //window.location.reload();
                                dispatch({
                                    type: types.INSERT_STORECONFIG_SUCCESS,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                }).then(function(){ 
                                    dispatch(getConfigureStoreList(true));
                                    })
                                
                            } else {
                                dispatch({
                                    type: types.INSERT_STORECONFIG_FAILED,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }
                        })    
  }

   
}



export function getConfigureStoreList(isLoading) {
    return (dispatch) => {
        dispatch({
            type: types.GET_CONFIGURESTORE_REQUEST,
            isLoading: isLoading
        });
        let url = 'ConfigureStore/get/ConfigureStoreDetails';
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_CONFIGURESTORE_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_CONFIGURESTORE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}


