import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import { history } from '../../../../history'
import AuthenticateService from '../../../../Services/AuthenticateServices';
const obj = new AuthenticateService();
//#region get order item list
export function getInventoryItemList(data, isLoading,filterColumn) {
    return (dispatch) => {
        dispatch({
            type: types.GET_DELTA_REQUEST,
            isLoading: isLoading
        });
        let url = 'Inventory/DeltaInventoryItemList';
        return post(url, data)
            .then((response) => {
                const { payload } = response;        
              
                if (response.status === 200) {
                    /* Logic */
                    // if (payload.values.length <= 0)
                    //     history.push("/NotFound");
                    $('#tblDelta').DataTable().destroy();
                    dispatch({
                        type: types.GET_DELTA_SUCCESS,
                        data: response.payload,
                    });
                    $('#tblDelta').DataTable({
                        "paging": false,
                        "ordering": false,
                        "info": false,
                        "searching": false,
                        "responsive": true,
                        "retrieve": true,
                        "oLanguage": {
                            "sEmptyTable": "No data found"
                        },
                        "initComplete": function( settings, json ) {
                           
                            if(!filterColumn.isSetItemCode)
                            {
                                $(".isSetItemCode").hide()
                            }
                            if(!filterColumn.isSetDiffNumber)
                            {
                                $(".isSetDiffNumber").hide()
                            }
                            if(!filterColumn.isSetItemCode)
                            {
                                $(".isSetItemCode").hide()
                            }
                            if(!filterColumn.isSetDescription)
                            {
                                $(".isSetDescription").hide()
                            }
                            if(!filterColumn.isSetDepartment)
                            {
                                $(".isSetDepartment").hide()
                            }
                            if(!filterColumn.isSetLastTime)
                            {
                                $(".isSetLastTime").hide()
                            }
                            if(!filterColumn.isSetOnHandQuantity)
                            {
                                $(".isSetOnHandQuantity").hide()
                            }
                            if(!filterColumn.isSetCategory)
                            {
                                $(".isSetCategory").hide()
                            }
                            
                          },
                        fixedHeader: true
                    })

                } else {
                    dispatch({
                        type: types.GET_DELTA_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get order item list
//#region set order item list param
export function setInventoryItemListParam(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_DELTA_DELTALISTPARAM,
            data: data,


        });
    }
}
//#endregion set order item list param
//#region get  order  detail
export function getInventoryDetail(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_INVENTORYDETAIL_REQUEST,

        });
        let url = 'Inventory/GetInventoryById';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              

                if (response.status === 200) {
                    /* Logic */

                    if (payload.code === "E08" || payload.code === "E03" || payload.code === "E05")
                        history.push("/InventoryList");
                    dispatch({
                        type: types.GET_INVENTORYDETAIL_SUCCESS,
                        data: response.payload,
                    });

                } else {
                    dispatch({
                        type: types.GET_INVENTORYDETAIL_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion get  order detail
//#region UpdateInventoryItem
export function UpdateInventoryItem(data, inventoryItemListParam,filterColumn) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_DELTAQUANTITY_REQUEST,
        });
        let url = 'Inventory/updateInventoryItemQty';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                // swal({
                //     title: payload.status,
                //     text: payload.message,
                //     icon: payload.status.toLowerCase(),
                //     timer: payload.status.toLowerCase() !== "error" ? 3000 : 0,
                //     button: payload.status.toLowerCase() !== "error" ? false : "Ok",
                // })
                if (response.status === 200) {
                    /* Logic */
                    dispatch(getInventoryItemList(inventoryItemListParam, true,filterColumn));
                    dispatch({
                        type: types.UPDATE_DELTAQUANTITY_SUCCESS,
                        data: response.payload,
                        status: response.payload.status,
                        message: response.payload.message
                    });

                } else {
                    dispatch({
                        type: types.UPDATE_DELTAQUANTITY_FAILED,
                        data: response.payload,
                    });
                }

            })
    }
}
//#endregion UpdateInventoryItem
