import * as types from "../../../../../constants/AppConstants";
import { post, get } from "../../../../../service/index";
import swal from 'sweetalert';

export function getDatabase() {
    
    return (dispatch) => {
        dispatch({
            type: types.GET_DATABASE_REQUEST,
        });
        let url = 'Setting/get/DBSetting';
        return get(url)
            .then((response) => {
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_DATABASE_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_DATABASE_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
export function setFormfields(data) {

    return (dispatch) => {
        dispatch({
            type: types.SET_DATABASE_VALUES,
            data: data
        });
    }
}
export function saveDatabase(data) {

    return (dispatch) => {
        dispatch({
            type: types.SAVE_DATABASE_REQUEST,
        });
        let url = 'Setting/updateDBSetting';
        return post(url, data)
            .then((response) => {
                swal({
                    title: response.payload.status,
                    text: response.payload.message,
                    icon: response.payload.status.toLowerCase(),
                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                })
                if (response.status === 'Success') {
                    /* Logic */
                    dispatch({
                        type: types.SAVE_DATABASE_SUCCESS,
                        response: response.payload,
                        data: data
                    });
                } else {
                    dispatch({
                        type: types.SAVE_DATABASE_FAILED,
                        response: response.payload,
                    });
                }
            })


    }

}


