import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import $ from 'jquery';
import AuthenticateService from '../../../../Services/AuthenticateServices';


const obj = new AuthenticateService();


//#region to get apiName
export function getICashOutSetting() {
  
     return (dispatch) => {
         dispatch({
             type: types.GET_ICASHOUT_REQUEST,
         });
         let url = 'ICashOutAPI/GetICashOutSetting';
         return get(url)
             .then((response) => {
                 const { payload } = response;
               
                 if (response.status === 200) {
                     /* Logic */
 
                     dispatch({
                         type: types.GET_ICASHOUT_SUCCESS,
                         data: response.payload,
                     });
                 } else {
                     dispatch({
                         type: types.GET_ICASHOUT_FAILED,
                         data: response.payload,
                     });
                 }
             })
     }
 }
 //#endregion to get apiName

 export function setCodeAlias(getCustomList) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_CODEORALIAS_LIST,
            data: getCustomList
        });
    }
}
 

export function CreateICashOutSetting(data,isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.INSERT_ICASHOUT_REQUEST,
            isLoading:isLoading
        });
        let url = 'ICashOutAPI/CreateICashOutSetting';
        return post(url,data)
            .then((response) => {
                const { payload } = response;
              debugger
                if (response.status === 200) {
                    /* Logic */
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.INSERT_ICASHOUT_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.INSERT_ICASHOUT_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

