import React from 'react';
import NumberFormat from 'react-number-format';
export default function SpecialComponent(props) {
    const {itemSpecial}=props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <div className="col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemSpecial.tagAlongItemCode} disabled />
                            <span className="highlight"></span>
                            <label>Tag along item</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="floating-label">
                            <NumberFormat fixedDecimalScale={true} value={itemSpecial.tagAlongQuantity} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" disabled />
                            <span className="highlight"></span>
                            <label>Quantities</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemSpecial.parentItemCode} disabled />
                            <span className="highlight"></span>
                            <label>Parent item</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="floating-label">
                            <NumberFormat fixedDecimalScale={true} value={itemSpecial.parentQuantity} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" disabled />
                            <span className="highlight"></span>
                            <label>Child quantity</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemSpecial.messageIDName} disabled />
                            <span className="highlight"></span>
                            <label>Item message</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <input className="floating-input" type="text" placeholder=" " value={itemSpecial.binLocation} disabled />
                            <span className="highlight"></span>
                            <label>Bin location</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="floating-label">
                            <NumberFormat fixedDecimalScale={true} value={itemSpecial.weight} type="text" pattern="[0-9]*" inputMode="numeric" className="floating-input" decimalScale="2" thousandSeparator={true} thousandsGroupStyle="lakh" disabled />
                            <span className="highlight"></span>
                            <label>Weight</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="floating-label">
                            <textarea className="floating-input" placeholder=" " value={itemSpecial.notes} disabled></textarea>
                            <span className="highlight"></span>
                            <label>Notes</label>
                        </div>
                    </div>
                    {/* <div className="col-12">
                                                <div className="floating-label">
                                                    <input className="floating-input" type="text" placeholder=" " value={itemSpecial.pictureName} disabled/>
                                                    <span className="highlight"></span>
                                                    <label>Picture</label>
                                                   
                                                </div>
                                            </div> */}
                </div>

            </div>
        </div>
    )
}