import React from 'react';
import OrderComponent from '../component/OrderComponent';
import {
  getOrderItemList,setOrderItemListParam,
  updateOrderItem,getExistingOrder,
  setCombineOrderRequestParam,combineOrder,
  deleteOrderItem,getOrderDetail,
  setSearchValue,getSearchList,setSearchData,
  setSearchTerm,insertOrderItem,getCustomers,
  setGenerateWORequestParam,generateWO,generatePO,generateTO,
  clearMessage,POGetsupplierList,checkOrderExist,setRequestParameter
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {history} from '../../../history'
import {encryptURL,decryptURL} from '../../../Custom'
window.$=$;

function handleOrderITEMGridDeleteIcon(classdata) {
  $("#tblOrderItem").on('click','.OrderITEMGridDeleteIcon',function() {
    var dataString=$(this).attr('data');
    classdata.deleteOrderItemHandler(dataString);
  });
}

function ScanItemContiner() {
  $(".itemscanshow").click(function() {
    $("#OrderSearchboxHide").hide();
    $("#ItemScanWrap").show();
  });
  $(".closeitemscan-wrap").click(function() {
    $("#ItemScanWrap").hide();
    $("#OrderSearchboxHide").show();
  });
}


function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
function debounce(func,wait,immediate) {
  var timeout;

  return function executedFunction() {
    var context=this;
    var args=arguments;

    var later=function() {
      timeout=null;
      if(!immediate) func.apply(context,args);
    };

    var callNow=immediate&&!timeout;

    clearTimeout(timeout);

    timeout=setTimeout(later,wait);

    if(callNow) func.apply(context,args);
  };
};
class OrderContainer extends React.Component {

  state={
    isScanItem: false,
    isQuantityChanged: false,
    isOpenCombineOrderModel: false,
    isOpenSearchBox: true,
    isOpenWorkOrderModel: false,
    isAskedQuantity: false,
    isOpenQuantityModel: false,
    insertItemParam: {
      orderId: 0,
      userId: 0,
      price: 0,
      itemLookupCode: "",
      quantity: 1,
      isNewlyCreated: false,
      oldQuantity: 1,
      selectedItem: ""
    },
    isOpenSupplierModal: false,
    generatePORequestParam: {
      orderId: 0,
      supplierId: 0
    },
    isRender: false,
    isShowOrderDatatable: false,
    isSetItemCode:true,
    isItemCode:true,
    isSetItemName:true,
    isItemName:true,
    isSetQuantity:true,
    isQuantity:true,
    isOnHandQuantity:true,
    isSetOnHandQuantity:true,
    isTO:false
  }

  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    let orderItemListParam={...this.props.orderItemListParam};
    orderItemListParam["orderId"]=parseInt(decryptURL(this.props.match.params.orderId));
    this.props.getOrderDetail(decryptURL(this.props.match.params.orderId));
    this.props.setOrderItemListParam(orderItemListParam);
    this.getOrderItemList();
    window.addEventListener('scroll',this.handleScroll);
    window.addEventListener('mouseup',this.handleMouseUp);
    handleOrderITEMGridDeleteIcon(this);
    ScanItemContiner();
    ColumnFilterCheckbox();
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager&&userDetails.role!==process.env.REACT_APP_UserRole_Cashier) {
        //   swal({
        //     title: "Warning",
        //     text: "Please get permission to view Work orders.",
        //     icon: "warning",
        //     button: "Ok"
        //   }).then(() => this.props.history.goBack());
        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  getOrderItemList=() => {
    let orderItemListParam={...this.props.orderItemListParam};
    orderItemListParam["orderId"]=parseInt(decryptURL(this.props.match.params.orderId));
    this.props.getOrderItemList(orderItemListParam,false,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    window.addEventListener('mouseup',this.handleMouseUp);
    $('#tblOrderItem').DataTable().destroy();
    $('#tblWOCustomers').DataTable().destroy();
    $('#tblOrderSupplierList').DataTable().destroy();
    $('#tblCombineOrderItem').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region handle mouse up event to close autocomplete
  handleMouseUp=(e) => {
    this.closeAutoComplete(e);
  }
  //#endregion handle mouse up event to close autocomplete
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        if(this.props.totalRecord!==this.props.orderItemList.length) {
          let orderItemListParam={...this.props.orderItemListParam};
          let pagesize=this.props.orderItemListParam.pageSize+50;
          orderItemListParam["orderId"]=parseInt(decryptURL(this.props.match.params.orderId));
          orderItemListParam["pageSize"]=pagesize;
          this.props.setOrderItemListParam(orderItemListParam);
          this.props.getOrderItemList(orderItemListParam,false,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
        }
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading
  //#region inline quantity change handler
  inlineEditChangeHandler=(e,id) => {
    try {
      var value=e.target.value;
      if(value.length>e.target.maxLength) {
        value=value.substring(0,e.target.maxLength);
      }
      if(parseInt(value)<0)
        value=0;
      var isQuantityChanged=false;
      var orderItemList=this.props.orderItemList;
      var elementsIndex=this.props.orderItemList.findIndex(element => element.itemId===id)
      if(orderItemList[elementsIndex]["quantity"].toString()!==value)
        isQuantityChanged=true;
      orderItemList[elementsIndex]={...orderItemList[elementsIndex],quantity: value!==""? parseInt(value):""}
      this.setState({isQuantityChanged});
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion inline quantity change handler
  //#region inline quantity update handler
  inlineEditBlurHandler=(e,itemId) => {
    try {
      var value=e.target.value!==""? parseInt(e.target.value):0;
      if(e.target.value===""||value<=0) {
        swal({
          title: "Error",
          text: types.REQUIRED_QUANTITY_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else if(this.state.isQuantityChanged&&e.target.value!=="") {

        let updateItemParam={
          orderId: parseInt(decryptURL(this.props.match.params.orderId)),
          userId: parseInt(userDetails.userId),
          itemId: parseInt(itemId),
          quantity: parseInt(e.target.value)
        }

        this.props.updateOrderItem(updateItemParam,this.props.orderItemListParam);
        this.setState({isQuantityChanged: false});
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion inline quantity update handler
  //#region  on row click redirect
  redirectItemEditComponent=(itemId) => {
    try {

      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager) {
        swal({
          title: "warning",
          text: types.ADDEDIT_PERMISSION_MESSAGE,
          icon: "warning",
          button: "Ok"
        })

      }
      else {
        window.open("/ItemEdit/"+encryptURL(itemId)+"/"+this.props.match.params.orderId+"/"+encryptURL("order"),'_blank');
      }
    } catch(error) {
      console.log(error);
    }
  }



  //#endregion  on row click redirect
  //#region toggle combine order model
  toggleCombineOrderModel=() => this.setState({isOpenCombineOrderModel: !this.state.isOpenCombineOrderModel});
  toggleOrderDatatable=() => {this.setState({isShowOrderDatatable: !this.state.isShowOrderDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowOrderDatatable: false})}
  //#endregion toggle combine order model
  //#region open combine order
  openCombineOrder=() => {
    const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
      if(this.props.isOrderExist) {
        let orderId=parseInt(decryptURL(this.props.match.params.orderId));
        this.props.getExistingOrder(orderId);
        this.toggleCombineOrderModel();
      }
      else {
        swal({
          title: "This order is no more exist!",
          text: 'Might be it is converted into purchase order or work order.',
          icon: 'error',
          button: "Ok",
        }).then(() => {
          history.push("/Dashboard");
        });
      }
    })

  }
  //#endregion open combine order

  //#region order Check Box Change Handler
  orderCheckBoxChangeHandler=(e) => {
    try {

      var orderIdsData=this.props.combineOrderRequestParam.orderIds!==""? this.props.combineOrderRequestParam.orderIds.split(","):[];
      var combineOrderRequestParam={...this.props.combineOrderRequestParam};
      if(e.target.checked)
        orderIdsData.push(parseInt(e.target.name));
      else
        orderIdsData=orderIdsData.filter((item) => parseInt(item)!==parseInt(e.target.name));
      combineOrderRequestParam["orderId"]=parseInt(decryptURL(this.props.match.params.orderId));
      combineOrderRequestParam["userId"]=parseInt(userDetails.userId);
      combineOrderRequestParam["orderIds"]=orderIdsData.toString();
      this.props.setCombineOrderRequestParam(combineOrderRequestParam);

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion order Check Box Change Handler
  //#region  combine order handler
  combineOrderHandler=() => {
    try {
      if(this.props.combineOrderRequestParam.orderIds==="") {
        swal({
          title: "Error",
          text: types.REQUIRED_ORDER_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else {
        this.props.combineOrder(this.props.combineOrderRequestParam,this.props.orderItemListParam,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
        this.toggleCombineOrderModel();
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion  combine order handler
  //#region delete order item
  deleteOrderItemHandler=(id) => {
    try {
      const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
        if(this.props.isOrderExist) {
          swal({
            title: types.DELETE_TITLE,
            text: types.DELETE_MESSAGE,
            icon: "warning",
            buttons: {
              confirm: true,
              cancel: true
            },
            dangerMode: true,
          })
            .then((willDelete) => {
              if(willDelete) {
                let deleteRequestParameter={
                  itemId: parseInt(id),
                  orderId: parseInt(decryptURL(this.props.match.params.orderId))
                };

                this.props.deleteOrderItem(deleteRequestParameter,this.props.orderItemListParam,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
                var orderItemListData=this.props.orderItemList.filter(element => element.itemId===parseInt(id));
                var searchData=this.props.searchData;
                var elementsIndex=this.props.searchData.findIndex(element => element.value===orderItemListData[0].itemLookupCode+" - "+orderItemListData[0].name);
                searchData[elementsIndex]={...searchData[elementsIndex],itemChecked: false};
              }
            });
        }
        else {
          swal({
            title: "This order is no more exist!",
            text: 'Might be it is converted into purchase order or work order.',
            icon: 'error',
            button: "Ok",
          }).then(() => {
            history.push("/Dashboard");
          });
        }
      })




    } catch(error) {
      console.log(error);
    }
  }
  //#endregion delete order item
  //#region search handler
  searchHandler=(record,cb) => {
    try {
      this.setState({isOpenSearchBox: true});
      let getdata;
      record=record.trim();
      if(record!=="") {
        let searchRequestParameter={
          searchText: record,
          orderId: parseInt(decryptURL(this.props.match.params.orderId))
        };
        this.props.getSearchList(searchRequestParameter);
      }
      else {
        getdata=[];
        this.props.setSearchData(getdata);
      }
    } catch(error) {
      console.log(error);
    }
  }
  //#endregion search handler

  //#region search change handler
  onSearchChangeHandler=(value) => {
    try {
      this.props.setSearchValue(value);
      this.setSearchTerm(value,(items) => {
        this.props.setSearchData(items);
      })
    } catch(error) {
      console.log(error);
    }
  }
  setSearchTerm=debounce((searchTerm,cb) => {
    try {
      this.props.setSearchTerm(searchTerm);
      this.searchHandler(searchTerm,cb);
    } catch(error) {
      console.log(error);
    }
  },1000)
  //#endregion search change handler 
  //#region search checkbox handler
  onSearchCheckBoxHandler=(e,itemId,value) => {
    try {
      const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
        if(this.props.isOrderExist) {
          var searchData=this.props.searchData;
          var elementsIndex=this.props.searchData.findIndex(element => element.value===value);

          let itemLookupCode=value.split(" - ")[0];
          var quantity=1;
          if(this.state.isAskedQuantity) {
            var orderItemList=this.props.orderItemList;
            var itemElementsIndex=this.props.orderItemList.findIndex(element => element.itemLookupCode===itemLookupCode);
            if(itemElementsIndex>=0)
              quantity=parseInt(orderItemList[itemElementsIndex]["quantity"]);
            else
              quantity=0;
          }
          else {
            searchData[elementsIndex]={...searchData[elementsIndex],itemChecked: true};
          }
          let insertItemParam={
            orderId: parseInt(decryptURL(this.props.match.params.orderId)),
            userId: parseInt(userDetails.userId),
            price: 0,
            itemLookupCode: itemLookupCode,
            quantity: 1,
            isNewlyCreated: false,
            oldQuantity: quantity,
            selectedItem: value
          };
          if(this.state.isAskedQuantity) {
            this.setState({insertItemParam,isOpenSearchBox: false},() => this.toggleQuantityModel());
          }
          else {
            this.props.insertOrderItem(insertItemParam,this.props.orderItemListParam,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
            this.setState({isOpenSearchBox: false});
          }
        }
        else {
          swal({
            title: "This order is no more exist!",
            text: 'Might be it is converted into purchase order or work order.',
            icon: 'error',
            button: "Ok",
          }).then(() => {
            history.push("/Dashboard");
          });
        }
      })


    } catch(error) {
      console.log(error);
    }
  }
  //#endregion search checkbox handler
  //#region open search box
  openSearchBox=() => {
    this.setState({isOpenSearchBox: true});
  }
  //#endregion open search box
  //#region close search box
  closeSearchBox=() => {
    this.setState({isOpenSearchBox: false})
  }
  //#region close search box
  //#region toggle work order model
  toggleWorkOrderModel=() => this.setState({isOpenWorkOrderModel: !this.state.isOpenWorkOrderModel});
  //#endregion toggle work order model
  //#region open work order
  openWorkOrder=() => {
    const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
      if(this.props.isOrderExist) {
        this.props.getCustomers(this.props.customerRequestParam,true);
        this.toggleWorkOrderModel();
      }
      else {
        swal({
          title: "This order is no more exist!",
          text: 'Might be it is converted into purchase order or work order.',
          icon: 'error',
          button: "Ok",
        }).then(() => {
          history.push("/Dashboard");
        });
      }
    })


  }
  //#endregion open work order
  //#region  customer check box handler
  customerCheckBoxHandler=(e,id) => {
    try {
      var isChecked=e.target.checked;
      var cbs=document.getElementsByClassName("customerCHK");
      for(var i=0;i<cbs.length;i++) {
        cbs[i].checked=false;
      }
      if(isChecked) {
        document.getElementById(e.target.name).checked=true;
        let generateWORequestParam={
          orderId: parseInt(decryptURL(this.props.match.params.orderId)),
          customerId: parseInt(id)
        };
        this.props.setGenerateWORequestParam(generateWORequestParam);
      }
      else {
        let generateWORequestParam={
          orderId: 0,
          customerId: 0
        };
        this.props.setGenerateWORequestParam(generateWORequestParam);
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion  customer check box handler
  //#region generate wo handler
  generateWOHandler=() => {
    try {
      if(this.props.generateWORequestParam.customerId<=0) {
        swal({
          title: "Error",
          text: types.REQUIRED_CUSTOMER_MESSAGE,
          icon: "error",
          button: "Ok",
        })
      }
      else {
        this.props.generateWO(this.props.generateWORequestParam,this.props.orderDetail.name);

      }
    } catch(error) {
      console.log(error);
    }

  }
  //#endregion generate wo handler
  //#region generate po handler
  generatePOHandler=() => {
    try {
      const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
        if(this.props.isOrderExist) {
          swal({
            title: types.DELETE_TITLE,
            text: "Do you want to assign supplier?",
            icon: "warning",
            buttons: {
              catch: {
                text: "Yes",
                value: "catch",
              },
              defeat: {
                text: "No",
                value: "defeat",
              },
            },
            dangerMode: true,

          })
            .then((value) => {
              switch(value) {
                case "catch":
                  this.setState({isTO:false});
                  this.openSupplierModel();
                  break;

                case "defeat":
                  let generatePORequestParam={
                    orderId: parseInt(decryptURL(this.props.match.params.orderId)),
                    supplierId: 0
                  };
                  this.props.generatePO(generatePORequestParam,this.props.orderDetail.name);

                  break;
              }
            });

        }
        else {
          swal({
            title: "This order is no more exist!",
            text: 'Might be it is converted into purchase order or work order.',
            icon: 'error',
            button: "Ok",
          }).then(() => {
            history.push("/Dashboard");
          });
        }
      })

    } catch(error) {
      console.log(error);
    }

  }
  //#endregion generate po handler

  //#region generate to handler
  generateTOHandler=() => {
    try {
      const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
        if(this.props.isOrderExist) {
          swal({
            title: types.DELETE_TITLE,
            text: "Do you want to assign supplier?",
            icon: "warning",
            buttons: {
              catch: {
                text: "Yes",
                value: "catch",
              },
              defeat: {
                text: "No",
                value: "defeat",
              },
            },
            dangerMode: true,

          })
            .then((value) => {
              switch(value) {
                case "catch":
                  this.setState({isTO:true});
                  this.openSupplierModel();
                  break;

                case "defeat":
                  let generatePORequestParam={
                    orderId: parseInt(decryptURL(this.props.match.params.orderId)),
                    supplierId: 0
                  };
                  this.props.generateTO(generatePORequestParam,this.props.orderDetail.name);

                  break;
              }
            });

        }
        else {
          swal({
            title: "This order is no more exist!",
            text: 'Might be it is converted into transfer out or work order.',
            icon: 'error',
            button: "Ok",
          }).then(() => {
            history.push("/Dashboard");
          });
        }
      })

    } catch(error) {
      console.log(error);
    }

  }
  //#endregion generate to handler

  //#region  setIsAskedQuantity
  setIsAskedQuantity=() => {
    const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
      if(this.props.isOrderExist) {
        this.setState({isAskedQuantity: !this.state.isAskedQuantity});
      }
      else {
        swal({
          title: "This order is no more exist!",
          text: 'Might be it is converted into purchase order or work order.',
          icon: 'error',
          button: "Ok",
        }).then(() => {
          history.push("/Dashboard");
        });
      }
    })



  }
  //#endregion setIsAskedQuantity
  //#region toggleQuantityModel
  toggleQuantityModel=() => {this.setState({isOpenQuantityModel: !this.state.isOpenQuantityModel});}
  //#endregion toggleQuantityModel
  //#region quantityModelChangeHandler
  quantityModelChangeHandler=(e) => {
    var value=e.target.value;
    this.setState({isInlineChangedCallOnce: value});
    if(value.length>e.target.maxLength) {
      value=value.substring(0,e.target.maxLength);
    }
    if(value>0||value==="") {
      let insertItemParam={...this.state.insertItemParam}
      insertItemParam["quantity"]=value!==""? parseInt(value):"";
      this.setState({insertItemParam});
    }
  }
  //#endregion quantityModelChangeHandler
  //#region onSaveQuantityModel
  onSaveQuantityModel=() => {
    if(this.state.insertItemParam.quantity===""||this.state.insertItemParam.quantity<=0) {
      swal({
        title: "Error",
        text: types.REQUIRED_QUANTITY_MESSAGE,
        icon: "error",
        button: "Ok",
      })
    }
    else {
      this.props.insertOrderItem(this.state.insertItemParam,this.props.orderItemListParam,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);
      var searchData=this.props.searchData;
      var elementsIndex=this.props.searchData.findIndex(element => element.value===this.state.insertItemParam.selectedItem);
      searchData[elementsIndex]={...searchData[elementsIndex],itemChecked: true};
      this.toggleQuantityModel();
    }
  }
  //#endregion onSaveQuantityModel
  openSupplierModel=() => {
    let data={
      pageNo: 0,
      pageSize: 0,
      searchText: ""
    }
    this.props.POGetsupplierList(data);
    this.toggleSupplierModel();
  }
  toggleSupplierModel=() => {

    this.setState({isOpenSupplierModal: !this.state.isOpenSupplierModal});
  }
  supplierCheckBoxHandler=(e,id) => {
    var isChecked=e.target.checked;
    var cbs=document.getElementsByClassName("orderSupplierCHK");
    for(var i=0;i<cbs.length;i++) {
      cbs[i].checked=false;
    }
    if(isChecked) {
      document.getElementById(e.target.name).checked=true;
      let generatePORequestParam={
        orderId: parseInt(decryptURL(this.props.match.params.orderId)),
        supplierId: parseInt(id)
      };
      this.setState({
        generatePORequestParam
      })
    }
    else {
      let generatePORequestParam={
        orderId: 0,
        supplierId: 0
      };
      this.setState({
        generatePORequestParam
      })
    }
  }
  supplierModalSave=() => {
    try {
      if(this.state.generatePORequestParam.supplierId<=0) {
        swal({
          title: "Error",
          text: "Please select the supplier.",
          icon: "error",
          button: "Ok",
        })
      }
      else {
        if(this.state.isTO)
        {
          this.props.generateTO(this.state.generatePORequestParam,this.props.orderDetail.name);
        }
        else
        {
          this.props.generatePO(this.state.generatePORequestParam,this.props.orderDetail.name);

        }
        this.setState({
          isOpenSupplierModal: false
        })
      }
    } catch(error) {
      console.log(error);
    }


  }
  showScanItem=() => {
    const result=this.props.checkOrderExist(parseInt(decryptURL(this.props.match.params.orderId))).then(() => {
      if(this.props.isOrderExist) {
        this.setState({
          isScanItem: true,
        })
      }
      else {
        swal({
          title: "This order is no more exist!",
          text: 'Might be it is converted into purchase order or work order.',
          icon: 'error',
          button: "Ok",
        }).then(() => {
          history.push("/Dashboard");
        });
      }
    })


  }

  closeAutoComplete=(e) => {
    try {
      var ele=document.getElementById("orderItemSearchMenu");
      var targetId="";
      var targetClass="";
      if(e.target.id!==null)
        targetId=e.target.id;
      if(e.target.className!==null)
        targetClass=e.target.className.split(" ")[0];
      if(ele.id!==targetId // if the target of the click isn't the container...
        &&ele.innerHTML.length>0&&targetClass!=="orderItemSearchControl") // ... nor a descendant of the container
      {
        this.setState({isOpenSearchBox: false});
      }
    } catch(error) {
      console.log(error);
    }
  }
  closeScanItem=() => {
    this.setState({
      isScanItem: false,
    })
  }

  //#region sort event  handler
  sortHandler=(sortColumnName) => {
    try {
      var sortdirection="";

      switch(sortColumnName) {
        case "name":
          sortdirection=this.props.requestParameter.sortColumnName!=="name"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "itemLookupCode":
          sortdirection=this.props.requestParameter.sortColumnName!=="itemLookupCode"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;
        case "quantity":
          sortdirection=this.props.requestParameter.sortColumnName!=="quantity"? "asc":this.props.requestParameter.sortDirection==="asc"? "desc":"asc";
          break;

        default:
          break;
      }

      var data={
        orderId: parseInt(decryptURL(this.props.match.params.orderId)),
        pageNo: this.props.requestParameter.pageNo,
        pageSize: this.props.requestParameter.pageSize,

        sortColumnName: sortColumnName,
        sortDirection: sortdirection,
        searchText: this.props.requestParameter.searchText
      };
      var requestParameter={...this.props.requestParameter};
      requestParameter["sortColumnName"]=sortColumnName;
      requestParameter["sortDirection"]=sortdirection;
      this.props.setRequestParameter(requestParameter);




      this.props.getOrderItemList(data,false,this.state.isSetItemName,this.state.isSetItemCode,this.state.isSetQuantity,this.state.isSetOnHandQuantity);

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion sort event handler

  //#region filter checkbox onchange function
  setItemNameCheckbox = () => {
    this.setState({ isItemName: !this.state.isItemName }, () => {
    this.setState({ isSetItemName: this.state.isItemName });
    });
  }

  setItemCodeCheckbox = () => {
    this.setState({ isItemCode: !this.state.isItemCode }, () => {
    this.setState({ isSetItemCode: this.state.isItemCode });
    });
  }

  setQuantityCheckbox = () => {
    this.setState({ isQuantity: !this.state.isQuantity }, () => {
    this.setState({ isSetQuantity: this.state.isQuantity });
    });
  }
  setOnHandQuantityCheckbox = () => {
    this.setState({ isOnHandQuantity: !this.state.isOnHandQuantity }, () => {
    this.setState({ isSetOnHandQuantity: this.state.isOnHandQuantity });
    });
  }
  //#endregion filter checkbox onchange function
  

  render() {
    //  const {orderItemList}=this.props  
    if(this.state.isOpenQuantityModel&&!!document.getElementById("newQuantity"))
      document.getElementById("newQuantity").focus();
    return (
      <OrderComponent
        {...this.props}
        userDetails={userDetails}
        isOpenCombineOrderModel={this.state.isOpenCombineOrderModel}
        showScanItem={this.showScanItem}
        isScanItem={this.state.isScanItem}
        inlineEditBlurHandler={this.inlineEditBlurHandler}
        inlineEditChangeHandler={this.inlineEditChangeHandler}
        redirectItemEditComponent={this.redirectItemEditComponent}
        toggleCombineOrderModel={this.toggleCombineOrderModel}
        openCombineOrder={this.openCombineOrder}
        orderCheckBoxChangeHandler={this.orderCheckBoxChangeHandler}
        combineOrderHandler={this.combineOrderHandler}
        deleteOrderItemHandler={this.deleteOrderItemHandler}
        onSearchChangeHandler={this.onSearchChangeHandler}
        closeScanItem={this.closeScanItem}
        onSearchCheckBoxHandler={this.onSearchCheckBoxHandler}
        isOpenSearchBox={this.state.isOpenSearchBox}
        openSearchBox={this.openSearchBox}
        closeSearchBox={this.closeSearchBox}
        isOpenWorkOrderModel={this.state.isOpenWorkOrderModel}
        toggleWorkOrderModel={this.toggleWorkOrderModel}
        openWorkOrder={this.openWorkOrder}
        customerCheckBoxHandler={this.customerCheckBoxHandler}
        generateWOHandler={this.generateWOHandler}
        getOrderItemList={this.getOrderItemList}
        generatePOHandler={this.generatePOHandler}
        closeAutoComplete={this.closeAutoComplete}
        toggleOrderDatatable={this.toggleOrderDatatable}
        isShowOrderDatatable={this.state.isShowOrderDatatable}
        isAskedQuantity={this.state.isAskedQuantity}
        isOpenQuantityModel={this.state.isOpenQuantityModel}
        setIsAskedQuantity={this.setIsAskedQuantity}
        toggleQuantityModel={this.toggleQuantityModel}
        insertItemParam={this.state.insertItemParam}
        quantityModelChangeHandler={this.quantityModelChangeHandler}
        onSaveQuantityModel={this.onSaveQuantityModel}
        isOpenSupplierModal={this.state.isOpenSupplierModal}
        toggleSupplierModel={this.toggleSupplierModel}
        supplierCheckBoxHandler={this.supplierCheckBoxHandler}
        supplierModalSave={this.supplierModalSave}
        isRender={this.state.isRender}
        sortHandler={this.sortHandler}
        setItemNameCheckbox={this.setItemNameCheckbox}
        setQuantityCheckbox={this.setQuantityCheckbox}
        setItemCodeCheckbox={this.setItemCodeCheckbox}
        isQuantity={this.state.isQuantity}
        isItemCode={this.state.isItemCode}
        isItemName={this.state.isItemName}
        isSetQuantity={this.state.isSetQuantity}
        isSetItemCode={this.state.isSetItemCode}
        isSetItemName={this.state.isSetItemName}
        isOnHandQuantity={this.state.isOnHandQuantity}
        isSetOnHandQuantity={this.state.isSetOnHandQuantity}
        setOnHandQuantityCheckbox={this.setOnHandQuantityCheckbox}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
        generateTOHandler={this.generateTOHandler}

      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getOrderItemList: (data,isLoading,ItemName,ItemCode,Quantity,OnHandQuantity) => dispatch(getOrderItemList(data,isLoading,ItemName,ItemCode,Quantity,OnHandQuantity)),
    getOrderDetail: (orderId) => dispatch(getOrderDetail(orderId)),
    setOrderItemListParam: (data) => dispatch(setOrderItemListParam(data)),
    getExistingOrder: (orderId) => dispatch(getExistingOrder(orderId)),
    setCombineOrderRequestParam: (data) => dispatch(setCombineOrderRequestParam(data)),
    updateOrderItem: (updateItemParam,orderItemListParam) => dispatch(updateOrderItem(updateItemParam,orderItemListParam)),
    combineOrder: (combineOrderRequestParam,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity) => dispatch(combineOrder(combineOrderRequestParam,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity)),
    deleteOrderItem: (deleteeRequestParameter,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity) => dispatch(deleteOrderItem(deleteeRequestParameter,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity)),
    setSearchValue: (data) => dispatch(setSearchValue(data)),
    getSearchList: (data) => dispatch(getSearchList(data)),
    setSearchData: (data) => dispatch(setSearchData(data)),
    setSearchTerm: (data) => dispatch(setSearchTerm(data)),
    generateWO: (data,listName) => dispatch(generateWO(data,listName)),
    generatePO: (data,listName) => dispatch(generatePO(data,listName)),
    setGenerateWORequestParam: (data) => dispatch(setGenerateWORequestParam(data)),
    insertOrderItem: (insertItemParam,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity) => dispatch(insertOrderItem(insertItemParam,orderItemListParam,ItemName,ItemCode,Quantity,OnHandQuantity)),
    getCustomers: (data,isLoading) => dispatch(getCustomers(data,isLoading)),
    clearMessage: () => dispatch(clearMessage()),
    POGetsupplierList: (data) => dispatch(POGetsupplierList(data)),
    checkOrderExist: (data) => dispatch(checkOrderExist(data)),
    setRequestParameter: (data) => dispatch(setRequestParameter(data)),
    generateTO: (data,listName) => dispatch(generateTO(data,listName)),

  };
};

const mapStateToProps=(state) => {
  const {isOrderExist,isLoading,supplierList,orderItemList,orderItemListParam,orders,combineOrderRequestParam,
    orderDetail,searchValue,searchData,SearchTerm,customerRequestParam,customers,generateWORequestParam,isNewlyCreated,totalRecord,status,message,requestParameter}=state.OrderReducer;
  return {
    orderItemList: orderItemList.values.length>0? orderItemList.values:[],
    orders: orders.values.length>0? orders.values:[],
    orderItemListParam: orderItemListParam,
    orderDetail: orderDetail.values,
    isLoading: isLoading,
    combineOrderRequestParam: combineOrderRequestParam,
    searchValue: searchValue,
    searchData: searchData.values.length>0? searchData.values:[],
    SearchTerm: SearchTerm,
    customerRequestParam: customerRequestParam,
    customers: customers.values.length>0? customers.values:[],
    generateWORequestParam: generateWORequestParam,
    isNewlyCreated: isNewlyCreated,
    totalRecord: totalRecord,status,message,
    supplierList: supplierList.values.length>0? supplierList.values:[],
    isOrderExist,
    requestParameter: requestParameter,

  };
};

export default connect(mapStateToProps,mapDispatchToProps)(OrderContainer);