import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    inventoryItemList: [],
    inventoryDetail: {},
    totalRecord: 0,
    inventoryItemListParam: {
        sortColumnName: "lastTime",
        sortDirection: "desc",
        pageNo: 0,
        pageSize: 50,
        searchText: "",
        departmentIds: [],
        categoryIds: [],
        supplierIds: []

    },
    searchData: [],
    searchRequestParameter: {
        searchText: "",
    },
    searchValue: "",
    searchTerm: "",
    isLoading: false,
    status: "",
    message: "",
    isModified: false
   
};

export default function DeltaReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_DELTA_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_DELTA_SUCCESS:
            return {
                ...state,
                inventoryItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                isLoading: false,
            }
        case types.GET_DELTA_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_INVENTORYDETAIL_SUCCESS:
            return {
                ...state,
                inventoryDetail: action.data,
                isModified: action.data.values !== undefined || action.data.values !== null ? action.data.values.isModified : false,
                isLoading: false,
            }
        case types.GET_INVENTORYDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_DELTA_DELTALISTPARAM:
            return {
                ...state,
                inventoryItemListParam: action.data,
                isLoading: false,
            }
        case types.UPDATE_DELTAQUANTITY_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_DELTAQUANTITY_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_DELTAQUANTITY_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.SET_DELTAS_SEARCHREQUESTPARAMETER:
            return {
                ...state,
                searchRequestParameter: action.data,
                isLoading: false,
            }
        case types.SET_DELTAS_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_DELTAS_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_DELTAS_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_DELTASEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_DELTASEARCH_SUCCESS:

            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_DELTASEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }

        default:
            return state;
    }
}