import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import swal from 'sweetalert';

export function getItemCustomPropertyList(data,isLoading) {
  
    return (dispatch) => {
        dispatch({
            type: types.GET_ITEMCUSTOM_REQUEST,
            isLoading: isLoading
        });
        let url = 'Item/GetItemExtById';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_ITEMCUSTOM_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_ITEMCUSTOM_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}

export function GetItemCustomList(data,isLoading) {
    return (dispatch) => {
        
                dispatch({
                    type: types.GET_ITEMCUSTOMLIST_REQUEST,
                    isLoading: isLoading,
                });
                let url = 'Item/GetItemCustomList';
                
                return post(url, data)
                    .then((response) => {                            
                        if (response.status === 200) {
                            /* Logic */
                            dispatch({
                                type: types.GET_ITEMCUSTOMLIST_SUCCESS,
                                data: response.payload,
                            });
                        } else {
                            dispatch({
                                type: types.GET_ITEMCUSTOMLIST_FAILED,
                                data: response.payload,
                            });
                        }
                    })
            }
        }



export function InsertItemExtendedProperties(data,isLoading) {

    return (dispatch) => {
        dispatch({
            type: types.INSERT_ITEMEXT_REQUEST,
            isLoading:isLoading
        });
        let url = 'Item/InsertItemExtendedProperties';
        return post(url,data)
            .then((response) => {
                const { payload } = response;
              debugger
                if (response.status === 200) {
                    /* Logic */
                    swal({
                        title: response.payload.status,
                        text: response.payload.message,
                        icon: response.payload.status.toLowerCase(),
                        timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                        button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                    })
                    dispatch({
                        type: types.INSERT_ITEMEXT_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.INSERT_ITEMEXT_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
    
export function setItemExtCustomList(getCustomList) {
    return (dispatch) => {
        dispatch({
            type: types.UPDATE_ITEMEXT_LIST,
            data: getCustomList
        });
    }
}