import * as types from "../../../../constants/AppConstants";
import { post, get } from "../../../../service/index";
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import swal from 'sweetalert';
import $ from 'jquery';
import AuthenticateService from '../../../../Services/AuthenticateServices';



//#region to get departments
export function getDepartments() {
    return (dispatch) => {
        dispatch({
            type: types.GET_FINALDEPARTMENTS_REQUEST,
        });
        let url = 'department/get/departmentList';
        return get(url)
            .then((response) => {
                const { payload } = response;
              
                if (response.status === 200) {
                    /* Logic */

                    dispatch({
                        type: types.GET_FINALDEPARTMENTS_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_FINALDEPARTMENTS_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get departments

//#region to get categories
export function getCategories(data) {
    return (dispatch) => {
        dispatch({
            type: types.GET_FINALCATEGORIES_REQUEST,
        });
        let url = 'category/GetCategoryListByDeptIds';
        return post(url, data)
            .then((response) => {
                const { payload } = response;
            
                if (response.status === 200) {
                    /* Logic */
                    dispatch({
                        type: types.GET_FINALCATEGORIES_SUCCESS,
                        data: response.payload,
                    });
                } else {
                    dispatch({
                        type: types.GET_FINALCATEGORIES_FAILED,
                        data: response.payload,
                    });
                }
            })
    }
}
//#endregion to get categories

export function setIsCategoryDisabled(data) {
    return (dispatch) => {
        dispatch({
            type: types.SET_IS_CATEGORYDISABLED,
            data: data,
        });
    }
}


export function setDropdownListParam(data) {

    return (dispatch) => {
        var ItemListParam={
            departmentIds: data.departmentIds,
            categoryIds: data.categoryIds,
        }
        dispatch({
            type: types.SET_INVENTORYITEM_INVENTORYITEMLISTPARAM,
            data: data,
        });
        dispatch({
            type: types.SET_DELTA_DELTALISTPARAM,
            data: ItemListParam,
        });
        dispatch({
            type: types.SET_NONESCANNEDITEM_NONESCANNEDITEMLISTPARAM,
            data: ItemListParam,
        });
    }
}


export function CreatePriceConfig(data,isLoading) {
  
    return (dispatch) => {
        
                    dispatch({
                        type: types.INSERT_PRICECONFIG_REQUEST,
                        isLoading: isLoading,
                    });
                    let url = 'item/PriceConfiguration';
                  
                    return post(url, data)
                        .then((response) => {                            
                            if (response.payload && response.payload.status === "Success") {
                                //window.location.reload();
                                dispatch({
                                    type: types.INSERT_PRICECONFIG_SUCCESS,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                }).then(function(){ 
                                    window.location.reload();
                                    })
                                
                                //window.location.reload();
                                //dispatch(setSuccessDialogue(true, response.payload.status, response.payload.message));
                                
                            } else {
                                dispatch({
                                    type: types.INSERT_PRICECONFIG_FAILED,
                                    data: response.payload,
                                });
                                swal({
                                    title: response.payload.status,
                                    text: response.payload.message,
                                    icon: response.payload.status.toLowerCase(),
                                    timer: response.payload.status.toLowerCase() !== "error" ? 3000 : 0,
                                    button: response.payload.status.toLowerCase() !== "error" ? false : "Ok",
                                })
                            }
                        })      
   
  }

   
}

export function setSuccessDialogue(check, status, message) {
    return (dispatch) => {
        dispatch({
            type: types.RESET_SUCCESS_DIALOGUE,
            isSuccessDialogue: check,
            status: status,
            message: message,
        });
    }
}

export function clearMessage() {
    return (dispatch) => {
        dispatch({
            type: types.CLEAR_PO_MESSAGE
        });
    }
}


