import React from 'react';
import PastInventoryCountComponent from '../component/PastInventoryCountComponent';
import {
  getPastInventoryList,setPastInventoryListParam,
  clearMessage
} from '../state/actions/index';
import {connect} from 'react-redux';
import Cookies from 'js-cookie'
import CryptoJS from 'crypto-js'
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-responsive'
import 'datatables.net-fixedheader'
import scrolldebounce from "lodash.debounce";
import swal from 'sweetalert';
import * as types from "../../../constants/AppConstants";
import {history} from '../../../history'
import {encryptURL,decryptURL} from '../../../Custom'
window.$=$;
function handlePastInventoryGridDownloadIcon(classdata) {
  $("#tblPastInventory").on('click','.PastInventoryGridDownloadIcon',function() {
  
    var dataString=$(this).attr('data');
    classdata.fileDownloadHandler(dataString);
  });
}
function ColumnFilterCheckbox() {
  $(".ColumnFilterCheckbox").click(function() {
   if($("input:checkbox").filter(":checked").length==0)
   {
     swal("","Please select at least one column","error");
     return false;
   }
  });
}
const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
class PastInventoryCountContainer extends React.Component {

  state={
    isInventoryScanItem: false,
    isRender: false,
    isShowPastInventoryDatatable: false,
    isUserName:true,
    isDate:true,
    isFile:true,
    isSetUserName:true,
    isSetDate:true,
    isSetFile:true

  }

  componentWillMount() {
    this.checkUserRoleValidation();
  }
  componentDidMount() {
    this.getPastInventoryList();
    handlePastInventoryGridDownloadIcon(this);
    ColumnFilterCheckbox();
    window.addEventListener('scroll',this.handleScroll);
    try {
      this.clearMessageIntervalID=setInterval(async () => {
        this.props.clearMessage();
      },10*1000);
    } catch(e) {
      console.log(e);
    }
  }
  //#region check user role validation 
  checkUserRoleValidation=() => {
    try {
      const userDetails=Cookies.get('userDetails')!==undefined? JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'),process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8)):"";
      if(userDetails.role!==process.env.REACT_APP_UserRole_Manager) {

        this.props.history.push("/NotFound");
      }
      else {
        this.setState({isRender: true});
      }

    } catch(error) {
      console.log(error);
    }
  }
  //#endregion check user role validation
  getPastInventoryList=() => {
  
    let pastInventoryListParam={...this.props.pastInventoryListParam};
    pastInventoryListParam["userRole"]=userDetails.role;
    this.props.setPastInventoryListParam(pastInventoryListParam);
    this.props.getPastInventoryList(pastInventoryListParam,false);
  }
  componentWillUnmount() {
    window.removeEventListener('scroll',this.handleScroll);
    $('#tblPastInventory').DataTable().destroy();
    clearInterval(this.clearMessageIntervalID);
  }
  //#region scroll event to on demand loading
  handleScroll=scrolldebounce(() => {
    try {

      // Checks that the page has scrolled to the bottom
      var scrollHeigt=(window.innerHeight+document.documentElement.scrollTop).toFixed(0);
      var offsetHeight=(document.documentElement.offsetHeight).toFixed(0);
      if(scrollHeigt===offsetHeight||(parseInt(scrollHeigt)+1).toString()===offsetHeight||(parseInt(scrollHeigt)-1).toString()===offsetHeight) {
        if(this.props.totalRecord!==this.props.pastInventoryList.length) {
          let pastInventoryListParam={...this.props.pastInventoryListParam};
          let pagesize=this.props.pastInventoryListParam.pageSize+50;
          pastInventoryListParam["pageSize"]=pagesize;
          this.props.setPastInventoryListParam(pastInventoryListParam);
          this.props.getPastInventoryList(pastInventoryListParam,false);
        }
      }
    } catch(error) {
      console.log(error);

    }
  },0);

  //#endregion scroll event to on demand loading
  fileDownloadHandler=(file) => {
  
    window.location.href=file;
  }
  togglePastInventoryDatatable=() => {this.setState({isShowPastInventoryDatatable: !this.state.isShowPastInventoryDatatable})}
  toggleHideOrderDatatable=() => {this.setState({isShowPastInventoryDatatable: false})}

  //#region filter checkbox onchange function
  setUserNameCheckbox = () => {
    this.setState({ isUserName: !this.state.isUserName }, () => {
    this.setState({ isSetUserName: this.state.isUserName });
    });
  }

  setDateCheckbox = () => {
    this.setState({ isDate: !this.state.isDate }, () => {
    this.setState({ isSetDate: this.state.isDate });
    });
  }

  setFileCheckbox = () => {
    this.setState({ isFile: !this.state.isFile }, () => {
    this.setState({ isSetFile: this.state.isFile });
    });
  }

  //#endregion filter checkbox onchange function
 
  render() {
    //  const {pastInventoryList}=this.props   
    return (
      <PastInventoryCountComponent
        {...this.props}
        userDetails={userDetails}
        getPastInventoryList={this.getPastInventoryList}
        isRender={this.state.isRender}
        fileDownloadHandler={this.fileDownloadHandler}
        togglePastInventoryDatatable={this.togglePastInventoryDatatable}
        isShowPastInventoryDatatable={this.state.isShowPastInventoryDatatable}
        setUserNameCheckbox={this.setUserNameCheckbox}
        setDateCheckbox={this.setDateCheckbox}
        setFileCheckbox={this.setFileCheckbox}
        isUserName={this.state.isUserName}
        isDate={this.state.isDate}
        isFile={this.state.isFile}
        isSetUserName={this.state.isSetUserName}
        isSetDate={this.state.isSetDate}
        isSetFile={this.state.isSetFile}
        toggleHideOrderDatatable={this.toggleHideOrderDatatable}
      />
    )
  }
}

const mapDispatchToProps=(dispatch) => {
  return {
    getPastInventoryList: (data,isLoading) => dispatch(getPastInventoryList(data,isLoading)),
    setPastInventoryListParam: (data) => dispatch(setPastInventoryListParam(data)),
    clearMessage: () => dispatch(clearMessage()),

  };
};

const mapStateToProps=(state) => {
  const {isLoading,pastInventoryList,pastInventoryListParam,
    totalRecord,status,message}=state.PastInventoryCountReducer;
  return {
    pastInventoryList: pastInventoryList.values.length>0? pastInventoryList.values:[],
    pastInventoryListParam: pastInventoryListParam,
    isLoading: isLoading,
    totalRecord: totalRecord,status,message,


  };
};

export default connect(mapStateToProps,mapDispatchToProps)(PastInventoryCountContainer);