import React from 'react';
import Loading from "../../loading/components/Loading";
import { Helmet } from 'react-helmet'
import Autocomplete from 'react-autocomplete';
export default function DashboardComponent(props) {
	const {isRender, isLoading, orderList, searchValue, searchData, userDetails } = props;


	return (
		<div>
			<Helmet>
				<title>Order-list | Modus Go!</title>
			</Helmet>
			{isLoading ? <Loading /> : ""}
			{isRender ?
			<div>			
			<div className="add-item-btn-mobile d-md-none" onClick={props.createOrderHandler}>
					<i className="material-icons">add</i>
				</div>
				<div className="main-content-area mobile-bottom-space">
					<div className="container-fluid d-none d-md-block">
						<div className="row">
							<div className="col position-relative px-0">
								<button className="position-absolute right-top mr-3 btn btn-primary rounded-pill" type="button" onClick={props.createOrderHandler}>Create order</button>
								<h2 className="text-left text-xl-center heading-text">Order list</h2>
							</div>
						</div>
					</div>

					<div className="container-fluid d-block d-md-none">
						<div className="row">
							
							<div className="col p-0 mobile-header-info">
							<img className="mobile-logo-icon" src="/assets/images/logo-icon.png" alt="" />
							<p className="text-darkblue font-weight-bold mobile-title-headline">Order list</p>
						</div>
						</div>
					</div>

					<div className="container-fluid mt-3">
						<div className="row">
							<div className="col p-0">
								<div className="searchbox-wrap header-shadow">
									<Autocomplete
										inputProps={{ id: 'orderlist-autocomplete' }}
										wrapperStyle={{ position: 'relative' }}
										value={searchValue}
										items={searchData}
										getItemValue={(item) => item.key}
										inputProps={{ placeholder: 'Search from order list' }}
										onSelect={record => props.onSearchChangeHandler(record)}
										onChange={(event, value) => props.onSearchChangeHandler(value)}
										renderMenu={children => (
											<div className="search-menu">
												{children}
											</div>
										)}
										renderItem={(item, isHighlighted) => (
											<div
												key={item.key}
											>{item.value}</div>
										)}
									/>
									<div className="icon-box rounded-circle">
										<i className="material-icons">search</i>
									</div>
								</div>
							</div>
						</div>
					</div>

					<div className="container-fluid bg-white header-shadow mt-4">
						<div className="row my-3">
							<div className="col-12">
								<div className="order-list-wrap">
									<div  className="checkbox-dropdown"  tabIndex="0">
										List filter
							  
									</div>
									<div id="open-check-list" className="">
									<ul className="checkbox-dropdown-list mt-3">
											<li>
												<label className="control control--checkbox">Order list
								      <input type="checkbox" defaultChecked={true} onChange={(e) => props.onFilterChangedHandler(e)} id="O" name="Order List" />
													<div className="control__indicator ol"></div>
												</label>
											</li>
											<li>
												<label className="control control--checkbox">Work order
								      <input type="checkbox" defaultChecked={true} onChange={(e) => props.onFilterChangedHandler(e)} id="WO" name="Work Order" />
													<div className="control__indicator wo"></div>
												</label>
											</li>
											{userDetails.role !== process.env.REACT_APP_UserRole_Cashier ? <li>
												<label className="control control--checkbox">Purchase order
								      <input type="checkbox" defaultChecked={true} onChange={(e) => props.onFilterChangedHandler(e)} id="PO" name="Purchase Order" />
													<div className="control__indicator po"></div>
												</label>
											</li> : ""}
											{userDetails.role !== process.env.REACT_APP_UserRole_Cashier ? <li>
												<label className="control control--checkbox">Transfer out
								      <input type="checkbox" defaultChecked={true} onChange={(e) => props.onFilterChangedHandler(e)} id="TO" name="Transfer Out" />
													<div className="control__indicator to"></div>
												</label>
											</li> : ""}
										</ul>
									</div>
									<div className="order-list-record">
										<ul>
											{orderList.length > 0 ? orderList.map((list) =>
												<li key={list.id}>
													<label  className={`badge ${list.lable.toLowerCase() === "order list" ? " ol-badge" : list.lable.toLowerCase() === "purchase order" ? " po-badge": list.lable.toLowerCase() === "transfer out" ? " to-badge" : " wo-badge"}`} >{list.lable}</label>
													<div onClick={() => props.onRowClickHandler(list.id, list.lable.toLowerCase())} className="order-list-title">{list.listName}</div>
													<div onClick={() => props.onRowClickHandler(list.id, list.lable.toLowerCase())} className="order-list-info"><span><i className="zmdi zmdi-account-circle"></i></span> {list.description}</div>
													{list.lable.toLowerCase() === "order list" ? <div className="delete-btn-wrap text-danger" onClick={() => props.deleteOrderHandler(list.id)}><i className="zmdi zmdi-close-circle-o mr-1"></i> Remove</div> : ""}
												</li>
											) : <li><div className="order-list-empty text-center">No data found</div></li>}

										</ul>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>				
			</div>
			:""}
		</div>
	)
}