import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    isLoading: false,
    status: "",
    message: "",
    centralUser:[]
 
};

export default function CentralUserReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
      
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,  
                updatemessage: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.GET_CENTRALUSER_REQUEST:
            return {
                ...state,
                isLoading: true,
             
            }
        case types.GET_CENTRALUSER_SUCCESS:
         
            return {
                ...state,
                isLoading: false,
                centralUser:action.data.values
            }
        case types.GET_CENTRALUSER_FAILED:
            return {
                ...state,
                isLoading: false,
               
            }
            case types.UPDATE_CENTRALUSER_REQUEST:
                return {
                    ...state,
                    isLoading: true,
                 
                }
            case types.UPDATE_CENTRALUSER_SUCCESS:
              
                return {
                    ...state,
                    isLoading: false,
                }
            case types.UPDATE_CENTRALUSER_FAILED:
                return {
                    ...state,
                    isLoading: false,
                   
                }
        default:
            return state;
    }
}