import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    isLoading: false,
    supplierList: [],
    status:"Error"
};

export default function PurchaseReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.UPDATE_MPQ_REQUEST:
            return {
                ...state,
                isLoading: true,
                status:"Error",
            }
        case types.UPDATE_MPQ_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status:action.status
            }
        case types.UPDATE_MPQ_FAILED:
            return {
                ...state,
                isLoading: false,
                status:"Error"
            }
        case types.SET_SUPPLIERLIST:
       
            return {
                ...state,
                supplierList: action.data,
            }
        default:
            return state;
    }
}