import * as types from "../../../../constants/AppConstants";
const INITIAL_STATE = {
    isLoading: false,
    itemExistPayload: true,   
    isSuccessDialogue: false,   
    status: "",
    message: "",
    products: [],
    
};

export default function InventoryScanItemReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.ITEM_EXIST_REQUEST:
            return {
                ...state,
            }
        case types.ITEM_EXIST_SUCCESS:
            return {
                ...state,
                itemExistPayload: action.data.result,
                products: action.data,
            }
        case types.ITEM_EXIST_FAILED:
            return {
                ...state,
                isItemChecked: true
            }
        case types.RESET_SUCCESS_DIALOGUE:

            return {
                ...state,
                isSuccessDialogue: action.isSuccessDialogue,                
                itemExistPayload: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
                
            }
        case types.CLEAR_PO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
     
        default:
            return state;
    }
}