import React from 'react';
import Loading from "../../loading/components/Loading";
export default function SubscriptionFailedComponent(props) {
    const { isLoading} = props;
    return (
        
        <div>
            {isLoading ? <Loading /> : ""}
            <div className="error-template">
                <h1>
                    Subscription Not Updated</h1>
                <div className="error-details">
                    Subscription key is not updated during last
                    6 hour.  If you key is valid then try to submit it with retry or contact to administrator.
                    </div>

                <div className="error-actions">
                    <button className="btn btn-primary rounded-pill" type="button" onClick={props.retryHandler}>Retry </button>
                </div>
            </div>
        </div>
    )
}