import React from 'react';

export default function SerialComponent(props) {
    const { itemSerial, checkedSerialNumber } = props;
    return (
        <div>
            <div className="card-body">
                <div className="row">
                    <div className="col-sm-6 col-12">
                        <label className="" >Serial number count:</label>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="Serial-radio1" name="serialCustomRadio" className="custom-control-input" checked={checkedSerialNumber === 1 ? true : false} disabled />
                            <label className="custom-control-label" htmlFor="Serial-radio1">1 Serial Number</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="Serial-radio2" name="serialCustomRadio" className="custom-control-input" checked={checkedSerialNumber === 2 ? true : false} disabled />
                            <label className="custom-control-label" htmlFor="Serial-radio2">2 Serial Number</label>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12">
                        <div className="custom-control custom-radio ml-2 my-2">
                            <input type="radio" id="Serial-radio3" name="serialCustomRadio" className="custom-control-input" checked={checkedSerialNumber === 3 ? true : false} disabled />
                            <label className="custom-control-label" htmlFor="Serial-radio3">3 Serial Number</label>
                        </div>
                    </div>
                    <div className="col-12 p-0">
                        <div className="table-responsive">
                            <table id="tblSerial" className="table w-100" >
                                <thead>
                                    {checkedSerialNumber > 2 ? <tr>
                                        <th className="plus-sort"></th>
                                        <th>Serial No 1</th>
                                        <th>Serial No 2</th>
                                        <th>Serial No 3</th>
                                        <th>Customer</th>
                                        <th>Account no</th>
                                        <th>Status</th>
                                    </tr>
                                        : checkedSerialNumber > 1 ? <tr>
                                            <th className="plus-sort"></th>
                                            <th>Serial No 1</th>
                                            <th>Serial No 2</th>
                                            <th>Customer</th>
                                            <th>Account no</th>
                                            <th>Status</th>
                                        </tr> : <tr>
                                                <th className="plus-sort"></th>
                                                <th>Serial No 1</th>
                                                <th>Customer</th>
                                                <th>Account no</th>
                                                <th>Status</th>
                                            </tr>}
                                </thead>
                                <tbody>
                                    {checkedSerialNumber > 2 ?
                                        itemSerial.map((item, index) =>
                                            <tr key={"tblItemSerial-" + index}>
                                                <td className="details-control plus-sort"></td>
                                                <td>{item.serialNumber}</td>
                                                <td>{item.serialNumber2}</td>
                                                <td>{item.serialNumber3}</td>
                                                <td>{item.customer}</td>
                                                <td>{item.accountNumber}</td>
                                                <td>{item.status}</td>
                                            </tr>
                                        ) : checkedSerialNumber > 1 ? itemSerial.map((item, index) =>
                                            <tr key={"tblItemSerial-" + index}>
                                                <td className="details-control plus-sort"></td>
                                                <td>{item.serialNumber}</td>
                                                <td>{item.serialNumber2}</td>
                                                <td>{item.customer}</td>
                                                <td>{item.accountNumber}</td>
                                                <td>{item.status}</td>
                                            </tr>
                                        ) : itemSerial.map((item, index) =>
                                            <tr key={"tblItemSerial-" + index}>
                                                <td className="details-control plus-sort"></td>
                                                <td>{item.serialNumber}</td>
                                                <td>{item.customer}</td>
                                                <td>{item.accountNumber}</td>
                                                <td>{item.status}</td>
                                            </tr>)}
                                </tbody>
                            </table>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}