import * as types from "../../../../constants/AppConstants";

const INITIAL_STATE = {
    orderItemList: [],
    orderDetail: {},
    customers: [],
    totalRecord: 0,
    customerRequestParam: {
        searchText: "",
        pageNo: 0,
        pageSize: 0
    },
    orderItemListParam: {
        woId: 0,
        searchText: "",
        pageNo: 0,
        pageSize: 50,
        sortColumnName: "",
        sortDirection: ""
    },
    isLoading: false,
    searchValue: "",
    searchTerm: "",
    searchData: [],
    customerDetails: {},
    woValues: {
        woid: 0,
        subTotal: 0,
        totalTax: 0,
        totalAmount: 0,
        itemEntries: 0,
        discount: 0,
        dueDate: "",
        prvDeposite: 0,
        addlDeposite: 0,
        balance: 0
    },
    status: "",
    message: "",
    changeCustomerRequestParam: {
        woid: 0,
        customerID: 0
    },
    changeTaxRequestParam: {
        woid: 0,
        woEntryID: 0,
        itemId: 0,
        taxId: 0
    },
    taxList: [],
    priceMustBeEntered: false

};

export default function WorkOrderEditReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case types.GET_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ORDERITEM_SUCCESS:
          
            return {
                ...state,
                orderItemList: action.data,
                totalRecord: action.data.pageInfo !== null ? action.data.pageInfo.totalRecord : 0,
                woValues: action.data,
                isLoading: false,
            }
        case types.GET_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ORDERDETAIL_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_ORDERDETAIL_SUCCESS:
            return {
                ...state,
                orderDetail: action.data,
                isLoading: false,
            }
        case types.GET_ORDERDETAIL_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDERITEM_ORDERITEMLISTPARAM:
            return {
                ...state,
                orderItemListParam: action.data,
                isLoading: false,
            }
        case types.INSERT_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.INSERT_ORDERITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
                priceMustBeEntered: false,
            }
        case types.INSERT_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.DELETE_ORDERITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DELETE_ORDERITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.DELETE_ORDERITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHVALUE:
            return {
                ...state,
                searchValue: action.data,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHDATA:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.SET_ORDERITEM_SEARCHTERM:
            return {
                ...state,
                searchTerm: action.data,
                isLoading: false,
            }
        case types.GET_ORDERITEMSEARCH_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ORDERITEMSEARCH_SUCCESS:
            return {
                ...state,
                searchData: action.data,
                isLoading: false,
            }
        case types.GET_ORDERITEMSEARCH_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_REQUEST:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case types.GET_CUSTOMERS_SUCCESS:
            return {
                ...state,
                customers: action.data,
                isLoading: false,
            }
        case types.GET_CUSTOMERS_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_CUST_BY_ID_SUCCESS:
            return {
                ...state,
                customerDetails: action.data,
            }
        case types.UPDATE_WOITEMINLINE_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.UPDATE_WOITEMINLINE_SUCCESS:
          
            return {
                ...state,
                woValues: action.data,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.UPDATE_WOITEMINLINE_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.CLEAR_WO_MESSAGE:
            return {
                ...state,
                status: "",
                message: "",
            }
        case types.APPLY_TAX_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.APPLY_TAX_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.APPLY_TAX_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_CHANGECUSTOMER_REQUESTPARAM:
            return {
                ...state,
                changeCustomerRequestParam: action.data,
                isLoading: false,
            }
        case types.CHANGE_CUSTOMER_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CHANGE_CUSTOMER_SUCCESS:
         
            return {
                ...state,
                woValues: action.data,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.CHANGE_CUSTOMER_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SAVE_COMMENT_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.SAVE_COMMENT_SUCCESS:
            return {
                ...state,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.SAVE_COMMENT_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.DUPLICATE_ITEM_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.DUPLICATE_ITEM_SUCCESS:
            return {
                ...state,
                isLoading: false,
            }
        case types.DUPLICATE_ITEM_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.APPLY_DISCOUNT_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.APPLY_DISCOUNT_SUCCESS:
            return {
                ...state,
                woValues: action.data,
                isLoading: false,
            }
        case types.APPLY_DISCOUNT_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_TAXLIST_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_TAXLIST_SUCCESS:
            return {
                ...state,
                taxList: action.data,
                isLoading: false,
            }
        case types.GET_TAXLIST_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_CHANGETAX_REQUESTPARAM:
            return {
                ...state,
                changeTaxRequestParam: action.data,
                isLoading: false,
            }
        case types.CHANGE_TAX_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.CHANGE_TAX_SUCCESS:
            return {
                ...state,
                woValues: action.data,
                isLoading: false,
                status: action.status !== undefined ? action.status : "",
                message: action.message !== undefined ? action.message : "",
            }
        case types.CHANGE_TAX_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.GET_ITEMOPTION_REQUEST:
            return {
                ...state,
                isLoading: true,
            }
        case types.GET_ITEMOPTION_SUCCESS:
            return {
                ...state,
                priceMustBeEntered: action.data.values !== undefined && action.data.values !== null ? action.data.values.priceMustBeEntered !== null ? action.data.values.priceMustBeEntered : false : false,
                isLoading: false,
            }
        case types.GET_ITEMOPTION_FAILED:
            return {
                ...state,
                isLoading: false,
            }
        case types.SET_PRICE_MUSTBE:
            return {
                ...state,
                priceMustBeEntered: action.priceMustBeEntered,
            }
        default:
            return state;
    }
}