import React from 'react';
import Loading from "../../loading/components/Loading";
import {Helmet} from 'react-helmet'

export default function APIColumnMappingComponent(props) {
	const {apiNameList,isLoading,isShowMapping,inputChangeHandlerAPIName,formFields,inputChangeHandlerTender,inputChangeHandler,apiColumnMappingList
	,updateColumnMappingParam,updatemessage,updatestatus,dbColumnNameList,isDisplayAddMapping,tenderList,tenderDetails}=props;
	return (
		<div>
			<Helmet>
				<title>API Column Mapping | Modus Go!</title>
			</Helmet>
			{isLoading? <Loading />:""}
			<div>
					
					<div className="main-content-area mobile-bottom-space">
					    <div className="container-fluid d-none d-md-block">
						<div className="row">
							<h6 className="value-red mb-3">**This Fields for schedular change</h6>
						</div>
							<div className="row mb-4">
								<div className="col-12 activity-title-block">
									<h2 className="text-left text-xl-center heading-text mt-2">API Fields with DB column Mapping</h2>

								</div>
							</div>
							
						</div>
{isShowMapping? 
	                 <div className="bg-white header-shadow mt-3 p-3 mb-3">
	                        <div className="container pt-3 ml-1">
								<div className="row">
									<h6 className={updatestatus.toLowerCase()==="success"? "value-green":"value-red"}>{updatemessage}</h6>
								</div>
							</div>
							
							<div className="row my-3">
								<div className="col-12">
									<span className="heading-text"><b>API Setting for: </b> {formFields.apiName}</span>
								</div>
							</div>
							
							<div className="row">
								<div className="col-md-6 col-12">
									<div className="floating-label">

										<input className="floating-input" disabled={updateColumnMappingParam.isFixedEntry} type="text" placeholder=" " name="apiColumnName" id="apiColumnName" onChange={(e) => inputChangeHandler(e)}  value={updateColumnMappingParam.apiColumnName}
										/>
										<span className="highlight"></span>
										<label>API Column Name</label>
									
									</div>
								</div>
								<div className="col-md-6 col-12">
								   <div className="floating-label">
										<select className="floating-select" tabIndex="2" id="dbColumnName" name="dbColumnName" defaultValue={updateColumnMappingParam.dbColumnName}  value={updateColumnMappingParam.dbColumnName} onChange={(e) => inputChangeHandler(e)} >
											<option key="I" value="0">Select DB Column Name</option> 
											{dbColumnNameList.map((apiName) => <option key={apiName.dbColumnName} value={apiName.dbColumnName} name={apiName.dbColumnName}>{apiName.dbColumnName}</option>)}
										
										</select>
										<span className="highlight"></span>
										<label>DB Column Name</label>
									</div>
								</div>
								<div className="itemscanbottom mt-2">
									<button className="btn rounded-pill btn-primary mr-1" id="btnScanPopupAccept" type="submit" onClick={() => props.createAPIColumnMapping()}>Save</button>
									<button type="button" className="btn rounded-pill btn-primary mr-1 closeitemscan-wrap" onClick={props.hideAddMapping}>Cancel</button></div>
							</div>
						</div>
						:	<div className="container-fluid mt-3">
							<div className="row align-items-center justify-content-between">
								<div className="col-sm-10 p-0 d-flex align-items-center">
									<div className="container-fluid mt-3">
										<div className="floating-label">
											<select className="floating-select" tabIndex="2" id="apiName" name="apiName" onChange={(e) => inputChangeHandlerAPIName(e)} >
												<option key="I" value="-1">Select API Name</option> 
												{apiNameList.filter(x=>x.isSendItem==true).map((apiName) => <option key={apiName.id} value={apiName.id} name={apiName.apiName}>{apiName.apiName}</option>)}
											
											</select>
											<span className="highlight"></span>
											<label>API Name</label>
										</div>
								    </div>
									<div className="container-fluid mt-3">
										
										<div className="floating-label">
											<select className="floating-select" tabIndex="2" id="description" name="description" onChange={(e) => inputChangeHandlerTender(e)} value={tenderDetails.tenderId} defaultValue={tenderDetails.tenderId} >
												<option key="I" value="0">Select Tender</option> 
												{tenderList.map((tender) => <option key={tender.id} value={tender.id} name={tender.id}>{tender.description}</option>)}
											
											</select>
											<span className="highlight"></span>
											<label>Tender</label>
										</div>
								    </div>
									<div className="container-fluid mt-3">
									    <button type="button" tabIndex="0" className="WOITEMGridEditIcon round-icon-wrap border-blue" onClick={() => props.saveTender()} ><i className="zmdi zmdi-save blue-icon"></i></button>
									</div>
							    </div>
						     	{isDisplayAddMapping?
								<div className="col-sm-2 p-0 d-flex align-items-center">
									<button className="position-absolute ml-4 btn btn-primary rounded-pill" onClick={props.showAddMapping} type="button" >Add Mapping</button>
								</div>:""
							    }
							</div>
		      	       </div>
}
						





					
						<div className="container-fluid bg-white header-shadow mt-4">
							<div className="row my-3">
								<div className="col-12 p-0">
									<div className="table-responsive">
										<table id="tblColumnMapping" className="table w-100" >
											<thead>
												<tr>
												<th>API Column Name</th>
												<th>DB Column Name</th>
												<th>Action</th>
												</tr>
											</thead>
											<tbody>

											{apiColumnMappingList.map((data) =>
													<tr>
														
														<td ><div >{data.apiColumnName}</div></td>
														<td ><div >{data.dbColumnName}</div></td>
														<td className="action-flex">
														<button type="button" disabled={data.isFixedEntry} tabIndex="0" className="ColumnMappingDelete round-icon-wrap border-red" data={data.id} ><i className="zmdi zmdi-close red-icon"></i></button>
														<button type="button"  tabIndex="0" className="ColumnMappingEdit round-icon-wrap border-blue" data={data.id}><i className="zmdi zmdi-edit blue-icon"></i></button>
													</td>
													</tr> )}
											</tbody>
										</table>
									</div>

								</div>
							</div>
						</div>
					</div>
				
			</div>
		

		</div>
	)
}